<template>
  <div v-if="!$root.nonEdit2act" class="create-modal">
    <div class="create-modal-box">
      <a-modal
        class="common-modal-style no-scroll"
        v-bind="modalProps"
        v-on="modalProps.on"
      >
        <div slot="title">
          {{ $t('actCreate.modalTitle') }}
        </div>
        <createCentent
          :categoryInfo="categoryInfo"
          ref="createCentent"
          :data="categoryArr"
          @change="handleChange"
        />
      </a-modal>
    </div>
  </div>
</template>

<script>
import createCentent from './index'
import { mapActions } from 'vuex'
export default {
  components: { createCentent },
  props: {
    categoryInfo: {
      default: () => {}
    },
    modalData: {
      default: () => {}
    }
  },
  data() {
    return {
      modalProps: {
        width: '850px',
        visible: false,
        okText: this.$t('global_button_save'),
        cancelText: this.$t('global_cancel'),
        okButtonProps: {
          props: {
            disabled: true
          }
        },
        on: {
          cancel: () => {
            let { modalData: md } = this
            this.$refs.createCentent.cancel(() => {
              md.visible = false
            })
          },
          ok: () => {
            this.$refs.createCentent.save(async (data) => {
              if (data && data.activity_id) {
                await this.updateActStepStatus2action({
                  activity_id: +data.activity_id,
                  status: 1,
                  step: 'choose_category',
                  language: 'All',
                  loading: true
                })
                const { href } = this.$router.resolve({
                  path: `/act/activity/basic/${data.activity_id}`,
                  params: data
                })
                setTimeout(() => {
                  klook.newOpenWin(href)
                  this.$router.go(0)
                }, 60)
              }
              this.modalData.visible = false
            })
          }
        }
      },
      categoryArr: []
    }
  },
  watch: {
    modalData: {
      handler() {
        this.initData()
      },
      deep: true,
      immediate: true
    }
  },
  created() {
    this.get_published_category_info()
  },
  methods: {
    ...mapActions([
      'getActPublishedCategoryInfo2action',
      'updateActStepStatus2action'
    ]),
    handleChange(params) {
      this.modalProps.okButtonProps.props.disabled = !(
        params && params.leaf_category_id
      )
    },
    initData() {
      _.merge(this.modalProps, this.modalData)
    },
    async get_published_category_info() {
      if (!this.categoryArr.length) {
        this.categoryArr = await this.getActPublishedCategoryInfo2action()
      }
    }
  }
}
</script>
