<template>
  <div>
    <a-form-model
      class="common-form-style"
      ref="form"
      :model="form"
      label-width="200px"
    >
      <template v-if="displayFactory('pkg_name') && !$root.isSPU">
        <tpl_special_multi_language_input
          ref="pkg_name"
          v-model="form.package_details"
          v-bind="getMultiAttrFactory('pkg_name')"
          lang-field="language_type"
          val-field="name"
          :init-schema="{ desc: '' }"
          :disabled="disabledPkgName"
        />
      </template>
      <template v-if="displayFactory('pkg_subname') && !$root.isSPU">
        <tpl_special_multi_language_input
          ref="pkg_subname"
          :description="$t('package_info_description_desc')"
          :maxlength="50"
          v-model="form.package_details"
          v-bind="
            getMultiAttrFactory('pkg_subname', {
              label: $t('package_info_desc'),
              placeholder: $t('package_info_description_placeholder'),
              hideLangTip: true
            })
          "
          lang-field="language_type"
          val-field="subname"
          :disabled="disabledPkgName"
        />
      </template>

      <template v-if="displayFactory('pkg_merchant')">
        <merchant-select
          ref="merchantSelect"
          v-model="form.merchant_id"
          :activity_id="activity_id"
          :package_info="form"
          :rules="getRulesFactory('pkg_merchant')"
          :description="getCurrentDescription('pkg_merchant')"
          @mounted="$emit('merchant_init')"
        ></merchant-select>
      </template>

      <template v-if="displayFactory('merchant_confirm')">
        <a-form-model-item
          :label="$t('merchant_self_confirm')"
          prop="merchant_self_confirm"
          :rules="getRulesFactory('merchant_confirm')"
          :colon="false"
        >
          <a-tooltip
            placement="right"
            overlayClassName="common-tooltip-style"
            :title="getCurrentDescription('merchant_confirm')"
          >
            <a-radio-group v-model="form.merchant_self_confirm">
              <a-radio :value="1" :disabled="disabledSelfConfirm()"
                >Yes</a-radio
              >
              <a-radio :value="0" :disabled="disabledSelfConfirm()">No</a-radio>
            </a-radio-group>
          </a-tooltip>
          <!-- <p>{{ $t('merchant_self_confirm_turn_on_btn_tips') }}</p> -->
        </a-form-model-item>
      </template>

      <template v-if="displayFactory('ticket_type')">
        <a-form-model-item
          :label="$t('ticket_type')"
          prop="ticket_type"
          :rules="getRulesFactory('ticket_type')"
          :colon="false"
        >
          <a-tooltip
            placement="right"
            overlayClassName="common-tooltip-style"
            :title="getCurrentDescription('ticket_type')"
          >
            <a-select
              v-model="form.ticket_type"
              :placeholder="$t('global_please_select')"
            >
              <a-select-option
                v-for="(key, name) in fnb_ticket_types"
                :key="key"
                :value="+key"
                >{{ $t(name) }}</a-select-option
              >
            </a-select>
          </a-tooltip>
        </a-form-model-item>
      </template>
      <template v-if="displayFactory('confirmation_time')">
        <a-form-model-item
          :label="$t('confirmation_time')"
          prop="confirmation_type"
          :rules="getRulesFactory('confirmation_time')"
          :colon="false"
        >
          <a-tooltip
            placement="right"
            overlayClassName="common-tooltip-style"
            :title="getCurrentDescription('confirmation_time')"
          >
            <a-select
              v-model="form.confirmation_type"
              :placeholder="$t('global_please_select')"
              @change="restrictChange"
            >
              <a-select-option :value="1">{{
                $t('instant_confirm')
              }}</a-select-option>
              <a-select-option :value="3">{{
                $t('24_confirm')
              }}</a-select-option>
              <a-select-option :value="4">{{
                $t('48_confirm')
              }}</a-select-option>
            </a-select>
          </a-tooltip>
        </a-form-model-item>
      </template>
      <template v-if="displayFactory('inventory_type')">
        <div class="dsp-none">
          <a-form-model-item
            :label="$t('stock_out_type')"
            prop="stock_out_type"
            :rules="getRulesFactory('inventory_type')"
            :colon="false"
          >
            <a-tooltip
              placement="right"
              overlayClassName="common-tooltip-style"
              :title="getCurrentDescription('inventory_type')"
            >
              <a-radio-group
                v-model="temp_stock_out_type"
                @change="restrictChange"
              >
                <a-radio
                  v-for="(name, value) in stock_out_options"
                  :key="name"
                  :value="value"
                  >{{ name }}</a-radio
                >
              </a-radio-group>
            </a-tooltip>
            <div>
              <span v-if="form.stock_out_type === 'API'" style="color: gray">{{
                $t('api_pkg_hint')
              }}</span>
              <span
                v-if="form.stock_out_type === 'INVENTORY'"
                style="color: gray"
                >{{ $t('inv_pkg_hint') }}</span
              >
            </div>
          </a-form-model-item>
        </div>
      </template>

      <template v-if="displayFactory('voucher_get_method')">
        <a-form-model-item
          :label="$t('ob_voucher_retr_method')"
          prop="voucher_get_method"
          :rules="getRulesFactory('voucher_get_method')"
          :colon="false"
        >
          <a-tooltip
            placement="right"
            overlayClassName="common-tooltip-style"
            :title="getCurrentDescription('voucher_get_method')"
          >
            <a-radio-group v-model="form.voucher_get_method">
              <a-radio
                v-for="(value, name) in voucher_get_methods"
                :key="name"
                :value="value"
                >{{ $t(name) }}</a-radio
              >
            </a-radio-group>
          </a-tooltip>
        </a-form-model-item>
      </template>

      <template
        v-if="displayFactory('voucher_type') && showVoucherType && e_voucher"
      >
        <a-form-model-item
          :label="$t('merchant_confirm_type')"
          prop="merchant_confirm_type"
          :rules="getRulesFactory('voucher_type')"
          :colon="false"
        >
          <a-tooltip
            placement="right"
            overlayClassName="common-tooltip-style"
            :title="getCurrentDescription('voucher_type')"
          >
            <a-select
              v-model="form.merchant_confirm_type"
              :placeholder="$t('global_please_select')"
              @change="restrictChange"
            >
              <a-select-option
                v-for="{ id, name } in merchant_confirm_types"
                :key="id"
                :value="+id"
                >{{ name }}</a-select-option
              >
            </a-select>
          </a-tooltip>
        </a-form-model-item>
      </template>

      <template v-if="displayFactory('cancellation_policy')">
        <a-form-model-item
          :label="$t('cancellation_policy')"
          prop="cancelation_type"
          :rules="getRulesFactory('cancellation_policy')"
          :colon="false"
        >
          <a-tooltip
            placement="right"
            overlayClassName="common-tooltip-style"
            :title="getCurrentDescription('cancellation_policy')"
          >
            <a-select
              v-model="form.cancelation_type"
              :placeholder="$t('global_please_select')"
            >
              <a-select-option
                v-for="(k, v) in cancel_policy"
                :key="v"
                :disabled="
                  !e_voucher && +k === cancel_policy.cancel_before_redeem
                "
                :value="+k"
                >{{ $t(v) }}</a-select-option
              >
            </a-select>
          </a-tooltip>
        </a-form-model-item>
      </template>

      <template v-if="displayFactory('voucher_usage') && e_voucher">
        <a-form-model-item
          :label="$t('voucher_usage')"
          prop="voucher_usage"
          :rules="{ required: true }"
          :colon="false"
        >
          <a-tooltip
            placement="right"
            overlayClassName="common-tooltip-style"
            :title="getCurrentDescription('voucher_usage')"
          >
            <a-select
              v-model="form.voucher_usage"
              :placeholder="$t('global_please_select')"
              @change="checkVoucherCode"
            >
              <a-select-option
                v-for="(k, v) in voucher_usages"
                :key="v"
                :value="+k"
                >{{ $t(v) }}</a-select-option
              >
            </a-select>
          </a-tooltip>
        </a-form-model-item>
        <div v-if="offline_voucher.includes(form.voucher_usage)">
          {{ $t('21820') }}
        </div>
      </template>

      <template v-if="displayFactory('voucher_code_level')">
        <a-form-model-item
          v-if="form.stock_out_type !== 'API' && e_voucher"
          :label="$t('ob_voucher_code_level')"
          prop="voucher_level"
          :rules="{ required: true }"
          :colon="false"
        >
          <a-tooltip
            placement="right"
            overlayClassName="common-tooltip-style"
            :title="getCurrentDescription('voucher_code_level')"
          >
            <a-select
              v-model="form.voucher_level"
              :placeholder="$t('global_please_select')"
              :disabled="disableVoucherLevel"
            >
              <a-select-option
                v-for="[key, name, title] in voucher_levels"
                :key="name"
                :value="key"
                :label="name"
              >
                {{ name }}
                &nbsp;
                <a-popover
                  placement="topLeft"
                  :content="title"
                  :overlayStyle="{ width: '200px', zIndex: 2000 }"
                >
                  <a-icon type="info-circle" theme="filled" />
                </a-popover>
              </a-select-option>
            </a-select>
          </a-tooltip>

          <!-- <p
            v-if="getCurrentDescription('voucher_code_level')"
            class="text--italic"
          >
            {{ getCurrentDescription('voucher_code_level') }}
          </p> -->
        </a-form-model-item>
      </template>
      <!-- Special Set-Up start-->
      <template v-if="displayFactory('min_max_bookings')">
        <a-form-model-item
          :label="$t('package_info_max_min_participants')"
          :colon="false"
        >
          <a-tooltip
            placement="right"
            overlayClassName="common-tooltip-style"
            :title="getCurrentDescription('min_max_bookings')"
          >
            <tpl_package_min_max_booking :value="form" />
          </a-tooltip>
        </a-form-model-item>
      </template>
      <template v-if="displayFactory('calendar_month')">
        <a-form-model-item
          prop="calendar_month"
          :label="$t('package_displayed_month_number')"
          :rules="getRulesFactory('calendar_month')"
          :colon="false"
        >
          <a-tooltip
            placement="right"
            overlayClassName="common-tooltip-style"
            :title="$t('package_displayed_month_tips')"
            :overlayStyle="{ maxWidth: '320px' }"
          >
            <a-select
              v-model="form.calendar_month"
              :placeholder="$t('global_please_select')"
            >
              <a-select-option
                v-for="(item, index) in extendMonthOptions"
                :key="index"
                :value="item.value"
                >{{ item.label }}
              </a-select-option>
            </a-select>
          </a-tooltip>
        </a-form-model-item>
      </template>
      <!-- Special Set-Up end-->
    </a-form-model>
  </div>
</template>

<script>
import tpl_special_multi_language_input from '../tpl_special_multi_language_input'
import tpl_package_min_max_booking from '../tpl_package_min_max_booking'
import {
  FNB_TICKET_TYPE as fnb_ticket_types,
  fnb_package_types
} from '@activity/pages/package/package_const.js'
const VOUCHER_RETRIEVAL_METHOD = {
  0: __('ob_e_voucher'),
  1: __('ob_m_voucher'),
  2: __('ob_no_voucher')
}

const voucher_levels = [
  ['UniqueVoucher', __('group_per_booking'), __('group_per_booking_tip')],
  ['UnitTypeVoucher', __('group_per_unit_type'), __('group_per_unit_type_tip')],
  ['UnitVoucher', __('group_per_unit'), __('group_per_unit_tip')]
]

const cancel_policy = {
  cancel_within_24h: 1,
  cancel_within_48h: 2,
  cancel_within_72h: 3,
  cancel_within_7d: 4,
  cancel_within_14d: 5,
  cancel_within_30d: 6,
  cancel_with_conditions: 7,
  cancel_before_start: 8,
  cancel_before_redeem: 9,
  no_cancel: 0
}

const voucher_usages = {
  print_mobile_voucher: 0,
  print_voucher: 1,
  evoucher_only: 2,
  evoucher_offline_voucher: 3,
  print_mobile_offline_voucher: 4
}

const voucher_get_methods = {
  e_voucher: 0,
  no_voucher: 1,
  mail_voucher: 2 // temp hidden @grace
}
import MerchantSelect from '../tpl_package_merchant/merchant_select'
import { getCurrentLocal } from '@activity/utils'

export default {
  name: 'common-field',
  directives: {
    linkDetail: {
      // this is deprecated after structed act

      // this could be put inside `change` event, use vue directive is to seperate concern
      update(el, binding, vnode) {
        /*
         * The directive’s value may or may not have changed,
         * but you can skip unnecessary updates by comparing the binding’s current and old values (see below on hook arguments).
         * https://vuejs.org/v2/guide/custom-directive.html#Hook-Functions
         */
        if (binding.value != binding.oldValue) {
          // https://stackoverflow.com/questions/43081391/vue-directive-to-access-the-vue-instance
          vnode.context.$root.$emit('link-tag', [binding.arg, binding.value])
        }
      }
    }
  },
  install(Vue) {
    // https://vuejs.org/v2/guide/plugins.html
    Vue.component(this.name, this)
  },
  components: {
    tpl_special_multi_language_input,
    MerchantSelect,
    tpl_package_min_max_booking
  },
  model: {
    prop: 'form',
    event: 'change'
  },
  props: {
    // ["form", "pkg", 'schemaConfig'],
    form: {
      type: Object,
      required: true
    },
    pkg: {
      type: Object,
      required: true
    },
    schemaConfig: {
      type: Object,
      required: true
    }
  },
  inject: ['refreshPkgs'],
  data() {
    return {
      fnb_ticket_types,
      fnb_package_types,
      data_inited: false,
      temp_stock_out_type: 'OTHERS',
      cancel_policy,
      voucher_levels,
      voucher_usages,
      voucher_get_methods,
      offline_voucher: [3, 4],

      VOUCHER_RETRIEVAL_METHOD,
      activity_id: +this.$route.params.id
    }
  },
  computed: {
    extendMonthOptions() {
      return Array(12)
        .fill()
        .map((item, index) => ({
          value: index + 1,
          label: index + 1
        }))
    },
    package_id() {
      return +this.pkg.package_id
    },
    cur_pkg() {
      return this.package_id
        ? this.$store.state.packages.find(
            (v) => v.package_id == this.package_id
          )
        : {}
    },
    disabledPkgName() {
      return (
        this.$root.isSPU || (this.cur_pkg && this.cur_pkg.spec_status === 1)
      )
    },
    e_voucher() {
      return this.form.voucher_get_method === this.voucher_get_methods.e_voucher
    },
    disableVoucherLevel() {
      return (
        // 保险活动也disable， 目前只在后端判断
        this.offline_voucher.includes(this.form.voucher_usage) ||
        this.form.stock_out_type === 'API'
      )
    },
    showVoucherType() {
      return this.temp_stock_out_type === 'OTHERS'
    },
    merchant_confirm_types() {
      return [
        {
          name: this.$t('klk_voucher_klk_code'),
          id: 1
        },
        ...klook.mergeIf(this.form.confirmation_type !== 1, [
          {
            name: this.$t('klk_voucher_merchant_code'),
            id: 2
          },

          {
            name: this.$t('merchant_voucher'),
            id: 3
          }
        ])
      ]
    },
    stock_out_options() {
      return {
        ...(this.form.confirmation_type === 1 && {
          API: 'API',
          INVENTORY: 'INVENTORY',
          OTHERS: 'Others'
        }),
        ...(this.form.confirmation_type !== 1 && {
          API: 'API',
          OTHERS: 'Others'
        })
      }
    }
  },
  watch: {
    'form.merchant_confirm_type': {
      immediate: true,
      handler(v) {
        if (v === 0) {
          this.form.merchant_confirm_type = undefined
        }
      }
    },
    // handle default value
    'form.confirmation_type'(v) {
      if (v == 0) {
        this.form.confirmation_type = undefined
      }

      let types = this.merchant_confirm_types
      if (types.every((item) => item.id !== this.form.merchant_confirm_type)) {
        this.form.merchant_confirm_type = types.length === 1 ? types[0].id : 0
      }
    },
    // handle default value
    'form.cancelation_type'(v) {
      if (v == -1) {
        this.form.cancelation_type = undefined
      }
    },
    // handle default value
    'form.voucher_usage'(v) {
      if (v == -1) {
        this.form.voucher_usage = undefined
      }
      this.checkVoucherCode(v)
    },
    'form.stock_out_type'(v) {
      if (v) {
        this.temp_stock_out_type = ['API', 'INVENTORY'].includes(v)
          ? v
          : 'OTHERS'
      }
    },
    'form.voucher_get_method'(v) {
      if (v !== this.voucher_get_methods.e_voucher) {
        if (
          this.form.cancelation_type === this.cancel_policy.cancel_before_redeem
        ) {
          this.form.cancelation_type = undefined
        }
        this.form.merchant_confirm_type = undefined
        this.form.voucher_usage = undefined
        this.form.voucher_level = undefined
      }
    }
  },
  async created() {
    await new Promise((resolve) => this.$on('init_data', resolve))
    console.log('com init')
    this.data_inited = true
  },
  updated() {
    const stock_out_map = {
      // merchant_confirm_type: stock_out_code
      1: 'KLOOKCODE',
      2: 'MERCHANTCODE',
      3: 'MERCHANTPDF'
    }
    this.form.voucher_type = undefined // voucher_type is deprecated
    if (this.temp_stock_out_type === 'OTHERS') {
      if (this.e_voucher) {
        if (this.form.confirmation_type === 1) {
          this.form.stock_out_type = 'KLOOKCODE'
        } else {
          this.form.stock_out_type =
            stock_out_map[this.form.merchant_confirm_type]
        }
      } else {
        this.form.stock_out_type = 'KLOOKCODE'
      }
    } else {
      this.form.stock_out_type = this.temp_stock_out_type
    }

    if (!this.showVoucherType) {
      // pass to backend
      if (
        this.form.confirmation_type === 1 &&
        this.temp_stock_out_type === 'KLOOKCODE'
      ) {
        this.form.merchant_confirm_type = 2
      } else {
        this.form.merchant_confirm_type = undefined
      }
    }
  },
  mounted() {
    this.$emit('mounted')
  },
  deactivated() {
    this.isDeactivated = true
  },
  methods: {
    // tpl_package_merchant start
    disabledSelfConfirm() {
      let confirm_order = 0
      if (this.form.merchant_id) {
        let current = _.find(this.$refs.merchantSelect.all_merchants, {
          id: this.form.merchant_id
        })
        if (current && current.confirm_order === 0) {
          // 需要根据找到的 merchant 信息，重置旧的数据
          this.form.merchant_self_confirm = 0
        }
        confirm_order = _.get(current, 'confirm_order', 0) // 1: can edit
      }
      return !confirm_order
    },
    // tpl_package_merchant end
    // tpl_package_info start
    getMultiAttrFactory(type, conf = {}) {
      let current = this.schemaConfig[type] || {}
      return {
        required: current.required,
        requiredEn: current.requiredEn,
        valField: type,
        label: getCurrentLocal(this.schemaConfig, {
          type,
          field: 'name_multilang',
          defaultVal: this.$t('package_info_name')
        }),
        placeholder: this.$t('package_info_name_placeholder'),
        description: getCurrentLocal(this.schemaConfig, {
          type,
          field: 'desc_multilang',
          defaultVal: ''
        }),
        ...conf
      }
    },
    // tpl_package_info end
    getCurrentDescription(type) {
      return this.getCurrentLocal(type, 'desc_multilang', '')
    },
    getCurrentLocal(
      type,
      field = 'desc_multilang',
      defaultVal = 'Please select'
    ) {
      return getCurrentLocal(this.schemaConfig, {
        field,
        type,
        defaultVal
      })
    },
    displayFactory(field) {
      return (
        (this.schemaConfig[field] &&
          this.schemaConfig[field].validation !== 3) ||
        false
      )
    },
    getRulesFactory(field) {
      return _.get(this.schemaConfig, `${field}.rules`, {})
    },
    checkVoucherCode(voucher_usage) {
      if (this.offline_voucher.includes(voucher_usage)) {
        this.form.voucher_level = 'UnitVoucher'
      }
    },
    async restrictChange() {
      if (!this.isDeactivated && this.data_inited && this.pkg.published) {
        console.log('unpb fist')
        try {
          await this.$confirm({
            title: __('pkg_restrict_hint')
          })
          await this.unpublishPkg()
        } finally {
          await this.refreshPkgs()
        }
      }
    },
    async unpublishPkg() {
      var data = {
        package_id: this.pkg.package_id,
        published: false
      }
      await ADMIN_API.pkgPublished({ data })
      this.$message.success('Success!')
    },

    async validate(noTip = false) {
      let flag = true
      if (this.displayFactory('pkg_name') && !this.$root.isSPU) {
        flag = (await this.$refs.pkg_name.validateForm()) && flag
      }
      let valid = await new Promise((resolve) =>
        this.$refs.form.validate((valid) => resolve(valid && flag))
      )
      if (!valid && !noTip) {
        this.$message.error('Please fill in the form')
      }
      return valid
    }
  }
}
</script>
