<template>
  <div class="tpl_package_copy-container">
    <!-- act_copy_from_another -->
    <a-button @click="copyHandle" :disabled="!copyPkgList.length">
      <slot>{{ $t('act_pkg_copy_price') }}</slot>
    </a-button>
    <a-modal
      :visible.sync="copyDialog.visible"
      :title="$t('package_copy_schedule_dialog_title')"
      @cancel="handleCancel"
    >
      <a-radio-group v-model="copyDialog.selection">
        <a-radio
          v-for="copy in copyDialog.options"
          :value="copy.key"
          :key="copy.key"
          :disabled="copy.disabled"
          :title="copy.disabled ? '不允许复制按出行日期定价的套餐价格' : ''"
          >{{ copy.value }}</a-radio
        >
      </a-radio-group>
      <span slot="footer">
        <a-button @click="handleCancel">{{ $t('global_cancel') }}</a-button>
        <a-button
          :loading="submitLoading"
          :disabled="!copyDialog.selection"
          type="primary"
          @click="submitCopyHandle"
          >{{ $t('global_submit') }}</a-button
        >
      </span>
    </a-modal>
  </div>
</template>

<script>
export default {
  props: {
    package_id: {
      required: true
    },
    url: {
      type: String,
      required: true
    },
    klk_gvars: {
      type: Object,
      required: klk_gvars
    },
    handleSave: {
      type: Function,
      default: null
    },
    copy_type: String
  },
  data() {
    return {
      submitLoading: false,
      copyDialog: {
        visible: false,
        selection: null,
        options: []
      }
    }
  },
  computed: {
    copyPkgList() {
      return (this.klk_gvars.package_all || []).filter(
        (item) => item.package_id !== +this.package_id
      )
    }
  },
  methods: {
    handleCancel() {
      this.submitLoading = false
      this.copyDialog.visible = false
    },
    getName(pkg) {
      return pkg.package_local && pkg.package_local[0].name
    },
    async copyHandle() {
      console.log('copy, cpo', this.copyPkgList)
      if (this.copy_type === 'price') {
        let ids = []
        for (let i of this.copyPkgList) {
          ids.push(i.package_id)
        }
        let pkgModel = {}
        let res = (
          await ADMIN_API.getPkgPricingModel({
            package_ids: ids.join(',')
          })
        ).result
        res.map((i) => (pkgModel[i.package_id] = i.pricing_model))
        this.$set(this, 'copyDialog', {
          visible: true,
          options: this.copyPkgList.map((pkg) => ({
            key: pkg.package_id,
            value: this.getName(pkg),
            disabled: !!pkgModel[pkg.package_id]
          })),
          selection: null
        })
      } else {
        this.$set(this, 'copyDialog', {
          visible: true,
          options: this.copyPkgList.map((pkg) => ({
            key: pkg.package_id,
            value: this.getName(pkg)
          })),
          selection: null
        })
      }
    },
    async submitCopyHandle() {
      let { copyDialog } = this
      if (copyDialog.selection) {
        this.submitLoading = true
        if (typeof this.handleSave === 'function') {
          await this.handleSave({
            source: copyDialog.selection
          })
          this.handleCancel()
          return
        }
      }
    }
  }
}
</script>

