// activity_status	商户后台的活动状态（非Klook后台的活动状态）; 活动状态：0:Unpublished, 1:Published
// approval_status	活动审核状态：0:edit(此时 Editable）；1: pending approval(此时Non-Editable)；2: Rejected（此时 Editable）；3: Approved(此时Non-Editable)
// activity_status	活动在klook后台的状态 - 状态：0:unpublished; 1:published; 2:draft; 3:content; 4:preview
// package_status	套餐状态：1 上架, 0 下架
// archive_status	1:存档, 0:非存档
import { platform } from '@/env'
const maps = {
  activity_status: [
    { value: 0, label: __('global_unpublished'), class: 'unpublished' },
    { value: 1, label: __('global_published'), class: 'published' }
  ],
  activity_status2admin: [
    { value: 0, label: __('global_unpublished'), class: 'unpublished' },
    { value: 1, label: __('global_published'), class: 'published' },
    { value: 2, label: __('global_draft'), class: 'draft' },
    { value: 3, label: __('act_list_content_to_edit'), class: 'emToEdit' },
    { value: 4, label: __('act_list_in_preview'), class: 'inPreview' }
  ],
  package_status: [
    { value: 0, label: __('global_unpublished'), class: 'unpublished' },
    { value: 1, label: __('global_published'), class: 'published' }
  ],
  package_archived: [
    {
      value: 0,
      mapValue: false,
      label: __('status.unarchived')
    },
    {
      value: 1,
      mapValue: true,
      label: __('status.archived')
    }
  ],
  approval_status: [
    { value: 0, label: __('merchant_none'), class: 'edit' }, // editable
    {
      value: 1,
      label: __('status.pendingApproval'),
      class: 'pendingApproval'
    }, // non-editable
    { value: 2, label: __('merchant_rejected'), class: 'rejected' }, // editable
    { value: 3, label: __('merchant_approved'), class: 'approved' } // non-editable
  ]
}
// 处理多平台映射数据
const platformMap = {
  platform_activity_status: {
    MERCHANT: maps.activity_status,
    ADMIN: maps.activity_status2admin
  }
}

const initOpt = (strOrOpt) => {
  let defOpt = { retKey: 'label', mapKey: 'value' }
  let opt = _.merge({}, defOpt)
  if (typeof strOrOpt === 'string' && strOrOpt) {
    opt.retKey = strOrOpt
  } else {
    _.merge(opt, strOrOpt)
  }
  return opt
}
maps.platformFn = function(key, value, strOrOpt) {
  // 默认处理差异方法
  let obj = platformMap[key]
  if (!obj) return ''
  let opt = initOpt(strOrOpt)
  opt.list = obj[platform]
  return maps.filterFn(key, value, opt)
}
//内置转换方法 start
maps.filterFn = function(key, value, strOrOpt) {
  let opt = initOpt(strOrOpt)
  let { retKey, mapKey, list } = opt
  !Array.isArray(list) && (list = maps[key])
  if (!list) return ''
  let arr = list.filter((item) => {
    return item[mapKey] === value
  })
  return arr[0] && arr[0][retKey]
}
//内置转换方法 end
export default maps
