<template>
  <div id="package-extra-editable" class="package_extra--container">
    <!-- 用户信息提示（未结构化） -->
    <a-alert
      :message="$t('other_info_non_standardized_warning')"
      banner
      type="info"
    />

    <!-- 用户信息（已结构化） -->
    <div class="package_extra--section">
      <SectionHeader :id="$t('other_info_standardized')">
        {{ $t('other_info_standardized') }}
      </SectionHeader>
      <a-spin :spinning="isNewOtherInfoLoading">
        <div v-if="standardizedTips.length" class="standardized-tips">
          <p v-if="standardizedTips.length < 2" v-html="standardizedTips[0]" />
          <template v-else>
            <p style="margin-bottom: 5px">
              {{ $t('other_info_standardized_tips_act_multi') }}
            </p>
            <p v-for="(tip, idx) in standardizedTips" :key="idx">
              -&nbsp;&nbsp;
              <span v-html="tip" />
            </p>
          </template>
        </div>
        <div class="package_extra-block">
          <a-collapse v-model="newCollapseActiveKey" :bordered="false">
            <a-collapse-panel key="contact" style="border: 0">
              <div slot="header">
                <p class="package_extra-block_title is-required">
                  {{ $t('other_info_contact') }}
                </p>
                <p class="package_extra-block_tips">
                  {{ $t('other_info_following_info_collected') }}
                </p>
              </div>
              <div class="collapse-card">
                <div class="package_extra-block_content">
                  <a-checkbox-group
                    :value="REQUIRED_INFO_CONF"
                    style="width: 100%"
                  >
                    <BasicOverflowCheckbox
                      v-for="item in REQUIRED_INFO_CONF"
                      :key="item"
                      disabled
                      :value="item"
                      width="20%"
                    >
                      {{ item }}
                    </BasicOverflowCheckbox>
                  </a-checkbox-group>
                </div>
              </div>
            </a-collapse-panel>
            <a-collapse-panel key="participants" style="border: 0">
              <div slot="header">
                <p class="package_extra-block_title">
                  {{ $t('other_info_required_participants') }}
                </p>
              </div>
              <div class="collapse-card">
                <div id="js-triangle-wrap" class="package_extra-block_content">
                  <label
                    class="info-title"
                    style="font-size: 14px; margin-right: 30px"
                  >
                    {{ $t('other_info_required') }}
                  </label>
                  <a-radio-group
                    v-model="travelerInfoType"
                    @change="changeTravelerType"
                  >
                    <a-radio
                      v-for="item in NEED_TRAVELER_INFO_CONF"
                      :key="item.value"
                      :disabled="isGlobalDisabled || disabledInfo"
                      :style="{
                        height: '26px',
                        lineHeight: '26px'
                      }"
                      :value="item.value"
                    >
                      {{ item.label }}
                    </a-radio>
                  </a-radio-group>
                  <div
                    v-show="travelerInfoType !== -1"
                    class="content-other-info triangle"
                    :style="{
                      '--offsetX': triangleOffsetX + 'px'
                    }"
                  >
                    <p style="margin-bottom: 10px">
                      {{ $t('other_info_less_likely_warn') }}
                    </p>
                    <other-info-group
                      ref="travellerGroups"
                      :original-groups="travellerGroups"
                      :group-checked-list.sync="groupCheckedList"
                      :disabled="isGlobalDisabled || disabledInfo"
                      @updateData="
                        (data) => handelUpdateData(data, 'traveller')
                      "
                    />
                  </div>
                </div>
              </div>
            </a-collapse-panel>
            <a-collapse-panel key="additional" style="border: 0">
              <div slot="header">
                <p class="package_extra-block_title">
                  {{ $t('other_info_additional') }}
                </p>
                <p class="package_extra-block_tips">
                  {{ $t('other_info_less_likely_warn_2') }}
                </p>
              </div>
              <div class="collapse-card">
                <div class="package_extra-block_content">
                  <other-info-group
                    ref="infoGroups"
                    :stripe="true"
                    :disabled="isGlobalDisabled || disabledInfo"
                    :original-groups="infoGroups"
                    :group-checked-list.sync="groupCheckedList"
                    @updateData="(data) => handelUpdateData(data, 'info')"
                  />
                </div>
              </div>
            </a-collapse-panel>
          </a-collapse>
        </div>
      </a-spin>
    </div>

    <!-- 复制（未结构化） -->
    <div style="margin-top: 12px">
      <a-button :disabled="isGlobalDisabled" @click="handleCopy">
        {{ $t('package_xinfo_copy') }}
      </a-button>
    </div>

    <!-- 其他用户信息（未结构化） -->
    <div class="package_extra--section">
      <SectionHeader :id="$t('other_info_non_standardized')">
        {{ $t('other_info_non_standardized') }}
      </SectionHeader>
      <a-spin :spinning="isOldOtherInfoLoading">
        <div class="package_extra-block">
          <a-collapse v-model="oldCollapseActiveKey" :bordered="false">
            <a-collapse-panel
              v-for="category in oldOtherInfoData"
              :key="category.category_id"
              :header="getCollapseTitle(category.category_id)"
              style="border: 0"
            >
              <div
                v-for="item in category.content"
                :key="item.extra_info_id"
                class="collapse-card"
              >
                <a-checkbox
                  :checked="
                    oldOtherInfoDataCheckList.includes(item.extra_info_id)
                  "
                  :value="item.extra_info_id"
                  :disabled="isGlobalDisabled"
                  @change="handleCheckboxChange"
                />
                <div
                  class="collapse-card-wrap"
                  @click="handleCollapseCardClick(item.extra_info_id)"
                >
                  <p class="collapse-card-content">
                    <span class="collapse-card-title">
                      {{ getCollapseCardTitle(item) }}
                    </span>
                    <span
                      class="collapse-card-type"
                      :style="{
                        color: item.ask_option === 1 ? '#00B8D9' : '#FFAB00'
                      }"
                    >
                      {{
                        item.ask_option === 1
                          ? $t('package_xinfo_ask_once')
                          : $t('package_xinfo_ask_each')
                      }}
                    </span>
                  </p>
                  <p class="collapse-card-tips">
                    {{ getCollapseCardHint(item) }}
                  </p>
                </div>
              </div>
            </a-collapse-panel>
          </a-collapse>
        </div>
      </a-spin>
    </div>

    <div
      v-if="
        newOtherInfoDataCheckList.length > 0 ||
          (oldOtherInfoData.length > 0 && oldOtherInfoDataCheckList.length > 0)
      "
      class="package_extra--section"
    >
      <SectionHeader :bar="false" style="margin: 0 0">
        {{ $t('other_info_all_selected_displayed') }}
      </SectionHeader>
      <div class="package_extra-block" style="margin: 0 0">
        <div
          v-if="newOtherInfoDataCheckList.length > 0"
          class="package_extra-draggable"
        >
          <div
            v-for="name in newOtherInfoDataCheckList"
            :key="name"
            class="list-group-item"
          >
            {{ name }}
          </div>
        </div>
        <template
          v-if="
            oldOtherInfoData.length > 0 && oldOtherInfoDataCheckList.length > 0
          "
        >
          <p class="package_extra-block_tips" style="margin: 0 0">
            {{ $t('other_info_only_non_standardized_selected') }}
          </p>
          <div class="package_extra-block_content">
            <DraggableList
              v-model="oldOtherInfoDataCheckList"
              class="package_extra-draggable"
              :disabled="isGlobalDisabled"
            >
              <template v-slot:default="slotProps">
                <div class="package_extra-draggable_content">
                  {{ getDraggableTitle(slotProps.item) }}
                  <svg-icon icon-name="menu" />
                </div>
              </template>
            </DraggableList>
          </div>
        </template>
      </div>
    </div>

    <CommonFooter>
      <a-button @click="handlePreview">
        {{ $t('global_preview') }}
      </a-button>
      <a-button
        type="primary"
        :disabled="
          isGlobalDisabled || isNewOtherInfoLoading || isOldOtherInfoLoading
        "
        @click="handleCheckForm"
      >
        {{ $t('global_save') }}
      </a-button>
    </CommonFooter>

    <!-- 提交成功弹窗 -->
    <a-modal
      v-model="submitModalVisible"
      :title="$t('js_post_success')"
      :width="600"
      class="package_extra_submit"
      @ok="() => (submitModalVisible = false)"
    >
      <template slot="footer">
        <a-button type="primary" @click="() => (submitModalVisible = false)">
          Close
        </a-button>
      </template>
      <div class="package_extra_submit--container">
        <div v-show="!packages_finished" id="hint">
          <strong style="line-height: 32px; font-weight: 600; font-size: 18px">
            {{ $t('package_xinfo_finish_hint') }}
          </strong>
          <!-- 其他主套餐未填写完毕，显示主套餐id -->
          <a-table
            v-if="hasUnfinishPackage"
            :columns="submitModalColumns"
            :data-source="unfinishPackageTableData"
            :pagination="false"
            size="middle"
            bordered
          />
        </div>
        <p
          v-show="isSPU || packages_finished"
          style="
            line-height: 32px;
            margin: 0;
            font-weight: 600;
            font-size: 18px;
          "
        >
          {{ $t('other_info_submit_finished') }}
        </p>
        <div v-show="!isSPU && packages_finished">
          <a-button type="link" @click="handleCreatePackage">
            >> {{ $t('package_another_main_package') }}
          </a-button>
        </div>
        <!-- 目前功能还没迁移过来，先干掉 -->
        <!-- <div v-show="packages_finished" id="to-overview">
          <a-button type="link" @click="handleOverviewPackage">
            >> {{ $t('package_take_overview') }}
          </a-button>
        </div> -->
        <div v-show="isSPU">
          <a-button type="link" @click="handleCopy">
            >> {{ $t('global_copy') }}
          </a-button>
        </div>
      </div>
    </a-modal>

    <!-- 复制弹窗 -->
    <a-modal
      v-model="copyModalVisible"
      :title="$t('global_copy')"
      :confirm-loading="copyConformLoading"
      :width="840"
      :ok-button-props="{ props: { disabled: selectedCopyList.length === 0 } }"
      @ok="handleCopyOk"
      @cancel="handleCopyCancel"
    >
      <a-spin :spinning="copyModalLoading">
        <div class="package_extra_copy--container">
          <a-collapse
            v-if="copyCollapseData.length > 0 && !copyModalLoading"
            v-model="copyCollapseActiveKey"
            :bordered="false"
          >
            <a-collapse-panel
              v-for="pkg in copyCollapseData"
              :key="pkg.key"
              :header="pkg.name"
              style="border: 0"
            >
              <a-config-provider>
                <template #renderEmpty>
                  <div style="text-align: center">
                    <p>{{ $t('global_bts_table_formatNoMatches') }}</p>
                  </div>
                </template>
                <div class="config-provider">
                  <a-table
                    :columns="copyModalColumns"
                    :data-source="pkg.data"
                    :row-selection="getRowSelection(pkg.key)"
                    :pagination="false"
                    size="middle"
                  />
                </div>
              </a-config-provider>
            </a-collapse-panel>
          </a-collapse>
          <p v-else-if="!copyModalLoading">
            {{ $t('other_info_copy_no_data') }}
          </p>
        </div>
      </a-spin>
    </a-modal>
  </div>
</template>

<script>
import {
  getUuid,
  getEditLang,
  bestMatchLang,
  scrollFormErrorIntoView
} from '@activity/utils'
import { commonInfoFields } from '@activity/utils/const'
import CommonFooter from '@activity/components/CommonFooter.vue'
import SectionHeader from '@activity/components/SectionHeader.vue'
import DraggableList from '@activity/components/DraggableList.vue'
import OtherInfoGroup from './OtherInfoGroup.vue'
import BasicOverflowCheckbox from '@activity/pages/components/BasicOverflowCheckbox.vue'

const typeObj = {
  1: __('package_xinfo_text_box'),
  2: __('package_xinfo_date'),
  3: __('package_xinfo_dropdown_list'),
  4: __('package_xinfo_time'),
  6: __('package_xinfo_checkbox')
}

export default {
  name: 'PackageExtra',
  components: {
    SectionHeader,
    OtherInfoGroup,
    DraggableList,
    CommonFooter,
    BasicOverflowCheckbox
  },
  props: {
    activity_id: {
      type: [Number, String],
      required: true
    },
    package_id: {
      type: [Number, String],
      required: true
    }
  },
  inject: ['rootChangeLoading'],
  data() {
    const copyColumns = [
      {
        title: this.$t('package_xinfo_type'),
        dataIndex: 'type',
        key: 'type',
        className: 'copy-column'
      },
      {
        title: this.$t('package_xinfo_name'),
        dataIndex: 'name',
        key: 'name',
        className: 'copy-column'
      },
      {
        title: this.$t('package_xinfo_repeat_rules'),
        dataIndex: 'rules',
        key: 'rules',
        className: 'copy-column'
      },
      {
        title: this.$t('package_xinfo_mandatory'),
        dataIndex: 'mandatory',
        key: 'mandatory',
        className: 'copy-column'
      }
    ]

    const submitColumns = [
      {
        title: `${this.$t('main_package')}id`,
        dataIndex: 'id',
        key: 'id'
      },
      {
        title: `${this.$t('main_package')}${this.$t('content_name')}`,
        dataIndex: 'name',
        key: 'name'
      },
      {
        title: this.$t('unfinish_field'),
        dataIndex: 'detail',
        key: 'detail'
      }
    ]

    return {
      // new
      isNewOtherInfoLoading: false,
      newCollapseActiveKey: ['contact', 'participants', 'additional'],
      travelerInfoType: -1,
      travellerGroups: [],
      infoGroups: [],
      groupCheckedList: [],
      triangleOffsetX: 160,

      disabledInfo: false,
      standardizedTips: [],

      // old
      isOldOtherInfoLoading: false,
      oldCollapseActiveKey: [],
      oldOtherInfoData: [],
      oldOtherInfoDataCheckList: [],

      // copy
      copyModalVisible: false,
      copyModalLoading: false,
      copyConformLoading: false,
      copyCollapseActiveKey: [],
      copyCollapseData: [],
      copyModalColumns: copyColumns,
      copyModalSelectedData: {},

      // save
      updateData: {
        info: [],
        traveller: []
      },

      // submit
      submitModalVisible: false,
      submitModalColumns: submitColumns,
      packages_finished: true,
      isSPU: !!this.$root.isSPU
    }
  },
  computed: {
    REQUIRED_INFO_CONF() {
      // https://docs.google.com/spreadsheets/d/1VRBRgWs-Pb9D_NJCBuMnAgvrrVQ9jc-hYVvLoBE00gA/edit#gid=1428169185
      let type1 = [
        1,
        2,
        4,
        7,
        8,
        9,
        14,
        15,
        16,
        20,
        21,
        22,
        187,
        192,
        206,
        211,
        220,
        223,
        228,
        230,
        232,
        249,
        251,
        254,
        256,
        258,
        260,
        263,
        266,
        275,
        279,
        282,
        287,
        300,
        303,
        306,
        309,
        311,
        313,
        331,
        356,
        360
      ]
      let type2 = [
        1,
        2,
        4,
        7,
        8,
        9,
        14,
        15,
        16,
        17,
        18,
        19,
        20,
        21,
        22,
        168,
        171,
        177,
        179,
        185,
        187,
        192,
        194,
        198,
        206,
        211,
        220,
        223,
        228,
        230,
        232,
        249,
        251,
        254,
        256,
        258,
        260,
        263,
        266,
        275,
        279,
        282,
        287,
        298,
        300,
        303,
        306,
        309,
        311,
        313,
        317,
        319,
        321,
        323,
        325,
        329,
        331,
        333,
        341,
        343,
        345,
        353,
        356,
        358,
        360,
        364
      ]

      let category_id = _.get(
        this.$store,
        'state.categoryInfo.sub_category_id',
        null
      )
      if ([...type1, ...type2].includes(category_id)) {
        let res = []
        if (type1.includes(category_id)) {
          res = [
            this.$t('other_info_last_name'),
            this.$t('other_info_first_name'),
            this.$t('other_info_country_region')
          ]
        }
        if (type2.includes(category_id)) {
          res = [
            ...res,
            this.$t('other_info_phone_number'),
            this.$t('other_info_email')
          ]
        }

        return res
      }

      return [
        this.$t('other_info_last_name'),
        this.$t('other_info_first_name'),
        this.$t('other_info_country_region'),
        this.$t('other_info_phone_number'),
        this.$t('other_info_email')
      ]
    },
    NEED_TRAVELER_INFO_CONF() {
      return [
        {
          label: this.$t('other_info_no_required'),
          value: -1
        },
        {
          label: this.$t('other_info_1_participant'),
          value: 0
        },
        {
          label: this.$t('other_info_all_participant'),
          value: 1
        }
      ]
    },
    // 未填写完毕的主套餐信息列表
    unfinishPackageList() {
      let { position } = this.$store.state
      return _.get(position, 'unfinish_package_info', [])
    },
    // 是否存在未填写完毕的主套餐信息列表
    hasUnfinishPackage() {
      let { unfinishPackageList } = this
      return unfinishPackageList && unfinishPackageList.length
    },
    unfinishPackageTableData() {
      return this.unfinishPackageList.map((pkg) => ({
        key: pkg.id,
        id: pkg.id,
        name: pkg.name,
        detail: (pkg.detail || []).join('、')
      }))
    },
    newOtherInfoDataCheckList() {
      let allGroups = [...this.travellerGroups, ...this.infoGroups]
      return this.groupCheckedList.reduce((acc, curr) => {
        const current = _.find(allGroups, {
          group_id: curr
        })
        acc.push(current.group_name)
        return acc
      }, [])
    },
    oldOtherInfoCategoryLength() {
      return this.oldOtherInfoData.length
    },
    oldOtherInfoDataCheckCategoryLength() {
      return this.oldOtherInfoData.reduce((acc, curr) => {
        const isChecked =
          !!curr.content &&
          curr.content.some((item) =>
            this.oldOtherInfoDataCheckList.includes(item.extra_info_id)
          )
        return isChecked ? acc + 1 : acc
      }, 0)
    },
    oldOtherInfoDataCheckData() {
      const list = []
      const idList = []

      this.oldOtherInfoData.forEach((category) => {
        category.content &&
          category.content.forEach((item) => {
            if (
              this.oldOtherInfoDataCheckList.includes(item.extra_info_id) &&
              !idList.includes(item.extra_info_id)
            ) {
              list.push(item)
              idList.push(item.extra_info_id)
            }
          })
      })

      return list
    },
    selectedCopyList() {
      const list = []
      Object.keys(this.copyModalSelectedData).forEach((item) =>
        list.push(...this.copyModalSelectedData[item])
      )
      return list
    },
    isGlobalDisabled() {
      // 编辑锁状态锁配置
      return this.$root.nonEdit2act || this.$root.nonEdit2status
    }
  },
  watch: {
    oldOtherInfoCategoryLength() {
      this.sendFloorTimelineData()
    },
    oldOtherInfoDataCheckCategoryLength() {
      this.sendFloorTimelineData()
    }
  },
  async mounted() {
    this.sendFloorTimelineData()
    await Promise.all([
      this.initExtraInfo(),
      this.getOldCommonExtraInfo(),
      this.$store.dispatch('actionGetPosition', {
        activityId: this.activity_id
      })
    ])
    this.changeTravelerType()

    if (
      !this.$store.state.position.main_package
      // @dep only check if main packages is finished
      // || klk_gvars.position.add_on_package === 1
    ) {
      // can't create new when there's unfinished packages, will show a msg instead
      this.packages_finished = false
      /* $("#myModal #hint").show().siblings().hide();*/
    }
  },
  methods: {
    handleCopy() {
      this.copyModalVisible = true
      this.getCollapseData()
    },
    async getCollapseData() {
      this.copyModalLoading = true

      const resList = await Promise.all([
        this.$store.dispatch('actionGetAllPackages', {
          activityId: this.activity_id
        }),
        ajax.get(ADMIN_API.act.get_all_old_otherinfo_by_activity, {
          params: {
            activity_id: this.activity_id,
            language: getEditLang()
          }
        })
      ])

      if (resList[0] && resList[1]) {
        const [allPackages, packageExtraInfos] = resList
        const otherPackages = allPackages.filter(
          (pkg) => pkg.package_id !== this.package_id
        )

        let index = 0 // 这里和老admin不同的是，这里删掉了没有extra_info的套餐
        otherPackages.forEach((item) => {
          const key = String(item.package_id) // a-collapse-panel 的 key 需要使用 string 类型
          const extraInfo = packageExtraInfos.packages.find(
            (pkg) => pkg.package_id === item.package_id
          )

          // 这里和老admin不同的是，这里删掉了没有extra_info的套餐
          if (extraInfo && extraInfo.other_infos.length > 0) {
            this.$set(this.copyModalSelectedData, item.package_id, [])
            this.copyCollapseActiveKey.push(key)
            this.copyCollapseData[index] = {
              key,
              name: item.tips,
              package_id: +item.package_id,
              data: extraInfo.other_infos.map((info) => ({
                key: info.extra_info_id,
                type: typeObj[info.type_flag],
                name: bestMatchLang('name', 'language', info.info),
                rules:
                  info.ask_option === 1
                    ? this.$t('package_xinfo_ask_once')
                    : this.$t('package_xinfo_ask_each'),
                mandatory:
                  info.required === 1
                    ? this.$t('global_yes')
                    : this.$t('global_no')
              }))
            }

            index += 1
          }
        })
      }

      this.copyModalLoading = false
    },
    getRowSelection(id) {
      return {
        selectedRowKeys: this.copyModalSelectedData[id],
        onChange: (selectedRowKeys) => {
          this.copyModalSelectedData[id] = selectedRowKeys
        }
      }
    },
    async handleCopyOk() {
      const list = [...this.oldOtherInfoDataCheckList, ...this.selectedCopyList]
      const removeDuplicationList = []

      // 去重
      list.forEach((id) => {
        if (!removeDuplicationList.includes(id)) {
          removeDuplicationList.push(id)
        }
      })

      this.copyConformLoading = true
      const res = await ajax.postBody(
        ADMIN_API.act.bind_package_old_otherinfos,
        {
          data: {
            package_id: this.package_id,
            old_otherinfo_id_list: removeDuplicationList
          }
        }
      )
      this.copyConformLoading = false

      if (res.success) {
        this.copyModalVisible = false
        this.resetCopyModalSelectedData()
        this.$message.success(this.$t('global_copy_success'))
        this.refreshOldExtraInfo()
      } else {
        this.$message.error(this.$t('global_copy_error'))
      }
    },
    async refreshOldExtraInfo() {
      this.isOldOtherInfoLoading = true
      const data = await ajax.get(
        ADMIN_API.act.get_package_otherinfos_old_style,
        {
          params: {
            package_id: this.package_id,
            language: getEditLang()
          }
        }
      )
      this.isOldOtherInfoLoading = false

      if (data && data.info) {
        this.initOldOtherInfoCheckList(data.info)
      }
    },
    handleCopyCancel() {
      this.copyModalVisible = false
      this.resetCopyModalSelectedData()
    },
    resetCopyModalSelectedData() {
      Object.keys(this.copyModalSelectedData).forEach(
        (item) => (this.copyModalSelectedData[item] = [])
      )
    },
    async initExtraInfo(hideLoading) {
      this.isNewOtherInfoLoading = !hideLoading

      const data = await ajax.get(ADMIN_API.act.get_package_otherinfos, {
        params: {
          package_id: this.package_id,
          language: getEditLang(),
          check_combo: 1
        }
      })

      if (data) {
        await this.initNewOtherInfoForm(data.new_styles)
        this.initOldOtherInfoCheckList(data.old_styles)
        this.updateDisabledStatus(data.support_new_otherinfo)
      }

      this.isNewOtherInfoLoading = false
    },
    initOldOtherInfoCheckList(data) {
      this.oldOtherInfoDataCheckList = data.map((item) => item.extra_info_id)
    },
    updateDisabledStatus(data) {
      this.disabledInfo = !_.get(data, 'support_new_otherinfo', true)

      let {
        is_api,
        is_combo_package,
        identity_types,
        is_combo_alone_package,
        combo_info
      } = data.package_status
      let tips = []

      if (is_api) {
        tips.push(this.$t('other_info_standardized_tips_act_api'))
      }

      if (is_combo_package) {
        tips.push(this.$t('other_info_standardized_tips_act_combo_main'))
      } else if (is_combo_alone_package) {
        let tip = ''
        let { combo_activity_id, combo_package_id } = combo_info
        if (KLK_LANG === 'en') {
          tip = klook.strformat(
            this.$t('other_info_standardized_tips_act_combo_temporarily'),
            combo_package_id,
            combo_activity_id
          )
        } else {
          tip = klook.strformat(
            this.$t('other_info_standardized_tips_act_combo_temporarily'),
            combo_activity_id,
            combo_package_id
          )
        }
        tips.push(tip)
      }

      if (identity_types.length) {
        tips.push(this.$t('other_info_standardized_tips_act_identity'))
      }

      this.standardizedTips = tips
    },
    async initNewOtherInfoForm(data) {
      let { group_id_list, groups } = data[0].instance_datas

      let allGroupIds = groups.map((group) => group.group_id)
      for (let v of _.chunk(allGroupIds, 50).values()) {
        await this.$store.dispatch('actionGetGroupItemByIds', {
          group_id_list: v
        })
        await this.$store.dispatch('actionGetTitleListByIds', {
          group_id_list: v
        })
      }

      groups = this.fmtGroupData(groups)
      this.travellerGroups = groups.filter((item) => item.info_type === 1) // traveller
      this.infoGroups = groups.filter((item) => item.info_type === 0) // otherinfo
      this.groupCheckedList = group_id_list || []

      let intersectionGroupIds = _.intersection(
        this.travellerGroups.map((group) => group.group_id),
        this.groupCheckedList
      )
      if (intersectionGroupIds.length) {
        let samplePkg = _.find(groups, {
          group_id: this.groupCheckedList[0]
        })
        this.travelerInfoType = _.get(samplePkg, 'write_level', -1)
      } else {
        this.travelerInfoType = -1
      }
    },
    fmtGroupData(groups) {
      return (groups || []).map((group) => {
        group.style.fields = group.style.fields.map((field) => {
          switch (field.options.type) {
            case 6: {
              !field.value &&
                (field.value = field.options.default || {
                  id: 0,
                  content: ''
                })
              break
            }
            case 7: {
              !field.value &&
                (field.value = field.options.default || {
                  min: '',
                  max: ''
                })
              break
            }
            default: {
              !field.value && (field.value = field.options.default || '')
            }
          }
          return field
        })

        return {
          ...group,
          uuid: group.uuid || getUuid()
        }
      })
    },
    async getOldCommonExtraInfo(hideLoading) {
      this.isOldOtherInfoLoading = !hideLoading

      const data = await ajax.get(ADMIN_API.act.get_old_common_otherinfos, {
        params: {
          package_id: +this.package_id,
          language: getEditLang()
        }
      })

      if (data) {
        this.oldOtherInfoData = data.info
      }

      this.isOldOtherInfoLoading = false
    },
    getCollapseTitle(id) {
      return commonInfoFields[id]
    },
    handelUpdateData(data, type) {
      this.updateData[type] = data
      this.sendFloorTimelineData()
    },
    handleCollapseCardClick(id) {
      if (this.isGlobalDisabled) {
        return
      }

      const index = this.oldOtherInfoDataCheckList.indexOf(id)

      if (index === -1) {
        this.oldOtherInfoDataCheckList.push(id)
      } else {
        this.oldOtherInfoDataCheckList.splice(index, 1)
      }
    },
    handleCheckboxChange(item) {
      const { value } = item.target
      this.handleCollapseCardClick(value)
    },
    getCollapseCardTitle(item) {
      return `${item.extra_info_id} - ${bestMatchLang(
        'name',
        'language',
        item.details
      )}`
    },
    getCollapseCardHint(item) {
      return bestMatchLang('hint', 'language', item.details)
    },
    getDraggableTitle(id) {
      const item = this.oldOtherInfoDataCheckData.find(
        (data) => data.extra_info_id === id
      )
      return item ? this.getCollapseCardTitle(item) : ''
    },
    changeTravelerType() {
      this.$nextTick(() => {
        const setting = document.querySelector('#js-triangle-wrap')
        const isChecked = setting.querySelector('.ant-radio-checked')

        this.triangleOffsetX =
          isChecked.getBoundingClientRect().x -
          setting.getBoundingClientRect().x
      })
    },
    handlePreview() {
      let url = `${klook.getUrlByEnv('')}/en-US/preview/activity/${
        this.activity_id
      }?lang=${getEditLang()}`
      window.open(url)
    },
    async handleCheckForm() {
      let groups =
        this.travelerInfoType === -1
          ? [this.$refs.infoGroups]
          : [this.$refs.travellerGroups, this.$refs.infoGroups]

      let valid = await Promise.all(groups.map((item) => item.validateForm()))

      if (valid.some((item) => !item)) {
        scrollFormErrorIntoView()
        return
      }

      await this.saveData()
    },
    async saveStepStatus() {
      // 上传保存状态
      await this.$store.dispatch('updatePkgStepStatus2action', {
        activity_id: this.activity_id,
        package_id: this.package_id,
        language: getEditLang(),
        status: 1,
        step: 'extra_information'
      })
      klook.bus.$emit('updatePkgInfos2bus')
    },
    async saveData() {
      this.rootChangeLoading(true)

      const params = {
        activity_id: this.activity_id,
        package_id: this.package_id,
        language: getEditLang(),
        common_otherinfo_ids: this.oldOtherInfoDataCheckList,
        groups: Object.values(this.updateData)
          .flat()
          .filter(
            (item) => !(this.travelerInfoType === -1 && item.info_type === 1)
          )
          .map((item) => {
            let write_level =
              this.travelerInfoType === -1 ? 0 : this.travelerInfoType // 0-booking级别, 1-unit级别,

            return {
              group_id: item.group_id,
              fields: item.fields,
              ...(item.info_type === 1 // traveller
                ? {
                    write_level
                  }
                : {})
            }
          })
      }

      const res = await ajax.postBody(ADMIN_API.act.save_package_otherinfos, {
        data: params
      })

      if (res.success) {
        // 这里为了不出现双层loading
        await Promise.all([this.saveStepStatus(), this.initExtraInfo(true)])
        this.rootChangeLoading(false)
        this.submitModalVisible = true
      } else {
        this.rootChangeLoading(false)
        this.$message.error(this.$t('act_save_fail'))
      }
    },
    handleCreatePackage() {
      this.$router.push({
        name: 'packageBasicInfo',
        params: { id: this.activity_id },
        query: {
          ...this.$route.query,
          package_id: ''
        }
      })
    },
    handleOverviewPackage() {
      // TODO: 需要先迁移套餐的overview页面
    },
    getUnfinishPackageDetail(detail) {
      detail = detail || []
      return detail.join('、')
    },
    sendFloorTimelineData() {
      const floorData = {
        list: [
          {
            title: this.$t('other_info_standardized'),
            require: true,
            num: this.updateData.info.length === 0 ? 2 : 3,
            count: 3,
            requireNum: 1,
            requireCount: 1
          },
          {
            title: this.$t('other_info_non_standardized'),
            require: false,
            num: this.oldOtherInfoDataCheckCategoryLength,
            count: this.oldOtherInfoCategoryLength,
            requireNum: 0,
            requireCount: 0
          }
        ]
      }
      klook.bus.$emit('floorTimeline2bus', { floorData })
    }
  }
}
</script>

