// import { View } from '@klook/admin-layout'
import { addDevTestRoutes } from './devRoutes'

const router = {
  base: '/',
  routes: [
    {
      path: 'act',
      meta: {
        title: 'Activity Manager'
      },
      children: [
        {
          path: '',
          component: () =>
            import(/* webpackChunkName: "activityIndex" */ '../pages/index'),
          redirect: 'management',
          meta: {
            title: 'Activity'
          },
          children: [
            {
              path: 'management',
              name: 'activityManagement',
              component: () =>
                import(
                  /* webpackChunkName: "activityManagement" */ '../pages/activityManagement'
                ),
              meta: {
                title: 'Approval List-Merchant',
                auth: ['experience/act/merchant/list_page']
              }
            },
            {
              hidden: true,
              path: 'activity/basic',
              name: 'activityBasic',
              component: () =>
                import(
                  /* webpackChunkName: "activityBasic" */ '../layout/actLayout'
                ),
              meta: {
                title: false,
                setLeftNav: false
              },
              children: [
                {
                  path: ':id',
                  name: 'basicInfo',
                  component: () =>
                    import(
                      /* webpackChunkName: "basicInfo" */ '../pages/activityManagement/basicInfo'
                    ),
                  meta: {
                    title: false,
                    hideSummary: true,
                    auth: [
                      'experience/act/activity_view',
                      'experience/act/activity_edit'
                    ]
                  }
                }
              ]
            },
            {
              hidden: true,
              path: 'activity/detail',
              name: 'activityDetail',
              component: () =>
                import(
                  /* webpackChunkName: "activityDetail" */ '../layout/actLayout'
                ),
              meta: {
                title: false,
                setLeftNav: false
              },
              children: [
                {
                  path: ':id',
                  name: 'detail',
                  component: () =>
                    import(
                      /* webpackChunkName: "detail" */ '../pages/activityManagement/detail'
                    ),
                  meta: {
                    auth: [
                      'experience/act/activity_view',
                      'experience/act/activity_edit'
                    ]
                  }
                }
              ]
            },
            {
              hidden: true,
              path: 'package',
              name: 'packageInfo',
              component: () =>
                import(
                  /* webpackChunkName: "packageInfo" */ '../layout/actLayout'
                ),
              meta: {
                title: false,
                setLeftNav: false
              },
              children: [
                {
                  path: 'info/:id',
                  name: 'packageBasicInfo',
                  component: () =>
                    import(
                      /* webpackChunkName: "packageBasicInfo" */ '../pages/package/basicInfo'
                    ),
                  meta: {
                    title: false,
                    hideSummary: true,
                    auth: [
                      'experience/act/activity_view',
                      'experience/act/activity_edit'
                    ]
                  },
                  props: (route) => ({
                    // pass props obj to router-view child component
                    package_id: +route.query.package_id,
                    activity_id: +route.params.id,
                    package_type: +route.query.package_type
                  })
                },
                {
                  path: 'detail/:id',
                  name: 'packageDetail',
                  component: () =>
                    import(
                      /* webpackChunkName: "packageDetail" */ '../pages/package/detail'
                    ),
                  meta: {
                    auth: [
                      'experience/act/activity_view',
                      'experience/act/activity_edit'
                    ]
                  },
                  props: (route) => ({
                    // pass props obj to router-view child component
                    package_id: +route.query.package_id,
                    activity_id: +route.params.id,
                    package_type: +route.query.package_type
                  })
                },
                {
                  path: 'schedule/:id',
                  name: 'packageSchedule',
                  component: () =>
                    import(
                      /* webpackChunkName: "packageSchedule" */ '../pages/package/schedule'
                    ),
                  meta: {
                    hideTimeline: true,
                    hideSave: true,
                    auth: [
                      'experience/act/activity_view',
                      'experience/act/activity_edit'
                    ]
                  },
                  props: (route) => ({
                    // pass props obj to router-view child component
                    package_id: +route.query.package_id,
                    activity_id: +route.params.id,
                    package_type: +route.query.package_type
                  })
                },
                {
                  path: 'extra/:id',
                  name: 'packageExtra',
                  component: () =>
                    import(
                      /* webpackChunkName: "packageExtra" */ '../pages/package/extra'
                    ),
                  meta: {
                    hideSave: true,
                    hideSummary: true,
                    auth: [
                      'experience/act/activity_view',
                      'experience/act/activity_edit'
                    ]
                  },
                  props: (route) => ({
                    // pass props obj to router-view child component
                    package_id: +route.query.package_id,
                    activity_id: +route.params.id,
                    package_type: +route.query.package_type
                  })
                }
              ]
            }
          ]
        }
      ]
    }
  ]
}

// 添加dev环境测试路由
addDevTestRoutes(router)

export default router
