<template>
  <div>
    <div class="address__header">
      <div class="address__operator">
        <a-input
          :value="form.address"
          class="address__input"
          :placeholder="$t('form.gpsLocation')"
          @input="handleInput"
        />
      </div>
      <div class="address__queryBtn">
        <a-button type="primary" @click="handleCheckCoordinate">
          {{ $t('taxonomy_query') }}
        </a-button>
      </div>
    </div>

    <area-map
      v-if="reloadPosition"
      ref="map"
      :key="form.place_id"
      :address="form.address"
      :place="form.place_id"
      :position="form.position"
      @map="getMapInfo"
    />

    <input type="hidden" class="trigger-input" :value="form.position" />

    <slot name="position_details" :data="positionDetails">
      <div v-if="positionDetails.success" class="position-detail">
        <p>
          {{ $t('global_location') }}：
          <span class="colorful-position">
            {{ positionDetails.position }}
          </span>
          <br />
        </p>
        <p>
          {{ $t('global_place_id') }}：
          <span class="colorful-position">
            {{ positionDetails.place_id || '-' }}
          </span>
          <br />
          <a
            target="_blank"
            :href="positionDetails.gmap_preview_url"
            @click="handleA(positionDetails.gmap_preview_url)"
            >Preview by Google Map URL↗</a
          >
        </p>
        <p class="tips">
          {{ $t('global_tips') }}
          <a
            target="_blank"
            :href="positionDetails.place_id_url"
            @click="handleA(positionDetails.place_id_url)"
            >Google Place ID</a
          >
          can help our users find the place easier using Google map app with
          more info provided. You can also
          <a
            target="_blank"
            :href="positionDetails.add_missing_url"
            @click="handleA(positionDetails.add_missing_url)"
            >Add a Missing Place</a
          >
          in Google Map if the prompted results are not desired.
        </p>
      </div>
    </slot>
  </div>
</template>

<script>
import AreaMap from '@activity/components/taxonomy/area_map'

export default {
  components: {
    AreaMap
  },
  props: {
    init: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      reloadPosition: true,
      positionDetails: {
        success: false
      },
      form: {
        address: '',
        place_id: '',
        position: ''
      }
    }
  },
  watch: {
    'form.address': {
      handler() {
        let input = this.jQ('.address__input')
        let autoComplete = new google.maps.places.Autocomplete(input)
        autoComplete.addListener('place_changed', () => {
          this.handleCheckCoordinate()
        })
      }
    },
    init: {
      immediate: true,
      deep: true,
      handler(v) {
        // 不用关心接口返回时序，只要有值，就会默认初始化一次
        v && this.$emit('hook:initMap', v)
      }
    }
  },
  beforeMount() {
    this.$once('hook:initMap', (v) => {
      Object.assign(this.form, v)
      this.reloadPosition = false
      this.$nextTick(() => {
        this.reloadPosition = true
      })
    })
  },
  methods: {
    getMapInfo(data) {
      const { place_id, location, success } = data
      let position = this.initLocation(location)

      let place_id_url = 'https://developers.google.com/places/place-id'
      let add_missing_url = 'https://support.google.com/maps/answer/6320846'
      let gmap_preview_url = `https://www.google.com/maps/search/?api=1&query=${position}&query_place_id=${place_id}`

      this.form.position = position
      this.form.address = document.querySelector('.address__input').value

      let details = {
        position,
        place_id,
        gmap_preview_url,
        place_id_url,
        add_missing_url,
        success
      }

      this.$set(this, 'positionDetails', details)
      this.$emit('change', details)
    },
    handleA(url) {
      GTAG_EVENTS.act.outbound_click(url)
      document.location = url
      return false
    },
    initLocation(location) {
      if (!_.isEmpty(location)) {
        let [lat, lng] = location.split(',')
        lat = _.ceil(lat, 6)
        lng = _.ceil(lng, 6)

        return lat + ',' + lng
      }
    },
    jQ(ele) {
      return document.querySelector(ele)
    },
    handleInput(event) {
      this.form.address = event.srcElement.value
    },
    handleCheckCoordinate() {
      this.$refs.map.$emit('submit-address')
    }
  }
}
</script>

<style lang="scss" scoped>
$box-width: 820px;

.address__header {
  display: flex;
  margin-bottom: 16px;
  .address__queryBtn {
    margin-left: 16px;
  }
}
.position-detail {
  margin-top: 16px;
  background-color: rgba(255, 171, 0, 0.05);
  padding: 8px;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.65);
  line-height: 14px;
  p {
    margin: 0;
  }
  .tips {
    margin-top: 16px;
  }
}
</style>
