<template>
  <div class="handle">
    <div class="handle-box"></div>
  </div>
</template>
<script>
const postUnarchive = async (row) => {
  return await ajax.postBody(
    ADMIN_API.act.un_archive_package,
    {
      data: {
        package_id: row.package_id
      }
    },
    {
      msgOpt: {
        isSucMsg: true
      },
      loading: true
    }
  )
}
const postArchive = async (row) => {
  return await ajax.postBody(
    ADMIN_API.act.archive_package,
    {
      data: {
        package_id: row.package_id
      }
    },
    {
      msgOpt: {
        isSucMsg: true
      },
      loading: true
    }
  )
}
const postWithdrawSubmit = async (row, keyFlag) => {
  let url =
    keyFlag === 'activity'
      ? ADMIN_API.act.withdraw2activity
      : ADMIN_API.act.withdraw2package
  let result = await ajax.postBody(
    {
      url,
      data: {
        activity_id: row.activity_id,
        package_id: keyFlag === 'activity' ? undefined : row.package_id
      }
    },
    {
      msgOpt: {
        isSucMsg: true
      },
      loading: true
    }
  )
  return result
}
const get_activity_submit_status = async (activity_id) => {
  return await ajax.get(
    ADMIN_API.act.get_activity_submit_status,
    {
      params: {
        activity_id
      }
    },
    {
      msgOpt: {
        errContent: __('activity_required_fields')
      },
      loading: true
    }
  )
}
const submit_to_klook2package = async (data) => {
  return await ajax.post(
    ADMIN_API.act.submit_to_klook2package,
    {
      data
      // data: {
      //   activity_id, // ,
      //   note: '', // 原因,
      //   package_id // ,
      // },
    },
    {
      msgOpt: {
        isSucMsg: true
      },
      loading: true
    }
  )
}
const submit_to_klook2activity = async (data) => {
  return await ajax.post(
    ADMIN_API.act.submit_to_klook,
    {
      data
      // data: {
      //   activity_id,
      //   note: '', // 原因,
      //   package_id_list: [
      //     // 活动和套餐必须都传，并且套餐属于活动,
      //   ]
      // },
    },
    {
      msgOpt: {
        isSucMsg: true
      },
      loading: true
    }
  )
}
const get_package_submit_status = async (package_id) => {
  return await ajax.get(
    ADMIN_API.act.get_package_submit_status,
    {
      params: {
        package_id
      }
    },
    {
      msgOpt: {
        errContent: __('package_required_fields')
      },
      loading: true
    }
  )
}
const postApprove = async function(row, isAct) {
  let { activity_id, package_id, note } = row
  let url = isAct ? ADMIN_API.act.actApprove : ADMIN_API.act.pkgApprove
  let data = isAct ? { activity_id } : { activity_id, package_id }
  return await ajax.postBody(
    url,
    {
      data: { ...data, note }
    },
    {
      msgOpt: {
        isSucMsg: true
      },
      loading: true
    }
  )
}
const postReject = async function(row, isAct) {
  let { activity_id, package_id, note } = row
  let url = isAct ? ADMIN_API.act.actReject : ADMIN_API.act.pkgReject
  let data = isAct ? { activity_id } : { activity_id, package_id }
  return await ajax.postBody(
    url,
    {
      data: { ...data, note }
    },
    {
      msgOpt: {
        isSucMsg: true
      },
      loading: true
    }
  )
}
export default {
  data() {
    return {}
  },
  methods: {
    async handleWithdrawSubmit(row, options = {}) {
      let { keyFlag, suc, err } = options
      let title =
        keyFlag === 'activity'
          ? klook.parseStr1(__('merchant_withdraw_activity'), {
              'AID-Name': `${row.activity_id}-${row.activity_title}`
            })
          : klook.parseStr1(__('merchant_withdraw_package'), {
              'PID-Name': `${row.package_id}-${row.package_title}`
            })
      this.$modal.confirm({
        zIndex: 1111,
        title,
        maskClosable: true,
        cancelText: this.$t('global_cancel'),
        okText: this.$t('global_submit'),
        // content: (h) => node,
        onOk: async () => {
          let reqData = await postWithdrawSubmit(row, keyFlag)
          if (reqData && reqData.success) {
            suc && suc()
          } else {
            err && err()
          }
        },
        onCancel() {
          console.log('Cancel')
        }
      })
    },
    get_activity_submit_status,
    submit_to_klook2package,
    submit_to_klook2activity,
    get_package_submit_status,
    handleApprove(row, options = {}) {
      let { isAct, suc, err } = options
      row.note = ''
      let title = isAct
        ? klook.parseStr1(__('activity_approve'), {
            'AID-Name': `${row.activity_id}-${row.activity_title}`
          })
        : klook.parseStr1(__('package_approve'), {
            'PID-Name': `${row.package_id}-${row.package_title}`
          })
      // let node = (
      //   <a-textarea
      //     placeholder={__('global_please_input')}
      //     v-model={row.note}
      //     rows={4}
      //     maxLength={500}
      //   ></a-textarea>
      // )
      this.$modal.confirm({
        title,
        maskClosable: true,
        cancelText: this.$t('global_cancel'),
        okText: this.$t('global_submit'),
        // content: (h) => node,
        onOk: async () => {
          let reqData = await postApprove(row, isAct)
          if (reqData && reqData.success) {
            suc && suc()
          } else {
            err && err()
          }
        },
        onCancel() {
          console.log('Cancel')
        }
      })
    },
    handleReject(row, options = {}) {
      let { isAct, suc, err } = options
      row.note = ''
      let title = isAct ? __('activity_reject') : __('package_reject')
      let okButtonProps = {
        props: {
          disabled: true
        }
      }
      let node = (
        <a-textarea
          v-model={row.note}
          placeholder={__('global_please_input')}
          rows={4}
          onkeyup={(e) => {
            row.note = _.trim(e.target.value)
            okButtonProps.props.disabled = !row.note
          }}
          maxLength={500}
        ></a-textarea>
      )
      this.$modal.confirm({
        title,
        maskClosable: true,
        cancelText: this.$t('global_cancel'),
        okText: this.$t('global_submit'),
        okButtonProps,
        content: (h) => node,
        onOk: async () => {
          let reqData = await postReject(row, this.isAct)
          if (reqData && reqData.success) {
            suc && suc()
          } else {
            err && err()
          }
        },
        onCancel() {
          console.log('Cancel')
        }
      })
    },
    handlerArchive(row, options = {}) {
      let { isAct, suc, err } = options
      row.note = ''
      let title = __('merchant_archive_package')
      this.$modal.confirm({
        zIndex: 1111,
        title,
        maskClosable: true,
        cancelText: this.$t('global_cancel'),
        okText: this.$t('global_submit'),
        // content: (h) => node,
        onOk: async () => {
          let reqData = await postArchive(row, isAct)
          if (reqData && reqData.success) {
            suc && suc()
          } else {
            err && err()
          }
        },
        onCancel() {
          console.log('Cancel')
        }
      })
    },
    handlerUnarchive(row, options = {}) {
      let { isAct, suc, err } = options
      row.note = ''
      let title = __('merchant_unarchive_package')
      this.$modal.confirm({
        zIndex: 1111,
        title,
        maskClosable: true,
        cancelText: this.$t('global_cancel'),
        okText: this.$t('global_submit'),
        // content: (h) => node,
        onOk: async () => {
          let reqData = await postUnarchive(row, isAct)
          if (reqData && reqData.success) {
            suc && suc()
          } else {
            err && err()
          }
        },
        onCancel() {
          console.log('Cancel')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.handle {
  .handle-box {
    //
  }
}
</style>
