<template>
  <a-input
    v-model="currentValue"
    v-bind="$props"
    type="textarea"
    :disabled="disabled"
  />
</template>

<script>
export default {
  name: 'NobreakTextarea',
  props: {
    value: {
      required: true,
      type: String
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      test: ''
    }
  },
  computed: {
    currentValue: {
      get() {
        return this.value.replace(/\n/g, '')
      },
      set(v) {
        this.$emit('input', v.replace(/\n/g, ''))
      }
    }
  }
}
</script>
