import Vue from 'vue'
import VueRouter from 'vue-router'

import demoRoutes from '@/modules/demo/routes/index.js'
import actRoutes from '@/modules/activity/routes/index.js'

import { getRoutes, useBackendNav } from '@klook/admin-cli-service'
import { merchant_language_codes } from '../../admin-cli.config.js'
import * as merchantLayout from '@klook/merchant-layout'

Vue.use(VueRouter)

let router

async function getLayout() {
  let layout = await import('@klook/merchant-layout')
  layout = layout.default || layout
  return layout
}

function getLocale() {
  const pathname = location.pathname
  let pathArr = pathname.split('/')
  const lang = pathArr[1]
  const languageList = merchant_language_codes.map((i) => i.value)

  if (lang && languageList.includes(lang)) {
    return lang
  }
  return ''
}

function getBase() {
  let base = process.env.VUE_APP_MERCHANT_ROOTPATH
  let multiLang = getLocale()
  return multiLang ? multiLang + base : base
}

export async function createRouter(_ctx) {
  if (router) return router

  let routes = getRoutes({
    default: {
      routes: [
        {
          path: '/',
          redirect: { path: 'act' },
          hidden: true
        }
      ]
    },
    demoRoutes,
    // add your routes,
    actRoutes
  })

  const KlkLayout = await getLayout()

  routes = routes.map((route) => ({
    ...route,
    // bypasss 404 etc..
    ...(route.component ? {} : { component: KlkLayout })
    // top route refresh page due to  MSPA restriction
    // beforeEnter(to, from, next) {
    //   if (from.fullPath === '/') {
    //     route.beforeEnter ? route.beforeEnter(to, from, next) : next()
    //   } else {
    //     window.location.href = `${process.env.VUE_APP_MERCHANT_ROOTPATH}${to.fullPath}`
    //   }
    // }
  }))

  const _router = new VueRouter({
    mode: 'history',
    base: getBase(),
    routes
  })

  router = _router
  // eslint-disable-next-line
  const initLayoutStyle = (to) => {
    // 会继承meta
    if (!to) return
    const layoutFnMaps = ['setLeftNav', 'setTopNav']
    const result = {}
    to.matched.forEach((item) => {
      layoutFnMaps.forEach((key) => {
        if (key in item.meta) {
          result[key] = item.meta[key]
        }
      })
    })
    for (let index in layoutFnMaps) {
      let key = layoutFnMaps[index]
      merchantLayout[key](typeof result[key] === 'boolean' ? result[key] : true)
    }
  }

  router.beforeEach((to, from, next) => {
    console.log('beforeEach', to, from)
    initLayoutStyle(to) // 设置 layout 导航栏
    next()
  })

  if (
    process.env.NODE_ENV == 'production' ||
    process.env.VUE_APP_DEBUG_ROUTE == 'DEBUG'
  ) {
    await useBackendNav(router)
  }

  _ctx.router = router

  return router
}

export default router
