<template>
  <a-form-model
    ref="form"
    :model="form"
    class="special_multi_language_input-container common-tpl-basic-style"
    @submit="handleSubmit"
  >
    <a-form-model-item
      v-for="(current, index) in form.data"
      :key="current[langField]"
      :label="index === 0 ? label : ''"
      :prop="`data.${index}.${valField}`"
      :rules="getRules(current, index)"
      :colon="false"
      :data-current="JSON.stringify(form.data)"
    >
      <a-tooltip
        placement="right"
        overlayClassName="common-tooltip-style"
        :title="$attrs.description"
      >
        <component
          :is="type === 'markdown' ? 'simple-mde' : 'a-input'"
          ref="inputItem"
          v-model="current[valField]"
          :type="type"
          :autoSize="{ minRows: 3, maxRows: 6 }"
          :placeholder="`${placeholder} (${getLangTitle(current[langField])})`"
          :config="{
            placeholder: `${placeholder} (${getLangTitle(current[langField])})`
          }"
          :disabled="disabledFactory(current)"
        />
      </a-tooltip>
    </a-form-model-item>
  </a-form-model>
</template>

<script>
import { getLangTitle, getEditLang, getRefLang } from '@activity/utils'
import SimpleMde from '../simple-md'

export default {
  components: { SimpleMde },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: Array,
      required: true
    },
    langField: {
      type: String,
      default: 'language'
    },
    valField: {
      type: String,
      default: 'value'
    },
    requiredEn: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: __('global_please_input')
    },
    type: {
      type: String,
      default: 'input'
    },
    initSchema: {
      type: Object,
      default: () => ({})
    },
    label: {
      type: String,
      default: ''
    },
    required: {
      type: Boolean,
      default: false
    },
    onlyEditLanguage: {
      // 用于控制仅仅显示当前编辑语言
      type: Boolean,
      default: false
    },
    currentCreateLang: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {}
  },
  computed: {
    editLanguage() {
      return this.currentCreateLang || getEditLang()
    },
    refLanguage() {
      return getRefLang()
    },
    currentRefLanguage() {
      if (this.editLanguage === 'en_US' || this.refLanguage) {
        return this.refLanguage
      }

      if (this.requiredEn) {
        return 'en_US'
      }

      return ''
    },
    allEditLanguage() {
      return [this.editLanguage, this.currentRefLanguage].filter((item) => item)
    },
    form: {
      get() {
        return {
          data: this.allEditLanguage
            .filter(
              (language) =>
                (this.onlyEditLanguage && language === this.editLanguage) ||
                !this.onlyEditLanguage //
            )
            .map((language) => this.getValue(language))
        }
      },
      set(v) {
        this.$emit('change', v)
      }
    }
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault()
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values)
        }
      })
    },
    getLangTitle: getLangTitle,
    disabledFactory(current) {
      let currentLang = current[this.langField]
      return (
        this.disabled ||
        (this.refLanguage && currentLang !== this.editLanguage) || // 有参考语言的时候都需要锁定
        (currentLang === this.currentRefLanguage &&
          this.currentRefLanguage !== 'en_US')
      )
    },
    validateForm() {
      return new Promise((resolve) =>
        this.$refs.form.validate((valid) => resolve(valid))
      )
    },
    getRules(current, index) {
      let disabled = this.disabledFactory(current)
      return {
        required: this.required && (index === 0 || !disabled),
        validator: (rule, value, callback) => {
          if (rule.required && !value) {
            callback(new Error('Please fill in all required'))
          } else {
            callback()
          }
        }
      }
    },
    getValue(language, data = this.value) {
      let index = _.findIndex(data, {
        [this.langField]: language
      })

      if (-1 === index) {
        this.value.push({
          ...this.initSchema,
          [this.langField]: language,
          [this.valField]: ''
        })
        return _.last(this.value)
      }

      return this.value[index]
    }
  }
}
</script>
<style lang="scss">
.special_multi_language_input-container {
  .ant-row.ant-form-item:not(:first-child) {
    min-height: auto;
  }
}
</style>
<style lang="scss" scoped>
.special_multi_language_input-container {
  display: flex;
  flex-direction: column;
}
</style>
