<template>
  <!-- 活动图片上传 -->
  <a-form-model
    ref="form"
    :model="form"
    label-width="200px"
    class="photo-activity"
  >
    <a-form-model-item :required="required" :rules="getRules()" prop="custom">
      <PhotoGroup
        ref="photo_group"
        :activity-id="activityId"
        :cur-lang="curLang"
        :ref-lang="refLang"
        :list-all="list_all_format"
        :list="photo_list"
        :need-alt="isEMpage"
        :need-desc="true"
        :cur-lang-only.sync="cur_lang_only"
        :cur-lang-only-label="$t('create_act_photo_for_lang')"
        :show-cur-lang-only="true"
        :need-valid-desc="false"
        :banner-count="bannerCount"
        :trigger-banner-check="triggerBannerCheck"
        @copy-photo="copyPhoto"
        @validate-form="validate"
        @banner-count-change="$emit('banner-count-change')"
        :minBannerCount="minBannerCount"
      />
    </a-form-model-item>
  </a-form-model>
</template>
<script>
// activity 或 guide
//   am: photo desc 选填 , photo alt 隐藏
//   em: photo desc 选填 , photo alt 必填
import PhotoGroup from '../photo-group'
import { savePhotos } from './api'
export default {
  name: 'PhotoActivity',
  components: { PhotoGroup },
  // am 不需要 alt 填写, 且 desc 非必填
  inject: ['activityId', 'curLang', 'refLang', 'isEMpage'],
  provide() {
    return {
      updateData: this.updateData
    }
  },
  props: {
    minBannerCount: {
      default: 0
    },
    // 图片 list, 所有语言
    listAll: {
      type: Array,
      default: () => [],
      required: true
    },
    // 是否必填
    required: {
      type: Boolean,
      default: false
    },
    // banner总数
    bannerCount: {
      type: Number,
      default: 3
    },
    triggerBannerCheck: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      // 使图片仅作用于当前语言
      cur_lang_only: false,
      // 当前语言的图片 list
      photo_list: [],
      // 所有语言的图片列表, 用于弹窗
      list_all_format: [],
      form: {}
    }
  },
  watch: {
    cur_lang_only: {
      handler(next) {
        // !删除当前语言下的图片
        if (!next) {
          let params = {
            activity_id: +this.activityId,
            banner: [],
            images: [{ language_type: this.curLang, image: [] }]
          }
          savePhotos(this.activityId, params)
        }
      }
    },
    listAll() {
      this.getCurLangPhoto()
    }
  },
  mounted() {
    // rainweb mock
    this.getCurLangPhoto()
  },
  methods: {
    // getCurLangPhoto
    getCurLangPhoto() {
      // 统一格式
      this.list_all_format = this.listAll.map((v) => {
        return {
          language_type: v.language_type,
          list: v.images_groups && v.images_groups.length ? v.images_groups : []
        }
      })
      //! 活动图片 ------------- 当前语言没有的话, 取 all, all没有再取 en_US 下的图片
      //! 第一步, 找到当前语言的图片
      let act_list =
        (
          this.list_all_format.find((v) => v.language_type === this.curLang) ||
          {}
        ).list || []
      //! 第二步, 判断是否勾选了 "创建当前语言的图片" is_default 标识只作用于当前语言
      if (act_list && act_list.length && act_list[0].is_default === 0) {
        this.cur_lang_only = true
      } else {
        this.cur_lang_only = false
      }
      //! 如果是, 直接读取当前语言图片
      if (this.cur_lang_only) {
        this.photo_list = act_list
        return
      }
      //! 如果不是, 先读取 All
      let all =
        (this.list_all_format.find((v) => v.language_type === 'ALL') || {})
          .list || []
      //! 如果 all 存在, 读取 All, 但是需要读取当前语言保存好的 desc && alt
      if (all.length) {
        all.forEach((v) => {
          //! 找到对应的图片, 读取描述 & alt
          let item = act_list.find(
            (img) => _.get(img, 'image.0.name') === _.get(v, 'image.0.name')
          )
          if (item) {
            v.desc = item.desc || ''
            v.alt = item.alt || ''
          }
        })
        this.photo_list = all
        return
      }
      //! 最后, 如果 all 没有, 则读取 en_US
      if (!all.length) {
        act_list =
          (this.list_all_format.find((v) => v.language_type === 'en_US') || {})
            .list || []
      }
      this.photo_list = act_list
    },
    //! 从上传的图片中获取(复制图片)
    copyPhoto(images) {
      // console
      this.photo_list = [
        ...this.photo_list,
        ...images.map((v, i) => {
          // 图片排序 === 原图片顺序基础上递增
          v.display_order = this.photo_list.length + i + 1
          return v
        })
      ]
    },
    //! 获取数据
    getList() {
      return this.$refs.photo_group.getList()
    },
    // 校验
    validate() {
      return new Promise((resolve) => {
        this.$refs.form.validate(resolve)
      })
    },
    //! 获取接口需要的参数
    getFormatParams() {
      let params = []
      let cur_list = _.cloneDeep(this.getList())
      // ! 是否保存到当前语言, 是的话 is_default: 0
      let is_default = +!this.cur_lang_only
      let image = cur_list.reduce((acc, cur) => {
        let base = {
          alt: cur.alt,
          banner_display: +cur.banner_display,
          desc: cur.desc,
          display_order: cur.display_order,
          img_id: 0,
          is_default
        }
        // delete cur.ORIGIN.url;
        // delete cur.DESKTOP.url;
        // delete cur.MOBILE.url;
        return [
          ...acc,
          ...[
            { ...cur.ORIGIN, ...base },
            { ...cur.DESKTOP, ...base },
            { ...cur.MOBILE, ...base }
          ]
        ]
      }, [])
      params = [
        {
          language_type: this.curLang,
          image
        }
      ]
      if (!this.cur_lang_only) {
        // !是否保存到当前语言, 否时, 同时添加到 ALL
        // } else {
        // ! All 不需要保留 alt , desc
        params.push({
          language_type: 'ALL',
          image: _.cloneDeep(image).map((v) => {
            v.desc = ''
            v.alt = ''
            return v
          })
        })
      }
      return params
    },
    // 校验
    getRules() {
      // 非必填(指数量), 即可以没有活动图片, 但是图片的字段是必填的
      let validation = [
        {
          validator: (rule, value, callback) => {
            let res = this.$refs.photo_group.validateFields()
            res.valid ? callback() : callback(new Error(res.message))
          }
        }
      ]
      if (this.required) {
        validation.push({
          required: true,
          validator: (rule, value, callback) => {
            let res = this.$refs.photo_group.validateLength()
            res.valid ? callback() : callback(new Error(res.message))
          }
        })
      }
      return validation
    }
  }
}
</script>
<style lang="scss">
.photo-activity {
  .ant-form-item-control-wrapper {
  }
}
</style>
