<template>
  <div class="drawer">
    <div class="drawer-box">
      <a-drawer v-bind="drawerProps" v-on="drawerProps.on">
        <template v-for="group in groupsSummary">
          <groupSummary
            v-if="group.summary && group.summary.length"
            :group="group"
            :key="group.id"
          />
        </template>
        <!--<div class="btns-wrap">-->
        <!--<a-button type="primary" @click="drawerProps.on.close">-->
        <!--{{ $t('btn.okay') }}-->
        <!--</a-button>-->
        <!--</div>-->
      </a-drawer>
    </div>
  </div>
</template>

<script>
import groupSummary from '@activity/components/groupSummary'
export default {
  components: {
    groupSummary
  },
  props: {
    drawerData: {
      default() {
        return {}
      }
    },
    groupsSummary: {
      default() {
        return []
      },
      required: true,
      type: Array
    }
  },
  data() {
    return {
      drawerProps: {
        visible: false,
        closable: true,
        width: 600,
        height: '100%',
        on: {
          close: () => {
            let { drawerData: dd } = this
            dd.visible = false
          }
        }
      }
    }
  },
  watch: {
    drawerData: {
      handler(dd) {
        this.drawerProps = _.merge(this.drawerProps, dd)
      },
      deep: true,
      immediate: true
    }
  }
}
</script>

<style lang="scss" scoped>
.drawer {
  .drawer-box {
    //
  }
}
</style>
