const getters = {
  getPackages: (state) => state.packages || [],
  getServerTime: (state) => state.serverTime || '',
  getCurrentPkgData: (state) => (package_id) =>
    _.cloneDeep(
      _.find(state.packages, {
        package_id: +package_id
      }) || {}
    ),
  getPkgInfos2getters: (state) => state.pkgInfos || {},
  getActItemData2getters: (state) => state.actItemData || [],
  getAllvariables2getters: (state) => state.allVariables || [],
  getCategoryInfo2getters: (state) => state.categoryInfo,
  getActStepStatus: (state) => state.actStepStatus,
  getPublishedcCategoryList: (state) => state.publishedcCategoryList,
  getLanguage: (state) => state.language,
  // other info
  getAllLanguagesOpts: (state) => state.allLanguages,
  getCountryOpts: (state) => state.countries,
  getOtherInfoGroupItemById: (state) => (id) =>
    _.get(state.groupItems.data, id, []),
  getOtherInfoGroupTitleById: (state) => (id) =>
    _.get(state.titleList.data, id, []),

  // 价格日历
  pkgList: (state) => (state.pkgInfos || {}).package_info || [],
  unitList: (state) => state.packageSkuData.units || [],
  mainUnit: (_, getters) => (getters.unitList || [])[0] || null,

  // taxonomy picker
  getAllCountries: (state) => state.allCountries || [],

  //
  getGroupsSummary: (state) => state.groupsSummary || [],
  //
  getBeforeEachInterceptor: (state) => state.beforeEachInterceptor || []
}

export default getters
