<template>
  <div
    class="compound-option"
    :class="{
      [`is-${type}`]: true,
      'is-selected': chosen,
      'is-edit': isEdit
    }"
  >
    <component
      :is="multi ? 'a-checkbox' : 'a-radio'"
      v-if="isText || isTemplate"
      :key="`option${option.id}`"
      :label="label"
      name="option"
      :disabled="option.is_free_text || readonly"
      class="compound-option__label dd"
      :value="`${option.id}${option.order || '0'}`"
    />
    <!-- text -->
    <div class="compound-option__content">
      <p v-if="isText">{{ option.value }}</p>

      <compose
        v-else-if="isTemplate"
        ref="composes"
        v-model="currentData"
        :ui_var_map="ui_var_map"
        :ref_data="ref_option_data"
        :label="option.value"
        :chosen="chosen"
        :option="option"
        v-bind="$attrs"
      />
      <div v-else class="freetext-option">
        <template v-if="isAdd">
          <nobreak-textarea
            v-if="isAdd"
            v-model.trim="addValue"
            :disabled="readonly"
            autofocus
            @keyup.enter.native="confirmAdd"
          />
        </template>

        <template v-else>
          <p v-if="isEN && !isENDraft">{{ option_data.value }}</p>
          <nobreak-textarea
            v-else
            v-model.trim="edit_value"
            autofocus
            :disabled="readonly"
          />
        </template>
        <a-input
          v-if="option_data && ref_lang"
          v-model.trim="ref_data.value"
          type="textarea"
          disabled
        />
      </div>
    </div>
    <slot name="operation" />

    <!-- icons -->
    <div
      v-if="
        !isAdd &&
          chosen &&
          ((copyTimes > 0 &&
            multi &&
            !option.is_free_text &&
            option.type !== 1) ||
            (option.is_free_text && !isEdit))
      "
      class="compound-option__icons"
    >
      <a-tooltip placement="top" title="Duplicate">
        <i
          v-if="
            !!(
              copyTimes > 0 &&
              multi &&
              !option.is_free_text &&
              option.type !== 1
            )
          "
          @click.stop.prevent="copyItem"
        >
          <a-icon
            type="copy"
            class="group-action-btn copy"
            size="small"
            :alt="$t('global_copy')"
          />
        </i>
      </a-tooltip>

      <a-tooltip
        placement="top"
        :title="
          isPriorLang
            ? $t('delete_free_text_hint')
            : $t('delete_free_text_disabled')
        "
      >
        <a-popover
          v-if="!!(option.is_free_text && !isEdit)"
          placement="top"
          width="160"
          trigger="click"
        >
          <template slot="content">
            <p style="font-size: 16px">
              Are you sure to delete this freetext item?
            </p>
            <div style="text-align: right; margin: 0">
              <a-button type="primary" size="default" @click="removeItem">
                {{ $t('confirm') }}
              </a-button>
            </div>
          </template>

          <a-button
            type="link"
            size="small"
            class="compound-option__btn is-delete"
            :disabled="!isPriorLang"
          >
            <a-icon type="delete" />
          </a-button>
        </a-popover>
      </a-tooltip>
    </div>
    <!-- 添加时的保存按钮 -->
    <div v-if="isAdd || isEdit" class="compound-option__btns">
      <a-space size="small">
        <a-button
          v-if="isAdd"
          type="primary"
          size="small"
          :disabled="!addValue"
          @click.stop.prevent="confirmAdd"
          >{{ $t('global_button_save') }}</a-button
        >
        <a-button
          v-if="isEdit"
          type="primary"
          size="small"
          :disabled="!edit_value"
          @click.stop.prevent="confirmEdit"
        >
          {{ $t('global_button_save') }}
        </a-button>
        <a-button
          v-if="!option_data || isEdit"
          size="small"
          @click.stop.prevent="cancelAdd"
        >
          {{ $t('global_button_cancel') }}
        </a-button>
      </a-space>
    </div>
  </div>
</template>

<script>
import { NobreakTextarea } from '../../../index'
import Compose from './Compose.vue'
import DuplicateSvg from '../svg/duplicate-svg.vue'
/**
 * 属性值 - 即单选 or 多选选项
 * 当前属性值只可以复制, 不可以删除 or 编辑
 */

let typeStruct = ADMIN_API.struct.interface({
  id: 'number',
  order: 'number?', // TODO options can duplicate ?
  type: 'number?',
  value: 'string',
  variable_map: 'undefined|object'
})
export default {
  name: 'CompoundOption',
  inject: ['ref_lang', 'edit_lang', 'locale_info'],
  componentName: 'CompoundOption',
  components: {
    NobreakTextarea,
    DuplicateSvg,
    Compose
  },
  props: {
    ref_data: Object,
    label: {
      type: String
    },
    chosen: {
      type: Boolean
    },
    // 顺序
    index: {
      type: Number
    },
    // 类型, 三种
    type: {
      type: String,
      default: 'text' // freetext or text or template or add
    },
    // 复杂类型配置
    option: {
      type: Object,
      default: () => {}
    },
    // 多选 or 单选
    multi: {
      type: Boolean,
      default: false
    },
    option_data: {
      type: Object
    },
    // 复制次数
    copyTimes: {
      type: Number,
      default: 0
    },
    // 总数
    total: {
      type: Number,
      default: 0
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      addValue: '',
      isEdit: false,
      edit_value: (this.option_data || {}).value
      // tempData: []
    }
  },
  computed: {
    isPriorLang() {
      return this.isOnlyLang || this.isEN
    },
    isOnlyLang() {
      return this.locale_info.react_data.filter((v) => v.status).length == 1
    },
    isENDraft() {
      let langStatus = { preview: 2 }

      return (
        _.get(
          this.locale_info.react_data.find((v) => v.language == 'en_US'),
          'status',
          0
        ) <= langStatus.preview
      )
    },
    isEN() {
      return this.edit_lang == 'en_US'
    },
    ref_option_data() {
      return this.ref_data.variable_map || {}
    },
    currentData: {
      get() {
        // this.tempData.push(this.option_data.variable_map);
        return this.option_data.variable_map || {}
      },
      set(v) {
        let result = typeStruct({
          ..._.cloneDeep(this.option),
          variable_map: v
        })
        this.$emit('change', result)
      }
    },
    ui_var_map() {
      if (this.option.type == 2) {
        let template_str = this.option.value
        let var_arr = template_str.match(/{{.+?}}/g) || []
        let sortted_keys = _.sortBy(
          Object.keys(this.option.variable_map),
          (v) => var_arr.findIndex((vv) => vv == v)
        )

        return sortted_keys.map((k) => this.option.variable_map[k])
      } else {
        return []
      }
    },
    // add
    isAdd() {
      return this.type === 'add'
    },
    // template or text
    isText() {
      let bool =
        // FIXME
        this.type != 'add' &&
        !this.option.is_free_text &&
        _.isEmpty(this.option.variable_map)
      return bool
    },
    // // isFreetext
    // isFreetext() {
    //   return this.type === "freetext";
    // },
    // isTemplate
    isTemplate() {
      // FIXME
      return this.type != 'add' && !_.isEmpty(this.option.variable_map)
    }
  },
  watch: {
    edit_value(next, old) {
      if (next === this.option_data.value) {
        this.isEdit = false
      } else {
        this.isEdit = true
      }
    }
  },
  methods: {
    async validate() {
      let composes = this.$refs.composes
      let valid = true
      if (composes && composes.chosen) {
        valid = await composes.validate()
      }
      return valid
    },
    // 拷贝
    copyItem() {
      let data = _.cloneDeep(this.option_data)
      if (!_.isEmpty(data.variable_map)) {
        Object.keys(data.variable_map).forEach((key) => {
          data.variable_map[key].values = []
        })
      }

      this.$emit('copy', data)
      this.$message.success('Successfully copy.')
    },
    // 删除
    removeItem() {
      this.$emit('remove', this.option)
    },
    // 保存
    confirmEdit() {
      this.$emit('confirm-edit', {
        ...this.option_data,
        value: this.edit_value
      })
      this.isEdit = false
    },
    // 确认添加
    confirmAdd() {
      if (!this.addValue) {
        return
      }
      if (_.isEmpty(this.option_data)) {
        this.$emit('confirm-add', {
          create: true,
          is_free_text: true,
          value: this.addValue
        })
      } else {
        this.$emit('confirm-add', {
          ...this.option_data,

          value: this.addValue
        })
      }
    },
    // 取消添加
    cancelAdd() {
      if (this.isEdit) {
        this.edit_value = (this.option_data || {}).value
      }
      this.$emit('cancel-add')
    }
  }
}
</script>

<style lang="scss">
// 选项边框
$optionBorderColor: #d0e6fe;
// 内容lineheight
$contentHeight: 25px;
// 操作按钮宽度
$handlerBtnWidth: 60px;
// 操作按钮颜色
$handlerBtnColor: #107afbb7;

$borderRadius: 4px;

/*.compound-option__label {*/
/*align-self: start;*/
/*}*/
// 外层
.compound-select .compound-option {
  display: flex;
  width: 100%;
  min-width: 520px;
  align-items: center;
  border-radius: $borderRadius;
  background: white;
  cursor: pointer;
  transition: all 0.2s;
  // 内容
  .compound-option__label {
    margin-right: 12px;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.85);
    line-height: 20px;
    .el-radio__label,
    .ant-radio + span,
    .el-checkbox__label,
    .ant-checkbox + span {
      display: none;
    }
  }
  // 内容
  .ant-radio-input {
    margin-top: 5px;
  }
  .compound-option__content {
    padding: 8px;
    /*margin-left: 4px;*/
    background-color: #fafafa;
    border-radius: $borderRadius;
    border: 1px dashed transparent;
    transition: all 0.6s;
  }
  &:hover .compound-option__content {
    border-color: #ccc;
  }
  // 添加输入框撑满
  &.is-add {
    display: block;
    overflow: hidden;
    margin-top: 12px;

    .compound-option__content {
      width: 100%;
    }
  }
  &.is-edit {
    display: block !important;
    overflow: hidden !important;
    padding: 15px !important;

    .compound-option__content {
      width: 100%;
    }
  }

  // 文字
  &__content {
    // 内容
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    // 处理文字长度超出
    word-break: break-word;
    p {
      line-height: $contentHeight;
      min-height: $contentHeight;
      white-space: normal;
      word-break: break-all;
      margin: 0;
      font-size: 13px;
    }
  }

  // 操作按钮样式
  &__icons {
    display: inline-block;
    margin-left: 12px;
    vertical-align: middle;
    opacity: 0;
    text-align: center;
    position: relative;
    left: 3px;
    transition: all 0.2s;
    color: $handlerBtnColor;
    cursor: pointer;
    font-size: 15px;
    font-weight: 400;
  }
  // 覆盖
  .compound-option__icons {
    line-height: 1;
    opacity: 1;
    left: 0;
    transition: all 0.3s;
    & > i:hover {
      cursor: pointer;
      color: lighten($handlerBtnColor, 10%);
    }
    & > i.el-icon-delete {
      &:hover {
