      }
    }
  }
  // 具体的按钮
  &__btn {
    margin-left: 6px;
    transition: all 0.3s;
    border-radius: 50%;
    &.is-delete {
      color: #ff4d4f;
    }
    &:hover {
      background-color: #fafafa;
    }
  }
  // 保存按钮
  &__btns {
    margin-top: 10px;
    float: right;
    display: inline-block;
  }
  .freetext-option {
    .ant-input + .ant-input,
    .ant-input + textarea.ant-input {
      margin-top: 10px;
    }
    ::v-deep textarea.ant-input {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &.is-selected {
    .compose {
      .variable {
        color: #27aa7a;
      }
      .variable-complete {
        color: #16aa77;
        &.variable-input {
          background-color: rgba(198, 241, 221, 0.3);
        }
      }
      .variable-undone {
        color: #e64340;
        &.variable-input {
          background-color: rgba(255, 210, 210, 0.3);
        }
      }
      .ant-form-item-children {
        width: 100%;
