<template>
  <div class="other-info-item-container">
    <a-form-model
      ref="form"
      :model="form"
      class="other-info-item__form"
      :label-col="{ span: 6 }"
      :wrapper-col="{ span: 16 }"
    >
      <template v-if="$attrs.headmost">
        <slot name="groupName">
          <label class="other-info__groupName">
            {{ group.group_name }}
            <span v-if="group.tips" class="--tips">
              {{ group.tips }}
            </span>
          </label>
        </slot>

        <template v-if="group.is_multi">
          <a-button
            v-if="!$attrs.polish"
            type="link"
            icon="plus"
            class="btn-groupAdd"
            :disabled="!!$attrs.disabled"
            @click="$emit('addGroup', group.group_id)"
          >
            {{ $t('global_add') }}
          </a-button>
        </template>
      </template>

      <div class="other-info--formItemContainer">
        <div
          class="--formItem"
          :class="{
            stripe: $attrs.stripe
          }"
          :style="`margin-top: ${$attrs.headmost ? '42px' : '12px'}`"
        >
          <div
            v-if="$attrs.decrease && !$attrs.polish"
            class="group--del-container"
          >
            <a-button
              type="text"
              class="--del"
              :disabled="!!$attrs.disabled"
              @click="$emit('deleteGroup', $attrs.index)"
            >
              {{ $t('global_delete') }}
            </a-button>
          </div>

          <a-form-model-item
            v-for="(field, index) in fields"
            :key="index"
            :prop="field.key"
            :label="field.name"
            :rules="{
              required: field.style.required,
              trigger: ['blur', 'change'],
              validator: (rule, value, callback) =>
                validator(group, field, rule, field.value, callback)
            }"
          >
            <BasicFormPopover
              :message="field.hover"
              :auto-adjust-overflow="false"
              trigger="hover"
              placement="right"
            >
              <!-- select -->
              <template v-if="field.style.type === 1">
                <template v-if="field.options.type === 7">
                  <number-range
                    :items="field.options.items"
                    :value.sync="field.value"
                    :disabled="$attrs.polish || !!$attrs.disabled"
                  />
                </template>
                <template v-else>
                  <lazy-long-select
                    v-model="field.value"
                    opt-value-key="value"
                    opt-label-key="label"
                    class="lazy-long-select"
                    :class="{
                      '--disabled': $attrs.polish || !!$attrs.disabled
                    }"
                    :select-hint="$t('type_to_search')"
                    :ops-length="20"
                    :remote="getItemOpts(field, group).length > 300"
                    :multiple="Boolean(field.style.multiple_selection)"
                    :full-opts="getItemOpts(field, group)"
                    :disabled="$attrs.polish || !!$attrs.disabled"
                    @change="(v) => handleChange(v, field)"
                  >
                    <!-- type 0:无ALL选项; 1:ALL的值是 int; 2:ALL的值是字符串 3:ALL后端特殊处理 -->
                    <template
                      v-if="
                        field.options.all_value && field.options.all_value.type
                      "
                      #headerOptions
                    >
                      <a-select-option :value="field.options.all_value.value">
                        All
                      </a-select-option>
                    </template>
                  </lazy-long-select>
                </template>
              </template>
              <!-- text -->
              <template v-else-if="field.style.type === 2">
                <a-input
                  v-if="field.options.type === 6"
                  v-model="field.value.content"
                  :disabled="!!$attrs.disabled"
                />
                <a-input
                  v-else
                  v-model="field.value"
                  :disabled="!!$attrs.disabled"
                />

                <template
                  v-if="
                    $attrs.reference &&
                      getReference($attrs.reference, group.group_id, field)
                  "
                >
                  <a-input
                    style="margin-top: 6px"
                    :value="
                      getReference($attrs.reference, group.group_id, field)
                    "
                    :disabled="true"
                  />
                </template>
              </template>

              <!-- checkbox -->
              <template v-else-if="field.style.type === 14">
                <component
                  :is="
                    field.style.multiple_selection
                      ? 'a-checkbox-group'
                      : 'a-radio-group'
                  "
                  v-model="field.value"
                >
                  <component
                    :is="
                      field.style.multiple_selection ? 'a-checkbox' : 'a-radio'
                    "
                    v-for="(item, idx) in getItemOpts(field, group)"
                    :key="idx"
                    :value="item.value"
                    :disabled="$attrs.polish || !!$attrs.disabled"
                  >
                    {{ item.label }}
                  </component>
                </component>
              </template>
            </BasicFormPopover>
          </a-form-model-item>
        </div>
      </div>
    </a-form-model>
  </div>
</template>

<script>
import LazyLongSelect from '@activity/components/LazyLongSelect.vue'
import NumberRange from '@activity/pages/components/NumberRange.vue'
import BasicFormPopover from '@activity/pages/components/BasicFormPopover.vue'

export default {
  name: 'OtherInfoItem',
  components: { LazyLongSelect, NumberRange, BasicFormPopover },
  inheritAttrs: false,
  model: {
    value: 'form',
    event: 'change'
  },
  props: {
    group: {
      type: Object,
      required: true
    },
    fields: {
      type: Array,
      required: true
    },
    validator: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      form: {},
      groupItems: {},
      titleList: {}
    }
  },
  async beforeMount() {
    this.form = new Proxy(
      {},
      {
        get: (target, prop, receiver) => {
          let current = _.find(this.fields, {
            key: prop
          })

          return (current && current.value) || Reflect.get(target, prop)
        }
      }
    )

    this.$on('getGroupItems', async function(group_id) {
      let options = await this.$store.dispatch('actionGetGroupItemById', {
        group_id
      })
      this.$set(this.groupItems, group_id, options)
    })

    this.$on('getTitleList', async function(group_id) {
      let options = await this.$store.dispatch('actionGetTitleListById', {
        group_id
      })
      this.$set(this.titleList, group_id, options)
    })
  },
  methods: {
    getReference(reference, group_id, langField) {
      let group = _.find(reference.groups, {
        group_id
      })
      if (!group) {
        return ''
      }
      let field = _.find(group.style.fields || [], {
        key: langField.key
      })

      return field && field.options.type === 6
        ? field.value.content
        : field.value || ''
    },
    validateForm() {
      return new Promise((resolve) =>
        this.$refs.form.validate((valid) => resolve(valid))
      )
    },
    handleChange(val, field) {
      let allValue = field.options.all_value.value
      if (Array.isArray(val)) {
        // multi-select
        if (val.length > 1) {
          if (val[0] === allValue) {
            // Should be delete allValue.
            field.value.splice(0, 1)
          } else if (val[val.length - 1] === allValue) {
            // The newly selected option value is allValue, should clear other values
            field.value = [allValue]
          }
        }
        if (val.length === 0) {
          if (field.options.all_value.type) {
            // has all-option
            field.value = [allValue]
          }
        }
      }
    },
    getItemOpts(field, group) {
      let { options } = field

      switch (options.type) {
        case 1:
          return this.$attrs.languages
        case 2:
          return this.$attrs.countries
        case 3: {
          let opts = _.get(this.groupItems, group.group_id, null)
          if (!opts) {
            this.$set(this, 'groupItems', {
              ...this.groupItems,
              [group.group_id]: []
            })
            this.$emit('getGroupItems', group.group_id)
          }
          return this.groupItems[group.group_id]
        }
        case 4: {
          let opts = _.get(this.titleList, group.group_id, null)
          if (!opts) {
            this.$set(this, 'titleList', {
              ...this.titleList,
              [group.group_id]: []
            })
            this.$emit('getTitleList', group.group_id)
          }
          return this.titleList[group.group_id]
        }
        case 5: {
          return options.items.map((item) => ({
            value: item.key,
            label: item.name
          }))
        }
      }
    }
  }
}
</script>

