import Vue from 'vue'
import { getBLanguage } from '@activity/utils'
import { isAdmin } from '@/env'
const actions = {
  async updateActItemData2actions(
    { commit, state },
    { activity_id, refresh = false } = {}
  ) {
    if (!activity_id) return
    if (refresh || JSON.stringify(state.actItemData) === '{}') {
      let data = await ajax.post(ADMIN_API.act.filter_activity, {
        data: {
          keyword: '' + activity_id
        }
      })
      if (data && data.items && data.items[0]) {
        let obj = data.items[0]
        commit('setActItemData2mutations', obj)
      }
    }
    return state.actItemData
  },
  async getActLang({ commit, state }, { activity_id, refresh = false } = {}) {
    if (refresh || JSON.stringify(state.language) === '{}') {
      let result = await ajax.get({
        url: ADMIN_API.act.get_activity_basic_info,
        params: {
          activity_id // in query, required, 活动ID,
        }
      })
      let obj = {
        language: result.pub_languages
      }
      commit('setLanguage', obj)
    }
    return state.language
  },
  async updateActStepStatus2action({ dispatch }, data) {
    await ajax.post(
      ADMIN_API.act.save_activity_step_status,
      {
        data
        // data: {
        //   activity_id: 1, // 活动 ID,
        //   language: 'string', // 语言, ALL表示所有语言,
        //   status: 1, // 状态：0-未完成 1-已完成,
        //   step: 'string' // Step 的编码,
        // }
      },
      {
        loading: data.loading
      }
    )
    dispatch('updateActivityStepStatusById', {
      ...data,
      refresh: true
    })
  },
  async updatePkgStepStatus2action({ dispatch }, data) {
    // 上传保存状态
    await ajax.post(ADMIN_API.act.save_package_step_status, {
      data
      // data: {
      //   activity_id: +this.activityId,
      //   package_id: +this.packageId,
      //   status: 1,
      //   step: 'extra_information',
      //   language: klook.getEditLang()
      // }
    })
    dispatch('updatePackageStepStatusById', {
      ...data,
      refresh: true
    })
  },
  async updatePackageStepStatusById(
    { state, commit },
    { package_id, language, refresh = false }
  ) {
    if (refresh || JSON.stringify(state.pkgStepStatus) === '{}') {
      let result = (
        (await ajax.get(ADMIN_API.act.get_package_step_status, {
          params: {
            package_id,
            language: language === 'ALL' ? klook.getEditLang() : language
          }
        })) || []
      ).reduce(
        (acc, curr) => ({
          ...acc,
          [curr.step]: curr.status
        }),
        {}
      )
      commit('setPkgStepStatus2mutations', result)
    }
    return state.pkgStepStatus
  },
  async updateActivityStepStatusById(
    { state, commit },
    { activity_id, language, refresh = false }
  ) {
    if (refresh || JSON.stringify(state.actStepStatus) === '{}') {
      let result = (
        (await ajax.get(ADMIN_API.act.get_activity_step_status, {
          params: {
            activity_id,
            language: language === 'ALL' ? klook.getEditLang() : language
          }
        })) || []
      ).reduce(
        (acc, curr) => ({
          ...acc,
          [curr.step]: curr.status
        }),
        {}
      )
      commit('setActStepStatus', result)
    }
    return state.actStepStatus
  },
  //查询 层级类目信息
  async getActPublishedCategoryInfo2action({ state, commit }, data = {}) {
    if (data.refresh || !state.publishedcCategoryList.length) {
      let result = await ajax.get(ADMIN_API.act.get_published_category_info)
      if (!result) return false
      let newResult = result.map((item) => {
        let obj = {
          value: item.id,
          label: item.name
        }
        item.subcategory_list &&
          item.subcategory_list.forEach((leaf) => {
            leaf.value = leaf.id
            leaf.label = leaf.name
          })
        return _.merge({}, item, obj)
      })
      commit('setPublishedCategoryList', newResult)
    }
    return state.publishedcCategoryList
  },
  //查询 层级类目信息
  async getActCategory2action({ state, commit }, data = {}) {
    if (data.refresh || !state.categoryInfo) {
      let result = await ajax.get(ADMIN_API.act.get_activity_category_id, {
        params: {
          activity_id: +data.activity_id
        }
      })
      if (!result) return false
      commit('setCategoryInfo', result)
    }
    return state.categoryInfo
  },
  async getActVariables2actions(
    { state, commit },
    data = { attr_id_list: '', language: getBLanguage() }
  ) {
    if (data.refresh || !state.allVariables.length) {
      let result = await ajax.get(ADMIN_API.act.get_variable_list, {
        cache: true,
        params: {
          attr_id_list: data.attr_id_list,
          language: data.language
        }
      })
      commit('setAllVariables2mutations', result)
    }
    return state.allVariables
  },

  // 活动下所有的套餐信息
  async actionGetPosition({ commit }, { activityId }) {
    const position =
      (await ajax.get(ADMIN_API.act.get_activity_position, {
        params: {
          activity_id: activityId
        }
      })) || {}

    commit('setPosition', position)
    return position
  },
  // 查询活动套餐列表 get
  async getPkgInfo2actions(
    { commit, state },
    { activity_id, custom_language, only_audit_package, refresh = false } = {}
  ) {
    if (refresh || !state.pkgInfos) {
      let data =
        (await ajax.get(ADMIN_API.act.get_activity_packages_info, {
          params: {
            activity_id,
            custom_language,
            only_audit_package,
            page_from:
              isAdmin && klook.getUrlQuery('review') ? 'bd_audit' : undefined
          }
        })) || {}

      commit('setPkgInfos2mutations', data)
    }
    return state.pkgInfos
  },
  // 活动下所有的套餐信息
  async actionGetAllPackages({ commit }, { activityId }) {
    const packages =
      (
        await ajax.get(ADMIN_API.act.get_activity_packages_info, {
          params: {
            activity_id: activityId,
            custom_language: klook.getEditLang(),
            page_from:
              isAdmin && klook.getUrlQuery('review') ? 'bd_audit' : undefined
          }
        })
      ).package_info || []

    // 根据 package_id 和 publish_status 进行排序
    const sortedPackages = _.sortBy(packages, 'package_id')
    const allPackages = [
      ...sortedPackages.filter((v) => v.publish_status === 1),
      ...sortedPackages.filter((v) => !v.publish_status !== 1)
    ]

    commit('setAllPackages', allPackages)
    return allPackages
  },

  // other info data
  actionGetTaxonomyCountries: _.debounce(
    async (
      { commit, state },
      {
        refresh = false,
        language = getBLanguage(),
        include_not_show_country = 1
      } = {}
    ) => {
      if (refresh || !state.countries.length) {
        const countries =
          (await ajax.get(ADMIN_API.act.get_all_countries, {
            params: {
              include_not_show_country,
              language: klook.getEditLang()
            }
          })) || []

        commit(
          'saveCountries',
          countries.map((item) => ({
            label: item.full_name,
            value: item.id
          }))
        )
      }

      return state.countries
    },
    200
  ),
  actionGetAllLanguage: _.debounce(
    async (
      { commit, state },
      { refresh = false, language = getBLanguage() } = {}
    ) => {
      if (refresh || !state.allLanguages.length) {
        const allLanguages =
          (await ajax.get(ADMIN_API.act.get_all_language, {
            params: {
              language: klook.getEditLang()
            }
          })) || []

        commit(
          'saveAllLanguages',
          allLanguages.map((item) => ({
            label: item.content,
            value: item.id
          }))
        )
      }

      return state.allLanguages
    },
    200
  ),

  // other info options
  async actionGetGroupItemById({ getters, state }, { group_id }) {
    let data = getters.getOtherInfoGroupItemById(group_id)
    if (data.length) {
      return data
    }
    await this.dispatch('actionGetGroupItemByIds', {
      group_id_list: [group_id]
    })

    return getters.getOtherInfoGroupItemById(group_id)
  },
  async actionGetGroupItemByIds(
    { commit, state },
    { group_id_list, language = getBLanguage() } = {}
  ) {
    if (
      _.intersection(group_id_list, state.groupItems.group_id_list).length ===
      group_id_list.length
    ) {
      return state.groupItems.data
    }

    const result =
      (await ajax.get(ADMIN_API.act.query_group_item_by_parent_ids, {
        params: {
          group_id_list: group_id_list.join(),
          language: klook.getEditLang()
        }
      })) || []

    Vue.set(state, 'groupItems', {
      group_id_list: _.uniq([
        ...state.groupItems.group_id_list,
        ...group_id_list
      ]),
      data: {
        ...state.groupItems.data,
        ...result.reduce((acc, curr) => {
          return {
            ...acc,
            [curr.parent_id]: curr.group_items.map((item) => {
              return {
                value: item.group_id,
                label: item.group_name
              }
            })
          }
        }, {})
      }
    })

    return state.groupItems.data
  },
  async actionGetTitleListById({ getters, state }, { group_id }) {
    let data = getters.getOtherInfoGroupTitleById(group_id)
    if (data.length) {
      return data
    }
    await this.dispatch('actionGetTitleListByIds', {
      group_id_list: [group_id]
    })

    return getters.getOtherInfoGroupTitleById(group_id)
  },
  async actionGetTitleListByIds(
    { commit, state },
    { group_id_list, language = getBLanguage() } = {}
  ) {
    if (
      _.intersection(group_id_list, state.titleList.group_id_list).length ===
      group_id_list.length
    ) {
      return state.titleList.data
    }

    const result =
      (await ajax.get(ADMIN_API.act.query_title_list_by_group_ids, {
        params: {
          group_id_list: group_id_list.join(),
          language: klook.getEditLang()
        }
      })) || []

    Vue.set(state, 'titleList', {
      group_id_list: _.uniq([
        ...state.titleList.group_id_list,
        ...group_id_list
      ]),
      data: {
        ...state.titleList.data,
        ...result.reduce((acc, curr) => {
          return {
            ...acc,
            [curr.group_id]: curr.title_items.map((item) => {
              return {
                label: item.title_name,
                value: item.title_id
              }
            })
          }
        }, {})
      }
    })

    return state.titleList.data
  },

  // 价格日历
  async actionGetPackageSkuData({ commit }, { packageId }) {
    const data = await ajax.get(ADMIN_API.act.query_package_sku_list, {
      params: {
        package_id: packageId,
        language: klook.getEditLang()
      }
    })
    commit('setPackageSkuData', data)
    return data
  }
}

export default actions
