<template>
  <div class="act-layout">
    <div class="act-layout-box">
      <div class="act-nav-wrap">
        <actNav />
      </div>
      <div class="act-main-wrap">
        <div class="main-top-box">
          <actInfo v-show="isActivity" v-bind="actGlobal" />
          <pkgInfo v-show="!isActivity" :categoryInfo="actGlobal.pkgInfo" />
        </div>
        <div class="main-body-box">
          <div class="main-content-wrap">
            <div v-if="isReloadShow">
              <keep-alive>
                <router-view
                  v-if="$route.meta.keepAlive"
                  :actGlobal="actGlobal"
                />
              </keep-alive>
              <router-view
                v-if="!$route.meta.keepAlive"
                :actGlobal="actGlobal"
              />
            </div>
          </div>
          <div v-if="!$route.meta.hideTimeline" class="floor-timeline-wrap">
            <floorTimeline />
          </div>
        </div>
      </div>
    </div>
    <div v-if="!$route.meta.hideSave" class="common-btns-wrap">
      <div class="common-btns-box">
        <a-button
          v-if="actGlobal.categoryInfo.activity_title"
          @click="handlerPreview"
          >{{ $t('global_preview') }}</a-button
        >
        <a-button
          v-if="!$root.nonEdit2act && !$root.nonEdit2status"
          type="primary"
          @click="saveThisSection('save')"
          >{{ $t('global_save') }}</a-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import actNav from '@activity/components/actNav'
import actInfo from '@activity/components/actInfo'
import pkgInfo from '@activity/components/pkgInfo'
import floorTimeline from '@activity/components/floorTimeline'
import { checkAuth } from '@/plugins/authDirective'
// import { isAdmin } from '@/env'

export default {
  components: { actNav, pkgInfo, actInfo, floorTimeline },
  provide() {
    return {
      getActGlobal2provide: () => this.actGlobal,
      reloadPage2provide: () => {
        this.reloadPage()
      }
    }
  },
  computed: {
    ...mapState({
      pkgInfos: 'pkgInfos',
      pkgStepStatus: 'pkgStepStatus'
    }),
    packageId() {
      return klook.parse2id(this.$route.query.package_id)
    },
    isActivity() {
      return this.$route.path.includes('/act/activity/')
    }
  },
  data() {
    return {
      actGlobal: {
        activity_id: +this.$route.params.id,
        categoryInfo: {
          // activity_id: 35704,
          // leaf_category_id: 338,
          // sub_category_id: 1,
          // template_id: 1
        },
        categoryArr: [],
        pkgInfo: {}
      },
      isReloadShow: true
    }
  },
  async created() {
    klook.bus
      .$off('addHandleSave2bus')
      .$on('addHandleSave2bus', this.addHandleSave2bus)
    klook.bus
      .$off('initQueryData2bus')
      .$on('initQueryData2bus', this.initQueryData)
    await this.initQueryData()
    await this.get_published_category_info()
  },
  watch: {
    '$route.path'() {
      this.checkStatusLock()
    },
    packageId(packageId, oldPkg) {
      this.getPkgData()
      packageId && packageId !== oldPkg && this.reloadPage()
    },
    'actGlobal.categoryInfo': {
      handler(obj) {
        this.checkStatusLock()
      },
      deep: true
    },
    'actGlobal.pkgInfo': {
      handler(obj) {
        this.checkStatusLock()
      },
      deep: true
    }
  },
  methods: {
    ...mapActions([
      'updateActItemData2actions',
      'getActPublishedCategoryInfo2action'
    ]),
    checkStatusLock() {
      let obj = this.isActivity
        ? this.actGlobal.categoryInfo
        : this.actGlobal.pkgInfo
      // 目前兼容了活动和套餐状态
      if (
        klook.getUrlQuery('review') ||
        (checkAuth('edit') && [1, 3].includes(obj.approval_status))
      ) {
        // 全局锁优先级大于状态锁
        this.$root.nonEdit2status = true
      } else {
        this.$root.nonEdit2status = !checkAuth('edit')
      }
    },
    reloadPage() {
      this.isReloadShow = false
      this.$nextTick(() => {
        this.isReloadShow = true
      })
    },
    addHandleSave2bus(callback = () => {}) {
      this.handleSave2bus = callback
    },
    async initQueryData() {
      let { actGlobal } = this
      let obj =
        (await this.updateActItemData2actions({
          activity_id: actGlobal.activity_id,
          refresh: true
        })) || {}
      this.checkRoute(obj)
      this.$set(actGlobal, 'categoryInfo', _.merge(obj, obj.category))
      this.getPkgData()
    },
    async getPkgData() {
      let { actGlobal, packageId } = this
      if (packageId) {
        let result =
          (await ajax.post(ADMIN_API.act.filter_package, {
            data: {
              keyword: this.$route.query.package_id
            }
          })) || {}
        let obj = (result.items && result.items[0]) || {}
        this.$set(actGlobal, 'pkgInfo', _.merge(obj, obj.category))
      } else {
        this.$set(actGlobal, 'pkgInfo', {
          package_id: actGlobal.categoryInfo.package_id,
          package_title: actGlobal.categoryInfo.package_title,
          package_status: actGlobal.categoryInfo.package_status,
          approval_status: 0,
          ...actGlobal.categoryInfo.category
        })
      }
    },
    checkRoute(obj) {
      let { query, path } = this.$route
      let newQuery = _.merge({}, query)
      if (obj.language !== newQuery.lang) {
        newQuery.lang = obj.language
        this.$router.replace({
          path,
          query: newQuery
        })
      }
    },
    saveThisSection(...args) {
      let callback = this.handleSave2bus
      typeof callback === 'function' && callback(...args)
    },
    async get_published_category_info() {
      let { actGlobal } = this
      let result = await this.getActPublishedCategoryInfo2action()
      if (!result) return
      actGlobal.categoryArr = result
    },
    handlerPreview() {
      let preview_url = klook.getUrlByEnv('')
      let activity_id = this.$route.params.id
      let language = this.$route.query.lang
      let url = `${preview_url}/en-US/preview/activity/${activity_id}?lang=${language}`
      window.open(url)
    }
  }
}
</script>

