<!--
     this component is specifically used for upload image to cloudinary and so forth
     the API is to pass `public_id` and `format`  as `file_name` to backend with size info
     but this may change.

i.e., {
     "alt": "string",
     "description": "string",
     "file_name": "string",
     "width": 0,
     "height": 0,
     "flag": 0,
     "id": 0,
     }

   -->
<template>
  <div class="image-upload">
    <div class="el-upload-dragger" @click="beforeUpload">
      <template v-if="filename">
        <img :src="preview_url" />
        <i
          class="el-icon-close"
          v-if="hasCloseBtn"
          @click.stop="filename = ''"
        ></i>
        <div v-if="hasShareBtn" class="copy-svg" @click.stop="copyUrl">
          <a-tooltip
            class="item"
            effect="dark"
            placement="top-start"
            content="Copy URL to clipboard"
          >
            <copy-svg></copy-svg>
          </a-tooltip>
        </div>
      </template>
      <i v-else class="el-icon-upload"></i>
      <div class="el-upload__text">
        <em>click to upload</em>
        {{ placeholder }}
        <template v-if="width && height">
          {{ width }}px * {{ height }}px
        </template>
      </div>
      <div></div>
    </div>

    <a-input
      size="small"
      v-if="canInput"
      placeholder="Please input URL"
      v-model="preview_url"
    ></a-input>
  </div>
</template>

<script>
import CopySvg from '../svg/copy_link'
export default {
  name: 'image-upload',
  components: {
    CopySvg
  },
  install(Vue) {
    Vue.component(this.name, this)
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    hasCloseBtn: {
      type: Boolean,
      default: true
    },
    hasShareBtn: {
      type: Boolean,
      default: false
    },
    value: {
      type: String
    },
    folder: {
      type: String,
      default: 'banner'
    },
    canInput: {
      type: Boolean,
      default: false
    },
    width: {
      type: [Number, String]
    },
    height: {
      type: [Number, String]
    },
    placeholder: {
      type: String,
      default: 'png, jpg, jpeg up to 4 MB'
    },
    cloudinaryOptions: {
      type: Object
      /* default: {},*/
    },
    cropping: {
      type: Boolean,
      default: false
    },
    fullurl: {
      type: Boolean,
      default: false
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      file: {},
      hostname: 'https://res.klook.com/image/upload'
    }
  },
  methods: {
    copyUrl() {
      // const watermark = "/w_80,x_15,y_15,g_south_west,l_klook_water";
      let url = `${this.hostname}/${this.filename}`
      klook.copyToClipboard(url)
    },
    async beforeUpload() {
      if (this.readonly) {
        return
      }
      var [result, error] = await new Promise((resolve) =>
        window.cloudinary.openUploadWidget(
          Object.assign(
            {
              folder: 'banner',
              cloud_name: 'klook',
              multiple: false,
              upload_preset: 'k8xu3bkg',
              sources: ['local'],
              client_allowed_formats: ['png', 'gif', 'jpg', 'jpeg', 'svg'],
              resource_type: 'image',
              max_file_size: 4000000, // 4M
              max_image_width: 3000
            },
            this.cloudinaryOptions
          ),
          (error, result) => resolve([result, error])
        )
      )
      if (error) {
        console.error(error)
        return
      }
      this.file = result[0]
      this.$emit('uploaded', this.file)
    }
  },
  watch: {
    file: {
      deep: true,
      handler(file) {
        if (this.file.width > 1920) {
          this.file.width = 1920
        }
        // var width = this.width ? `w_${this.width}` : `w_${this.file.width}`;
        // var height = this.height ? `h_${this.height}` : "";

        // var params = `fl_lossy.progressive,q_85/c_fill,${width},${height}`;
        var filename = `${this.file.public_id}.${this.file.format}`
        // this.file.url.replace("/upload/", `/upload/${params}/`).replace('http://', 'https://')
        this.$emit(
          'change',
          this.fullurl ? file.url.replace('http', 'https') : filename
        )
      }
    },
    value: {
      immediate: true,

      handler(v) {
        // if (this.fullurl) {
        //   let file_name = v.match(/[^/]*\.[^.]+$/)[0];
        //   this.filename = `${this.folder ? `${this.folder}/` : ""}${file_name}`;
        //
        //
        //   console.log(this.filename);
        // } else {
        //   this.filename = v;
        // }
      }
    }
  },
  computed: {
    filename: {
      // bind `value`
      get() {
        if (this.fullurl) {
          if (this.value) {
            let file_name = this.value.match(/[^/]*\.[^.]+$/)[0]
            return `${this.folder ? `${this.folder}/` : ''}${file_name}`
          } else {
            return ''
          }
        } else {
          return this.value
        }
      },
      set(v) {
        let full = v ? `${this.hostname}${v}` : ''
        this.$emit('change', this.fullurl ? full : v)
      }
    },
    preview_url() {
      console.log(this.filename)
      return `${this.hostname}/w_400/${this.filename}` // NOTE this may change
    }
  }
}
</script>

