  max-width: 360px;
  .el-icon-close,
  .copy-svg {
    padding: 8px;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 2;
    :hover {
      filter: brightness(1.5); // NOT WORKING?
    }
  }
  img {
    max-width: 100%;
    height: 100%;
  }
  .info {
    position: absolute;
    bottom: 0;
    right: 0;
  }
  input {
    margin-top: 16px;
  }
}
</style>
