import Logquery from '@klook/logquery'
import { isProd, isMerchant } from '@/env'

const logquery = new Logquery({
  url: isProd
    ? 'https://log.klook.com/v2/frontlogsrv/log/web'
    : 'https://dev-frontsrv-new.dev.klook.io/v2/frontlogsrv/log/web',
  headers: { 'X-Platform': 'mobile' },
  timeout: 3000,
  queue: {
    interval: 5000,
    size: 15
  }
})

export const errorLogs = (error, info) => {
  const logObj = {
    timestamp: Date.now(),
    level: 'E',
    tag: 'client',
    message: {
      subtype: 'experiencesadmincommon:frontend:error_log',
      errorMessage: `${error.message} (${info})`,
      errorStack: error.stack,
      url: window.location.href,
      userAgent: window.navigator.userAgent,
      isMerchant: isMerchant.toString()
    }
  }

  logquery.general(logObj)
}
