import { photo_tags } from '@activity/common/const'
import xssFilters from 'xss-filters'
import { isMerchant } from '@/env'

const PhoneBox = require('./phoneBox/main').default

let uploadImage = {
  name: 'image',
  action: async function customFunction(editor, value) {
    var [result, error] = await new Promise((resolve) =>
      window.cloudinary.openUploadWidget(
        Object.assign({
          folder: 'admin-markdown',
          cloud_name: 'klook',
          multiple: false,
          upload_preset: 'k8xu3bkg',
          sources: ['local'],
          client_allowed_formats: ['png', 'jpg', 'jpeg', 'svg'],
          resource_type: 'image',
          max_file_size: 4000000, // 4M
          max_image_width: 3000,
          tags: [photo_tags.act_markdown]
        }),
        (error, result) => resolve([result, error])
      )
    )

    if (error) {
      // console.error(error)
      return
    }
    var cm = editor.codemirror
    var img = result[0]
    var imageName = img.original_filename
    var url = img.url.replace('http', 'https')
    var output = `![${imageName}](${url})`
    cm.replaceSelection(output)
  },
  className: 'fa fa-picture-o',
  title: 'Custom Button'
}

const previewRender = function(plainText, preview) {
  const commonmark = require('commonmark')
  const reader = new commonmark.Parser()
  const writer = new commonmark.HtmlRenderer()

  plainText = plainText.replace(
    /javascript:/g,
    encodeURIComponent('javascript:')
  )

  let parsed = reader.parse(xssFilters.inHTMLData(plainText))
  return writer.render(parsed)
}

let addPhoneCall = {
  name: 'phoneCall',
  className: 'fa fa-phone',
  action: function(editor) {
    let cm = editor.codemirror
    PhoneBox().then((result) => {
      let output = `[${result.prefix} ${result.phoneNumber}](tel:${result.prefix}${result.phoneNumber})`
      cm.replaceSelection(output)
    })
  }
}

var toolbar = [
  'bold',
  'heading',
  'unordered-list',
  'link',
  uploadImage,
  addPhoneCall,
  'preview'
]

var toolbarMerchant = [
  'bold',
  'heading',
  'unordered-list',
  addPhoneCall,
  'preview'
]

const common_defaults = {
  previewRender: previewRender,
  spellChecker: false,
  toolbar: isMerchant ? toolbarMerchant : toolbar,
  forceSync: true,
  status: false
}

export { common_defaults }
