<template>
  <a-form-model
    ref="form"
    :model="form"
    class="common-tpl-basic-style"
    :colon="false"
  >
    <!-- languages -->
    <a-form-model-item
      :label="$t('global_language')"
      prop="published_language"
      :rules="{
        required: true,
        message: 'Please fill this language'
      }"
    >
      <a-tooltip
        placement="right"
        overlayClassName="common-tooltip-style"
        :title="$t('language_selection_onboard')"
      >
        <a-select
          v-model="form.published_language"
          filterable
          :disabled="isCreated"
          :placeholder="$t('global_please_select')"
        >
          <a-select-option
            v-for="(name, value) in langObj"
            :key="value"
            :value="value"
            >{{ name }}</a-select-option
          >
        </a-select>
      </a-tooltip>
    </a-form-model-item>
    <slot />
    <!-- Destination Model Type -->
    <template v-if="showDestination">
      <tpl_region_binding_1
        v-if="templateType === 1"
        ref="region_binding"
        v-bind="form.area"
        :country-options="countryOptions"
        :city-options="cityOptions"
        :poi-options="poiOptions"
        :required="required"
        @change="changeAreaHandle"
      />

      <tpl_region_binding_2
        v-if="templateType === 2"
        ref="region_binding"
        v-bind="form.area"
        :country-options="countryOptions"
        :city-options="cityOptions"
        :poi-options="poiOptions"
        :required="required"
        @change="changeAreaHandle"
      />
    </template>
    <!-- 我是一条指引
    <div
      v-if="$attrs.description"
      class="description-wrap"
      style="margin-top: 6px"
    >
      {{ $attrs.description }}
    </div> -->
  </a-form-model>
</template>

<script>
import tpl_region_binding_1 from './tpl_region_binding_1'
import tpl_region_binding_2 from './tpl_region_binding_2'
// import tpl_region_binding_3 from "../components/tpl_region_binding_3";

const region_binding_mixin = {
  components: {
    tpl_region_binding_1,
    tpl_region_binding_2
    // tpl_region_binding_3
  },
  data() {
    return {
      countryOptions: [],
      cityOptions: [],
      poiOptions: []
    }
  },
  async created() {
    this.countryOptions = await ajax.get(ADMIN_API.act.get_all_countries, {
      params: {
        include_not_show_country: 1
      }
    })
    // 这里是最后所有数据加载完之后保存的一份数据.
    this.cacheForm = _.cloneDeep(this.form)
  },
  computed: {
    templateType() {
      return this.schemaConfig.model || null
    },
    validation() {
      return this.schemaConfig.validation || 3
    },
    showDestination() {
      return this.validation !== 3
    },
    required() {
      return this.validation === 1
    }
  },
  watch: {
    form: {
      handler(form) {
        this.$emit('formChange', form)
      },
      deep: true
    },
    'form.published_language': {
      immediate: true,
      deep: true,
      handler(v) {
        this.$emit('published_language', v)
      }
    },
    schemaConfig: {
      deep: true,
      immediate: true,
      async handler() {
        let result = this.result
        Object.assign(this.form, result)
        if (klook.urlParam('lang') && klook.getEditLang()) {
          this.form.published_language = klook.getEditLang()
        } else {
          this.form.published_language = klook.getAPILang(
            klook.getLocalStorage('language')
          )
        }
        this.getActivityBindingAreaFn()
      }
    }
  },
  methods: {
    changeAreaHandle(v) {
      Object.assign(this.form.area, v)
      this.$refs.form && this.$refs.form.validate()
    },
    async getActivityBindingAreaFn() {
      if (this.templateType === 2) {
        this.form.area.city_id = []
      }
      let result = this.result
      if (!result) return
      let { area_items, departure_items, poi_items } = result.binded_area

      switch (this.templateType) {
        case 1:
        case 2: {
          this.form.area.country_id = area_items.length
            ? (area_items[0].country && area_items[0].country.area_id) || ''
            : ''
          this.form.area.ids = poi_items.map((item) => item.area_id)
          this.poiOptions = poi_items.map((item) => ({
            id: item.area_id,
            name: item.area_name
          }))

          if (this.templateType === 1) {
            this.form.area.city_id = area_items.length
              ? (area_items[0].city && area_items[0].city.area_id) || ''
              : ''
          } else {
            this.form.area.city_id = area_items.map((item) => item.area_id)
            this.cityOptions = area_items.map((item) => ({
              id: item.area_id,
              name: item.area_name
            }))
            this.form.area.city_id_from = departure_items.length
              ? (departure_items[0].city && departure_items[0].city.area_id) ||
                ''
              : ''
            this.form.area.country_id_from = departure_items.length
              ? (departure_items[0].country &&
                  departure_items[0].country.area_id) ||
                ''
              : ''
          }
          break
        }
        case 3: {
          let node_type = area_items.map((item) => item.node_type)
          if (node_type.includes(0)) {
            this.form.area.level = 0
            this.form.area.continent = area_items.length
              ? area_items[0].area_id
              : ''
          } else {
            if (node_type.includes(2)) {
              this.$set(this.form, 'area', {
                ...this.form.area,
                level: 2,
                country_id: area_items.map((item) => item.area_id)
              })
            } else {
              this.$set(this.form, 'area', {
                ...this.form.area,
                level: 3,
                country_id: area_items.map((item) => item.country.area_id),
                city_id: area_items.map((item) => item.area_id)
              })
              this.cityOptions = area_items.map((item) => ({
                id: item.area_id,
                name: item.area_name
              }))
            }
          }
        }
      }
    },
    saveAreaData() {
      let data
      let {
        city_id,
        city_id_from,
        country_id,
        level,
        continent
      } = this.form.area
      if (this.templateType === 1) {
        data = [
          {
            area_id_list: [city_id],
            area_type: 1,
            departure_id_list: []
          }
        ]
      } else if (this.templateType === 2) {
        data = [
          {
            area_id_list: _.flatten([city_id]),
            area_type: 1,
            departure_id_list: _.flatten([city_id_from])
          }
        ]
      } else {
        let area_id_list = []
        switch (level) {
          case 0: {
            area_id_list = [continent]
            break
          }
          case 2: {
            area_id_list = _.flatten([country_id])
            break
          }
          case 3: {
            area_id_list = _.flatten([city_id])
            break
          }
        }
        data = [
          {
            area_id_list,
            area_type: 1,
            departure_id_list: []
          }
        ]
      }

      return data
    }
  }
}

export default {
  nameL: 'activity_basic_info_language_destination',
  inject: ['refreshPage'],
  mixins: [region_binding_mixin],
  props: {
    activity_id: {
      type: [Number, String],
      required: true
    },
    schemaConfig: {
      type: Object,
      default: () => {}
    },
    result: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      isRequired: {
        required: this.required,
        message: 'Please fill this form'
      },

      isSPU: this.$root.isSPU,
      isYSIM: this.$root.isYSIM,
      isHotel: this.$root.isHotel,

      destinations: [],

      form: {
        sales_channels: 0,
        published_language: undefined,
        dest_ids: [],
        area: {
          ids: [],
          country_id: '',
          city_id: '',

          country_id_from: '',
          city_id_from: '',

          level: '',
          continent: ''
        }
      },

      cacheForm: {}
    }
  },
  computed: {
    isCreated() {
      return !!this.$route.query.lang
    },
    langObj() {
      return this.isCreated
        ? lang_conf.getLangObj('B_LANG', 'LANG_TITLE')
        : lang_conf.getLangObjNoEn('B_LANG', 'LANG_TITLE')
    }
  },
  methods: {
    getName(locales) {
      return _.get(
        locales.find((v) => v.language == 'en_US'),
        'name',
        'Invalid Name'
      )
    },
    validateForm() {
      return new Promise((resolve) =>
        this.$refs.form.validate((valid) => resolve(valid))
      )
    },
    isChanged() {
      return _.isEqual(this.form, this.cacheForm)
    },
    async save() {
      // if (_.isEqual(this.form, this.cacheForm)) {
      //   // 如果数据没被修改，则不用提交。
      //   return
      // } else {
      //   // 修改的话，需要copy一下.
      //   this.cacheForm = _.cloneDeep(this.form)
      // }
      this.cacheForm = _.cloneDeep(this.form)
      await this.validateForm()

      if (
        !this.isCreated &&
        this.form.published_language.match('en') &&
        this.form.published_language !== 'en_US'
      ) {
        await this.$confirm({
          content: this.$t('act_create_en_hint')
        })
        klook.abort()
      }

      let form = {
        ...this.form,
        published_language: [this.form.published_language]
      }
      delete form.area

      let binded_areas
      if (this.showDestination) {
        binded_areas = this.saveAreaData()
      }
      return binded_areas
    }
  }
}
</script>
