<template>
  <div class="modal-antd">
    <div class="modal-antd-box">
      <a-modal v-bind="modalProps" v-on="modalProps.on">
        <template slot="title">
          {{
            $t(
              isAct
                ? 'merchant_submit_activity_action'
                : 'merchant_submit_package_action'
            )
          }}
        </template>
        <div class="modal-content-box">
          <div v-if="selectArr.length">
            <div v-if="isAct && selectArr.length" class="stk-text-box">
              {{ choosePackagesWithActivity }}
            </div>
            <div v-if="!isAct && !isSubmited2act" class="stk-text-box">
              {{ sureActivityWithPackage }}
            </div>
            <a-form-model-item>
              <!-- <a-checkbox-group
                v-if="isAct && submitArr.length"
                name="sbumited"
                v-model="submitValueArr"
                :disabled="true"
              >
                <a-checkbox
                  v-for="(pkg, i) in submitArr"
                  :key="'submited' + i"
                  :value="pkg.package_id"
                  >{{ `${pkg.package_id}-${pkg.package_title}` }}</a-checkbox
                >
              </a-checkbox-group> -->
              <a-checkbox-group
                v-if="isAct && selectArr.length"
                v-model="modalData.form.package_id_list"
              >
                <a-checkbox
                  v-for="(pkg, i) in selectArr"
                  :key="i"
                  :value="pkg.package_id"
                  >{{ `${pkg.package_id}-${pkg.package_title}` }}</a-checkbox
                >
              </a-checkbox-group>
              <a-checkbox-group
                v-if="!isAct && !isSubmited2act"
                v-model="defCheckedList"
              >
                <a-checkbox
                  v-for="(act, i) in modalData.packageArr"
                  :disabled="true"
                  :key="i"
                  :value="act.activity_id"
                  >{{ `${act.activity_id}-${act.activity_title}` }}</a-checkbox
                >
              </a-checkbox-group>
            </a-form-model-item>
          </div>
          <a-form-model-item>
            <a-textarea
              class="textarea-box"
              v-model="modalData.form.note"
              v-bind="textareaProps"
              v-on="textareaProps.on"
            />
          </a-form-model-item>
        </div>
      </a-modal>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    modalData: {
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {
      modalProps: {
        destroyOnClose: true,
        visible: false,
        keyFlag: '',
        packageArr: [],
        okText: this.$t('global_confirm'),
        cancelText: this.$t('global_cancel'),
        okButtonProps: {
          props: {
            disabled: false
          }
        },
        on: {
          cancel: () => {
            let { modalData: md } = this
            md.form = {}
            md.visible = false
          },
          ok: () => {}
        }
      },
      textareaProps: {
        maxLength: 500,
        placeholder: this.$t('form.stkTextarea'),
        autoSize: { minRows: 3, maxRows: 7 },
        on: {}
      }
    }
  },
  computed: {
    choosePackagesWithActivity() {
      let row = this.modalData.row
      let str = row.activity_id
        ? `${row.activity_id}-${row.activity_title}`
        : ''
      return klook.parseStr1(this.$t('stkModal.choosePackagesWithActivity'), {
        'AID-Name': str
      })
    },
    sureActivityWithPackage() {
      let row = this.modalData.row
      let str = row.package_id ? `${row.package_id}-${row.package_title}` : ''
      return klook.parseStr1(this.$t('stkModal.sureActivityWithPackage'), {
        'PID-Name': str
      })
    },
    isAct() {
      return this.modalData.keyFlag === 'activity'
    },
    isSubmited2act() {
      let obj = this.modalData.activity_item || {}
      return [1, 3].includes(obj.approval_status)
    },
    submitValueArr() {
      return this.submitArr.map((o) => o.package_id)
    },
    submitArr() {
      let arr = this.modalData.packageArr.filter((item) => item.isFlag)
      return arr
    },
    selectArr() {
      let arr = this.modalData.packageArr.filter((item) => !item.isFlag)
      return arr
    },
    defCheckedList() {
      return this.modalProps.packageArr.map((item) => item.activity_id)
    }
  },
  watch: {
    modalData: {
      handler(md) {
        let props = this.modalProps.okButtonProps.props
        if (md.keyFlag === 'activity') {
          let isSubmitPkg = md.packageArr.some((item) => item.isFlag)
          if (isSubmitPkg) {
            props.disabled = false
          } else {
            props.disabled = !(
              md.form.package_id_list && md.form.package_id_list.length
            )
          }
        } else {
          // pkg不用锁
          props.disabled = false
        }
        _.merge(this.modalProps, md)
      },
      immediate: true,
      deep: true
    }
  }
}
</script>

<style lang="scss" scoped>
.modal-content-box {
  .textarea-box {
    margin-top: 20px;
  }
  .ant-form-item {
    margin: 0;
    border-radius: 4px;
  }
  .ant-checkbox-group {
    width: 100%;
    .ant-checkbox-wrapper {
      background: #fafafa;
      display: block;
      font-size: 14px;
      line-height: 20px;
      color: #000;
      padding: 16px 8px;
      margin: 10px 0 0 0;
    }
  }
}
</style>
