<template>
  <!-- 两种形式 : 1. 单选or多选选项(属性值),  2 freetext,不带勾选, 后续可能混杂 -->
  <div class="compound-select">
    <!-- 多选 -->
    <component
      :is="`${multi ? 'a-checkbox' : 'a-radio'}-group`"
      v-model="checkList"
      :max="title.select_limit"
    >
      <!-- <transition-group name="list"> -->
      <!-- TODO should use v-for on data fields and v-model instead of @chagne -->
      <draggable
        v-model="option_list"
        v-bind="{
          animation: 300,
          group: 'people',
          scroll: true,
          scrollSpeed: 2000,
          scrollSensitivity: 10
        }"
        handle=".sort-btn"
      >
        <div
          v-for="(option, index) in option_list"
          :key="genUUID(option)"
          class="compound-options"
        >
          <CompoundOption
            ref="struct_option"
            v-bind="$attrs"
            :multi="title.select_limit > 1"
            :label="genUUID(option)"
            :option="option"
            :index="index"
            :total="option_list.length"
            :ref_data="getOptionRefData(option)"
            :option_data="getOptionData(option)"
            :copy-times="1"
            :type="getOptionType(option)"
            :chosen="isChosen(option)"
            @change="(option) => updateOptionData(option)"
            @remove="removeItem"
            @copy="copyItem"
            @confirm-add="confirmAdd"
            @confirm-edit="confirmEdit"
          >
            <!-- eslint-disable vue/html-self-closing -->
            <template slot="operation"></template>
          </CompoundOption>
          <!-- 排序 -->
          <div class="compound-option__sort">
            <a-icon type="menu" class="sort-btn" />
          </div>
        </div>
      </draggable>
      <!-- 添加 -->
      <CompoundOption
        v-if="isAdding"
        key="compound-select-add-new"
        type="add"
        @confirm-add="confirmAdd"
        @cancel-add="isAdding = false"
      />
      <!-- </transition-group> -->
    </component>
    <div v-show="leftTimes > 0" class="compound-extend">
      <a-button
        type="link"
        size="small"
        :disabled="!isPriorLang || $attrs.readonly"
        @click="addNew"
        >+ {{ $t('global_button_add') }}</a-button
      >
    </div>
  </div>
</template>

<script>
import CompoundOption from './Option'
import draggable from 'vuedraggable'

let dataStruct = ADMIN_API.struct([
  ADMIN_API.struct.interface({
    create: 'boolean?',
    ui_index: 'number?',
    order: 'number?', // TODO
    dup_limit: 'number?',
    id: 'number?',
    type: 'number?',
    is_free_text: 'boolean?',
    value: 'string',
    variable_map: 'any?',
    // 新增, 可否多选由该字段判断
    config: 'string?' //  "{ default_selected: false, multiple_selection_variables: [] }", // 配置信息 JSON 串,
  })
])

const genUUID = (v) => `${v.id}${v.order || '0'}`
export default {
  name: 'CompoundGroup',
  inject: ['ref_lang', 'edit_lang', 'locale_info'],
  components: { CompoundOption, draggable },
  model: {
    prop: 'data',
    event: 'change'
  },
  props: {
    ref_data: Array,
    data: { type: Array },
    title: { type: Object },
    // 选中值
    value: {
      type: [Array],
      default: () => {}
    }
    // // 必填
    // required: {
    //   type: Boolean,
    //   default: () => {}
    // },
  },
  data() {
    return {
      // 最大勾选数量
      max: 1000,
      // list: [],
      optionTypeMap: {
        1: 'text',
        2: 'template',
        0: 'freetext'
      },
      // 剩余可添加次数
      // 允许添加次数, 0, 不允许添加
      // 正在添加
      isAdding: false,
      ui_index_list: []
    }
  },
  computed: {
    isPriorLang() {
      return this.isOnlyLang || this.isEN
    },
    isOnlyLang() {
      return this.locale_info.react_data.filter((v) => v.status).length == 1
    },
    isEN() {
      return this.edit_lang === 'en_US'
    },
    leftTimes() {
      return (
        this.title.freetext_limit -
        this.data.filter((option) => option.is_free_text).length
      )
    },
    // FIXME this is not well binded
    option_list: {
      // data may duplicate or missing
      // we generation a unioned array
      // with property from both data and ui
      // and with duplicated data as standalone option
      // FIXME
      get() {
        let result
        if (!this.title.attr_value_list.length && this.title.freetext_limit) {
          result = this.data
        } else {
          // // 选中的排在前, 没选中的排在后
          // 不包含排序字段, 选中的排在前, 没选中的排在后
          let selected_options = this.data.map((v) => {
            let attr_ui =
              this.title.attr_value_list.find((ui) => ui.id === v.id) || {}
            let obj = {
              ...v,
              ...attr_ui
            }
            if (v.ui_index !== undefined) {
              obj.ui_index = v.ui_index
            } else {
              obj.ui_index = attr_ui.ui_index
            }
            return obj
          })
          let selected_ids = this.data.map((v) => v.id)
          let unselected_options = this.title.attr_value_list.filter(
            (v) => !selected_ids.includes(v.id)
          )
          // 合并
          let arr = [...selected_options, ...unselected_options]
          result = _.sortBy(arr, 'ui_index')
        }
        return dataStruct(result)
      },
      set(new_list) {
        this.handleSort(new_list)
      }
    },
    checkList: {
      get() {
        // console.info("get group");
        let result
        if (_.isEmpty(this.data)) {
          result = this.multi ? [] : ''
        } else {
          // is_free_text 没有勾选
          let checked = (
            this.data.filter((v) => !v.is_free_text) || []
          ).map((v) => genUUID(v))
          if (!checked.length) {
            result = this.multi ? [] : ''
          } else {
            result = this.multi ? checked : checked[0]
          }
        }

        return this.multi
          ? ADMIN_API.struct(['string'])(result)
          : ADMIN_API.struct('string')(result)
      },
      set(v) {
        let freetext_items = _.cloneDeep(this.option_list).filter(
          (op) => op.is_free_text
        )
        let options = _.cloneDeep(this.option_list).filter((option) =>
          this.multi ? v.includes(genUUID(option)) : v == genUUID(option)
        )
        // pick data default property from ui
        options.forEach((option) => {
          if (option.type == 2) {
            Object.entries(option.variable_map).forEach(([key, obj]) => {
              option.variable_map[key] = {
                attr_item_id: obj.id,
                key: obj.key
              }
            })
          }
        })
        let chosen_old = _.filter(this.data, (option) =>
          this.multi ? v.includes(genUUID(option)) : v == genUUID(option)
        )
        let attrs = _.unionBy(freetext_items, chosen_old, options, genUUID)

        // make sure options are post in the order from uitree
        attrs = this.sortOptions(attrs)

        let result = ADMIN_API.struct([
          ADMIN_API.struct.interface({
            id: 'number?',
            value: 'string',
            variable_map: 'undefined|any'
          })
        ])(attrs)
        console.info('set group by checklist', result)
        this.$emit('change', _.cloneDeep(result))
        this.$forceUpdate()
      }
    },
    multi() {
      return this.title.select_limit > 1
    }
    // TODO
    // isAllFreetext
    /* isAllFreetext() {
     *   return (
     *     !this.title.attr_value_list.length && this.title.freetext_limit > 0
     *   );
     * },*/
  },
  watch: {
    option_list: {
      handler(next, old) {
        if (!this.hasSort && this.title.attr_value_list.length) {
          this.handleSort(next)
        }
      }
    }
  },
  methods: {
    handleSort(new_list) {
      this.hasSort = true
      // 更新新的 ui_index
      new_list.forEach((v, i) => (v.ui_index = i))
      let indexMap = new_list.reduce((acc, cur, idx) => {
        acc[genUUID(cur)] = idx
        return acc
      }, {})
      // 记录位置
      this.title.attr_value_list.forEach((v) => {
        if (indexMap[genUUID(v)] !== undefined) {
          v.ui_index = indexMap[genUUID(v)]
        } else {
          let key = Object.keys(indexMap).find((idx) => idx.includes(v.id))
          v.ui_index = indexMap[key]
        }
      })
      // 找出已选中的项目
      let new_data = this.data.filter(
        (item) => this.checkList.includes(genUUID(item)) || item.is_free_text
      )
      // 更新实例数据顺序
      new_data.forEach((v) => {
        v.ui_index = indexMap[genUUID(v)]
      })
      // 重排
      new_data = _.sortBy(new_data, 'ui_index')
      this.$emit('change', new_data)
    },
    getOptionType(option) {
      if (
        option.is_free_text &&
        this.getOptionData(option).value === undefined
      ) {
        return 'add'
      }
      return this.optionTypeMap[option.type]
    },
    // leftCopyTimes(option) {
    //   debugger
    //   return option.dup_limit - this.data.filter(v => v.id == option.id).length;
    // },
    genUUID,
    sortOptions(options) {
      // by the order in ui tree
      return _.sortBy(options, (v) =>
        this.option_list.findIndex((option) => option.id == v.id)
      )
    },
    updateOptionData(option) {
      // let options = _.unionBy([option], this.data, genUUID);
      let options = _.cloneDeep(this.data)
      options.some((v) => {
        if (genUUID(v) === genUUID(option)) {
          v = {
            ...v,
            ...option
          }
        }
      })
      if (this.multi) {
        options = options.filter(
          (v) => this.checkList.includes(genUUID(v)) || v.is_free_text
        )
      } else {
        options = [options.find((v) => this.checkList === genUUID(v))]
      }

      options = options.filter((item) => item)

      let result = dataStruct(this.sortOptions(options))
      console.info('set group by option data', result)
      this.$emit('change', result)
    },
    getOptionData(option) {
      return (
        _.find(this.data, (item) => genUUID(item) === genUUID(option)) || {}
      )
    },
    getOptionRefData(option) {
      return (
        _.find(this.ref_data, (item) => genUUID(item) === genUUID(option)) || {}
      )
    },
    initCheckList() {},
    // Template 是否允许编辑, 未勾选该选项, 则该template不用编辑
    isChosen(op) {
      return op.is_free_text || this.checkList.includes(genUUID(op))
    },
    async validate() {
      let valid = true
      if (this.title.required && !this.data.length) {
        valid = false
      }
      let options = this.$refs.struct_option || []
      for (let option of options) {
        let option_valid = await option.validate()
        valid = valid && option_valid
      }
      return valid
    },
    // 删除
    removeItem(option) {
      let index = -1

      let attrs = _.cloneDeep(this.data)
      attrs.find((v, i) => {
        if (_.isEqual(v, option)) {
          index = i
          return true
        }
      })

      attrs.splice(index, 1)
      this.$emit('change', attrs)
    },
    // 复制属性值(选项)
    // TDOO
    copyItem(option_data) {
      let index = _.findLastIndex(this.data, (v) => v.id == option_data.id)
      console.log(index)
      let attrs = _.cloneDeep(this.data)
      console.log({ attrs })
      let new_option = _.cloneDeep(option_data)
      new_option.order = +new Date()
      attrs.splice(index + 1, 0, new_option)
      // attrs
      //   .filter(v => v.id == option_data.id)
      //   .forEach((v, i) => (v.order = i));

      console.log({ attrs })
      this.$emit('change', attrs)
    },
    // 编辑
    addNew() {
      this.isAdding = true
    },
    // 确认添加
    confirmAdd(option) {
      let attrs = _.cloneDeep(this.data)
      // 新增
      if (option.create) {
        attrs.push({
          ...option,
          id: +_.uniqueId()
          // order: this.option_list.length
        })
      } else {
        let index = attrs.findIndex((v) => v.id == option.id)
        attrs.splice(index, 1, option)
      }
      this.$emit('change', attrs)
      this.isAdding = false
    },
    confirmEdit(option) {
      let attrs = _.cloneDeep(this.data)
      let index = attrs.findIndex((v) => v.id == option.id)
      attrs.splice(index, 1, option)
      this.$emit('change', attrs)
    }
  }
}
</script>

<style lang="scss">
$borderColor: #d0e6fe;
// 整体group
.compound-select {
  .ant-radio-group,
  .ant-checkbox-group {
    &:first-of-type {
      width: 100%;
    }
  }
}
// animation
.list-enter-active {
  transition: all 0.3s;
}
/** 移除过程 **/
.list-leave-active {
  transition: all 0.3s;
}
/*** 开始插入、移除结束的位置变化 ***/
.list-enter,
.list-leave-to {
  max-height: 0;
  margin-bottom: 0;
  padding: 0 15px;
  // padding: 0;
  opacity: 0;
  transform: translateY(20px);
}
// 排序图标
.compound-option__sort {
  i {
    position: relative;
    width: 30px;
    top: calc(50% - 11px);
    font-size: 18px;
  }
  color: #8b8b8b;
  cursor: move;
  position: relative;
  &:hover {
    transition: all 0.2s;
    color: #1079fb;
  }
}

.compound-extend {
  margin-top: 12px;
  &--add-new {
    width: min-content;
  }
}

.compound-options {
  display: flex;
  margin-top: 12px;
  &:first-child {
    margin-top: 0;
  }
}
</style>
