<template>
  <div>
    <a-popover v-if="showPopover && this.message" v-bind="$attrs">
      <div slot="content" style="max-width: 300px;">
        {{ message }}
      </div>
      <slot />
    </a-popover>
    <slot v-else />
  </div>
</template>

<script>
export default {
  name: 'BasicFormPopover',
  props: {
    message: {
      type: String,
      default: ''
    },
    showPopover: {
      type: Boolean,
      default: true
    }
  }
}
</script>
