<template>
  <a-form-model ref="form" :model="form" label-width="0">
    <a-form-model-item prop="value" :rules="rules">
      <simple-mde v-model="form.value" :disabled="readonly" />
      <template v-if="refData">
        <simple-mde v-model="refData.value" style="margin-top: 12px" disabled />
      </template>
    </a-form-model-item>
  </a-form-model>
</template>

<script>
import SimpleMde from '@activity/components/simple-md'

export default {
  name: 'MarkdownWidget',
  components: {
    SimpleMde
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: Object,
      required: true
    },
    required: {
      type: Boolean,
      default: false
    },
    config: {
      type: Object,
      default: () => ({})
    },
    refData: {
      type: Object,
      default: null
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    rules() {
      return {
        required: this.required,
        message: 'Please fill in this field.'
      }
    },
    conf() {
      return {
        allow_copy: false,
        allow_free_text: false,
        multiple_selection: false,
        ...this.config
      }
    }
  },
  data() {
    return {
      form: {}
    }
  },
  watch: {
    value: {
      immediate: true,
      deep: true,
      handler(v, oldV) {
        if (!_.isEqual(v, oldV)) {
          this.$set(this, 'form', v)
        }
      }
    },
    form: {
      deep: true,
      handler(v) {
        this.$emit('change', v)
      }
    }
  },
  methods: {
    validateForm() {
      return new Promise((resolve) =>
        this.$refs.form.validate((valid) => resolve(valid))
      )
    }
  }
}
</script>
