<template>
  <div class="attribute-widget">
    <Title
      v-if="active"
      :key="`${ui.id}${ui.display_order}`"
      :ref="`widget_${ui.id}_${ui.display_order}`"
      v-bind="$attrs"
      :title.sync="ui_data"
      :data="current_data"
      :ref_data="current_ref_data"
      @change="(val) => handleChange(val)"
    />
  </div>
</template>
<script>
import Title from './Title'
import { mapActions } from 'vuex'
// Attribute 组件
export default {
  name: 'Attribute',
  components: { Title },
  model: {
    prop: 'data',
    event: 'change'
  },
  provide() {
    return {
      updateText: this.updateText
    }
  },
  props: {
    // 控制是否展示
    active: {
      type: Boolean,
      default: true
    },
    // widget ui 配置
    ui: {
      type: Object,
      default: () => {}
    },
    // 当前语言属性值数据
    data: {
      type: [Array, Object],
      default: () => []
    },
    // 参考语言的属性值数据
    refData: {
      type: [Array, Object],
      default: () => []
    }
  },
  data() {
    return {
      current_data: {},
      current_ref_data: {},
      ui_data: { attr_value_list: [] },
      // 所有属性
      all_variables: []
    }
  },
  computed: {
    // 变量映射
    variable_map() {
      let obj = {}
      this.all_variables.forEach((v) => {
        obj[v.id] = v
      })
      return obj
    }
  },
  watch: {
    // 当前语言数据
    data: {
      immediate: true,
      handler(next) {
        let is_arry = next instanceof Array
        let attr_values = _.cloneDeep(is_arry ? next : [next])
        this.current_data = _.cloneDeep({
          attr_values
        })
      }
    },
    // 参考语言数据
    refData: {
      immediate: true,
      handler(next) {
        let is_arry = next instanceof Array
        let attr_values = _.cloneDeep(is_arry ? next : [next])
        this.current_ref_data = _.cloneDeep({
          attr_values
        })
      }
    },
    ui: {
      immediate: true,
      handler: async function(next) {
        this.ui_data = await this.formatUIData(next)
      }
    }
  },
  mounted() {
    this.$emit('widget-mounted')
  },
  methods: {
    ...mapActions(['getActVariables2actions']),
    // 转换 ui 数据
    async formatUIData(ui) {
      try {
        this.all_variables = await this.getActVariables2actions()
      } catch (e) {
        console.log(e)
      }
      //! 没有变量信息? , 请求获取
      let ui_data = _.cloneDeep(ui)
      // 现根据 allow_copy 判断能否复制
      ui_data.attr_dup_limit = _.get(ui, 'config.allow_copy') ? 1000 : 0
      // 现根据 allow_free_text 判断能否增加 freetext
      ui_data.freetext_limit = _.get(ui, 'config.allow_free_text') ? 1000 : 0
      // 现根据 multiple_selection 判断是否多选
      ui_data.select_limit = _.get(ui, 'config.multiple_selection') ? 1000 : 0
      // 原为 Boolean 现为 1 / 0 , 转换为 boolean
      ui_data.required = !!ui.required
      // 匹配变量
      ui_data.attr_value_list.forEach((v) => {
        if (!v) return
        //! ui_tree 只返回了每个变量的 id, 这里做匹配, 拿到变量的详情
        for (let variable in v.var_map) {
          let obj = this.variable_map[v.var_map[variable]] || {}
          v.var_map[variable] = {
            ...obj,
            key: variable,
            values: obj.attr_value_list,
            validation: obj.validation || '{}'
          }
        }
      })
      // var_map => variable_map
      ui_data.attr_value_list.forEach((v) => {
        v.variable_map = v.var_map
      })
      // 返回 数据
      return ui_data
    },
    // 更改事件
    handleChange(val) {
      // 更新数据
      let is_arry = val.attr_values instanceof Array
      let attr_values = _.cloneDeep(
        is_arry ? val.attr_values : [val.attr_values]
      )
      // 更新数据
      this.current_data = val
      this.$emit('change', attr_values)
    },
    getData() {
      return this.current_data.attr_values
    },
    //! 校验事件 返回 promise
    validateForm() {
      return this.$refs[
        `widget_${this.ui.id}_${this.ui.display_order}`
      ].validate()
    },
    // 记录指定的文案
    updateText(option, text) {
      // 记录指定的文案
      let index = this.current_data.attr_values.findIndex(
        (v) => v.id === option.id && option.order === v.order
      )
      if (index >= 0) {
        if (this.current_data.attr_values[index].showText === text) {
          return
        }
        this.current_data.attr_values[index] = {
          ...this.current_data.attr_values[index],
          showText: text
        }
        this.$emit('change', this.current_data.attr_values)
      }
    }
  }
}
</script>
