<template>
  <div>
    <a-form-model
      ref="form"
      :model="form"
      :rules="formRules"
      label-width="200px"
      @submit.native.prevent
    >
      <template v-if="displayFactory('pkg_merchant')">
        <merchant-select
          ref="merchantSelect"
          v-model="form.merchant_id"
          :activity_id="activity_id"
          :package_info="form"
          :rules="getRulesFactory('pkg_merchant')"
          :description="getCurrentDescription('pkg_merchant')"
          @mounted="$emit('merchant_init', $event)"
        ></merchant-select>
      </template>

      <template v-if="displayFactory('merchant_confirm')">
        <a-form-model-item
          :label="$t('merchant_self_confirm')"
          prop="merchant_self_confirm"
          :rules="getRulesFactory('merchant_confirm')"
          :colon="false"
        >
          <a-tooltip
            placement="right"
            overlayClassName="common-tooltip-style"
            :title="getCurrentDescription('merchant_confirm')"
          >
            <a-radio-group v-model="form.merchant_self_confirm">
              <a-radio :value="1">Yes</a-radio>
              <a-radio :value="0">No</a-radio>
            </a-radio-group>
          </a-tooltip>
          <!-- <p>{{ $t('merchant_self_confirm_turn_on_btn_tips') }}</p> -->
        </a-form-model-item>
      </template>

      <template v-if="displayFactory('pkg_contact')">
        <a-form-model-item
          :label="$t('merchant_package_contact')"
          class="merchant-package-contact"
          prop="merchant_contact"
          :rules="getRulesFactory('pkg_contact')"
          :colon="false"
        >
          <template
            v-for="(mpc, index) in form.merchant_contact || [
              {
                media: 'phone',
                number: ''
              }
            ]"
          >
            <div :key="index" :style="{ marginTop: index !== 0 ? '15px' : '' }">
              <a-input-group compact>
                <a-select v-model="mpc.media" style="width: 110px">
                  <a-select-option
                    v-for="type in merchantPkgContactType"
                    :key="type.key"
                    :value="type.key"
                    >{{ type.text }}</a-select-option
                  >
                </a-select>
                <a-tooltip
                  placement="right"
                  overlayClassName="common-tooltip-style"
                  :title="getCurrentDescription('pkg_contact')"
                >
                  <a-input
                    v-model="mpc.number"
                    :placeholder="$t('global_please_input')"
                    style="width: 330px"
                  />
                  <i
                    v-if="index !== 0"
                    class="common-delete-btn"
                    @click="handleDelMerchantPkgContact(mpc)"
                    style="margin-top: -3px"
                  >
                    <svg-icon icon-name="trash" />
                  </i>
                  <!-- <a-button
                    style="margin-left: 12px"
                    v-if="index !== 0"
                    type="danger"
                    @click="handleDelMerchantPkgContact(mpc)"
                    >Delete</a-button
                  > -->
                </a-tooltip>
              </a-input-group>
            </div>
          </template>
          <a-button
            class="btn-merchantPkgContact-add form-width"
            @click="handleAddMerchantPkgContact"
            >{{ $t('global_add') }}</a-button
          >
        </a-form-model-item>
      </template>
    </a-form-model>
  </div>
</template>

<script>
import MerchantSelect from './merchant_select'
import { getCurrentLocal } from '@activity/utils'

const MERCHANT_PKG_CONTACT_TYPE = [
  {
    text: __('merchant_package_contact_type_phone'),
    key: 'phone'
  },
  {
    text: __('merchant_package_contact_type_wechat'),
    key: 'wechat'
  },
  {
    text: __('merchant_package_contact_type_email'),
    key: 'email'
  },
  {
    text: __('merchant_package_contact_type_whatsapp'),
    key: 'whatsapp'
  },
  {
    text: __('merchant_package_contact_type_line'),
    key: 'line'
  },
  {
    text: __('merchant_package_contact_type_facebook'),
    key: 'facebook'
  },
  {
    text: __('merchant_package_contact_type_kakaotalk'),
    key: 'kakaotalk'
  }
]

export default {
  components: {
    MerchantSelect
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: Object,
      required: true
    },
    activity_id: {
      type: Number,
      required: true
    },
    schemaConfig: {
      type: Object,
      required: true
    }
  },
  data() {
    let validMerchantContact = (rule, value, callback) => {
      if (!_.isArray(value)) {
        this.formValid = false
      } else {
        this.formValid = value.every((item, index) => {
          if (value.length === 1 && index === 0) {
            // 对于第一个对象：
            // true: 都填 或 都不填 或 只填了media
            // false: 只填了number
            return !(_.isEmpty(item.media) && !_.isEmpty(item.number))
          } else {
            // 对于其它对象：
            // true: 都填
            // false: 只填了某一个 或 都不填
            return !(_.isEmpty(item.media) || _.isEmpty(item.number))
          }
        })
        if (this.formValid) {
          callback()
        } else {
          callback(new Error('请选择或输入联系方式'))
        }
      }
    }

    return {
      formRules: {
        merchant_contact: [{ validator: validMerchantContact, trigger: 'blur' }]
      },

      merchant_id: ''
    }
  },
  watch: {
    'form.merchant_id': {
      handler(v, oldV) {
        if (oldV) {
          this.$nextTick(() => {
            this.form.merchant_self_confirm = 0
          })
        }
      }
    }
  },
  computed: {
    form: {
      get() {
        return this.value
      },
      set(v) {
        this.$emit('change', v)
      }
    },
    merchantPkgContactType() {
      return MERCHANT_PKG_CONTACT_TYPE
    }
  },
  methods: {
    getCurrentLocal: getCurrentLocal,
    getCurrentDescription(type) {
      return this.getCurrentLocal(this.schemaConfig || {}, {
        type,
        field: 'desc_multilang',
        defaultVal: ''
      })
    },
    disabledSelfConfirm() {
      let confirm_order = 0
      if (this.form.merchant_id && this.$refs.merchantSelect) {
        let current = _.find(this.$refs.merchantSelect.all_merchants, {
          merchant_id: this.form.merchant_id
        })
        if (current) {
          // 需要根据找到的 merchant 信息，重置旧的数据
          this.form.merchant_self_confirm = current.merchant_self_confirm
        } else {
          this.form.merchant_self_confirm = 0
        }
        confirm_order = _.get(current, 'confirm_order', 0) // 1: can edit
      }
      return !confirm_order
    },
    displayFactory(field) {
      return (
        (this.schemaConfig[field] &&
          this.schemaConfig[field].validation !== 3) ||
        false
      )
    },
    getRulesFactory(field) {
      return _.get(this.schemaConfig, `${field}.rules`, {})
    },
    handleDelMerchantPkgContact(obj) {
      this.form.merchant_contact.splice(
        _.findIndex(this.form.merchant_contact, (item) => obj === item),
        1
      )
    },
    handleAddMerchantPkgContact() {
      this.form.merchant_contact.push({
        media: '',
        number: ''
      })
    },
    async validateForm() {
      return new Promise((resolve) =>
        this.$refs.form.validate((valid) => resolve(valid))
      )
    }
  }
}
</script>

