<template>
  <a-modal
    :visible.sync="_visible"
    :title="$t('act_binding_destination_city_request')"
    @cancel="
      () => {
        _visible = false
      }
    "
  >
    <a-form-model ref="form" :model="form" label-width="120px">
      <a-form-model-item
        :label="$t('act_binding_destination_country_or_region')"
        prop="country"
        placeholder="Please select"
        :rules="{ required: true, message: 'Please select Country Or Region' }"
      >
        <a-select
          v-model="form.country"
          show-search
          allow-clear
          :placeholder="$t('type_to_search')"
        >
          <a-select-option
            v-for="{ id, name } in countryOptions"
            :key="id"
            :label="`${id} - ${name}`"
            :value="name"
            >{{ `${id} - ${name}` }}</a-select-option
          >
        </a-select>
      </a-form-model-item>
      <a-form-model-item
        :label="$t('act_binding_destination_city_name')"
        prop="city"
        placeholder="Please select"
        :rules="{ required: true, message: 'Please select City' }"
      >
        <a-input v-model="form.city" />
      </a-form-model-item>
    </a-form-model>
    <span slot="footer" style="text-align: center">
      <a-button @click="_visible = false">{{ $t('global_cancel') }}</a-button>
      <a-button type="primary" @click="submitHandle">{{
        $t('global_submit')
      }}</a-button>
    </span>
  </a-modal>
</template>

<script>
export default {
  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      form: {
        country: '',
        city: ''
      }
    }
  },
  computed: {
    _visible: {
      get() {
        return this.visible
      },
      set(val) {
        if (val) {
          this.form = this.$options.data().form
        }
        this.$emit('update:visible', val)
      }
    },
    countryOptions() {
      return this.$attrs['country-options'] || []
    }
  },
  methods: {
    submitHandle() {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          ajax
            .post(ADMIN_API.act.send_add_city_application_email, {
              data: {
                area_name: this.form.country,
                city_name: this.form.city
              }
            })
            .then(() => {
              this.$message.success(this.$t('global_success'))
            })
          this._visible = false
        } else {
          this.$message.error('Please fill in all field.')
        }
      })
    }
  }
}
</script>

<style scoped lang="scss"></style>
