<template>
  <a-form-model-item
    :prop="row.key"
    :class="{ disabled: disabled }"
    :label="row.key.replace(/(^{{)|(}}$)/g, '')"
    :rules="getRule"
  >
    <div style="display: inline-block; width: 100%;">
      <a-input
        v-model="currentValue"
        size="small"
        :disabled="disabled || $attrs.readonly"
        placeholder="Please input"
      />
      <a-input
        v-if="ref_lang && chosen"
        v-model="refValue"
        type="textarea"
        size="small"
        :placeholder="`Reference Input (${ref_lang})`"
        :disabled="true"
      />
    </div>
  </a-form-model-item>
</template>

<script>
const isDecimal = (val) =>
  val &&
  String(val).includes('.') &&
  isInteger(String(val).split('.')[0]) &&
  isInteger(String(val).split('.')[1])
// 整数
const isInteger = _.isInteger

import cpnt_mixin from './cpnt_mixin.js'
export default {
  name: 'StructInput',
  mixins: [cpnt_mixin],
  inject: ['ref_lang'],
  //   event: "change"
  model: {
    prop: 'data_var_map',
    event: 'change'
  },
  data() {
    return {}
  },
  // model: {
  //   prop: "value",
  // },
  computed: {
    refValue() {
      let data = this.ref_data
      let v = _.get(data, 'values[0].value', '')
      return v
    },
    currentValue: {
      get() {
        let data = this.data_var_map
        let v = _.get(data, 'values[0].value', '')
        this.$emit('update:form', v)
        this.$emit('update:display_html', v)
        return v
      },
      set(v) {
        let data = []
        if (_.get(this.data_var_map, 'values[0].id')) {
          data = [
            {
              ...this.data_var_map.values[0],
              create: false,
              value: v
            }
          ]
        } else {
          data = v
            ? [
                {
                  create: true,
                  value: v
                }
              ]
            : []
        }
        this.$emit('change', { ...this.data_var_map, values: data })
      }
    },
    getRule() {
      // let rules = [{ required: this.var_map.required }];
      let rules = [{ required: true }]
      let obj = JSON.parse(this.data_var_map.validation || '{}')

      let inputType = obj.input_type
      if (inputType === 'int') {
        rules.push({
          trigger: ['blur', 'change'],
          validator(rule, value, callback) {
            return isInteger(value) ? callback() : callback('not integer')
          }
        })
      } else if (inputType === 'float') {
        rules.push({
          trigger: ['blur', 'change'],
          validator(rule, value, callback) {
            return isDecimal(value) ? callback() : callback('not decimal')
          }
        })
      }
      return rules
    }
  }
}
</script>
