<template>
  <div class="image-crop-wrap">
    <div class="common-spin-style">
      <a-spin />
    </div>
    <div class="image-crop">
      <!-- show -->
      <div
        ref="previewImage"
        class="image-crop__preview"
        :style="{ 'background-image': `url(${value})` }"
        @click.stop.prevent
      />
      <!-- mask handle-icon -->
      <div class="image-crop__mask">
        <!--<i class="el-icon-view handle-icon" @click.stop="openPreview" />-->
        <a-icon class="handle-icon" type="zoom-in" @click="openPreview" />
        <a-icon
          class="handle-icon"
          v-if="needCrop"
          type="edit"
          @click="openDialog"
        />
        <a-icon
          class="handle-icon"
          v-if="needUpload"
          type="upload"
          @click="upload"
        />

        <!--<i-->
        <!--v-if="needCrop"-->
        <!--class="el-icon-edit handle-icon"-->
        <!--style="font-size: 22px;"-->
        <!--@click.stop="openDialog"-->
        <!--/>-->
        <!--<i-->
        <!--v-if="needUpload"-->
        <!--class="el-icon-upload2 handle-icon"-->
        <!--style="font-size: 22px;"-->
        <!--@click.stop="upload"-->
        <!--/>-->
      </div>
      <!-- crop dialog -->
      <a-modal
        ref="dialog"
        v-model="dialogVisible"
        class="submit-confirm-dialog"
        title="Crop"
        :maskClosable="false"
        :close="closeDialog"
      >
        <div
          v-if="dialogVisible"
          style="height: 100%; width: 100%; overflow: hidden"
        >
          <Cropper
            ref="cropper"
            :view-mode="1"
            style="width: 100%; height: 100%"
            :src="originImageUrl"
            :background="false"
            :aspect-ratio="aspectRatio"
            :initial-aspect-ratio="aspectRatio"
            :data="coordinates"
            :ready="hideLoading"
            :zoomable="false"
            :auto-crop-area="1"
          />
        </div>
        <div slot="footer">
          <a-button type="primary" @click="confirm"> Confirm </a-button>
        </div>
      </a-modal>
      <a-modal
        v-model="preview_show"
        :centered="true"
        dialogClass="common-fullscreen-style"
      >
        <div class="image-preview-fullscreen">
          <img :src="value" />
        </div>
      </a-modal>
    </div>
  </div>
</template>
<script>
import { Cropper } from '@activity/components/Cropper'
import { imageBaseUrl, folder } from '../const'
import { cloudinaryOptions } from '../const'

export default {
  name: 'ImageCrop',
  components: { Cropper },
  props: {
    //! 图片 url, v-model
    value: {
      type: String,
      default: ''
    },
    //! 图片名
    imageName: {
      type: String,
      default: ''
    },
    //! 裁剪值前缀
    preProcess: {
      type: String,
      default: ''
    },
    //! 图片比例
    ratio: {
      type: String,
      default: ''
    },
    //! activityId 活动 ID
    activityId: {
      type: [String, Number],
      default: ''
    },
    //! 打开上传功能
    needUpload: {
      type: Boolean,
      default: false
    },
    //! 关闭裁剪功能
    needCrop: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      //! 裁剪弹窗 是否可见
      dialogVisible: false,
      //! 裁剪坐标
      coordinates: {
        x: undefined,
        y: undefined,
        width: undefined,
        height: undefined
      },
      //! loading
      loading: true,
      //! 展示预览
      preview_show: false,
      //! cloudinary 配置 -> tag 为活动 ID
      cloudinaryOptions: cloudinaryOptions(this.activityId),
      //! cloudinary上传部件实例
      cloudinaryInstance: {}
    }
  },
  computed: {
    //! 初始缩放比例
    aspectRatio() {
      let arr = this.ratio.split(':')
      //! undefined 标识为任意比例
      if (arr.length <= 1) {
        return
      }
      return arr[0] / arr[1]
    },
    //! 原始图片地址, 作为裁剪基础
    originImageUrl() {
      return imageBaseUrl + folder + this.imageName
    }
  },
  watch: {
    //! 裁剪的参数
    // 1. c_crop,h_513,w_1296,x_323,y_566/c_fill,ar_1160:460,w_400/
    // 2. u_activities:ja6cz6rej05uaphxmagj,h_1.0,ar_1160:460,c_scale,e_blur:10000/c_fill,ar_1160:460,w_400
    preProcess: {
      immediate: true,
      handler(next) {
        //! 设置了不裁剪原图设置 banner, 坐标为空
        if (next.includes('u_activities')) {
          this.coordinates = {}
        } else {
          let { x, y, w, h } = this.transformCoordinates(next)
          // console.log("Coordinates:", { x: +x, y: +y, w: +w, h: +h });
          this.coordinates = { x: +x, y: +y, width: +w, height: +h }
        }
      }
    }
  },
  methods: {
    //! 确定裁剪
    confirm() {
      this.confirmCrop()
      //! 只要裁剪了图片, "不裁剪设置banner"这个选项就去掉勾选
      this.closeDialog()
    },
    //! 关闭弹窗
    closeDialog() {
      this.dialogVisible = false
    },
    //! 打开弹窗
    openDialog() {
      this.dialogVisible = true
    },
    //! 转换 pre_process 为坐标
    transformCoordinates(pre_process) {
      if (!pre_process) {
        let { x, y, w, h } = {}
        return { x, y, w, h }
      }
      let str = pre_process
        .replace('/', '')
        .replace(/_/g, '":"')
        .replace(/,/g, '","')
      return JSON.parse(`{"${str}"}`)
    },
    //! 裁剪
    confirmCrop() {
      // c_crop,h_513,w_1296,x_323,y_566/c_fill,ar_1160:460,w_400/
      let { x, y, width, height } = this.$refs.cropper.getData()
      x = Math.floor(x)
      y = Math.floor(y)
      width = Math.floor(width)
      height = Math.floor(height)
      let pre_process = `c_crop,h_${height},w_${width},x_${x},y_${y}/`
      this.$emit('update:pre-process', pre_process)
      this.$emit('set-no-crop-banner', this)
    },
    //! 关闭 loading
    hideLoading() {
      this.loading = false
      this.$refs.cropper.setData(this.coordinates)
    },
    //! 上传
    async upload() {
      //! 打开上传组件
      if (this.cloudinaryInstance.open) {
        this.cloudinaryInstance.open()
        return
      }
      //! 创建实例
      this.cloudinaryInstance = cloudinary.createUploadWidget(
        this.cloudinaryOptions,
        (error, result) => {
          //! error
          if (error) {
            console.log(error)
            this.$emit('error', result.info)
            return
          }
          //! success
          if (result.length) {
            let info = result[0]
            this.$emit('uploaded', info)
            this.previewUrl = info.url.replace('http', 'https')
            this.showName = info.original_filename + '.' + info.format
            this.$emit('change', this.previewUrl)
          }
        }
      )
      this.cloudinaryInstance.open()
    },
    openPreview() {
      //! console.log(11);
      this.preview_show = true
    },
    //! 关闭预览
    closePreview() {
      this.preview_show = false
    }
  }
}
</script>
