<template>
  <div ref="footer" class="bulk-modification-footer">
    <GeneralAlert
      v-if="alertMsg"
      banner
      :show-icon="false"
      :message="alertMsg"
      style="margin-bottom: 10px;"
    />
    <div v-show="type === 'button'">
      <a-button
        v-if="!isFixedPriceModel"
        :style="{ marginRight: '12px', padding: 0 }"
        type="link"
        @click="handleOpenDefaultPriceSetting"
      >
        <a-icon type="edit" />
        {{ $t('package_bulk_modification_default_price_setting') }}
      </a-button>
      <a-button
        type="primary"
        :style="{ marginRight: '12px' }"
        @click="() => (type = 'price')"
      >
        <a-icon type="edit" />
        {{ $t('package_bulk_modification_edit_price') }}
      </a-button>
      <a-button
        v-if="isMainUnit"
        type="primary"
        :style="{ marginRight: '12px' }"
        @click="() => (type = 'inventory')"
      >
        <a-icon type="edit" />
        {{ $t('package_bulk_modification_edit_inventory') }}
      </a-button>
      <a-popover v-else>
        <template slot="content">
          <span>{{ $t('package_bulk_modification_edit_warning') }}</span>
          <a-button
            type="link"
            style="margin: 0; padding: 0;"
            @click="handleSelectMainUnit"
          >
            {{ mainUnitName }}
          </a-button>
        </template>
        <a-button disabled type="primary" :style="{ marginRight: '12px' }">
          <a-icon type="edit" />
          {{ $t('package_bulk_modification_edit_inventory') }}
        </a-button>
      </a-popover>
      <a-button
        v-if="isMainUnit"
        type="primary"
        @click="() => (type = 'cutoff')"
      >
        <a-icon type="edit" />
        {{ $t('package_bulk_modification_edit_cutoff') }}
      </a-button>
      <a-popover v-else placement="topRight">
        <template slot="content">
          <span>{{ $t('package_bulk_modification_edit_warning') }}</span>
          <a-button
            type="link"
            style="margin: 0; padding: 0;"
            @click="handleSelectMainUnit"
          >
            {{ mainUnitName }}
          </a-button>
        </template>
        <a-button disabled type="primary">
          <a-icon type="edit" />
          {{ $t('package_bulk_modification_edit_cutoff') }}
        </a-button>
      </a-popover>
    </div>
    <div v-show="type !== 'button'">
      <a-spin :spinning="loading">
        <div style="max-height: 300px; overflow: auto;">
          <a-form-model ref="form" layout="inline" :model="form" :rules="rules">
            <div v-if="type === 'price'" class="form-wrapper">
              <a-form-model-item
                :label="$t('package_price_cost')"
                style="margin-right: 60px;"
                prop="costPrice"
                :colon="false"
              >
                <a-input
                  v-model="form.costPrice"
                  :suffix="costCurrency || defaultCostCurrency"
                  placeholder="Please input"
                  @keyup.native="handleRoundPrice('costPrice')"
                  @blur="handleRoundPrice('costPrice')"
                />
              </a-form-model-item>
              <a-form-model-item
                :label="$t('package_price_retail')"
                prop="retailPrice"
                :colon="false"
              >
                <a-input
                  v-model="form.retailPrice"
                  :suffix="sellingCurrency || defaultSellingCurrency"
                  placeholder="Please input"
                  @keyup.native="handleRoundPrice('retailPrice')"
                  @blur="handleRoundPrice('retailPrice')"
                />
              </a-form-model-item>
            </div>
            <div v-else-if="type === 'inventory'" class="form-wrapper">
              <a-form-model-item
                :label="$t('package_inventory')"
                prop="isLimited"
                :colon="false"
                required
              >
                <a-radio-group v-model="form.isLimited">
                  <a-radio :value="false" :style="{ lineHeight: '33px' }">
                    {{ $t('package_inventory_unlimited') }}
                  </a-radio>
                  <a-radio :value="true" :style="{ lineHeight: '33px' }">
                    {{ $t('package_inventory_limited_per_day') }}
                  </a-radio>
                </a-radio-group>
                <a-input-number
                  v-model="form.inventory"
                  style="width: 200px;"
                  :max="MAX_INVENTORY - 1"
                />
              </a-form-model-item>
            </div>
            <div v-else-if="type === 'cutoff'" class="form-wrapper">
              <a-form-model-item
                :label="$t('package_cut_off_time')"
                prop="cutoffType"
                :colon="false"
                required
              >
                <a-select
                  v-model="form.cutoffType"
                  style="width: 200px;margin-right: 12px;"
                >
                  <a-select-option
                    v-for="cutoffOption in cutoffTypeList"
                    :key="cutoffOption.value"
                    :value="cutoffOption.value"
                  >
                    {{ cutoffOption.text }}
                  </a-select-option>
                </a-select>
                <a-time-picker
                  :value="getTimeslot(form.cutoffTime)"
                  format="HH:mm"
                  style="width: 200px;margin-bottom: 0;"
                  @change="(time, timeString) => (form.cutoffTime = timeString)"
                />
              </a-form-model-item>
            </div>
          </a-form-model>
        </div>

        <div>
          <a-button :style="{ marginRight: '8px' }" @click="handleCancel">
            {{ $t('global_button_cancel') }}
          </a-button>
          <a-button
            v-if="selectedRowKeys.length > 0"
            type="primary"
            :loading="saveLoading"
            @click="handleSave"
          >
            {{ $t('global_save') }}
          </a-button>
          <a-popover v-else placement="topRight">
            <template slot="content">
              <span>{{ $t('package_bulk_modification_save_tips') }}</span>
            </template>
            <a-button
              type="primary"
              disabled
              :loading="saveLoading"
              @click="handleSave"
            >
              {{ $t('global_save') }}
            </a-button>
          </a-popover>
        </div>
      </a-spin>
    </div>
    <DefaultPriceSettingMerchant ref="defaultPriceSetting" />
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { round2Decimal, getTimeOffset, getEditLang } from '@activity/utils'
import {
  MAX_INVENTORY,
  cutoffTypeList
} from '@activity/pages/package/package_const.js'
import GeneralAlert from '@activity/pages/components/GeneralAlert.vue'
import DefaultPriceSettingMerchant from './DefaultPriceSettingMerchant.vue'

export default {
  name: 'BulkModificationFooterMerchant',
  components: {
    GeneralAlert,
    DefaultPriceSettingMerchant
  },
  inject: ['packageId', 'bulkModification'],
  props: {
    data: {
      type: Object,
      required: true
    },
    selectedUnit: {
      type: Number,
      default: 0
    },
    selectedRowKeys: {
      type: Array,
      default: () => []
    }
  },
  data() {
    const validateInventory = (rule, value, callback) => {
      const { isLimited, inventory } = this.form
      if (isLimited && !inventory && ![0, '0'].includes(inventory)) {
        callback(new Error(this.$t('global_please_input')))
      } else {
        callback()
      }
    }

    return {
      MAX_INVENTORY,
      cutoffTypeList,

      type: 'button',
      loading: false,
      saveLoading: false,
      costCurrency: '',
      sellingCurrency: '',
      form: {
        costPrice: '',
        retailPrice: '',
        cutoffType: 0,
        cutoffTime: '00:00',
        isLimited: false,
        inventory: ''
      },
      defaultForm: {
        costPrice: '',
        retailPrice: '',
        cutoffType: 0,
        cutoffTime: '00:00',
        isLimited: false,
        inventory: ''
      },
      rules: {
        costPrice: [
          {
            required: true,
            message: this.$t('global_please_input'),
            trigger: 'blur'
          }
        ],
        retailPrice: [
          {
            required: true,
            message: this.$t('global_please_input'),
            trigger: 'blur'
          }
        ],
        isLimited: [
          { validator: validateInventory, trigger: 'blur' },
          {
            required: true,
            message: this.$t('global_please_select'),
            trigger: 'blur'
          }
        ]
      }
    }
  },
  computed: {
    ...mapState({
      packageSkuData: (state) => state.packageSkuData,
      priceModel: (state) => state.priceModel
    }),
    ...mapGetters(['mainUnit']),
    isMainUnit() {
      return this.mainUnit && this.mainUnit.sku_id === this.selectedUnit
    },
    isFixedPriceModel() {
      return this.priceModel === 0
    },
    mainUnitName() {
      if (!this.mainUnit) {
        return ''
      }

      const target = (this.mainUnit.local || []).find(
        (item) => item.language === getEditLang()
      )
      return target ? target.unit_name : ''
    },
    alertMsg() {
      if (this.type === 'price' && this.isFixedPriceModel) {
        return this.$t('package_bulk_modification_fixed_price_tips')
      }

      if (this.type === 'inventory') {
        return this.$t('package_bulk_modification_inventory_tips')
      }

      if (this.type === 'cutoff') {
        return this.$t('package_bulk_modification_cutoff_tips')
      }

      return ''
    },
    defaultCostCurrency() {
      const { currency } = this.packageSkuData
      return (currency || {}).merchant_currency || ''
    },
    defaultSellingCurrency() {
      const { currency } = this.packageSkuData
      return (currency || {}).selling_currency || ''
    },
    selectedList() {
      return (this.data.calendar || []).filter((item) =>
        this.selectedRowKeys.includes(item.schedule_id)
      )
    }
  },
  watch: {
    async type(val) {
      // 固定价格模型要先请求价格数据
      if (
        this.isFixedPriceModel &&
        (val === 'price' || val === 'sellingPrice')
      ) {
        await this.initFixedPrice()
        this.$emit('toggleFrozen', true)
      }

      // 选择price的时候，table中的price要高亮，其它同理
      if (val === 'button') {
        this.$emit('modifyHighlightList', null)
      } else if (val === 'price') {
        this.$emit('modifyHighlightList', ['costPrice', 'retailPrice'])
      } else if (val === 'inventory') {
        this.$emit('modifyHighlightList', ['inventory'])
      }

      // 重新设置父容器的下间距
      this.$nextTick(() => {
        const footerDom = this.$refs.footer
        this.$emit('setMargin', footerDom ? footerDom.offsetHeight : 0)
      })
    }
  },
  methods: {
    async initFixedPrice() {
      this.loading = true
      const res = ajax.get(ADMIN_API.act.query_fixed_price, {
        params: {
          sku_id: this.selectedUnit
        }
      })

      this.costCurrency = res.cost_currency
      this.sellingCurrency = res.selling_currency
      this.form.costPrice = res.cost_price
      this.form.retailPrice = res.retail_price

      this.loading = false
    },
    handleOpenDefaultPriceSetting() {
      this.$refs.defaultPriceSetting &&
        this.$refs.defaultPriceSetting.open(this.selectedUnit)
    },
    handleRoundPrice(field) {
      this.form[field] = round2Decimal(this.form[field])
    },
    handleCancel() {
      this.type = 'button'
      this.$emit('toggleFrozen', false)
    },
    getTimeslot(str) {
      return str ? moment(str, 'HH:mm') : ''
    },
    handleSelectMainUnit() {
      this.$emit('selectUnit', this.mainUnit.sku_id)
    },
    reset() {
      this.handleCancel()
    },
    getInventory() {
      return this.form.isLimited ? Number(this.form.inventory) : MAX_INVENTORY
    },
    async handleSave() {
      const valid = await this.$refs.form.validate().catch(() => false)

      if (!valid) {
        return
      }

      this.saveLoading = true
      let res = {}

      if (this.type === 'price') {
        res = await this.savePrice()
      } else if (this.type === 'inventory' || this.type === 'cutoff') {
        res = await this.saveInventoryOrCutoff()
      }

      if (res.success) {
        this.saveLoading = false
        this.type = 'button'
        this.bulkModification.update()
        this.$message.success(this.$t('global_modify_success'))
      } else {
        this.saveLoading = false
        this.$message.error(this.$t('global_modify_error'))
      }
    },
    async savePrice() {
      const apiUrl = this.isFixedPriceModel
        ? ADMIN_API.act.update_fixed_price
        : ADMIN_API.act.update_calendar_price
      const res =
        (await ajax.postBody(apiUrl, {
          data: {
            sku_id: this.selectedUnit,
            cost_currency: this.costCurrency || this.defaultCostCurrency,
            cost_price: Number(this.form.costPrice),
            retail_price: Number(this.form.retailPrice),
            selling_currency:
              this.sellingCurrency || this.defaultSellingCurrency,
            time_point: this.selectedList.map((item) => item.start_time)
          }
        })) || {}

      return res
    },
    async saveInventoryOrCutoff() {
      const cutoffTime =
        this.form.cutoffType * 24 * 60 * 60 +
        getTimeOffset(this.form.cutoffTime)
      const res =
        (await ajax.postBody(ADMIN_API.act.update_schedule, {
          data: {
            sku_id: this.selectedUnit,
            package_id: this.packageId,
            cut_off_time: this.type === 'cutoff' ? cutoffTime : -1,
            inventory: this.type === 'inventory' ? this.getInventory() : -1,
            schedule_id_list: this.selectedRowKeys
          }
        })) || {}

      return res
    }
  }
}
</script>

<style lang="scss">
.bulk-modification-footer {
  .ant-form-item-label > label {
    padding-right: 8px;
    white-space: break-spaces;
    display: inline-block;
    line-height: normal;
  }
}
</style>

<style lang="scss" scoped>
.bulk-modification-footer {
  position: absolute;
  padding: 10px 16px;
  right: 0;
  bottom: 0;
  width: 100%;

  border-top: 1px solid #e9e9e9;
  background: #fff;
  text-align: right;
  z-index: 3;

  .align-left {
    text-align: left;
  }

  .w-800 {
    width: 800px;
  }

  .form-wrapper {
    margin: 0 0 10px;
    text-align: left;
  }
}
</style>
