<template>
  <div id="basic-info-editable" class="basic-info">
    <div class="basic-info-box">
      <!-- <div v-for="boxKey in orderArr" :key="boxKey" class="common-basic-box">
        <h2>{{ $data[boxKey].title }}</h2>
        <div class="content-box">
          <component :is="$data[boxKey].component" />
        </div>
      </div> -->
      <div v-if="!initLoadFinish" class="common-spin-box"><a-spin /></div>
      <div v-show="initLoadFinish" class="common-basic-box">
        <h2 :id="basicInfo.title">{{ basicInfo.title }}</h2>
        <activity_basic_info_language_destination
          v-if="!isEMpage"
          ref="language_destination"
          :schema-config="destinationModelTypeConf"
          :activity_id="+activity_id"
          v-bind="getMultiAttrFactory('destination_model_type')"
          :result="basicInfoResult"
          @published_language="publishedLanguage"
          @formChange="formChange"
        >
          <blocklist
            ref="blocklist"
            :result="basicInfoResult"
            :activity-id="+activity_id"
          />
        </activity_basic_info_language_destination>
        <a-form-model disabled ref="form" :model="form" @submit.native.prevent>
          <div class="content-box">
            <!-- <component :is="$data[boxKey].component" /> -->
            <!-- title -->
            <template v-if="displayFactory('activity_title')">
              <tpl_special_multi_language_input
                ref="activity_title"
                v-model="form.activity_detail_list"
                class="large-input"
                :current-create-lang="published_language"
                v-bind="getMultiAttrFactory('activity_title')"
              />
            </template>

            <!-- subtitle -->
            <template v-if="displayFactory('activity_subtitle')">
              <tpl_special_multi_language_input
                ref="activity_subtitle"
                v-model="form.activity_detail_list"
                :current-create-lang="published_language"
                v-bind="getMultiAttrFactory('activity_subtitle')"
              />
            </template>

            <template v-if="displayFactory('activity_address')">
              <tpl_special_multi_language_input
                ref="activity_address"
                v-model="form.activity_detail_list"
                class="large-input"
                :required="getRequired('activity_address')"
                :current-create-lang="published_language"
                v-bind="
                  getMultiAttrFactory('activity_address', {
                    label: $t('ob_act_addr')
                  })
                "
              />
            </template>
            <!-- activity_address -->
            <template v-if="displayFactory('activity_address') && !isEMpage">
              <a-form-model-item
                class="basic-form-item"
                prop="address"
                :label="$t('act_address')"
                :rules="{
                  required: getRequired('activity_address'),
                  message: 'No Location yet~'
                }"
                :colon="false"
              >
                <tpl_activity_map
                  ref="activity_map"
                  :init="form.address ? { position: form.address } : null"
                  @change="handleMapChange"
                />
              </a-form-model-item>
            </template>

            <template v-if="displayFactory('activity_banner')">
              <div class="common-basic-box">
                <h2 :id="bannerPhoto.title">
                  <i class="common-required-star">*</i>
                  {{ bannerPhoto.title }}
                </h2>
                <div class="content-box">
                  <PhotoBanner
                    ref="photo_banner"
                    :list-all="photo_banner"
                    :required="getRequired('activity_banner')"
                    :banner-count="banner_count"
                    :trigger-banner-check="trigger_banner_check"
                    v-bind="getMultiAttrFactory('activity_banner')"
                    @banner-count-change="handleBannerCountChange"
                  />
                </div>
              </div>
            </template>
            <template v-if="displayFactory('activity_picture')">
              <div class="common-basic-box">
                <h2 :id="activityPhoto.title">
                  <i class="common-required-star">*</i>{{ activityPhoto.title }}
                </h2>
                <div class="content-box">
                  <PhotoActivity
                    ref="photo_activity"
                    :list-all="photo_activity"
                    :required="getRequired('activity_picture')"
                    :banner-count="banner_count"
                    :trigger-banner-check="trigger_banner_check"
                    @banner-count-change="handleBannerCountChange"
                    :minBannerCount="3"
                  />
                  <div v-show="isErrorTip" class="common-error-style">
                    Please upload photo
                  </div>
                </div>
              </div>
            </template>
          </div>
        </a-form-model>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import { getEditLang, getRefLang } from '@activity/utils'
import activity_basic_info_language_destination from '@activity/components/activity_basic_info_language_destination'
import PhotoBanner from '@activity/components/photo-upload/photo_banner'
import PhotoActivity from '@activity/components/photo-upload/photo_activity'
import tpl_special_multi_language_input from '@activity/components/tpl_special_multi_language_input'
import tpl_activity_map from '@activity/components/tpl_activity_map'
import Blocklist from '@activity/components/activity_blocklist'
// import { isMerchant } from '@/env'

export default {
  components: {
    activity_basic_info_language_destination,
    tpl_special_multi_language_input,
    tpl_activity_map,
    Blocklist,
    PhotoBanner,
    PhotoActivity
  },
  provide() {
    return {
      activityId: this.activity_id,
      curLang: getEditLang(),
      refLang: getRefLang(),
      isEMpage: this.isEMpage,
      getBasicInfoResult2provide: () => this.basicInfoResult
    }
  },
  inject: ['rootChangeLoading'],
  data() {
    return {
      isErrorTip: false,
      initLoadFinish: false,
      myForm: {},
      reqData: {
        params: {
          activity_details: [
            // {
            //   activity_address: '', // ,
            //   activity_sub_title: '', // ,
            //   activity_title: '', // ,
            //   language: '' // ,
            // }
          ],
          activity_id: parseInt(this.$route.params.id), // ,
          binded_areas: [
            // 绑定的区域,
            // {
            //   // ,
            //   area_id_list: [
            //     // 绑定的区域ID，默认第一个为主区域,
            //     1
            //   ],
            //   area_type: 1, // 绑定的区域类型,1:标准区域, 3:POI, 其他值非法,
            //   departure_id_list: [
            //     // 绑定的出发地区域ID,
            //     1 // ,
            //   ]
            // }
          ],
          block_status: {
            // block status 数据,
            // language_list: [
            //   // 待更新的语言列表,
            //   'string' // ,
            // ],
            // status: 1 // 活动语言黑名单状态 0-正常, 1-黑名单,
          },
          location: '', // 地址经纬度 latlng,
          place_id: '', // google 地图 place id,
          snapshot_modify_id: '' // ,
        }
      },
      basicInfoResult: undefined,
      activity_id: parseInt(this.$route.params.id),
      orderArr: ['basicInfo', 'bannerPhoto', 'activityPhoto'],
      basicInfo: {
        title: this.$t('activity_basic_page_basic_info'),
        // require: true,
        style: 'finish',
        countObj: {
          language: {
            require: true,
            mapValue: 'activity_detail_list.0.language',
            callback: () => {
              return this.published_language || undefined
            }
          },
          activity_title: {
            mapValue: 'activity_detail_list.0.activity_title',
            callback: () => {
              return (
                (this.form.activity_detail_list.length &&
                  this.form.activity_detail_list.every(
                    (item) => item.activity_title
                  )) ||
                undefined
              )
            }
          },
          activity_subtitle: {
            mapValue: 'activity_detail_list.language.activity_subtitle',
            callback: () => {
              let arr = this.form.activity_detail_list || []
              let curLang = arr.length
                ? arr.filter((o) => o.language === this.published_language)
                : []
              return curLang.length ? curLang[0].activity_subtitle : ''
            }
          },
          activity_address: {
            mapValue: 'activity_detail_list.language.activity_address',
            callback: () => {
              let arr = this.form.activity_detail_list || []
              let curLang = arr.length
                ? arr.filter((o) => o.language === this.published_language)
                : []
              return curLang.length ? curLang[0].activity_address : ''
            }
          },
          address: {
            require: true,
            mapValue: 'address'
          }
        }
      },
      bannerPhoto: {
        title: this.$t('activity_basic_page_banner_photo'),
        // require: true,
        countObj: {
          activity_banner: {
            mapValue: 'photos.banner.0.image.0.name',
            callback: () => {
              return (
                this.all_banners_desktop.filter(
                  (item) => item.typeFlag === 'banner'
                ).length || undefined
              )
            }
          }
        }
      },
      activityPhoto: {
        title: this.$t('basic.activityPhoto'),
        // require: false,
        countObj: {
          activity_banner: {
            mapValue: 'photos.images.0.images_groups',
            callback: () => {
              const photo_activity = this.$refs.photo_activity
                ? this.$refs.photo_activity.getList() || []
                : []
              return photo_activity.length >= 3 || undefined
            }
          }
        }
      },

      // old code
      isRequired: {
        required: true,
        message: 'Please fill this form',
        trigger: 'blur,change'
      },
      form: {
        affiliate: 0,
        best_price_guarantee: 0,
        activity_detail_list: []
      },

      cacheForm: {},

      schemaConfig: {},

      MULTI_LANG_INPUT: [
        'activity_title',
        'activity_subtitle',
        'act_description',
        'activity_address'
      ],

      photo_banner: [],
      photo_activity: [],
      banner_count: 0,
      all_banners_desktop: [],
      trigger_banner_check: 0,
      // 去水印
      hide_water_mark: 0,
      published_language: klook.urlParam('lang') || undefined,
      blocklist: []
    }
  },
  computed: {
    modelConfig() {
      let destination_model_type = this.schemaConfig[
        'destination_model_type'
      ] || { config: {} }
      return destination_model_type.config
    },
    isEMpage() {
      return document.URL.includes('/act/content/')
    },
    isCreated() {
      return !_.isEmpty(klook.urlParam('lang'))
    },
    destinationModelTypeConf() {
      return (
        (this.schemaConfig.destination_model_type &&
          this.schemaConfig.destination_model_type.config) ||
        {}
      )
    }
  },
  watch: {
    all_banners_desktop(arr) {
      this.initFloorTimelineData()
    },
    myForm: {
      handler() {
        this.initFloorTimelineData()
      },
      deep: true
    },
    form: {
      handler() {
        this.initFloorTimelineData()
      },
      deep: true
    },
    published_language(v) {
      if (v) {
        this.$set(
          this.form,
          'activity_detail_list',
          this.form.activity_detail_list.filter(
            (item) =>
              item.language === this.published_language ||
              item.language === 'en_US'
          )
        )
      }
    }
  },
  async created() {
    await this.initObConfig()
    await this.initBannerInfo()
    this.cacheData()
    klook.bus.$emit('addHandleSave2bus', this.handleSave)
    this.initLoadFinish = true
    this.$root.setMutationObserver('#basic-info-editable', this)
  },
  methods: {
    ...mapActions(['getActLang', 'updateActStepStatus2action']),
    getCurrentLocal: klook.getCurrentLocal,
    formChange: _.debounce(function(myForm) {
      this.myForm = myForm
    }, 300),
    initData(result) {
      result.language
      let obj = {
        activity_detail_list: result.activity_details,
        activity_id: result.activity_id,
        activity_title_en: result.activity_title_en,
        address: result.location,
        place_id: result.place_id
      }
      for (let key in klook.clearNull(obj)) {
        this.$set(this.form, key, obj[key])
      }
    },
    async handleSave() {
      this.isErrorTip = false
      let modify_id =
        new Date().valueOf() + '' + parseInt(Math.random() * 10000)
      // am 才能设置屏蔽语言
      if (!this.isEMpage) {
        this.$refs.blocklist && (await this.$refs.blocklist.remind())
      }
      this.rootChangeLoading(true)
      if (!this.validateBannerCount()) {
        this.rootChangeLoading(false)
        return
      }
      if (await this.getValidateFromStatus()) {
        const photo_activity = this.$refs.photo_activity
          ? this.$refs.photo_activity.getList() || []
          : []
        if (
          this.displayFactory('activity_picture') &&
          photo_activity.length < 3
        ) {
          this.isErrorTip = true
          this.$message.error({
            content: __('upload_4_photos_at_least')
          })
          this.rootChangeLoading(false)
          klook.scrollElError(document.getElementById('Activity Photo'))
          return
        }

        let { ...data } = this.form
        // 参数 处理 start
        let params = this.reqData.params
        data.activity_detail_list.forEach((item) => {
          item.activity_subtitle &&
            (item.activity_sub_title = item.activity_subtitle) //接口参数变更补充
        })
        _.merge(params.activity_details, data.activity_detail_list)
        params.snapshot_modify_id = modify_id
        params.location = data.address
        params.place_id = data.place_id
        params.binded_areas = await this.$refs.language_destination.save()
        params.block_status = this.$refs.blocklist.save()
        // 参数 处理 end
        let resp = [
          ...(await Promise.all([
            (async () => {
              let res = await ajax.postBody(
                ADMIN_API.act.update_activity_basic_info,
                {
                  data: params
                }
              )
              if (res.success) {
                klook.bus.$emit('initQueryData2bus')
                this.$refs.blocklist.saveSucCallback(res.success) // 回写数据
              }
              return res.success
            })(),
            // 校验banner数量以及比例
            (async () => {
              if (
                (this.displayFactory('activity_picture') ||
                  this.displayFactory('activity_banner')) &&
                ((this.$refs.photo_banner &&
                  !_.isEqual(
                    this.cacheForm.photo_banner,
                    this.$refs.photo_banner.getFormatParams()
                  )) ||
                  (this.$refs.photo_activity &&
                    !_.isEqual(
                      this.cacheForm.photo_activity,
                      this.$refs.photo_activity.getFormatParams()
                    )) ||
                  !_.isEqual(
                    this.cacheForm.hide_water_mark,
                    this.hide_water_mark
                  ))
              ) {
                let data_photo = {
                  activity_id: +this.activity_id,
                  banner: [],
                  images: []
                }

                data_photo.banner =
                  (this.$refs.photo_banner &&
                    this.$refs.photo_banner.getFormatParams()) ||
                  []
                data_photo.images =
                  (this.$refs.photo_activity &&
                    this.$refs.photo_activity.getFormatParams()) ||
                  []
                let res = await ajax.postBody({
                  url: ADMIN_API.act.update_activity_photo,
                  data: data_photo
                })

                return res.success
              }
              return true
            })()
          ]))
        ]
        if (resp.every((item) => item)) {
          this.updateActStepStatus2action({
            activity_id: +this.activity_id,
            status: 1,
            step: 'activity_basic_info',
            language:
              new URLSearchParams(location.search).get('lang') ||
              this.published_language
          })
          this.rootChangeLoading(false)
          this.$message.success(this.$t('global_success'))
          this.$router.push({
            path: `/act/activity/detail/${this.activity_id}`,
            query: {
              ...this.$route.query,
              lang: this.published_language
            }
          })
        } else {
          let dict = [
            'Language or Destination(City, Country)',
            'Activity Title/Sub-title & Address Desc. & Location Coordinates',
            'Banner & Activity Photo'
          ]
          let msg = resp
            .reduce((acc, curr, index) => {
              return curr ? acc : [...acc, dict[index]]
            }, [])
            .join()
          this.$message.warning(
            `There are edits that the system failed to save, please fill out and save the edits again after clicking confirm.  Failed saving - [${msg}]`
          )
        }
      } else {
        this.rootChangeLoading(false)
        this.$message.error('Please fill in all required')
        klook.scrollElError()
        return false
      }
      await ajax.get(ADMIN_API.act.modify_completed, {
        params: {
          snapshot_modify_id: modify_id
        }
      })
      this.rootChangeLoading(false)
    },
    initFloorTimelineData: _.debounce(function() {
      let arr = _.cloneDeep(this.orderArr)
      this.displayFactory('activity_picture') || arr.pop()
      let obj = {
        floorData: {
          list: arr.map((key) => {
            let obj = _.merge({}, this[key], this.getCountObj(this[key], key))
            return obj
          })
        }
      }
      klook.bus.$emit('floorTimeline2bus', obj)
      return obj
    }),
    getCountObj(data, key) {
      let { form, schemaConfig } = this
      let { countObj = {} } = data
      let countKeys = Object.entries(countObj)
      let num = 0,
        count = countKeys.length
      let requireNum = 0,
        requireCount = 0
      countKeys.forEach((arr) => {
        let basic_attr_config = schemaConfig[arr[0]] || {}
        let val = arr[1]
        let requireFlag = false
        if (typeof val === 'string') {
          val = _.get(form, val)
        } else {
          if (val.require) {
            requireFlag = val.require
            val.require
          }
          let tempVal = _.get(form, val.mapValue)
          val =
            typeof val.callback === 'function' ? val.callback(tempVal) : tempVal
        }
        let isAdd = val || typeof val === 'number' || typeof val === 'boolean'
        isAdd && num++
        if (basic_attr_config.validation === 1 || requireFlag) {
          requireCount++
          isAdd && requireNum++
        }
        // console.log(arr, val, basic_attr_config.validation === 1)
      })
      //特殊处理 start
      if (key === 'basicInfo') {
        let model = this.modelConfig.model
        let area = this.myForm.area || {}
        let arr = []
        if (model === 1) {
          arr = ['country_id', 'city_id']
          count += 2
          requireCount += 2
        } else if (model === 2) {
          arr = ['country_id', 'city_id', 'country_id_from', 'city_id_from']
          count += 4
          requireCount += 4
        }
        arr.forEach((key) => {
          let val = area[key]
          if (klook.getType(val) === 'Array' && val.length) {
            // 数组
            num++
            requireNum++
          } else if (klook.getType(val) !== 'Array' && val) {
            num++
            requireNum++
          }
        })
      }
      //特殊处理 end
      let obj = {
        num,
        count,
        requireNum,
        requireCount,
        require: !!requireCount
      }
      return obj
    },
    cacheData() {
      this.cacheForm.form = _.cloneDeep(this.form)
      this.cacheForm.hide_water_mark = _.cloneDeep(this.hide_water_mark)
      if (this.$refs.photo_banner) {
        this.cacheForm.photo_banner = _.cloneDeep(
          this.$refs.photo_banner.getFormatParams()
        )
      }
      if (this.$refs.photo_activity) {
        this.cacheForm.photo_activity = _.cloneDeep(
          this.$refs.photo_activity.getFormatParams()
        )
      }
    },
    // 获取所有 banner desktop
    getAllbannersDesktop() {
      const photo_banner = this.$refs.photo_banner
        ? this.$refs.photo_banner.getList() || []
        : []
      let banner_desktops =
        photo_banner.map((v) => {
          return {
            ...v.DESKTOP,
            typeFlag: 'banner'
          }
        }) || []
      const photo_activity = this.$refs.photo_activity
        ? this.$refs.photo_activity.getList() || []
        : []
      const banner_in_activity_images =
        photo_activity.filter((v) => v.banner_display) || []
      banner_desktops = [
        ...banner_desktops,
        ...banner_in_activity_images.map((v) => {
          return {
            ...v.DESKTOP,
            typeFlag: 'activity'
          }
        })
      ]
      return banner_desktops
    },
    // 初始化 banner info
    async initBannerInfo() {
      let promise_arr = []
      if (this.displayFactory('activity_banner')) {
        promise_arr.push(
          this.$refs.photo_banner &&
            this.$refs.photo_banner.$nextTick((_) => true)
        )
      }
      if (this.displayFactory('activity_picture')) {
        promise_arr.push(
          this.$refs.photo_activity &&
            this.$refs.photo_activity.$nextTick((_) => true)
        )
      }
      await Promise.all(promise_arr)
      this.all_banners_desktop = this.getAllbannersDesktop()
      this.banner_count = this.all_banners_desktop.length
    },
    async initMediaInfo(data) {
      if (
        this.displayFactory('activity_picture') ||
        this.displayFactory('activity_banner')
      ) {
        let { banner, images, hide_water_mark } = data.photos
        this.hide_water_mark = hide_water_mark
        this.photo_banner = banner
        this.photo_activity = images
      }
    },
    async initObConfig() {
      let result = await ajax.get({
        url: ADMIN_API.act.get_activity_basic_info,
        params: {
          activity_id: this.activity_id // in query, required, 活动ID,
        }
      })
      // merchant 独立配置
      // if (isMerchant) {
      //   let activity_picture = result.basic_attr_config.filter(
      //     (item) => item.attr_id === 'activity_picture'
      //   )
      //   if (activity_picture && activity_picture[0]) {
      //     activity_picture[0].validation = 1
      //     activity_picture[0].config = '{"validation": 1}'
      //   }
      // }
      document.title = `${result.activity_id}-${result.activity_title}`
      this.basicInfoResult = result
      // OB 不需要配置，但这里需要的
      let common = {
        address_desc: {
          validation: 2,
          required: false
        },
        affiliate: {
          validation: 2,
          required: false
        }
      }
      let { basic_attr_config = [] } = result
      this.schemaConfig = {
        ...common,
        ...basic_attr_config.reduce(
          (acc, curr) => ({
            ...acc,
            [curr.attr_id]: {
              ...curr,
              config: (curr.config && JSON.parse(curr.config)) || {},
              ...this.getCustomConf(curr)
            }
          }),
          {}
        )
      }
      this.form.best_price_guarantee = _.get(
        this.schemaConfig,
        'best_price_guarantee.config.default_choices',
        0
      )
      this.initData(result)
      await this.initMediaInfo(result)
    },
    // 根据OB 的字段，生成
    getCustomConf(data) {
      let common = {
        required: data.validation === 1
      }
      if (this.MULTI_LANG_INPUT.includes(data.attr_id)) {
        return {
          ...common,
          requiredEn: data.attr_id === 'activity_title'
        }
      } else if (
        ['activity_banner', 'activity_picture'].includes(data.attr_id)
      ) {
        return {
          rules: {
            ...common,
            validator: this.validatorDefault
          }
        }
      } else {
        return {
          rules: {
            ...common,
            validator: this.validatorDefault
          }
        }
      }
    },
    displayFactory(field) {
      return (
        (this.schemaConfig[field] &&
          this.schemaConfig[field].validation !== 3) ||
        false
      )
    },
    getRequired(field) {
      return (
        (this.schemaConfig[field] &&
          this.schemaConfig[field].validation === 1) ||
        false
      )
    },
    getMultiAttrFactory(type, conf = {}) {
      let current = this.schemaConfig[type] || {}
      return {
        required: current.required,
        requiredEn: current.requiredEn,
        valField: type,
        label: this.getCurrentLocal(this.schemaConfig, {
          type,
          field: 'name_multilang'
        }),
        description: this.getCurrentLocal(this.schemaConfig, {
          type,
          field: 'desc_multilang',
          defaultVal: ''
        }),
        ...conf
      }
    },
    handleMapChange({ position, place_id }) {
      this.form.address = position
      this.form.place_id = place_id
    },
    publishedLanguage(v) {
      if (!this.isCreated) {
        // 仅在创建的时候才能更改
        this.published_language = Array.isArray(v) ? v[0] : v
      }
    },

    handleBannerCountChange() {
      this.all_banners_desktop = this.getAllbannersDesktop()
      this.banner_count = this.all_banners_desktop.length
      this.validateBannerCount()
    },
    // 转换图片的pre_process: c_crop,h_750,w_1891,x_494,y_250/
    getCropObj(pre_process) {
      let str = pre_process
        .replace('/', '')
        .replace(/_/g, '":"')
        .replace(/,/g, '","')
      return JSON.parse(`{"${str}"}`)
    },
    // 检测 banner 数量以及比例是否匹配
    validateBannerCount() {
      const photo_banner = this.$refs.photo_banner
        ? this.$refs.photo_banner.getList() || []
        : []
      const photo_activity = this.$refs.photo_activity
        ? this.$refs.photo_activity.getList() || []
        : []
      const all_photo = [
        ...photo_banner.map((v) => v.DESKTOP),
        ...photo_activity.map((v) => v.DESKTOP)
      ]
      // banner 数量大于 1
      if (this.banner_count > 1) {
        // 如果有 banner 图片的比例是 1160:460, 提示全部重裁
        let unvalidate_item = all_photo.find((v) => {
          if (!v.pre_process) {
            return false
          } else if (v.pre_process.includes('1160:460')) {
            return true
          } else {
            let obj = this.getCropObj(v.pre_process)
            return +(obj.w / obj.h).toFixed(1) === 2.5
          }
        })
        if (unvalidate_item) {
          this.$info({
            content: __('act_photo_add_banner'),
            title: __('global_confirm'),
            type: 'info'
          })
          this.trigger_banner_check += 1
          return false
        }
      } else {
        // 如果有 banner 图片的比例是 960:460, 提示全部重裁
        let unvalidate_item = all_photo.find((v) => {
          if (!v.pre_process) {
            return false
          } else if (v.pre_process.includes('960:460')) {
            return true
          } else {
            let obj = this.getCropObj(v.pre_process)
            return +(obj.w / obj.h).toFixed(1) === 2.1
          }
        })
        if (unvalidate_item) {
          this.$info({
            content: this.$t('act_photo_less_banner'),
            title: __('global_confirm'),
            type: 'info'
          })
          this.trigger_banner_check += 1
          return false
        }
      }
      return true
    },

    validateForm() {
      return new Promise((resolve) =>
        this.$refs.form.validate((valid) => resolve(valid))
      )
    },
    async getValidateFromStatus() {
      let result = await Promise.all([
        (async () => {
          return (
            ((await this.$refs.language_destination) &&
              this.$refs.language_destination.validateForm()) ||
            true
          )
        })(),
        (async () => {
          return await this.validateForm()
        })(),
        ...this.MULTI_LANG_INPUT.reduce((acc, curr) => {
          if (
            _.get(this, `schemaConfig[${curr}].validation`, 3) !== 3 &&
            this.$refs[curr]
          ) {
            return [...acc, this.$refs[curr].validateForm()]
          }
          return acc
        }, []),
        (async () => {
          // 校验 banner / activity 图片
          const photo_banner = this.$refs.photo_banner
          const photo_activity = this.$refs.photo_activity
          // let minCount = photo_activity.getList().length >= 3
          const validate_pass = await Promise.all([
            photo_banner ? photo_banner.validate() : true,
            photo_activity ? photo_activity.validate() : true
          ])
          return !validate_pass.includes(false)
        })()
      ])
      return result.every((item) => item)
    }
  }
}
</script>
