<template>
  <div class="floor-timeline">
    <a-spin :spinning="spinning" :delay="200">
      <div class="floor-timeline-box">
        <a-anchor
          :offsetTop="72"
          class="common-anchor-style hide-all"
          @change="handleChangeAnchor"
        >
          <a-timeline class="common-timeline-style">
            <a-timeline-item
              v-for="(item, i) in floorData.list"
              :key="i"
              @click="handleClick(item)"
            >
              <template slot="dot">
                <div
                  class="common-cicle-style"
                  :style="
                    `#${item.id || item.text || item.title}` == anchorId
                      ? styleObj['finish']
                      : ''
                  "
                />
              </template>
              <a-anchor-link :href="`#${item.id || item.text || item.title}`">
                <div class="content-box" slot="title">
                  <span class="common-required-star star-box">{{
                    item.require || item.requireCount ? '*' : ''
                  }}</span>
                  <span :title="item.text || item.title" class="text">
                    <div class="common-two-line">
                      {{ item.text || item.title }}
                    </div>
                  </span>
                  <span v-if="item.count" class="fields">{{
                    `${item.num}/${item.count}`
                  }}</span>
                  <span class="require-fields">
                    <requireFields
                      :num="item.requireNum"
                      :count="item.requireCount"
                    />
                  </span>
                </div>
              </a-anchor-link>
            </a-timeline-item>
          </a-timeline>
          <div v-if="!$route.meta.hideSummary" class="group-summary-box">
            <a-button
              type="primary"
              @click="drawerData.visible = !drawerData.visible"
              >{{ $t('act_group_summary') }}</a-button
            >
            <summaryDrawer
              v-if="drawerData.visible"
              :drawer-data="drawerData"
              :groups-summary="groupsSummary"
            />
          </div>
        </a-anchor>
      </div>
    </a-spin>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import requireFields from '@activity/components/requireFields'
import summaryDrawer from '@activity/components/groupSummary/drawer'

export default {
  components: { requireFields, summaryDrawer },
  data() {
    return {
      anchorId: '',
      drawerData: {
        visible: false
      },
      spinning: false,
      styleObj: {
        finish: { borderColor: '#0091FF' }
      },
      floorData: {
        list: [
          // {
          //   require: true,
          //   text: 'Basic Info',
          //   style: 'finish'
          // },
          // { require: true, text: 'Banner Photo' },
          // { require: false, text: 'Activity Photo' },
        ]
      }
    }
  },
  watch: {
    $route: {
      deep: true,
      handler(v, oldV) {
        if (oldV.name && v.name !== oldV.name) {
          this.spinning = true
        }
      }
    }
  },
  computed: {
    groupsSummary() {
      return this.getGroupsSummary()
    }
  },
  created() {
    klook.bus
      .$off('floorTimeline2bus', this.floorTimeline2bus)
      .$on('floorTimeline2bus', this.floorTimeline2bus)
  },
  methods: {
    handleClick({ id }) {
      klook.bus.$emit('floorTimelineAnchorChange2bus', id)
    },
    handleChangeAnchor(link) {
      this.anchorId = link
    },
    ...mapGetters(['getGroupsSummary']),
    floorTimeline2bus(data = {}, method = 'update') {
      typeof this[method] === 'function' && this[method](data)
      this.spinning = false
    },
    update(data) {
      for (let key in data) {
        this.$set(this, key, _.merge({}, data[key]))
      }
    },
    handleClickFloor(e, item, i) {
      // let titles = document.querySelectorAll('h2')
    }
  }
}
</script>

