<template>
  <div ref="footer" class="bulk-modification-footer">
    <a-spin :spinning="isTakeRateLoading">
      <TakeRateTips
        v-if="type !== 'button' && (overrideTakeRate || lastTakeRate)"
        style="margin: 0 0 10px;"
        :override-take-rate="overrideTakeRate"
        :last-take-rate="lastTakeRate"
      />
    </a-spin>
    <div v-show="type === 'button'">
      <a-button
        v-if="!isFixedPriceModel"
        :style="{ marginRight: '12px', padding: 0 }"
        type="link"
        @click="handleOpenDefaultPriceSetting"
      >
        <a-icon type="edit" />
        {{ $t('package_bulk_modification_default_price_setting') }}
      </a-button>
      <a-button
        v-if="!disableSellingPrice"
        type="primary"
        :style="{ marginRight: '12px' }"
        @click="() => (type = 'sellingPrice')"
      >
        <a-icon type="edit" />
        {{ $t('package_bulk_modification_edit_selling_price') }}
      </a-button>
      <a-popover v-else placement="topRight">
        <template slot="content">
          <span>{{ $t('package_bulk_modification_cost_price_tips') }}</span>
          <a-button
            type="link"
            style="margin: 0; padding: 0;"
            @click="handleOpenFilter"
          >
            {{ $t('global_filter') }}
          </a-button>
        </template>
        <a-button disabled type="primary" :style="{ marginRight: '12px' }">
          <a-icon type="edit" />
          {{ $t('package_bulk_modification_edit_selling_price') }}
        </a-button>
      </a-popover>
    </div>
    <div v-show="type !== 'button'">
      <a-spin :spinning="loading">
        <div style="max-height: 300px; overflow: auto;">
          <a-form-model
            ref="form"
            layout="horizontal"
            :model="form"
            :rules="rules"
          >
            <div v-if="type === 'sellingPrice'" class="align-left w-800">
              <div class="form-wrapper">
                <div style="width: 50%">
                  <a-form-model-item
                    :label="$t('package_price_selling')"
                    prop="sellingPrice"
                    :labelCol="{ span: 10 }"
                    :wrapperCol="{ span: 13 }"
                  >
                    <a-input
                      v-model="form.sellingPrice"
                      :suffix="sellingCurrency"
                      :placeholder="$t('global_please_input')"
                      style="width: 240px;"
                      @keyup.native="handleRound('sellingPrice')"
                      @blur="computeTakeRate"
                    />
                  </a-form-model-item>
                </div>
                <div style="width: 50%">
                  <a-form-model-item
                    :label="$t('new_take_rate')"
                    prop="takeRate"
                    :labelCol="{ span: 10 }"
                    :wrapperCol="{ span: 13 }"
                  >
                    <a-input
                      v-model="form.takeRate"
                      suffix="%"
                      :placeholder="$t('global_please_input')"
                      style="width: 240px;"
                      @keyup.native="handleRoundTakeRate"
                      @blur="handleTakeRateBlur"
                    />
                  </a-form-model-item>
                </div>
              </div>
              <a-form-model-item
                :label="$t('package_custom_selling')"
                prop="specialSellingPriceList"
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 19 }"
              >
                <SpecialSellingPriceList
                  ref="ssp"
                  :selling-currency="sellingCurrency"
                  :special-selling-price-list="form.specialSellingPriceList"
                />
              </a-form-model-item>
              <a-form-model-item
                :label="$t('reason_for_price_changing')"
                prop="reason"
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 19 }"
              >
                <a-select
                  v-model="form.reason"
                  style="width: 240px;"
                  @change="handleReasonSelect"
                >
                  <a-select-option
                    v-for="reason in priceChangeReasonList"
                    :key="reason.value"
                    :value="reason.value"
                  >
                    {{ reason.text }}
                  </a-select-option>
                </a-select>
              </a-form-model-item>
              <a-form-model-item
                v-if="form.reason === 0"
                label=" "
                prop="reasonText"
                :colon="false"
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 19 }"
              >
                <a-textarea
                  id="js-reason-text"
                  v-model="form.reasonText"
                  :placeholder="$t('global_please_input')"
                  :rows="4"
                  :max-length="100"
                />
              </a-form-model-item>
            </div>
          </a-form-model>
        </div>

        <div>
          <a-button :style="{ marginRight: '8px' }" @click="handleCancel">
            {{ $t('global_button_cancel') }}
          </a-button>
          <a-popover v-if="disableSellingPrice" placement="topRight">
            <template slot="content">
              <span>{{ $t('package_bulk_modification_cost_price_tips') }}</span>
              <a-button
                type="link"
                style="margin: 0; padding: 0;"
                @click="handleOpenFilter"
              >
                {{ $t('global_filter') }}
              </a-button>
            </template>
            <a-button disabled type="primary">
              {{ $t('global_save') }}
            </a-button>
          </a-popover>
          <a-button
            v-else-if="selectedRowKeys.length > 0"
            type="primary"
            :loading="saveLoading"
            @click="handleSave"
          >
            {{ $t('global_save') }}
          </a-button>
          <a-popover v-else placement="topRight">
            <template slot="content">
              <span>{{ $t('package_bulk_modification_save_tips') }}</span>
            </template>
            <a-button
              type="primary"
              disabled
              :loading="saveLoading"
              @click="handleSave"
            >
              {{ $t('global_save') }}
            </a-button>
          </a-popover>
        </div>
      </a-spin>
    </div>
    <DefaultPriceSettingAdmin ref="defaultPriceSetting" />
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import {
  round2Decimal,
  getEditLang,
  provingTakeRate,
  computeTR,
  setTRAndGetBase
} from '@activity/utils'
import { priceChangeReasonList } from '@activity/pages/package/package_const.js'
import TakeRateTips from '@activity/pages/components/TakeRateTips.vue'
import SpecialSellingPriceList from '@activity/pages/components/SpecialSellingPriceList.vue'
import DefaultPriceSettingAdmin from './DefaultPriceSettingAdmin.vue'

export default {
  name: 'BulkModificationFooterAdmin',
  components: {
    TakeRateTips,
    SpecialSellingPriceList,
    DefaultPriceSettingAdmin
  },
  inject: ['packageId', 'bulkModification'],
  props: {
    data: {
      type: Object,
      required: true
    },
    selectedUnit: {
      type: Number,
      default: 0
    },
    selectedRowKeys: {
      type: Array,
      default: () => []
    }
  },
  data() {
    const validateSpecialSellingPriceList = (rule, value, callback) => {
      const isValid = value.every((item) => !!item.price)

      if (isValid) {
        callback()
      } else {
        callback(new Error(this.$t('global_please_input')))
      }
    }

    return {
      priceChangeReasonList,

      type: 'button',
      loading: false,
      isTakeRateLoading: false,
      saveLoading: false,
      overrideTakeRate: '',
      lastTakeRate: '',
      exchangeRate: '',
      costPrice: '',
      retailPrice: '',
      form: {
        sellingPrice: '',
        takeRate: '0',
        specialSellingPriceList: [],
        reason: 1,
        reasonText: ''
      },
      defaultForm: {
        sellingPrice: '',
        takeRate: '0',
        specialSellingPriceList: [],
        reason: 1,
        reasonText: ''
      },
      rules: {
        sellingPrice: [
          {
            required: true,
            message: this.$t('global_please_input'),
            trigger: 'blur'
          }
        ],
        takeRate: [
          {
            required: true,
            message: this.$t('global_please_input'),
            trigger: 'blur'
          }
        ],
        specialSellingPriceList: [
          {
            validator: validateSpecialSellingPriceList,
            trigger: 'blur'
          }
        ],
        reason: [
          {
            required: true,
            message: this.$t('global_please_select'),
            trigger: 'blur'
          }
        ],
        reasonText: [
          {
            required: true,
            message: this.$t('global_please_input'),
            trigger: 'blur'
          }
        ]
      }
    }
  },
  computed: {
    ...mapState({
      packageSkuData: (state) => state.packageSkuData,
      calendarUnit: (state) => state.calendarUnit,
      priceModel: (state) => state.priceModel
    }),
    ...mapGetters(['mainUnit']),
    isMainUnit() {
      return this.mainUnit && this.mainUnit.sku_id === this.selectedUnit
    },
    isFixedPriceModel() {
      return this.priceModel === 0
    },
    mainUnitName() {
      if (!this.mainUnit) {
        return ''
      }

      const target = (this.mainUnit.local || []).find(
        (item) => item.language === getEditLang()
      )
      return target ? target.unit_name : ''
    },
    costCurrency() {
      const { currency } = this.packageSkuData
      return (currency || {}).merchant_currency || ''
    },
    sellingCurrency() {
      const { currency } = this.packageSkuData
      return (currency || {}).selling_currency || ''
    },
    selectedList() {
      return (this.data.calendar || []).filter((item) =>
        this.selectedRowKeys.includes(item.schedule_id)
      )
    },
    disableSellingPrice() {
      return this.selectedList.length > 0
        ? !this.selectedList.every(
            (item) => item.cost_price === this.selectedList[0].cost_price
          )
        : false
    },
    defaultCostPrice() {
      return this.selectedList.length > 0 ? this.selectedList[0].cost_price : ''
    }
  },
  watch: {
    async type(val) {
      // 固定价格模型要先请求价格数据
      if (
        this.isFixedPriceModel &&
        (val === 'price' || val === 'sellingPrice')
      ) {
        await this.initFixedPrice()
        this.$emit('toggleFrozen', true)
      } else if (val === 'sellingPrice') {
        await this.getTakeRate()
      }

      // 重新设置父容器的下间距
      this.$nextTick(() => {
        const footerDom = this.$refs.footer
        this.$emit('setMargin', footerDom ? footerDom.offsetHeight : 0)
      })

      // 选择price的时候，table中的price要高亮，其它同理
      // if (val === 'button') {
      //   this.$emit('modifyHighlightList', null)
      // } else if (val === 'sellingPrice') {
      //   this.$emit('modifyHighlightList', ['sellingPrice'])
      // }
    },
    selectedList() {
      if (
        this.type === 'sellingPrice' &&
        !this.isFixedPriceModel &&
        this.selectedList.length > 0
      ) {
        this.getTakeRate()
      }
    },
    defaultCostPrice() {
      this.computeTakeRate()
    }
  },
  async created() {
    await this.getExchangeRate()
  },
  methods: {
    async getExchangeRate() {
      this.loading = true
      const res = await ajax.get(ADMIN_API.act.get_exchange_rate, {
        params: {
          currency_from: this.costCurrency,
          currency_to_list: this.sellingCurrency
        }
      })
      this.exchangeRate = res[0].to_value
      this.loading = false
    },
    async initFixedPrice() {
      this.loading = true
      this.isTakeRateLoading = true
      const res = await ajax.get(ADMIN_API.act.query_fixed_price, {
        params: {
          sku_id: this.selectedUnit
        }
      })

      this.overrideTakeRate = res.override_take_rate
      this.lastTakeRate = res.last_take_rate
      this.costPrice = res.cost_price
      this.retailPrice = res.retail_price
      this.form.sellingPrice = res.selling_price
      this.form.takeRate = computeTR(
        res.cost_price,
        res.selling_price,
        this.exchangeRate
      )
      this.form.specialSellingPriceList = res.special_selling_price
      this.form.reason = 1
      this.form.reasonText = ''

      this.loading = false
      this.isTakeRateLoading = false
    },
    async getTakeRate() {
      this.isTakeRateLoading = true
      const res = await ajax.post(
        ADMIN_API.act.get_take_rate_for_update_calendar_price,
        {
          data: {
            sku_id: this.calendarUnit,
            time_point: this.selectedList.map((item) => item.start_time)
          }
        }
      )
      this.isTakeRateLoading = false

      this.lastTakeRate = res.last_take_rate
      this.overrideTakeRate = res.override_take_rate
    },
    handleOpenDefaultPriceSetting() {
      this.$refs.defaultPriceSetting &&
        this.$refs.defaultPriceSetting.open(this.selectedUnit)
    },
    handleRound(field) {
      this.form[field] = round2Decimal(this.form[field])
    },
    handleCancel() {
      this.type = 'button'
      this.form = _.cloneDeep(this.defaultForm)
      this.$emit('toggleFrozen', false)
    },
    handleOpenFilter() {
      this.$emit('openFilter')
    },
    reset() {
      this.handleCancel()
    },
    handleReasonSelect() {
      if (this.form.reason === 0) {
        this.$nextTick(() => {
          document.querySelector('#js-reason-text').scrollIntoView()
        })
      }
    },
    handleRoundTakeRate() {
      this.form.takeRate = provingTakeRate(this.form.takeRate)
    },
    computeTakeRate() {
      this.form.takeRate = computeTR(
        this.costPrice || this.defaultCostPrice,
        this.form.sellingPrice,
        this.exchangeRate
      )
    },
    handleTakeRateBlur() {
      const res = setTRAndGetBase(
        this.costPrice || this.defaultCostPrice,
        this.form.sellingPrice,
        this.form.takeRate,
        this.exchangeRate
      )

      this.form.sellingPrice = res.base
    },
    async handleSave() {
      const valid = await this.$refs.form.validate().catch(() => false)

      if (!valid) {
        return
      }

      this.saveLoading = true
      let res = {}
      if (this.type === 'sellingPrice') {
        res = await this.saveSellingPrice()
      }
      this.saveLoading = false

      if (res.success) {
        this.type = 'button'
        this.bulkModification.update()
        this.$message.success(this.$t('global_modify_success'))
      } else {
        this.$message.error(this.$t('global_modify_error'))
      }
    },
    async saveSellingPrice() {
      const apiUrl = this.isFixedPriceModel
        ? ADMIN_API.act.audit_update_fixed_price
        : ADMIN_API.act.audit_update_calendar_price

      let note = this.form.reasonText
      if (this.form.reason !== 0) {
        const target = this.priceChangeReasonList.find(
          (item) => item.value === this.form.reason
        )
        note = target ? '' : target.text
      }

      const specialSellingPriceData = this.$refs.ssp.getSpecialSellingPriceData()
      const res =
        (await ajax.postBody(apiUrl, {
          data: {
            note,
            sku_id: this.selectedUnit,
            selling_currency: this.sellingCurrency,
            selling_price: Number(this.form.sellingPrice),
            time_point: this.selectedList.map((item) => item.start_time),
            special_selling_price: specialSellingPriceData.map((item) => ({
              ...item,
              price: Number(item.price)
            }))
          }
        })) || {}

      return res
    }
  }
}
</script>

<style lang="scss">
.bulk-modification-footer {
  .ant-form-item-label > label {
    padding-right: 8px;
    white-space: break-spaces;
    display: inline-block;
    line-height: normal;
  }
}
</style>

<style lang="scss" scoped>
.bulk-modification-footer {
  position: absolute;
  padding: 10px 16px;
  right: 0;
  bottom: 0;
  width: 100%;

  border-top: 1px solid #e9e9e9;
  background: #fff;
  text-align: right;
  z-index: 3;

  .align-left {
    text-align: left;
  }

  .w-800 {
    width: 800px;
  }

  .form-wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}
</style>
