<template>
  <div class="special-selling-price-list">
    <div class="special-selling-price-wrap">
      <template v-for="(item, index) in specialSellingPriceList">
        <div
          v-if="item.flag !== 1"
          :key="`${index}-${item.id}-${item.flag}`"
          class="special-selling-price-item"
        >
          <a-input
            v-model="item.price"
            style="width: 46%;"
            :placeholder="$t('global_please_input')"
            @keyup.native="handleRoundSpecialSellingPrice(item)"
          />
          <a-select
            show-search
            v-model="item.currency"
            style="width: 35%;margin-left: 8px;"
            :filter-option="filterOption"
          >
            <a-select-option
              v-for="currency in sortedCurrencyList"
              :key="currency"
              :value="currency"
              :disabled="selectedCurrencyList.includes(currency)"
            >
              {{ currency }}
            </a-select-option>
          </a-select>
          <CircleDeleteButton
            style="margin-left: 8px;"
            @click="handleDeleteSpecialSellingPrice(index, item)"
          />
        </div>
      </template>
    </div>
    <div class="add-btn-wrap">
      <a-button type="link" icon="plus" @click="handleAddSpecialSellingPrice">
        {{ $t('global_add') }}
      </a-button>
    </div>
  </div>
</template>

<script>
import { round2Decimal } from '@activity/utils'
import { SUPPORT_CURRENCY_SYMBO } from '@activity/pages/package/package_const.js'
import CircleDeleteButton from '@activity/components/CircleDeleteButton.vue'

export default {
  name: 'SpecialSellingPriceList',
  components: {
    CircleDeleteButton
  },
  props: {
    sellingCurrency: {
      type: String,
      required: true
    },
    specialSellingPriceList: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      cachedSpecialSellingPriceList: [],
      defaultSpecialSellingPrice: {
        id: 0,
        flag: -1, // 注意，这里新建的flag置为-1，后面处理的时候要处理为0
        price: '',
        currency: ''
      }
    }
  },
  computed: {
    sortedCurrencyList() {
      return SUPPORT_CURRENCY_SYMBO.map((item) => item[0]).sort()
    },
    selectedCurrencyList() {
      return this.specialSellingPriceList.map((item) => item.currency)
    }
  },
  watch: {
    specialSellingPriceList: {
      immediate: true,
      handler() {
        this.cachedSpecialSellingPriceList = _.cloneDeep(
          this.specialSellingPriceList.filter((item) => item.flag !== -1)
        )
      }
    }
  },
  methods: {
    filterOption(input, option) {
      console.log('filterOption', input, option)
      return (
        option.componentOptions.children[0].text
          .toLowerCase()
          .indexOf(input.toLowerCase()) >= 0
      )
    },
    handleDeleteSpecialSellingPrice(index, item) {
      // 如果 id 是 0 的话，就直接删除；否则把 flag 置为 1
      if (item.id === 0) {
        this.specialSellingPriceList.splice(index, 1)
      } else {
        this.specialSellingPriceList[index].flag = 1
      }
    },
    handleRoundSpecialSellingPrice(item) {
      item.price = round2Decimal(item.price)
    },
    handleAddSpecialSellingPrice() {
      const item = _.cloneDeep(this.defaultSpecialSellingPrice)
      item.currency = this.sellingCurrency
      this.specialSellingPriceList.push(item)
    },
    getSpecialSellingPriceData() {
      // 后端限制，在一些场景下（固定价格改价，日历价格改基本设置）
      // 由于后端是增量修改，不是直接覆盖，所以需要 diff 一遍，获取增量
      const deletedList = this.cachedSpecialSellingPriceList
        .filter(
          (item) =>
            item.currency && !this.selectedCurrencyList.includes(item.currency)
        )
        .map((item) => ({ ...item, flag: 1 }))
      const selectedList = this.specialSellingPriceList.map((item) => {
        // 重置 id，并且把新建的 id 置为0
        const target = this.cachedSpecialSellingPriceList.find(
          (cachedItem) => item.currency === cachedItem.currency
        )
        const { id, flag } = target || {}
        return {
          ...item,
          id: id || 0,
          flag: flag && flag === -1 ? 0 : flag
        }
      })
      return [...deletedList, ...selectedList]
    }
  }
}
</script>

<style lang="scss" scoped>
.special-selling-price-list {
  .special-selling-price-wrap {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
  }

  .special-selling-price-item {
    width: 45%;
    margin-bottom: 12px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  .add-btn-wrap {
    .ant-btn {
      padding: 0;
    }
  }
}
</style>
