<template>
  <div>
    <a-form-model-item
      :label="$t('act_merchant')"
      prop="merchant_id"
      :rules="rules"
      :colon="false"
    >
      <!-- <a-tooltip
        placement="right"
        overlayClassName="common-tooltip-style"
        :title="$attrs.description"
      > -->
      <a-input disabled :value="`${merchant_id}-${merchants.merchant_name}`" />
      <!-- </a-tooltip> -->
      <p v-if="published">{{ $t('revise_price_published_merchant') }}</p>
    </a-form-model-item>

    <template v-if="dialogTierVisible">
      <!-- <merchant-select-tier
        :visible.sync="dialogTierVisible"
        :package_info="package_info"
        :activity_id="activity_id"
        :new_currency="new_currency"
        @close="cancelMerchantChange"
        :package_id="package_id"
      ></merchant-select-tier> -->
    </template>
    <!-- <unit-price
      :package_id="+package_id"
      :activity_id="+activity_id"
      :base_currency="pkg_base_currency"
      :cost_currency="new_currency"
      :unit_ids="unit_ids"
      ref="unit_price"
      v-if="changeM"
      @done="cancelMerchantChange(true)"
      @cancel="cancelMerchantChange"
    >
    </unit-price> -->
  </div>
</template>

<script>
// import unitPrice from '../../package/price_calender/unit_price'
// import merchantSelectTier from '../../price/tpl_merchant_select_change_tier.vue'
import { mapActions } from 'vuex'
import { bestMatchLang } from '@activity/utils'

var change_merchant_mixin = {
  data() {
    return {
      new_currency: '',
      dialogVisible: false,
      pkg: {},
      canSubmit: true
    }
  },
  methods: {
    test(...args) {
      console.log(args)
    },
    closeDialog() {
      this.$emit('close-dialog', false)
    },
    bestMatchLang: bestMatchLang
  }
}

const tier_mixin = {
  data() {
    return {
      dialogTierVisible: false
    }
  },
  methods: {
    async cancelMerchantChange(isConfirm) {
      this.dialogTierVisible = false
      if (!isConfirm) {
        this.merchant_id = this.cache_merchant_id
      } else {
        await ADMIN_API.updateMerchantAndCost({
          data: {
            merchant_id: this.merchant_id
          },
          package_id: this.package_id
        })

        this.cache_merchant_id = this.merchant_id
        this.$message.success(this.$t('global_success'))
      }
    }
  }
}

export default {
  name: 'merchant-select',
  components: {
    // merchantSelectTier,
    // unitPrice
  },
  props: {
    activity_id: {
      type: Number
    },
    package_info: {
      type: Object,
      required: true
    },
    value: {
      type: [Number, String],
      required: true
    },
    rules: {
      type: [Array, Object],
      default: () => ({
        required: true
      })
    }
  },
  install(Vue) {
    // https://vuejs.org/v2/guide/plugins.html
    Vue.component(this.name, this)
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  data() {
    return {
      temp_new_merchant_id: '',
      merchants: [],
      all_merchants: [],

      merchant_id: '',
      cache_merchant_id: '',
      published: false,
      unit_ids: [],
      pkg_model: '',
      changeM: false,
      pkg_base_currency: ''
    }
  },
  computed: {
    package_id() {
      //这里不知道为什么打印出来package_info里面有package_id，单独打印package_info.package_id为undefined
      return _.isEmpty(this.package_info)
        ? 0
        : { ...this.package_info }.package_id || 0
    }
  },
  watch: {
    package_info: {
      immediate: true,
      deep: true,
      handler(v) {
        this.published = _.get(v, 'published', false)
      }
    },
    value() {
      if (this.value !== 0) {
        this.merchant_id = this.value
      }
    },
    merchant_id: {
      async handler(newVal, oldVal) {
        if (newVal && oldVal === '') {
          this.cache_merchant_id = newVal
        } else if (oldVal && newVal !== this.cache_merchant_id) {
          await this.changeMerchant(newVal)
        }
        this.$emit('change', newVal)
      },
      immediate: true
    }
    // async package_id(v) {
    //   this.pkg_model = (
    //     await ADMIN_API.getPkgPricingModel({
    //       package_ids: v + ''
    //     })
    //   ).result[0].pricing_model
    // }
  },
  methods: {
    ...mapActions(['updateActItemData2actions']),
    remoteMethod: _.debounce(function(query) {
      this.merchants = this.all_merchants
        .filter((v) =>
          `${v.merchant_id}${v.en_name}${v.cn_name}`
            .toLowerCase()
            .includes(String(query).toLowerCase())
        )
        .slice(0, 30)
    }, 200),
    disableMerchant(confirm_order, settlement_time) {
      return (
        this.package_info.merchant_confirm_type == 3 && settlement_time == 1
      ) // merchant pdf can not use  pay by redemption merchant
    },
    async changeMerchant(merchant_id) {
      if (!merchant_id) {
        return
      }

      var originalMerchantId = this.package_info.merchant_id
      if (!originalMerchantId) {
        return
      }
      this.new_currency = this.merchants.find(
        (ele) => ele.id === merchant_id
      ).currency

      let originalMerchant = this.all_merchants.find(
        (ele) => ele.id === originalMerchantId
      )
      let originalCurrency =
        (originalMerchant && originalMerchant.currency) || null

      if (!originalCurrency) {
        return
      }

      let act_data = (
        await ADMIN_API.getActPkgPrice({
          activity_id: this.activity_id
        })
      ).result

      if (
        _.isEmpty(act_data.package_summary) ||
        _.isEmpty(
          act_data.package_summary.find(
            (pkg) => pkg.package_id == this.package_info.package_id
          ).price
        )
      ) {
        // break if no unit
        console.info('no unit, return;')
        return
      }
      if (this.pkg_model === 1) {
        let pkg = act_data.package_summary.find(
          (pkg) => pkg.package_id == this.package_info.package_id
        )
        this.unit_ids = pkg.price
        this.pkg_base_currency = pkg.sell_currency
        this.changeM = true
        this.$nextTick(() => {
          this.$refs.unit_price.dialogVisible = true
        })
        return
      }
      let pkg_position = (
        await ADMIN_API.getPackagesPosition({
          package_id: this.package_id
        })
      ).result

      if (!pkg_position.price) {
        return
      }

      this.dialogTierVisible = true
    }
  },
  mixins: [change_merchant_mixin, tier_mixin],
  async mounted() {
    let data = await this.updateActItemData2actions({
      activity_id: this.activity_id
    })
    if (!data) return
    this.merchant_id = data.merchant_id
    this.merchants = data
    this.all_merchants = [data]
    console.log('merchant-select mounted')
    this.$emit('mounted', this.merchants)
  }
}
</script>
