<template>
  <a-form-model
    class="basic-new-take-form"
    ref="form"
    layout="horizontal"
    :model="form"
    :rules="rules"
  >
    <div class="form-wrapper">
      <div style="width: 50%">
        <a-form-model-item
          :label="$t('package_price_selling')"
          prop="sellingPrice"
          :labelCol="{ span: 10 }"
          :wrapperCol="{ span: 13 }"
        >
          <a-input
            v-model="form.sellingPrice"
            :suffix="sellingCurrency"
            :placeholder="$t('global_please_input')"
            @keyup.native="handleRoundSellingPrice"
            @blur="computeTakeRate"
          />
        </a-form-model-item>
      </div>
      <div style="width: 50%">
        <a-form-model-item
          :label="$t('new_take_rate')"
          prop="takeRate"
          :labelCol="{ span: 10 }"
          :wrapperCol="{ span: 13 }"
        >
          <a-input
            v-model="form.takeRate"
            suffix="%"
            :placeholder="$t('global_please_input')"
            @keyup.native="handleRoundTakeRate"
            @blur="handleTakeRateBlur"
          />
        </a-form-model-item>
      </div>
    </div>
    <a-form-model-item
      :label="$t('package_custom_selling')"
      prop="specialSellingPriceList"
      :labelCol="{ span: 5 }"
      :wrapperCol="{ span: 19 }"
    >
      <SpecialSellingPriceList
        ref="ssp"
        :selling-currency="sellingCurrency"
        :special-selling-price-list="form.specialSellingPriceList"
      />
    </a-form-model-item>
  </a-form-model>
</template>

<script>
import {
  round2Decimal,
  provingTakeRate,
  computeTR,
  setTRAndGetBase
} from '@activity/utils'
import SpecialSellingPriceList from '@activity/pages/components/SpecialSellingPriceList.vue'

export default {
  name: 'BasicTakeRateForm',
  components: {
    SpecialSellingPriceList
  },
  props: {
    // sellingPrice、costPrice、takeRate、specialSellingPriceList
    form: {
      type: Object,
      required: true
    },
    sellingCurrency: {
      type: String,
      required: true
    },
    exchangeRate: {
      type: [String, Number],
      default: ''
    }
  },
  data() {
    const validateSpecialSellingPriceList = (rule, value, callback) => {
      const isValid = value.every((item) => !!item.price)

      if (isValid) {
        callback()
      } else {
        callback(new Error(this.$t('global_please_input')))
      }
    }

    return {
      defaultSpecialSellingPrice: {
        price: '',
        currency: ''
      },
      rules: {
        sellingPrice: [
          {
            required: true,
            message: this.$t('global_please_input'),
            trigger: 'blur'
          }
        ],
        takeRate: [
          {
            required: true,
            message: this.$t('global_please_input'),
            trigger: 'blur'
          }
        ],
        specialSellingPriceList: [
          {
            required: true,
            validator: validateSpecialSellingPriceList,
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    handleRoundSellingPrice() {
      this.form.sellingPrice = round2Decimal(this.form.sellingPrice)
    },
    handleRoundTakeRate() {
      this.form.takeRate = provingTakeRate(this.form.takeRate)
    },
    computeTakeRate() {
      this.form.takeRate = computeTR(
        this.form.costPrice,
        this.form.sellingPrice,
        this.exchangeRate
      )
    },
    handleTakeRateBlur() {
      const res = setTRAndGetBase(
        this.form.costPrice,
        this.form.sellingPrice,
        this.form.takeRate,
        this.exchangeRate
      )

      this.form.sellingPrice = res.base
    },
    handleRoundSpecialSellingPrice(item) {
      item.price = round2Decimal(item.price)
    },
    validateForm() {
      return this.$refs.form.validate().catch(() => false)
    },
    clearValidate() {
      this.$refs.form.clearValidate()
    },
    handleDeleteSpecialSellingPrice(index) {
      this.form.specialSellingPriceList.splice(index, 1)
    },
    handleAddSpecialSellingPrice() {
      const item = _.cloneDeep(this.defaultSpecialSellingPrice)
      item.currency = this.sellingCurrency
      this.form.specialSellingPriceList.push(item)
    },
    getSpecialSellingPriceData() {
      return this.$refs.ssp.getSpecialSellingPriceData()
    }
  }
}
</script>

