<template>
  <div class="detail-groups-container">
    <!-- Copy -->
    <div v-if="copyGroupList" class="common-copy-wrap">
      <a-button :disabled="disableCopyPkg" @click="handleCopyGroupList">{{
        $t('21857')
      }}</a-button>
    </div>

    <a-skeleton :loading="initLoading">
      <div
        class="common-basic-box group-item"
        v-for="(item, i1) in groupList"
        :key="item.id"
        :id="`group-${item.id}`"
        :ref="`group-${item.id}`"
      >
        <div class="group__header common-name-font">
          <div class="__title">
            <span class="group__name">
              <i
                v-if="item.required_widget_num > 0"
                class="common-required-star"
                >*</i
              >
              {{ item.name }}
            </span>
            <span class="group__stat">
              <!-- Search group -->
              <div class="search-group">
                <a-input
                  :class="{
                    'search-group--show': groupSearchObj[item.id].status
                  }"
                  v-model="groupSearchObj[item.id].value"
                  @change="(evt) => changeSearch(evt, item)"
                >
                  <a-select
                    slot="addonBefore"
                    default-value="All"
                    style="padding: 0 4px"
                    v-model="groupSearchObj[item.id].select"
                    @change="changeGroupSearchSelection(item)"
                  >
                    <a-select-option value="All">{{
                      $t('global_button_all')
                    }}</a-select-option>
                    <a-select-option value="Completed">
                      {{ $t('global_completed') }}
                    </a-select-option>
                    <a-select-option value="Unfilled">
                      {{ $t('global_unfilled') }}
                    </a-select-option>
                  </a-select>

                  <template slot="addonAfter">
                    <a-icon
                      v-if="!groupSearchObj[item.id].status"
                      type="search"
                      class="group-btn-search"
                      @click="handleSearchClick(item, true)"
                    />

                    <a-icon
                      v-else
                      type="close"
                      class="group-btn-search"
                      @click="handleSearchClick(item, false)"
                    />
                  </template>
                </a-input>
              </div>

              <a-icon
                type="right"
                class="group-btn-fold common-btn"
                :class="{
                  unfold: mapObj.activeWidget[item.id].length
                }"
                @click="switchFold(item.id)"
              />
            </span>
          </div>
          <div v-if="getGroupDesc(item)" class="__desc common-desc">
            {{ getGroupDesc(item) }}
          </div>
        </div>

        <a-spin :spinning="getSaveSpinning(item.id)" :delay="300">
          <!-- group details -->
          <a-collapse
            v-show="item.id && reqResult.groups_instance[item.id].length"
            :ref="`widgetsList${item.id}`"
            class="group__item common-collapse-detail-style drag-items"
            :class="`drag-items-${item.id}`"
            :key="item.id"
            :bordered="false"
            v-model="mapObj.activeWidget[item.id]"
            @change="changeCollapse"
          >
            <template
              v-for="(widget, index) in reqResult.groups_instance[item.id]"
            >
              <a-collapse-panel
                :key="widget.uuid"
                :name="widget.uuid"
                v-show="!groupSearchObj[item.id].hidden.includes(widget.uuid)"
                :ref="`widget-${widget.uuid}`"
                :class="[
                  {
                    'have-fill-in': haveFillIn(widget)
                  }
                ]"
              >
                <template slot="header">
                  <div class="collapse-title" style="user-select: none">
                    <div class="__name">
                      <span
                        v-if="widget.ui_conf.required"
                        class="common-required-star"
                      >
                        *
                      </span>
                      {{ widget.ui_conf.name }}
                    </div>
                    <div
                      v-if="getWidgetDesc(widget.id, item.id)"
                      class="__desc common-desc"
                    >
                      {{ getWidgetDesc(widget.id, item.id) }}
                    </div>
                  </div>
                </template>

                <!-- operation -->
                <template slot="extra">
                  <span v-if="!readonly" class="header--actions-box">
                    <CircleDeleteButton
                      type="delete"
                      class="group-action-btn del"
                      v-if="
                        canDeleteWidget(widget.id, item.id) &&
                          mapObj.activeWidget[item.id].includes(widget.uuid)
                      "
                      ghost
                      size="small"
                      :alt="$t('global_delete')"
                      @click.stop.prevent="handleDeleteWidget(index, item.id)"
                    />

                    <copyButton
                      type="copy"
                      class="group-action-btn copy"
                      v-if="
                        widget.ui_conf.config.allow_copy &&
                          mapObj.activeWidget[item.id].includes(widget.uuid)
                      "
                      size="small"
                      @click.stop.prevent="handleCopyWidget(index, item.id)"
                      :alt="$t('global_copy')"
                    ></copyButton>

                    <a-button
                      size="small"
                      v-if="mapObj.activeWidget[item.id].includes(widget.uuid)"
                      @click.stop.prevent="handleResetWidget(index, item.id)"
                    >
                      {{ $t('global_reset') }}</a-button
                    >

                    <div
                      v-if="
                        canDraggable(widget, item.id) &&
                          !mapObj.activeWidget[item.id].includes(widget.uuid)
                      "
                      class="drag-to-order"
                    >
                      <div style="cursor: move; color: #1079fb" />
                      <a-icon class="drag_img sort-btn" type="menu" />
                    </div>
                  </span>
                </template>

                <a-icon
                  slot="indicator"
                  type="loading"
                  style="font-size: 24px"
                  spin
                />

                <!-- widget_type: 1,// widget 类型：1-Attribute 2-Single Text 3-Markdown 4-Image 5-Address, 6: duration-->
                <!-- 属性 -->
                <attribute
                  v-if="widget.ui_conf.widget_type === 1"
                  :key="widget.uuid"
                  :uuid="widget.uuid"
                  :ref="`G-${item.id}-widget`"
                  :data-ref="`widget${i1}`"
                  v-model="widget.attr_values"
                  :required="!!widget.ui_conf.required"
                  :ui="widget.ui_conf"
                  :data="widget.attr_values"
                  :ref-data="getRefData(index, widget.ui_conf.widget_type)"
                  :readonly="readonly"
                  @hook:mounted="widgetMounted"
                  @change="getCompleteStat"
                />
                <!-- 图片 -->
                <template v-else-if="widget.ui_conf.widget_type === 4">
                  <p>
                    {{ $t('act_image_en_us_handle') }}
                  </p>
                  <photo-guide
                    :key="widget.uuid"
                    :uuid="widget.uuid"
                    :ref="`G-${item.id}-widget`"
                    :data-ref="`widget${i1}`"
                    v-model="widget.attr_values"
                    :required="!!widget.ui_conf.required"
                    :ui="widget"
                    :current-group="reqResult.groups_schema[item.id]"
                    :language-all="locale_info.react_data"
                    :data="widget.attr_values"
                    :ref-data="getRefData(index, widget.ui_conf.widget_type)"
                    @change="getCompleteStat"
                    @hook:mounted="widgetMounted"
                  />
                </template>
                <!-- 其他 -->
                <component
                  v-else
                  :is="widgetType[widget.ui_conf.widget_type]"
                  :key="widget.uuid"
                  :uuid="widget.uuid"
                  :ref="`G-${item.id}-widget`"
                  :data-ref="`widget${i1}`"
                  v-model="widget.attr_values[0]"
                  :required="!!widget.ui_conf.required"
                  :config="widget.ui_conf.config || {}"
                  :ui="widget"
                  :data="widget.attr_values[0]"
                  :ref-data="getRefData(index)"
                  :readonly="readonly"
                  @change="getCompleteStat"
                />
              </a-collapse-panel>
            </template>
          </a-collapse>
        </a-spin>
      </div>
    </a-skeleton>
  </div>
</template>

<script>
import {
  getEditLang,
  getRefLang,
  getLangTitle,
  getUuid,
  broadcastDisabled,
  scrollElError
} from '@activity/utils'
import { computedGroupSummary } from '../../../components/groupSummary/utils'
import { mapGetters, mapMutations, mapActions } from 'vuex'
// import { isAdmin } from '@/env'

import SimpleMde from '@activity/components/simple-md'
import DurationWidget from '@activity/components/duration_widget'
import SingleWidget from '@activity/components/single_widget'
import MarkdownWidget from '@activity/components/markdown_widget'
import Attribute from '@activity/components/widgets/Attribute/src/Widget'
import PhotoGuide from '@activity/components/photo-upload/photo_guide'
import CircleDeleteButton from '@activity/components/CircleDeleteButton.vue'
import copyButton from '@activity/components/copyButton.vue'

import Mark from 'mark.js'
const markdownIt = require('markdown-it')().disable(['code'])
const Sortable = require('sortablejs')

const render_queue_mixin = {
  data() {
    return {
      currentRenderGroupId: null,
      renderQueueUUIDList: []
    }
  },
  methods: {
    /*
      Queue to render the widgets in the current group
      @data, rendered UUID array
     */
    queueRenderWidgetOnGroup(groupId, data = null, cb = null) {
      if (data !== null && !Array.isArray(data)) {
        data = [data]
      }
      this.$set(this, 'renderQueueUUIDList', []) // reset render items
      let groups_instance = _.get(this.reqResult.groups_instance, groupId, null)

      this.currentRenderGroupId = groupId
      if (groups_instance) {
        let currentActive = this.mapObj.activeWidget[this.currentRenderGroupId]
        this.renderQueueUUIDList =
          data ||
          groups_instance
            .map((widget) => widget.uuid)
            .filter((uuid) => !currentActive.includes(uuid))

        const step = () => {
          requestAnimationFrame(() => {
            if (this.renderQueueUUIDList.length) {
              let currentUUID = null
              while (!currentUUID && this.renderQueueUUIDList.length) {
                let uuid = this.renderQueueUUIDList.shift()
                if (!currentActive.includes(uuid)) {
                  currentUUID = uuid
                }
              }

              if (currentUUID) {
                // this.nextRender(currentUUID)
                this.$set(this.mapObj.activeWidget, this.currentRenderGroupId, [
                  ...new Set([
                    ...this.mapObj.activeWidget[this.currentRenderGroupId],
                    currentUUID
                  ])
                ])

                this.$forceUpdate()
                // 不停的入栈，直到当前的 widget animate 函数周期执行完
                // ANT openAnimation.js animate 函数是 Vue.nextTick
                setTimeout(() => {
                  // node.rcEndAnimTimeout 函数是 setTimeout 回调
                  setTimeout(() => {
                    // animate end() 也是 setTimeout 回调
                    step()
                  })
                })
              } else {
                cb && cb()
                this.$emit('queueRenderWidgetFinished')
              }
            } else {
              cb && cb()
              this.$emit('queueRenderWidgetFinished')
            }
          })
        }

        if (this.renderQueueUUIDList.length) {
          step()
        } else {
          this.$set(this.mapObj.activeWidget, groupId, [])
          this.$forceUpdate()
        }
      }
    }
  }
}

export default {
  name: 'DetailGroups',
  provide() {
    // `provide` can be hoisted up in higher component
    return {
      activity_id: +this.activity_id,
      ref_lang: getRefLang(),
      edit_lang: getEditLang(),
      locale_info: this.locale_info,
      extra_id: this.extraType === 'ACT' ? +this.activity_id : +this.package_id,
      extra_type: this.extraType
    }
  },
  inject: ['refreshPkgs', 'refreshPage'],
  mixins: [render_queue_mixin],
  components: {
    Attribute,
    MarkdownWidget,
    SingleWidget,
    DurationWidget,
    SimpleMde,
    PhotoGuide,
    CircleDeleteButton,
    copyButton
  },
  props: {
    activity_id: {
      type: [Number, String],
      required: true
    },
    extraType: {
      type: String,
      required: true,
      validator: function(value) {
        return ['ACT', 'PKG'].includes(value)
      }
    },
    package_id: {
      type: [Number, String]
    },
    // 部分页面是不需要 next 按钮的
    copyGroupList: {
      type: Boolean,
      default: false
    },
    // 是否允许保存
    allowSave: {
      type: Boolean,
      default: true
    },
    readonly: {
      type: Boolean,
      default: false
    },
    filterOptions: { type: Object, default: () => {} },
    disableCopyPkg: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      saveSpinning: {},
      mapObj: {
        old_markdown_content: {},
        activeNames: {},
        activeWidget: {},
        cacheWidgetsData: {},
        // oldTableData: {},
        sortable: {}
      },
      reqResult: {
        groups_instance: {},
        groups_schema: {},
        groups_summary: []
      },
      groupList: [],
      currentGroup: {
        widget_list: []
      },

      currentGroupId: null,

      widgetsData: [],
      refWidgetsData: [],
      currentRefGroup: {
        id: null, // cache
        data: {}
      },

      locale_info: {
        react_data: []
      },

      // completedStat: null,

      visibleOldMarkdownContent: false,

      allVariables: [],
      refAllVariables: [],
      // firstInitDataFlag: true,
      drag: false,

      // Group search status compilations
      groupSearchObj: {},

      initLoading: true
    }
  },
  computed: {
    completedStat(groupId) {
      if (!groupId) return null
      let arr = (this.getCompleteStat() && this.getCompleteStat().data) || []
      return arr
    },
    refLang() {
      return getRefLang()
    },
    editLang() {
      return getEditLang()
    },
    widgetType() {
      // widget_type: 1,// widget 类型：1-Attribute 2-Single Text 3-Markdown 4-Image 5-Address, 6: duration
      return {
        1: 'attribute',
        2: 'single-widget',
        3: 'markdown-widget',
        4: 'photo-guide',
        // 5: 'address',
        6: 'duration-widget'
      }
    }
  },
  watch: {
    'reqResult.groups_instance': {
      deep: true,
      handler() {
        this.getCompleteStat()
      }
    },
    'mapObj.old_markdown_content': {
      handler(obj) {
        for (let key in obj) {
          let v = obj[key]
          let ref = this.$refs[`widgetsList${key}`]
          if (v && ref) {
            broadcastDisabled.call(
              ref,
              ['a-input', 'a-button', 'a-radio', 'a-checkbox'],
              null
            )
          }
        }
      },
      immediate: true,
      deep: true
    }
  },
  activated() {
    // this.addBeforeUnloadBefore()
  },
  deactivated() {
    // this.firstInitDataFlag = false
  },
  async created() {
    window.s = this

    this.$on('groupListInited', function() {
      this.initLoading = false
      if (this.$route.hash) {
        this.handleFloorTimeline(this.$route.hash.slice(1))
      }
      this.getCompleteStat()
      this.setGroupsCache()
    })

    if (!this.$root.nonEdit2act) {
      this.initBeforeEachInterceptor()
    }

    // init data
    this.allVariables = await this.getActVariables2actions()
    await this.getGroupList()
  },
  async mounted() {
    // init event listener
    klook.bus.$emit('addHandleSave2bus', this.handleNext)
    klook.bus.$on('floorTimelineAnchorChange2bus', this.handleFloorTimeline)

    this.locale_info.react_data = await ajax.get(
      ADMIN_API.act.get_activity_status_by_langs,
      {
        params: {
          activity_id: this.activity_id,
          language_list: getEditLang()
        }
      }
    )
  },
  methods: {
    ...mapGetters(['getSubcategoryId', 'getGroupsSummary']),
    ...mapMutations(['setBeforeEachInterceptor', 'clearBeforeEachInterceptor']),
    ...mapActions([
      'getActCategory2action',
      'getActVariables2actions',
      'updateActStepStatus2action',
      'updatePkgStepStatus2action'
    ]),
    getLangTitle: getLangTitle,
    initBeforeEachInterceptor() {
      this.setBeforeEachInterceptor({
        before: this.beforeUnload,
        callback: async (action) => {
          if (action === true) {
            // x close
            return true
          } else if (action === 'cancel') {
            return true
          } else {
            // confirm
            await this.handleNext(false)
            return false
          }
        }
      })
    },
    // beforeEach interceptor
    beforeUnload() {
      return new Promise((resolve) => {
        if (!this.readonly && !this.groupsIsEqual()) {
          this.$confirm({
            content: 'Please save the edits that you made',
            cancelText: this.$t('global_cancel'),
            onOk() {
              return resolve('ok')
            },
            onCancel() {
              return resolve('cancel')
            }
          })
        } else {
          resolve(true)
        }
      })
    },
    // the search selection of group, that the result need to control over the hiding and display of widgets
    changeGroupSearchSelection({ id }) {
      let { select } = this.groupSearchObj[id]
      let widgetsData = this.reqResult.groups_instance[id]

      let allIds = widgetsData.map((widget) => widget.uuid)
      let unfilledIds = widgetsData
        .filter((widget) => {
          let { attr_values, ui_conf } = this.getWidgetDefaultData(widget)
          if (_.isEqual(widget.attr_values, attr_values)) {
            return true
          }
          if (ui_conf.widget_type === 6) {
            if (
              widget.attr_values.some(
                (item) => item.value === '{}' || _.isEqual(item.value, {})
              )
            ) {
              return true
            }
          }
          if ([1, 4].includes(ui_conf.widget_type)) {
            return widget.attr_values.length === 0
          }
          return widget.attr_values.some((item) => item.value === '')
        })
        .map((item) => item.uuid)

      let completedIds = _.difference(allIds, unfilledIds)

      let ids = [] // 需要 show 的
      if (select === 'Completed') {
        ids = completedIds
      } else if (select === 'Unfilled') {
        ids = unfilledIds
      } else {
        ids = allIds
      }
      let { mapObj, groupSearchObj } = this
      this.queueRenderWidgetOnGroup(id, ids)
      this.$set(mapObj.activeNames, id, ids)
      this.$set(groupSearchObj[id], 'hidden', _.difference(allIds, ids))
    },
    // handle the search icon click
    handleSearchClick({ id }, status) {
      // reset search
      this.$set(this, 'groupSearchObj', {
        ...this.groupSearchObj,
        [id]: {
          ...this.groupSearchObj[id],
          status,
          value: ''
        }
      })

      this.$nextTick(() => {
        let input = document.querySelector(
          `#group-${id} .group__header .ant-input`
        )
        if (status) {
          input.focus()
          input.autoFocus = 'autofocus'
        } else {
          let $el = this.$refs[`group-${id}`]
          new Mark($el).unmark()
          input.value = ''
          this.$set(this.groupSearchObj[id], 'hidden', [])
        }
      })
    },
    // the search input of group
    changeSearch: _.debounce(function(evt, group) {
      let query = evt.target.value.trim()
      let { id } = group
      let widgetsData = this.reqResult.groups_instance[id]
      let allIds = widgetsData.map((widget) => widget.uuid)
      let $el = this.$refs[`group-${id}`]
      new Mark($el).unmark()

      if (query.length) {
        query = query.toLowerCase()
        let widgetsData = this.reqResult.groups_instance[id]
        let activeWidget = widgetsData.reduce((acc, widget) => {
          let { widget_type, attr_value_list, name } = widget.ui_conf
          if (widget_type === 1) {
            if (
              attr_value_list.some((attr) =>
                attr.value.toLowerCase().includes(query)
              )
            ) {
              return [...acc, widget.uuid]
            }
          }
          if (-1 !== name.toLowerCase().indexOf(query)) {
            return [...acc, widget.uuid]
          }
          return acc
        }, [])

        if (activeWidget.length) {
          this.queueRenderWidgetOnGroup(id, _.cloneDeep(activeWidget), () => {
            this.$set(
              this.groupSearchObj[id],
              'hidden',
              _.difference(allIds, activeWidget)
            )
            new Mark($el).mark(query, {
              separateWordSearch: false
            })

            this.$forceUpdate()
          })
        } else {
          this.$set(this.groupSearchObj[id], 'hidden', allIds)
          this.$forceUpdate()
        }
      } else {
        this.$set(this.groupSearchObj[id], 'hidden', [])
      }
    }, 100),
    switchFold(groupId) {
      let switchGroup = !this.groupSearchObj[groupId].switchGroup
      this.$set(this.groupSearchObj[groupId], 'switchGroup', switchGroup)

      if (
        switchGroup &&
        this.mapObj.activeWidget[groupId].length !==
          this.reqResult.groups_instance[groupId].length
      ) {
        this.queueRenderWidgetOnGroup(groupId)
      } else {
        this.queueRenderWidgetOnGroup(groupId, [])
      }
    },
    // click on the right of floor
    handleFloorTimeline(link) {
      if (document.querySelector('#' + link)) {
        let groupId = +_.get(link.match(/\d+/g), 0, 0)
        if (groupId) {
          this.queueRenderWidgetOnGroup(
            groupId,
            _.get(this.reqResult.groups_instance, groupId, []).map(
              (widget) => widget.uuid
            )
          )
        }
      }
    },
    changeCollapse: _.debounce(function() {
      this.$forceUpdate()
    }, 100),
    handleCopyGroupList() {
      this.$emit('copyGroupList', {
        list: this.groupList
      })
    },
    getSaveSpinning(groupId) {
      return _.get(this, `saveSpinning.${groupId}`, false)
    },
    getGroupDesc(item) {
      return _.get(this.reqResult.groups_schema, `${item.id}.desc`, '')
    },
    getWidgetDesc(id, groupId) {
      let current =
        _.find(this.reqResult.groups_schema[groupId].widget_list, {
          id
        }) || {}

      return _.get(current, 'desc', '')
    },
    groupsIsEqual() {
      let { mapObj } = this
      let { groups_instance } = this.reqResult

      return this.groupList.every((group) => {
        return this.widgetsDataIsEqual(
          groups_instance[group.id],
          mapObj.cacheWidgetsData[group.id]
        )
      })
    },
    widgetsDataIsEqual(originData, cacheData) {
      let origin = _.cloneDeep(originData)
      let cache = _.cloneDeep(cacheData)

      if (Array.isArray(origin) && Array.isArray(cache)) {
        if (origin.length !== cache.length) return false
        return cache.every((item, index) =>
          this.widgetsDataIsEqual(origin[index], item)
        )
      }

      if (toString.call(origin) === '[object Object]') {
        ;['ui_index', 'showText'].forEach((key) => {
          delete origin[key]
          delete cache[key]
        })

        if (!_.isEqual(Object.keys(origin), Object.keys(cache || {})))
          return false

        return Object.entries(origin).every(([key, val]) =>
          this.widgetsDataIsEqual(val, cache[key])
        )
      }

      return _.isEqual(
        this.parseJSONString(origin),
        this.parseJSONString(cache)
      )
    },
    parseJSONString(data) {
      let isJSONString = true
      try {
        JSON.parse(data)
      } catch (e) {
        isJSONString = false
      }

      return isJSONString ? JSON.parse(data) : data
    },
    setGroupsCache() {
      let { mapObj } = this
      let { groups_instance } = this.reqResult
      this.groupList.forEach((item) => {
        let groupId = item.id
        let widgetsData = groups_instance[groupId]
        mapObj.cacheWidgetsData[groupId] = _.cloneDeep(widgetsData)
      })
    },
    // sortable 需要重写
    widgetMounted: _.debounce(function() {
      this.groupList.forEach((item) => {
        let groupId = item.id
        this.initSortable(groupId)
      })
    }, 1000),
    async handleRefLang() {
      if (this.currentRefGroup.id !== this.reqResult.groups_schema.id) {
        this.currentRefGroup.id = this.reqResult.groups_schema.id
        this.currentRefGroup.data = this.getCurrentUITreeByGroupId(
          this.reqResult.groups_schema.id,
          getRefLang()
        )
      }
      if (!this.refAllVariables.length) {
        this.refAllVariables = await ajax.get(ADMIN_API.act.get_variable_list, {
          params: {
            attr_id_list: '',
            language: getRefLang()
          }
        })
      }
    },
    handleOldMarkdownContent() {
      this.visibleOldMarkdownContent = true
    },
    computedGroupSummary: computedGroupSummary,
    // 通过计算接口所给的完成度去实现
    getCompleteStat: _.debounce(function() {
      let summaries = []
      this.groupList = this.groupList.map((group) => {
        let widgetsData = this.reqResult.groups_instance[group.id]
        let summary = this.computedGroupSummary(
          widgetsData,
          this.reqResult.groups_schema[group.id].widget_list,
          this.allVariables
        )

        group.filled_widget_num = summary.length
        group.filled_required_widget_num = summary.filter(
          (item) => item.required
        ).length
        summaries.push({
          ...group,
          summary
        })

        return group
      })

      let data = this.groupList.reduce(
        (acc, curr, index) => {
          let { filled_required_widget_num, required_widget_num, id } = curr
          let flag = filled_required_widget_num === required_widget_num
          return {
            data: [
              ...acc.data,
              {
                filled_required_widget_num,
                required_widget_num,
                flag,
                groupId: id
              }
            ],
            errIndex: -1 === acc.errIndex && !flag ? index : acc.errIndex // 记录第一个错误Index
          }
        },
        {
          data: [],
          errIndex: -1
        }
      )

      this.debounceWatchGroupsDataState(summaries, this.groupList)

      return data
    }, 100),
    debounceWatchGroupsDataState: _.debounce(function(summaries, groupList) {
      this.$store.commit('setGroupsSummary', summaries)

      let obj = {
        floorData: {
          list: this.groupList.map((group) => ({
            title: group.name,
            id: `group-${group.id}`,
            num: group.filled_widget_num,
            count: group.total_widget_num,
            requireNum: group.filled_required_widget_num,
            requireCount: group.required_widget_num
          }))
        }
      }

      klook.bus.$emit('floorTimeline2bus', obj)
    }, 300),
    async handleNext(clearInterceptor = true) {
      let completedStat = this.getCompleteStat()
      let { errIndex, data } = completedStat

      if (-1 !== errIndex) {
        let groupId = data[errIndex].groupId
        let { summary } = _.find(this.getGroupsSummary(), {
          id: groupId
        })
        let currentInstance = this.reqResult.groups_instance[groupId]

        for (let widget of currentInstance) {
          if (widget.ui_conf.required) {
            let currentSummary = _.find(summary, {
              id: widget.id
            })

            if (!currentSummary) {
              this.queueRenderWidgetOnGroup(groupId, [widget.uuid], () => {
                let $el = this.$refs[`widget-${widget.uuid}`][0].$el
                $el.scrollIntoView({
                  behavior: 'smooth',
                  block: 'center'
                })
              })
              break
            }
          }
        }

        // this.queueRenderWidgetOnGroup(data[errIndex].groupId)
        await this.$message.warning(this.$t('act_tips_complete_all_groups'))
      } else {
        this.handleSaveAll(clearInterceptor)
      }
    },
    async handleSaveAll(clearInterceptor = true) {
      let { groupList } = this
      let resp = await Promise.all(
        groupList.map((item, index) => this.handleSave(index, item.id))
      )
      setTimeout(() => {
        this.$set(this, 'saveSpinning', {})
      }, 300)

      // await this.getGroupList() // 更新 group list 的 required 情况
      // _.merge(this.completedStat, this.getCompleteStat().data)
      await ajax.get(ADMIN_API.act.modify_completed, {
        params: {
          snapshot_modify_id:
            new Date().valueOf() + '' + parseInt(Math.random() * 10000)
        }
      })

      if (resp.every((item) => item)) {
        let data = {
          status: 1,
          activity_id: +this.activity_id,
          language: new URLSearchParams(location.search).get('lang')
        }

        if (this.extraType === 'ACT') {
          await this.updateActStepStatus2action({
            ...data,
            step: 'activity_detail_info'
          })
          klook.bus.$emit('updateActStepStatus2bus')
        } else {
          await this.updatePkgStepStatus2action({
            ...data,
            step: 'package_detail_info',
            package_id: +this.package_id
          })
          klook.bus.$emit('updatePkgInfos2bus')
          await this.refreshPkgs()
        }

        this.$message.success(this.$t('global_success'))
        clearInterceptor && this.clearBeforeEachInterceptor()

        setTimeout(() => {
          let { name, query } = this.$route

          if (name === 'packageDetail') {
            // this.$router.push({
            //   name: 'packageSchedule',
            //   query,
            //   params
            // })
          } else {
            let packages = this.$store.getters.pkgList

            _.get(packages, '[0].package_id', '') ||
              this.$router.push({
                name: 'packageBasicInfo',
                query: {
                  ...query,
                  package_id: _.get(packages, '[0].package_id', ''),
                  package_type: _.get(packages, '[0].package_type', '1')
                },
                params: {
                  id: this.activity_id
                }
              })
          }
        }, 300)
      }
    },
    async handleSave(index, groupId, updateFlag = false) {
      let { groups_instance } = this.reqResult
      let widgetsData = groups_instance[groupId]
      let { mapObj } = this
      let cacheWidgetsData = mapObj.cacheWidgetsData[groupId]
      // let oldTableData = mapObj.oldTableData[groupId]
      if (
        this.widgetsDataIsEqual(cacheWidgetsData, widgetsData)
        // && _.isEqual(oldTableData, widgetsData)
      ) {
        return true
      }

      let modify_id =
        new Date().valueOf() + '' + parseInt(Math.random() * 10000)

      let validate = await Promise.all(
        (this.$refs[`G-${groupId}-widget`] || []).map((item) =>
          (() => {
            return item.validateForm && item.validateForm()
          })()
        )
      )

      if (validate.every((item) => item)) {
        let postData = {
          activity_id: +this.activity_id,
          language: getEditLang(),
          groups: [
            {
              extra_id:
                this.extraType === 'ACT' ? +this.activity_id : +this.package_id,
              group_id: groupId,
              extra_type: this.extraType,
              widgets: widgetsData
                .map((item, index) => ({
                  ...item,
                  ...(item.create // for backend, only
                    ? {
                        attr_values: (item.attr_values || []).map(
                          (attr_value) => ({
                            ...attr_value,
                            value:
                              '[object Object]' ===
                              toString.call(attr_value.value)
                                ? JSON.stringify(attr_value.value)
                                : attr_value.value, // String
                            ...(attr_value.is_free_text
                              ? {
                                  create: true,
                                  variable_map_value: {
                                    ...attr_value.variable_map_value,
                                    values: (
                                      _.get(
                                        attr_value,
                                        'variable_map_value.values'
                                      ) || []
                                    ).map((value) => ({
                                      ...value,
                                      create: true
                                    }))
                                  }
                                }
                              : {})
                          })
                        )
                      }
                    : {}),
                  order: index
                }))
                .map((item) => {
                  // 按 order 排序
                  if (item.ui_conf.widget_type === 1) {
                    item.attr_values = _.sortBy(item.attr_values, 'ui_index')
                    item.attr_values.forEach((v, i) => (v.order = i))
                  }
                  return item
                })
            }
          ]
        }

        _.set(this, `saveSpinning.${groupId}`, true)
        this.$forceUpdate()

        let url =
          ADMIN_API.act[
            this.extraType === 'ACT'
              ? 'save_activity_instance_data'
              : 'save_package_instance_data'
          ]

        console.warn('Url: ', url)
        await ajax.post(url, {
          params: {
            modify_id
          },
          data: postData
        })

        // await ajax.get(ADMIN_API.act.modify_completed, {
        //   params: {
        //     snapshot_modify_id: modify_id
        //   }
        // })
        // updateFlag && (await this.getGroupList()) // 更新 group list 的 required 情况
        mapObj.cacheWidgetsData[groupId] = _.cloneDeep(widgetsData)

        // _.merge(this.completedStat, this.getCompleteStat().data)
        return true
      } else {
        this.saveWarnHandle(validate, groupId, widgetsData)
        return false
      }
    },
    saveWarnHandle: _.throttle(function(validate, groupId) {
      let errorIndex = validate.findIndex((v) => !v)
      let currentWidget = this.$refs[`G-${groupId}-widget`][errorIndex]
      let uuid = currentWidget.uuid || currentWidget.$attrs.uuid
      this.$message.error('Please fill in all required fields')
      this.queueRenderWidgetOnGroup(groupId, [uuid], scrollElError)
    }, 300),
    getRefData(index, widget_type) {
      if (widget_type && [1, 4].includes(widget_type)) {
        return _.get(this.refWidgetsData, `${index}.attr_values`, [])
      }
      return _.get(this.refWidgetsData, `${index}.attr_values[0]`, null)
    },
    getCurrentUITreeByGroupId(group_id) {
      if (!group_id) return {}
      // 更新UI tree
      let currentGroup = this.reqResult.groups_schema[group_id]
      currentGroup.widget_list || (currentGroup.widget_list = [])
      if (((this.filterOptions || {}).excludeOptions || {})[group_id]) {
        currentGroup.widget_list = (currentGroup.widget_list || []).filter(
          (item) => {
            return !this.filterOptions.excludeOptions[group_id].includes(
              item.id
            )
          }
        )
      }

      return {
        ...currentGroup,
        config: (currentGroup.config && JSON.parse(currentGroup.config)) || {},
        widget_list: (currentGroup.widget_list || []).map((widget) => ({
          ...widget,
          config: (widget.config && JSON.parse(widget.config)) || {}
        }))
      }
    },
    // 对于接口返回的 null, admin 需要初始化组件对应的数据结构
    getWidgetDefaultData(widget) {
      let { ui_conf } = widget
      // widget_type: 1,// widget 类型：1-Attribute 2-Single Text 3-Markdown 4-Image 5-Address, 6: duration
      if ([1, 4].includes(ui_conf.widget_type)) {
        return {
          ui_conf,
          id: ui_conf.id,
          attr_values: ui_conf.attr_value_list.reduce((acc, curr) => {
            let current = null
            if (curr.config) {
              current = JSON.parse(curr.config)
              if (current.default_selected) {
                return [
                  ...acc,
                  {
                    id: curr.id,
                    value: curr.value,
                    order: acc.length
                  }
                ]
              }
            }
            return acc
          }, [])
        }
      }

      return {
        ui_conf,
        id: ui_conf.id,
        attr_values: [
          {
            create: true,
            is_free_text: true,
            value: [5, 6].includes(ui_conf.widget_type) ? {} : '',
            id: ui_conf.ref_attr_id,
            widget_type: ui_conf.widget_type
          }
        ]
      }
    },
    getCurrentGroupData(group_id) {
      let { groups_schema, groups_instance } = this.reqResult
      let groups = groups_instance[group_id] ? [groups_instance[group_id]] : []
      let widgets = (groups[0] && groups[0].widgets) || []
      // 未结构化 Markdown
      let old_markdown_content =
        (groups[0] && groups[0].old_markdown_content) || ''
      if (old_markdown_content) {
        old_markdown_content = markdownIt.render(old_markdown_content)
      }
      this.mapObj.old_markdown_content[group_id] = old_markdown_content

      // widget_list 的 widget 需要获得重复的情况
      let currentGroup = groups_schema[group_id]
      return currentGroup.widget_list.reduce((acc, widget) => {
        let currentWidget = _.find(widgets, {
          id: widget.id
        })

        if (currentWidget) {
          return [
            ...acc,
            ...widgets
              .filter((item) => item.id === widget.id) // 可能存在重复的ID
              .map((item) => ({
                ui_conf: widget,
                ...item
              }))
          ]
        } else {
          return [
            ...acc,
            this.getWidgetDefaultData({
              ui_conf: widget
            })
          ]
        }
      }, [])
    },
    async getGroupList() {
      let { activity_id, package_id } = this
      let categoryInfo = await this.getActCategory2action({
        activity_id
      })
      this.categoryInfo = categoryInfo
      let reqUrl =
        ADMIN_API.act[
          this.extraType === 'ACT'
            ? 'get_activity_group_list'
            : 'get_package_group_list'
        ]
      let params = this.extraType === 'ACT' ? { activity_id } : { package_id }

      let reqResult = await ajax.get(reqUrl, {
        params: {
          page_from: this.$route.query.review ? 'bd_audit' : undefined,
          ...params,
          category_id: categoryInfo.sub_category_id, // in query, required, 类目 ID,
          language: getEditLang() // in query, 编辑语言,
        }
      })
      this.reqResult = reqResult
      let _result = reqResult.groups_summary
      if (((this.filterOptions || {}).includeGroups || []).length) {
        _result = _result.filter((item) => {
          return this.filterOptions.includeGroups.includes(item.id)
        })
      }
      let { mapObj } = this
      reqResult.groups_summary.forEach((item) => {
        reqResult.groups_schema[item.id] = this.getCurrentUITreeByGroupId(
          item.id
        )
        this.groupSearchObj[item.id] = {
          status: false,
          value: '',
          select: 'All',
          hidden: [],
          switchGroup: false
        }

        let widgetsData = this.getCurrentGroupData(item.id)
        widgetsData.forEach((widget) => {
          widget.uuid = getUuid()
        })

        // mapObj.activeNames[item.id] = mapObj.activeWidget[
        //   item.id
        // ] = widgetsData
        //   .filter((widget) => widget.ui_conf.required)
        //   .map((widget) => widget.uuid)
        // 默认全折叠
        mapObj.activeNames[item.id] = mapObj.activeWidget[item.id] = []

        reqResult.groups_instance[item.id] = widgetsData
      })

      this.groupList = _result
      this.$emit('groupListInited', _result)
    },
    canDeleteWidget(id, groupId) {
      return (
        this.reqResult.groups_instance[groupId].filter((item) => item.id === id)
          .length > 1
      )
    },
    canDraggable(widget, groupId) {
      if (
        widget.ui_conf.config.allow_copy &&
        this.canDeleteWidget(widget.id, groupId)
      ) {
        return true
      }
    },
    handleDeleteWidget(index, groupId) {
      let { groups_instance } = this.reqResult
      groups_instance[groupId].splice(index, 1)
      // this.mapObj.cacheWidgetsData[groupId].splice(index, 1)
      _.find(this.groupList, { id: groupId }).total_widget_num -= 1

      this.$message.success('Successfully delete.')
      this.$forceUpdate()
    },
    handleCopyWidget(index, groupId) {
      let { groups_instance } = this.reqResult
      let { mapObj } = this
      let widgetsData = groups_instance[groupId]
      let currentCopy = widgetsData[index]
      let widgetData = {
        ...this.getWidgetDefaultData(currentCopy),
        uuid: getUuid(),
        create: true
      }
      if (mapObj.activeNames[groupId].includes(currentCopy.uuid)) {
        this.$set(mapObj.activeNames, groupId, [
          ...mapObj.activeNames[groupId],
          widgetData.uuid
        ])
      }

      index += 1
      widgetsData.splice(index, 0, _.cloneDeep(widgetData))
      // this.cacheWidgetsData.splice(index, 0, _.cloneDeep(widgetData));
      _.find(this.groupList, { id: groupId }).total_widget_num += 1

      this.$message.success('Successfully copy.')
      this.$forceUpdate()
    },
    handleResetWidget(index, groupId) {
      let { groups_instance } = this.reqResult
      let widgetsData = groups_instance[groupId]
      let uuid = widgetsData[index].uuid
      this.$set(widgetsData, index, {
        ...this.getWidgetDefaultData(widgetsData[index]),
        uuid
      })

      this.$message.success('Successfully reset.')
      this.$forceUpdate()
    },
    haveFillIn(widget) {
      if (this.$refs.summary) {
        return this.$refs.summary._widgetsList
          .map((item) => item.id)
          .includes(widget.id)
      }
      return false
    },
    reviewEventLogs() {
      window.open(
        `/${window.KLK_LANG_PATH}act/activity/event_logs/${this.activity_id}?${
          this.extraType === 'PKG' ? 'package_id=' + this.package_id : ''
        }&edit_flag=1,2,3`,
        '_blank'
      )
    },
    initSortable(groupId) {
      this.$nextTick(() => {
        let { mapObj } = this
        let { groups_instance } = this.reqResult
        let widgetsData = groups_instance[groupId]
        let el = document.querySelector(`.drag-items-${groupId}`)
        if (!el) return
        mapObj.sortable[groupId] = Sortable.default.create(el, {
          handle: '.drag-to-order',
          preventOnFilter: true,
          onStart: function(evt) {
            // evt.target.
            // mapObj.oldTableData[groupId] = _.cloneDeep(widgetsData)
          },
          onEnd: async (evt) => {
            let dataList = widgetsData
            let currRow = dataList.splice(evt.oldIndex, 1)[0]
            dataList.splice(evt.newIndex, 0, currRow)

            if (
              (evt.newIndex > evt.oldIndex &&
                dataList[evt.newIndex - 1].id !== currRow.id) ||
              (evt.newIndex < evt.oldIndex &&
                dataList[evt.newIndex + 1].id !== currRow.id)
            ) {
              this.$set(
                groups_instance,
                groupId,
                _.cloneDeep(mapObj.cacheWidgetsData[groupId])
              )
              this.$message.error('Can only be sorted under the same title')
              //  重新刷新页面
            }
            // this.cacheWidgetsData = _.cloneDeep(dataList);
            this.$nextTick(async () => {
              this.initSortable(groupId)
              this.currentGroupId++
            })
          }
        })
      })
    }
  }
}
</script>

