<template>
  <a-form-model
    class="unit-price-form"
    ref="form"
    layout="vertical"
    :model="form"
    :rules="rules"
  >
    <a-form-model-item
      :label="$t('package_unit_type')"
      style="margin-bottom: 16px;"
      prop="unitType"
    >
      <a-button
        v-for="unit in unitOptions"
        :key="unit.value"
        :disabled="isUnitSaved"
        :class="unit.value === form.unitType ? 'is-active' : ''"
        class="unit-price-form-unit-type"
        @click="handleUnitClick(unit.value, unit.text)"
      >
        {{ unit.text }}
      </a-button>
    </a-form-model-item>
    <a-form-model-item
      v-if="[1, 2, 3, 4].includes(form.unitType)"
      :label="$t('package_unit_age_number')"
      style="margin-bottom: 16px;"
      prop="isAgeLimited"
      required
    >
      <a-radio-group v-model="form.isAgeLimited">
        <a-radio :value="false">
          {{ $t('package_inventory_unlimited') }}
        </a-radio>
        <a-radio :value="true">
          {{ $t('package_unit_limited') }}
        </a-radio>
      </a-radio-group>
    </a-form-model-item>
    <a-form-model-item
      v-if="[1, 2, 3, 4].includes(form.unitType) && form.isAgeLimited"
      :label="$t('package_min_max_age')"
      style="margin-bottom: 16px;"
      prop="minAge"
    >
      <a-input-number v-model="form.minAge" :min="0" style="width: 300px;" />
      -
      <a-input-number
        v-model="form.maxAge"
        :max="MAX_AGE - 1"
        style="width: 300px;"
        @blur="handleMaxAgeblur"
      />
      <span class="widespan">&nbsp;Age</span>
    </a-form-model-item>
    <a-form-model-item
      v-if="form.unitType === 5"
      :label="$t('package_unit_name')"
      style="margin-bottom: 16px;"
      prop="unitName"
    >
      <BasicFormPopover
        :message="$t('package_unit_name_tips')"
        :auto-adjust-overflow="false"
        trigger="focus"
        placement="right"
      >
        <a-input
          v-model="form.unitName"
          style="width: 500px;"
          :maxLength="50"
          :placeholder="$t('global_please_input')"
        />
      </BasicFormPopover>
    </a-form-model-item>
    <a-form-model-item
      :label="$t('package_unit_buy')"
      style="margin-bottom: 16px;"
      prop="required"
      required
    >
      <BasicFormPopover
        :message="$t('package_unit_requirement_tips')"
        :auto-adjust-overflow="false"
        trigger="hover"
        placement="right"
      >
        <a-radio-group v-model="form.required" @change="handleRequiredChange">
          <a-radio :value="true">
            {{ $t('global_yes') }}
          </a-radio>
          <a-radio :value="false">
            {{ $t('global_no') }}
          </a-radio>
        </a-radio-group>
      </BasicFormPopover>
    </a-form-model-item>
    <a-form-model-item
      :label="$t('package_unit_number')"
      style="margin-bottom: 16px;"
      prop="isNumLimited"
      required
    >
      <a-radio-group
        v-model="form.isNumLimited"
        @change="handleIsNumLimitedChange"
      >
        <a-radio :value="false">
          {{ $t('package_unlimited') }}
        </a-radio>
        <a-radio :value="true">
          {{ $t('package_unit_limited') }}
        </a-radio>
      </a-radio-group>
    </a-form-model-item>
    <a-form-model-item
      v-if="form.isNumLimited"
      :label="$t('package_min_max_units')"
      style="margin-bottom: 16px;"
      prop="minNum"
    >
      <a-input-number v-model="form.minNum" :min="0" style="width: 300px;" />
      -
      <a-input-number
        v-model="form.maxNum"
        :max="MAX_UNIT - 1"
        style="width: 300px;"
        @blur="handleMaxNumblur"
      />
      <span class="widespan">&nbsp;{{ $t('package_price_units') }}</span>
    </a-form-model-item>
    <!-- 先不要这个表单 -->
    <!-- <a-form-model-item
      label="Voucher Type"
      style="margin-bottom: 16px;"
      prop="zeroPriceSelect"
      required
    >
      <a-radio-group v-model="form.zeroPriceSelect">
        <a-radio :value="0">
          Regular
        </a-radio>
        <a-radio :value="2">
          Free Voucher
        </a-radio>
        <a-radio :value="1">
          Reservation Service
        </a-radio>
      </a-radio-group>
    </a-form-model-item> -->
    <div v-if="globalForm.priceModel !== null" style="display: flex;">
      <a-form-model-item
        :label="
          globalForm.priceModel === 0
            ? $t('package_price_cost')
            : $t('package_default_price_cost')
        "
        style="width: 300px; margin-bottom: 16px; margin-right: 24px;"
        prop="costPrice"
      >
        <a-input
          v-model="form.costPrice"
          :placeholder="$t('global_please_input')"
          :suffix="globalForm.merchantCurrency"
          style="width: 100%;"
          :disabled="isUnitSaved"
          @keyup.native="handleRoundPrice('costPrice')"
          @blur="handleBlur('costPrice')"
        />
      </a-form-model-item>
      <a-form-model-item
        :label="
          globalForm.priceModel === 0
            ? $t('package_price_retail')
            : $t('package_default_price_retail')
        "
        style="width: 300px; margin-bottom: 16px;"
        prop="retailPrice"
      >
        <a-input
          v-model="form.retailPrice"
          :placeholder="$t('global_please_input')"
          :suffix="globalForm.sellingCurrency"
          style="width: 100%;"
          :disabled="isUnitSaved"
          @keyup.native="handleRoundPrice('retailPrice')"
          @blur="handleBlur('retailPrice')"
        />
      </a-form-model-item>
    </div>
    <GeneralAlert
      v-if="globalForm.priceModel === 1"
      :show-icon="false"
      :message="$t('package_unit_type_price_warning')"
      banner
    />
  </a-form-model>
</template>

<script>
import { bestMatchLang, round2Decimal } from '@activity/utils'
import {
  MAX_AGE,
  MAX_UNIT,
  isNotSPUType
} from '@activity/pages/package/package_const.js'
import GeneralAlert from '@activity/pages/components/GeneralAlert.vue'
import BasicFormPopover from '@activity/pages/components/BasicFormPopover.vue'

export default {
  name: 'UnitPriceForm',
  components: {
    GeneralAlert,
    BasicFormPopover
  },
  props: {
    defaultValue: {
      type: Object,
      default: null
    }
  },
  inject: ['globalForm'],
  data() {
    const validateUnitType = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(this.$t('global_please_input')))
      } else {
        callback()
      }
    }
    const validateUnitName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(this.$t('global_please_input')))
      } else {
        callback()
      }
    }
    const validateMinAge = (rule, value, callback) => {
      if (!this.form.isAgeLimited) {
        callback()
      } else if (!this.form.minAge && this.form.minAge !== 0) {
        callback(new Error(this.$t('global_please_input')))
      } else if (!_.isInteger(this.form.minAge)) {
        callback(new Error(this.$t('package_unit_age_integer')))
      } else if (!this.form.maxAge && this.form.maxAge !== 0) {
        callback(new Error(this.$t('global_please_input')))
      } else if (!_.isInteger(this.form.maxAge)) {
        callback(new Error(this.$t('package_unit_age_integer')))
      } else if (this.form.minAge > this.form.maxAge) {
        callback(new Error(this.$t('package_unit_age_comparison')))
      } else {
        callback()
      }
    }
    const validateNumLimited = (rule, value, callback) => {
      if (this.form.required && !this.form.isNumLimited) {
        callback(new Error(this.$t('package_unit_limited_when_required')))
      } else {
        callback()
      }
    }
    const validateMinNum = (rule, value, callback) => {
      if (this.form.required && this.form.minNum < 1) {
        callback(new Error(this.$t('package_unit_min_num_when_required')))
      } else if (!this.form.isNumLimited) {
        callback()
      } else if (!this.form.minNum && this.form.minNum !== 0) {
        callback(new Error(this.$t('global_please_input')))
      } else if (!_.isInteger(this.form.minNum)) {
        callback(new Error(this.$t('package_unit_num_integer')))
      } else if (!this.form.maxNum && this.form.maxNum !== 0) {
        callback(new Error(this.$t('global_please_input')))
      } else if (!_.isInteger(this.form.maxNum)) {
        callback(new Error(this.$t('package_unit_num_integer')))
      } else if (this.form.minNum > this.form.maxNum) {
        callback(new Error(this.$t('package_unit_num_comparison')))
      } else {
        callback()
      }
    }
    const validateCostPrice = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(this.$t('global_please_input')))
      } else {
        callback()
      }
    }
    const validateRetailPrice = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(this.$t('global_please_input')))
      } else {
        callback()
      }
    }
    return {
      MAX_AGE,
      MAX_UNIT,
      unitOptions: isNotSPUType,
      isInited: false,
      // 这里的表单要和unitPrice里面的表单一致
      form: {
        unitType: '',
        unitName: '',
        isAgeLimited: false,
        minAge: 0,
        maxAge: MAX_AGE - 1,
        required: false,
        isNumLimited: false,
        minNum: 0,
        maxNum: MAX_UNIT - 1,
        // zeroPriceSelect: 0,
        costPrice: '',
        retailPrice: ''
      },
      defaultForm: {
        unitType: '',
        unitName: '',
        isAgeLimited: false,
        minAge: 0,
        maxAge: MAX_AGE - 1,
        required: false,
        isNumLimited: false,
        minNum: 0,
        maxNum: MAX_UNIT - 1,
        // zeroPriceSelect: 0,
        costPrice: '',
        retailPrice: ''
      },
      rules: {
        unitType: [
          { validator: validateUnitType, trigger: 'blur' },
          {
            required: true,
            message: this.$t('global_please_input'),
            trigger: 'blur'
          }
        ],
        unitName: [
          { validator: validateUnitName, trigger: 'blur' },
          {
            required: true,
            message: this.$t('global_please_input'),
            trigger: 'blur'
          }
        ],
        minAge: [
          { validator: validateMinAge, trigger: 'blur' },
          {
            required: true,
            message: this.$t('global_please_input'),
            trigger: 'blur'
          }
        ],
        isNumLimited: [
          { validator: validateNumLimited, trigger: 'blur' },
          {
            required: true,
            message: this.$t('global_please_select'),
            trigger: 'blur'
          }
        ],
        minNum: [
          { validator: validateMinNum, trigger: 'blur' },
          {
            required: true,
            message: this.$t('global_please_input'),
            trigger: 'blur'
          }
        ],
        costPrice: [
          { validator: validateCostPrice, trigger: 'change' },
          {
            required: true,
            message: this.$t('global_please_input'),
            trigger: 'blur'
          }
        ],
        retailPrice: [
          { validator: validateRetailPrice, trigger: 'change' },
          {
            required: true,
            message: this.$t('global_please_input'),
            trigger: 'blur'
          }
        ]
      }
    }
  },
  computed: {
    isUnitSaved() {
      return !!this.defaultValue && !!this.defaultValue.sku_id
    }
  },
  watch: {
    defaultValue: {
      immediate: true,
      handler() {
        // 这里为了保留用户编辑的内容，在刷新数据之后只初始化部分内容
        if (!this.isInited) {
          this.form = this.init()
          this.isInited = true
        } else {
          this.partialInit()
          this.$emit('updateModified', this.checkModified())
        }
      }
    },
    'globalForm.priceModel'() {
      // 如果没有保存的话，需要清空price
      if (!this.isUnitSaved) {
        this.form.costPrice = ''
        this.form.retailPrice = ''
      }
    }
  },
  methods: {
    init() {
      let form = {}

      if (this.isUnitSaved) {
        // 初始化表单
        form.unitType = this.defaultValue.unit_type
        form.unitName = bestMatchLang(
          'unit_name',
          'language',
          this.defaultValue.local
        )
        form.isAgeLimited =
          this.defaultValue.min_age_range !== 0 ||
          this.defaultValue.max_age_range !== 0
        form.minAge = this.defaultValue.min_age_range
        form.maxAge = this.defaultValue.max_age_range
        form.required = !!this.defaultValue.required
        form.isNumLimited =
          this.defaultValue.min_num !== 0 || this.defaultValue.max_num !== 0
        form.minNum = this.defaultValue.min_num
        form.maxNum = this.defaultValue.max_num
        form.costPrice = this.defaultValue.price.cost_price
        form.retailPrice = this.defaultValue.price.retail_price
      } else {
        form = _.cloneDeep(this.defaultForm)
        form.unitType = this.unitOptions[0].value
        form.unitName =
          this.unitOptions[0].value === 5 ? '' : this.unitOptions[0].text
      }

      return form
    },
    partialInit() {
      // 部分初始化，只初始化price
      this.form.unitType = this.defaultValue.unit_type
      this.form.costPrice = this.defaultValue.price.cost_price
      this.form.retailPrice = this.defaultValue.price.retail_price
    },
    cancelEdit() {
      this.form = this.init()
    },
    checkModified() {
      const defaultForm = this.init()

      // 这里不能用 JSON.stringify 进行比较，因为在unlimited的时候，min和max要忽略掉
      // 这里只比较能改动的
      if (
        defaultForm.unitType === this.form.unitType &&
        defaultForm.unitName !== this.form.unitName
      ) {
        return true
      }

      if (defaultForm.isAgeLimited !== this.form.isAgeLimited) {
        return true
      }

      if (defaultForm.isAgeLimited && this.form.isAgeLimited) {
        return (
          defaultForm.minAge !== this.form.minAge ||
          defaultForm.maxAge !== this.form.maxAge
        )
      }

      if (defaultForm.required !== this.form.required) {
        return true
      }

      if (defaultForm.isNumLimited !== this.form.isNumLimited) {
        return true
      }

      if (defaultForm.isNumLimited && this.form.isNumLimited) {
        return (
          defaultForm.minNum !== this.form.minNum ||
          defaultForm.maxNum !== this.form.maxNum
        )
      }

      return false
    },
    handleUnitClick(unitType, unitName) {
      this.form.unitType = unitType
      // unit_type === 5 is other, so should set empty string
      this.form.unitName = unitType === 5 ? '' : unitName
    },
    handleMaxNumblur() {
      this.$refs.form.validateField('minNum')
    },
    handleMaxAgeblur() {
      this.$refs.form.validateField('minAge')
    },
    validateForm() {
      return this.$refs.form.validate().catch(() => false)
    },
    handleRoundPrice(field) {
      this.form[field] = round2Decimal(this.form[field])
    },
    handleBlur(field) {
      this.handleRoundPrice(field)
      this.$refs.form.validateField(field)
    },
    getFormData() {
      return this.form
    },
    handleRequiredChange() {
      if (this.form.required) {
        this.$refs.form.validateField('isNumLimited')
      }
    },
    handleIsNumLimitedChange() {
      if (this.form.required && this.form.isNumLimited) {
        this.$nextTick(() => {
          this.$refs.form.validateField('minNum')
        })
      }
    }
  }
}
</script>

<style lang="scss">
.unit-price-form {
  &-unit-type {
    margin-right: 16px;
    height: 24px;
    padding: 0 8px;

    font-size: 12px;
    line-height: 12px;
    color: rgba(0, 0, 0, 0.65);

    &.is-active,
    &.is-active[disabled]:hover {
      color: #40a9ff;
      border-color: #40a9ff;
    }
  }
}
</style>
