  position: absolute;
  right: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgb(233, 233, 233);
  padding: 10px 16px;
  background: rgb(255, 255, 255);
  text-align: right;
  z-index: 1;
  button {
    margin-left: 12px;
  }
}

.globalpicker-content {
  display: flex;
