import Vue from 'vue'
import SvgIcon from '../modules/activity/components/SvgIcon.vue'
import { language_code as language_code_all } from '@activity/common/lang.config.server.js'

/**
 *   通过URL获取用户访问的链接的语言，并改写path
 */
function detect_url_lang() {
  // const p = this.path;
  const p = window.location.pathname
  window.KLK_LANG = 'en'
  window.KLK_LANG_PATH = ''
  var langs = language_code_all['F_LANG']
  for (var i = 0, len = langs.length; i < len; i++) {
    var lang = langs[i]
    var reg = new RegExp('^/' + lang + '/')

    if (lang == 'en') {
      continue
    }
    //其他语言的时候
    if (reg.test(p)) {
      window.KLK_LANG = lang
      window.KLK_LANG_PATH = window.klk_lang + '/'
      //修改path,然后保存到klk_lang
      // this.path = p.replace(new RegExp("^/" + this.klk_lang), "");
      break
    }
  }
}

export async function registerUtils(ctx) {
  Vue.prototype.copy = _.cloneDeep
  detect_url_lang()

  // localstorage目前有问题
  // window.KLK_LANG = window.localStorage.getItem('language') || 'en';
  // window.KLK_LANG_PATH = window.klk_lang + "/";

  // https://webpack.js.org/guides/dependency-management/#context-module-api
  const requireAll = (requireContext) =>
    requireContext.keys().map(requireContext)
  const req = require.context('../assets/svg-icons', false, /\.svg$/)
  requireAll(req)
  // 引入 svg-icon
  Vue.component('svg-icon', SvgIcon)
}
