<template>
  <div class="other-info-group-container">
    <a-checkbox-group
      v-if="!$attrs.polish"
      v-model="checkedGroups"
      style="width: 100%;"
    >
      <BasicOverflowCheckbox
        v-for="group in checkList"
        :key="group.group_id"
        :value="group.group_id"
        :disabled="!!$attrs.disabled"
        width="20%"
      >
        {{ group.group_name }}
      </BasicOverflowCheckbox>
    </a-checkbox-group>

    <template v-for="(group, index) in groups">
      <other-info-item
        v-if="validateItem(group)"
        ref="otherInfoItem"
        :key="group.uuid"
        v-bind="$attrs"
        :fields="group.style.fields"
        :group="group"
        :index="index"
        :countries="countries"
        :headmost="headmostCompute(group, index)"
        :decrease="decreaseCompute(group)"
        :languages="allLanguages"
        :validator="validator"
        @addGroup="handleAddGroup"
        @deleteGroup="handleDelGroup"
      />
    </template>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import OtherInfoItem from './OtherInfoItem.vue'
import { getUuid } from '@activity/utils'
import BasicOverflowCheckbox from '@activity/pages/components/BasicOverflowCheckbox.vue'

export default {
  name: 'OtherInfoGroup',
  components: {
    OtherInfoItem,
    BasicOverflowCheckbox
  },
  props: {
    originalGroups: {
      required: true,
      type: Array
    },
    groupCheckedList: {
      required: true,
      type: Array
    }
  },
  data() {
    return {
      groups: [],
      checkList: []
    }
  },
  computed: {
    allLanguages: {
      get() {
        return this.getAllLanguagesOpts()
      },
      set() {}
    },
    countries: {
      get() {
        return this.getCountryOpts()
      },
      set() {}
    },
    originalGroupsIdList() {
      return this.originalGroups.map((item) => item.group_id)
    },
    checkedGroups: {
      set(newV) {
        // antdv 把不在options里面的id删掉了，需要补回来
        const differenceCheckedGroups = _.difference(
          this.groupCheckedList,
          this.originalGroupsIdList
        )
        this.$emit('update:groupCheckedList', [
          ...newV,
          ...differenceCheckedGroups
        ]) // .sync, binding data
        this.$nextTick(() => {
          this.$emit('updateData', this.getPostData())
        })
      },
      get() {
        return this.groupCheckedList
      }
    }
  },
  watch: {
    groups: {
      deep: true,
      immediate: true,
      handler() {
        this.$emit('updateData', this.getPostData())
      }
    },
    checkedGroups: {
      deep: true,
      handler(newV, oldV) {
        let xor = _.xorWith(newV, oldV)

        let groups = _.cloneDeep(this.checkedGroups)
          .sort((a, b) => a - b)
          .reduce((acc, curr) => {
            let current = this.groups.filter((group) => group.group_id === curr)

            if (current.length) {
              if (xor.includes(curr)) {
                return oldV.length > newV.length
                  ? acc
                  : [
                      // add one
                      ...acc,
                      ...current.map((group) =>
                        this.getGroupDefaultValue(group)
                      )
                    ]
              } else {
                return [...acc, ...current]
              }
            }

            let originIndex = _.findIndex(this.originalGroups, {
              group_id: curr
            })

            // checkedGroups 是共用的，但 originalGroups 是私有的，所以需要排除非当前的 group 的情况
            if (-1 === originIndex) return acc

            let copy = _.cloneDeep(this.originalGroups[originIndex])
            return [...acc, this.getGroupDefaultValue(copy)]
          }, [])

        this.$set(this, 'groups', groups)
      }
    },
    originalGroups: {
      immediate: true,
      deep: true,
      handler(newV, oldV) {
        if (!_.isEqual(newV, oldV)) {
          this.checkList = _.uniqBy(this.originalGroups, 'group_id')
          this.groups = _.cloneDeep(
            this.originalGroups.map((group) => {
              group.style.fields = group.style.fields.map((field) => {
                if (!field.value) {
                  field.value = this.getFieldDefaultValue(field)
                }
                return field
              })
              return group
            })
          )
        }
      }
    }
  },
  async beforeMount() {
    this.countries = await this.$store.dispatch('actionGetTaxonomyCountries', {
      language: window.KLK_LANG
    })
    await this.$store.dispatch('actionGetAllLanguage')
  },
  methods: {
    ...mapGetters(['getAllLanguagesOpts', 'getCountryOpts']),
    validator(group, field, rule, value, callback) {
      if (!rule.required) {
        callback()
        return
      }

      if (
        field.style.type === 1 && // select
        field.options.type === 7
      ) {
        ;(value &&
        !(isNaN(value.max) || isNaN(value.min) || value.min >= value.max) && // number range
          callback()) ||
          callback(new Error(this.$t('package_price_data_error')))
        return
      }

      if (field.style.multiple_selection) {
        ;(Array.isArray(value) && value.length && callback()) ||
          callback(new Error(this.$t('can_not_be_empty')))
        return
      }

      if (field.style.is_unique) {
        let currentKeyValues = this.groups
          .filter((item) => group.group_id === item.group_id)
          .reduce((acc, curr) => {
            curr.style.fields.forEach((f) => {
              if (f.key === field.key) {
                acc = [...acc, f.value]
              }
            })
            return acc
          }, [])

        if (
          _.uniqWith(currentKeyValues, _.isEqual).length !==
          currentKeyValues.length
        ) {
          callback(new Error(this.$t('other_info_validate_warn_duplicate')))
          return
        }
      }

      ;(value && callback()) || callback(new Error(this.$t('can_not_be_empty')))
    },
    async validateForm() {
      let all = await Promise.all(
        (this.$refs.otherInfoItem || []).map((item) => item.validateForm())
      )
      return all.every((item) => item)
    },
    getFieldDefaultValue(field) {
      switch (field.options.type) {
        case 6: {
          field.value = field.options.default || {
            id: 0,
            content: ''
          }
          break
        }
        case 7: {
          field.value = field.options.default || {
            min: '',
            max: ''
          }
          break
        }
        default: {
          if (field.style.multiple_selection) {
            field.value =
              (field.options.default && [field.options.default]) || []
          } else {
            field.value = field.options.default || ''
          }
        }
      }
      return field
    },
    getGroupDefaultValue(group) {
      return {
        ...group,
        style: {
          ...group.style,
          fields: group.style.fields.map((field) =>
            this.getFieldDefaultValue(field)
          )
        }
      }
    },
    getPostData() {
      return this.groups.reduce((acc, curr) => {
        if (this.checkedGroups.includes(curr.group_id)) {
          return [
            ...acc,
            {
              ...curr,
              fields: curr.style.fields.map((field) => ({
                key: field.key,
                value: field.value
              }))
            }
          ]
        }
        return acc
      }, [])
    },
    validateItem(group) {
      return Boolean(
        this.checkedGroups.includes(group.group_id) &&
          _.get(group, 'style.fields', []).length
      )
    },
    handleAddGroup(group_id) {
      let originIndex = _.findIndex(this.originalGroups, { group_id })
      let copy = _.cloneDeep(this.originalGroups[originIndex])

      let index = _.findLastIndex(this.groups, { group_id })
      this.groups.splice(index + 1, 0, {
        ...this.getGroupDefaultValue(copy),
        uuid: getUuid()
      })
      this.$forceUpdate()
    },
    decreaseCompute(group) {
      return (
        this.groups.filter((item) => group.group_id === item.group_id).length >
        1
      )
    },
    handleDelGroup(index) {
      this.$confirm({
        title: this.$t('other_info_confirm_del'),
        onOk: () => {
          this.groups.splice(index, 1)
        }
      })
    },
    headmostCompute(group, index) {
      return _.findIndex(this.groups, { group_id: group.group_id }) === index
    }
  }
}
</script>

<style lang="scss">
.other-info-group-container {
  .ant-checkbox-wrapper + .ant-checkbox-wrapper {
    margin-left: 0;
  }

  .ant-checkbox-wrapper {
    margin-right: 8px;
  }
}
</style>
