<template>
  <div>
    <keep-alive v-if="routerAlive">
      <router-view v-if="$route.meta.keepAlive" />
    </keep-alive>

    <router-view v-if="routerAlive && !$route.meta.keepAlive" />
  </div>
</template>

<script>
import storeConf from '../store/index'
import { checkAuth } from '@/plugins/authDirective'
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store(storeConf)
export default {
  store,
  provide() {
    return {
      refreshPkgs: async () => {
        return await this.refreshPkgs()
      },
      refreshPage: this.refreshPage
    }
  },
  data() {
    return {
      routerAlive: true
    }
  },
  computed: {
    beforeEachInterceptor() {
      return store.getters.getBeforeEachInterceptor
    }
  },
  created() {
    this.$root.nonEdit2act = this.$root.nonEdit2act = !checkAuth('edit') //不能在main.js里面checkPermission
    this.$router.beforeHooks.push(async (to, from, next) => {
      let isDiff =
        to.fullPath.replace(to.hash, '') !==
        from.fullPath.replace(from.hash, '')

      if (isDiff && this.beforeEachInterceptor.length) {
        let flag = true
        for (let interceptor of this.beforeEachInterceptor) {
          let { before, callback } = interceptor
          let action = before === undefined ? true : await before()
          let status = callback === undefined ? true : await callback(action)
          if (!status) {
            flag = false
            break
          }
        }

        if (flag) {
          store.commit('clearBeforeEachInterceptor')
        }

        next(flag)
      } else {
        next()
      }
    })
  },
  methods: {
    refreshPage() {
      this.routerAlive = false
      this.$nextTick(() => {
        this.routerAlive = true
      })
    },
    async refreshPkgs() {
      // preload packages data in vuex
      let activity_id = +this.$route.params.id
      if (!activity_id) return
      let result = await this.$store.dispatch('getPkgInfo2actions', {
        activity_id,
        refresh: true
      })
      if (!result || typeof result === 'string') return
      let packages = result.package_info || []
      packages = this.sortUnit(packages)
      store.commit('setPackages', packages)
      store.commit('setServerTime', result.server_time)
      window.klk_gvars.package_all = packages
    },
    sortUnit(units) {
      {
        units = _.sortBy(units, 'package_id')
        return [
          ...units.filter((v) => v.published),
          ...units.filter((v) => !v.published)
        ]
      }
    }
  }
}
</script>
