<template>
  <a-form-model ref="form" :model="form" label-width="0">
    <a-form-model-item prop="value" :rules="rules">
      <a-input v-model="form.value" :disabled="readonly" />
      <template v-if="refData">
        <a-input v-model="refData.value" style="margin-top: 12px;" disabled />
      </template>
    </a-form-model-item>
  </a-form-model>
</template>

<script>
import AFormModelItem from 'ant-design-vue/es/form-model/FormItem'

export default {
  name: 'SingleWidget',
  components: {
    AFormModelItem
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: Object,
      required: true
    },
    required: {
      type: Boolean,
      default: false
    },
    config: {
      type: Object,
      default: () => ({})
    },
    refData: {
      type: Object,
      default: null
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      form: {
        value: ''
      }
    }
  },
  watch: {
    value: {
      immediate: true,
      deep: true,
      handler(v, oldV) {
        if (!_.isEqual(v, oldV)) {
          this.$set(this, 'form', {
            value: '',
            ...v
          })
        }
      }
    },
    form: {
      deep: true,
      handler(v) {
        this.$emit('change', v)
      }
    }
  },
  computed: {
    rules() {
      return {
        required: this.required,
        message: 'Please fill in this field.'
      }
    },
    conf() {
      return {
        allow_copy: false,
        allow_free_text: false,
        multiple_selection: false,
        ...this.config
      }
    }
  },
  methods: {
    validateForm() {
      return new Promise((resolve) =>
        this.$refs.form.validate((valid) => resolve(valid))
      )
    }
  }
}
</script>
