      {{ $t('global_delete') }}
    </a-button>
  </a-popconfirm>
</template>

<script>
export default {
  name: 'AutoExtendTableOperations',
  props: {
    data: {
      type: Object,
      default: () => ({})
    }
  },
  methods: {
    handleDelete(id) {
      this.$emit('delete', id)
    }
  }
}
</script>

