import Vue from 'vue'
import 'normalize.css/normalize.css' // A modern alternative to CSS resets
import App from './App'
import registerPlugins from './plugins'
import { isMerchant, isAdmin, isDev, isTest, isProd } from './env'
import { Modal, message, notification } from 'ant-design-vue'
import { mapGetters } from 'vuex'
import { errorLogs } from '@activity/utils/logquery'

import {
  initAuthDirective,
  setDisabledLoop,
  setMutationObserver
} from './plugins/authDirective'

Vue.config.productionTip = false

if (isProd) {
  Vue.config.errorHandler = function(err, vm, info) {
    errorLogs(err, info)
    console.error(err)
  }
}

Vue.prototype.$modal = Modal
Vue.prototype.$message = message
Vue.prototype.$notification = notification
const admin_const = require('@/admin_const')
const context = {
  isMerchant
}

// expose edit_lang from here
typeof window.klk_gvars === 'object' || (window.klk_gvars = {})
klk_gvars.edit_lang = []
klk_gvars.position = []

async function createApplication() {
  registerPlugins(context).then(() => {
    initAuthDirective()
    createApp()
  })
}
const editRoutes = ['/act/activity/', '/act/package/']
function createApp() {
  const { i18n, router, isMerchant, antLocale = {} } = context
  const app = new Vue({
    el: '#app',
    router,
    i18n,
    mounted() {
      console.log(Object.entries(process.env).join('\n'), this)
    },
    render: function(h) {
      if (isMerchant) {
        return (
          <a-config-provider locale={antLocale}>
            <App />
          </a-config-provider>
        )
      }
      return <App />
    },
    data() {
      return {
        isMerchant,
        isAdmin,
        nonEdit2act: false, //全局锁
        nonEdit2status: false, //状态锁
        template_id: 0
      }
    },
    methods: {
      ...mapGetters(['getCategoryInfo2getters']),
      setMutationObserver(el, vm) {
        setMutationObserver(el, () => {
          // this.setDisabled2calss(el)
          this.setDisabledLoop(vm || this)
        })
      },
      setDisabled2calss: _.debounce(function(el) {
        const disabeldClass = 'common-disabled-style-global'
        let node = document.querySelector(el)
        let claArr = node.className.split(/\s+/)
        let hasDisabled = claArr.some((cls) => cls === disabeldClass)
        if (!hasDisabled) {
          claArr.push(disabeldClass)
          node.className = claArr.join(' ')
        }
      }, 300),
      setDisabledLoop: _.debounce(function(vm) {
        let isStatusLock =
          this.nonEdit2status &&
          editRoutes.some((path) => this.$route.path.indexOf(path) !== -1)
        if (!this.nonEdit2act && !isStatusLock) return
        let arr = [
          'AInput',
          'ASelect',
          'Input',
          'Select',
          'ACheckboxGroup',
          'ACheckbox',
          'Checkbox',
          'ARadioGroup',
          'ARadio',
          'ATextarea',
          'AButton',
          'SimpleMde'
        ]
        let oldSilent = Vue.config.silent
        ;(isDev || isTest) && (Vue.config.silent = true) //临时关闭报错
        arr.forEach((key) => {
          setDisabledLoop.call(vm, key, {
            $props: {
              disabled: true
            }
          })
        })
        ;(isDev || isTest) && (Vue.config.silent = oldSilent) //
      }, 300)
    },
    computed: {
      superEditLock() {
        return this.nonEdit2act || this.nonEdit2status
      }, //超级锁
      isWifiSim() {
        return [7, 8].includes(this.template_id)
      },
      isFNB() {
        return this.template_id == 4
      },
      isSIM() {
        return this.template_id === 8
      },
      isSPU() {
        return admin_const.spu_template_ids.includes(this.template_id)
      },
      isYSIM() {
        return this.template_id === 9
      },
      isHotel() {
        return this.template_id === 14
      }
    }
  })
  window.app = app
  return app
}

createApplication()
