<template>
  <div class="outer">
    <!--<p class="header">-->
    <!--<slot />-->
    <!--</p>-->
    <transition name="fade">
      <div v-if="showError" class="error-message">{{ validateMessage }}</div>
    </transition>
    <compound-group
      v-if="active || forceDisplay"
      ref="group"
      v-model="currentData"
      :title="title"
      v-bind="$attrs"
      :ref_data="ref_group_data"
    />
  </div>
</template>

<script>
import CompoundGroup from './Group.vue'

export default {
  components: {
    'compound-group': CompoundGroup
  },
  model: {
    prop: 'data',
    event: 'change'
  },
  props: {
    active: {
      type: Boolean,
      default: true
    },
    title: {
      type: Object,
      default: () => {}
    },
    data: {
      type: Object,
      default: () => {}
    },
    ref_data: { type: Object, default: () => {} }
  },
  data() {
    return {
      forceDisplay: false, // used to lazy render detail content for better perf
      showError: false,
      validateMessage: ''
    }
  },
  computed: {
    ref_group_data() {
      return this.ref_data.attr_values || []
    },
    currentData: {
      get() {
        // console.info("get title");
        return this.data.attr_values || []
      },
      set(v) {
        let dataStruct = ADMIN_API.struct({
          id: 'number',
          order: 'number?',
          attr_values: ['any']
        })

        let result = dataStruct(
          _.isEmpty(this.data)
            ? {
                id: this.title.id,
                order: this.title.display_order,
                attr_values: v
              }
            : {
                id: this.title.id,
                order: this.title.display_order,
                ...this.data,
                attr_values: v
              }
        )
        // console.info("set title", result);
        this.$emit('change', result)
      }
    }
  },
  watch: {
    title: {
      deep: true,
      handler(v) {}
    },
    data: {
      deep: true,
      handler(v) {}
    }
  },
  methods: {
    // 获取数据
    async validate() {
      // need to make sure every component get rendered before validate
      this.forceDisplay = true
      await new Promise((resolve) => this.$nextTick(resolve))
      console.log(`valid title ${this.title.id}`)
      let valid = true
      let group_valid = await this.$refs.group.validate() // TODO
      if (!group_valid) {
        valid = false
      }
      this.$emit('validate', valid)
      console.log(`fin valid title ${this.title.id}`)
      return valid
    },
    async getData() {
      await this.validate()
      return this.$refs.group.getData()
    }
  }
}
</script>
