<template>
  <div class="act-nav">
    <div class="act-nav-box">
      <a-anchor :offsetTop="72" class="common-anchor-style hide-all">
        <a-collapse
          class="common-collapse-style"
          v-model="activeKeys"
          :bordered="false"
        >
          <a-collapse-panel
            style="box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.09)"
            key="1"
            :header="$t('global_activity')"
            :show-arrow="false"
          >
            <ul class="menu-box menu-act">
              <li
                v-for="(item, i) in actObj.list"
                :key="item.key"
                :class="[
                  $route.path.includes(item.path) ? 'on' : '',
                  changeLock <= i ? 'lock' : ''
                ]"
                @click="handleGoRoute(item, i)"
              >
                <i v-if="item.require" class="common-required-star require">*</i
                ><span class="text">{{ item.text }}</span>
                <i :class="['right-icon', item.finish ? 'finish' : '']"
                  ><a-icon type="check-circle" theme="filled"
                /></i>
              </li>
            </ul>
          </a-collapse-panel>
          <a-collapse-panel class="nav-pkg-box" key="2" :show-arrow="false">
            <template slot="header">
              <div @click="clickPkgHeader" class="header-box">
                <span>{{ $t('global_package') }} </span>
                <div
                  v-if="changeLock > 2"
                  @click.prevent.stop
                  class="action-box"
                >
                  <a-input-search
                    class="search-box common-search-style"
                    v-model="pkgObj.searchVal"
                    :placeholder="$t('global_button_search')"
                    allowClear
                  />
                  <span
                    v-if="isMerchant"
                    @click="clickAddPackage"
                    class="btn-add-action"
                    ><a-icon type="plus" />{{ $t('global_add') }}</span
                  >
                </div>
              </div>
            </template>
            <a-collapse
              class="common-collapse-style"
              v-model="activePkgIds"
              :bordered="false"
              @change="handleChangeActive"
            >
              <a-collapse-panel
                v-for="pkg in pkgFilterList || []"
                :key="'' + pkg.package_id"
                class="panel-menu-wrap"
                :showArrow="false"
              >
                <template slot="header">
                  <a-tooltip placement="right">
                    <template slot="title">
                      <div>
                        {{
                          `${
                            pkg.package_local ? pkg.package_local[0].name : ''
                          }`
                        }}
                      </div>
                      <div
                        :class="
                          pkg.publish_status === 1
                            ? 'status-publish-style'
                            : 'status-unpublish-style'
                        "
                      >
                        {{
                          $t(
                            pkg.publish_status === 1
                              ? 'global_published'
                              : 'global_unpublished'
                          )
                        }}
                      </div>
                    </template>
                    <div
                      class="left-line common-two-line"
                      :class="pkg.publish_status === 1 ? 'publish-line' : ''"
                    >
                      {{
                        `${pkg.package_id} - ${
                          pkg.package_local ? pkg.package_local[0].name : ''
                        }`
                      }}
                    </div>
                  </a-tooltip>
                </template>
                <ul class="menu-box">
                  <li
                    v-for="(item, i) in pkgObj.list"
                    :key="item.key"
                    :class="[
                      calcPkgLock(i, pkg.package_id) ? 'lock' : '',
                      getPkgActive(pkg, i)
                    ]"
                    @click="handleGoRoute(item, i, pkg)"
                  >
                    <i v-if="item.require" class="common-required-star require"
                      >*</i
                    ><span class="text">{{ item.text }}</span>
                    <i
                      :class="[
                        'right-icon',
                        calcPkgFinished(pkg.package_id, item.step)
                          ? 'finish'
                          : ''
                      ]"
                      ><a-icon type="check-circle" theme="filled"
                    /></i>
                  </li>
                </ul>
              </a-collapse-panel>
            </a-collapse>
            <div
              v-show="
                !$route.query.package_id &&
                  $route.path.indexOf('/act/package/') !== -1
              "
              class="menu-wrap"
            >
              <h3>-</h3>
              <ul class="menu-box">
                <li
                  v-for="(item, i) in pkgObj.addList"
                  :key="item.key"
                  :class="[
                    changeLock <= i + actObj.list.length ? 'lock' : '',
                    i === 0 ? 'pkg-active' : ''
                  ]"
                  @click="handleGoRoute(item, i)"
                >
                  <i v-if="item.require" class="common-required-star require"
                    >*</i
                  ><span class="text">{{ item.text }}</span>
                  <i :class="['right-icon', item.finish ? 'finish' : '']"
                    ><a-icon type="check-circle" theme="filled"
                  /></i>
                </li>
              </ul>
            </div>
          </a-collapse-panel>
        </a-collapse>
      </a-anchor>
      <div v-show="navLoading" class="common-spin-style"><a-spin /></div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex'
import { parseStr, getEditLang } from '@activity/utils'
import { isMerchant } from '@/env'
export default {
  inject: ['reloadPage2provide'],
  data() {
    return {
      isMerchant,
      navLoading: false,
      pkgStepStatusMap: {},
      activePkgIds: [],
      activeKeysDef: ['1', '2'],
      pkgInfos: {
        package_info: []
      },
      activity_id: this.$route.params.id,
      package_id: +this.$route.query.package_id,
      changeLock: 1,
      actObj: {
        list: [
          {
            step: 'activity_basic_info', // Step 的编码,
            require: true,
            key: 'basicInfo',
            text: __('activity_basic_page_basic_info'),
            path: '/act/activity/basic/',
            route: () => {
              this.$router.push({
                path: `/act/activity/basic/${this.activity_id}`,
                query: {
                  ...this.$route.query,
                  lang: getEditLang()
                }
              })
            },
            finish: false
          },
          {
            step: 'activity_detail_info',
            require: true,
            key: 'activityDetail',
            text: __('act_activity_details'),
            path: '/act/activity/detail/',
            route: () => {
              this.$router.push({
                path: `/act/activity/detail/${this.activity_id}`,
                query: {
                  ...this.$route.query,
                  lang: getEditLang()
                }
              })
            }
          }
        ]
      },
      pkgObj: {
        addFlag: true,
        searchVal: '',
        addList: [
          {
            require: true,
            text: __('package_tag_info'),
            path: '/act/package/info/',
            route: () => {
              if (this.$route.path.includes('/act/package/info/')) return
              this.$router.push({
                path: `/act/package/info/${this.activity_id}`,
                query: {
                  ...this.$route.query,
                  package_type: 1,
                  package_id: ''
                }
              })
            }
          },
          {
            require: true,
            text: __('package_tag_package_details')
          },
          {
            require: true,
            text: __('actNav.sellingInfo')
          },
          {
            require: true,
            text: __('package_tag_extra_info')
          }
        ],
        list: [
          {
            step: 'package_basic_info',
            require: true,
            key: 'packageInfo',
            text: __('package_tag_info'),
            path: '/act/package/info/'
          },
          {
            step: 'package_detail_info',
            require: true,
            key: 'packageDetails',
            text: __('package_tag_package_details'),
            path: '/act/package/detail/'
          },
          {
            step: ['price', 'timeslot'],
            require: true,
            key: 'sellingInfo',
            text: __('actNav.sellingInfo'),
            path: '/act/package/schedule/'
          },
          {
            step: 'extra_information',
            require: true,
            key: 'extraInfomation',
            text: __('package_tag_extra_info'),
            path: '/act/package/extra/'
          }
        ]
      }
    }
  },
  watch: {
    pkgStepStatusMap(obj) {
      this.setPkgStepStatusMap2mutations(obj)
    },
    actStepStatusObj: {
      handler() {
        this.updateFinishStatus()
      },
      deep: true
    }
  },
  computed: {
    activeKeys: {
      get() {
        return this.activeKeysDef
      },
      set() {
        this.activeKeysDef = ['1', '2']
      }
    },
    ...mapState({
      actStepStatusObj: 'actStepStatus'
    }),
    showAddPkg() {
      return (
        !this.$route.query.package_id &&
        this.$route.path.indexOf('/act/package/') !== -1
      )
    },
    tipText() {
      return parseStr(this.$t('actNav.regText'), { number: 446 })
    },
    pkgFilterList() {
      let {
        pkgObj: { searchVal },
        pkgInfos
      } = this
      if (!searchVal) {
        return pkgInfos.package_info || []
      }
      let arr = pkgInfos.package_info.filter((pkg) => {
        return `${pkg.package_id} - ${
          pkg.package_local ? pkg.package_local[0].name : ''
        }`.includes(searchVal)
      })
      return arr
    },
    // old
    isContent() {
      return document.URL.includes('/act/content/')
    },
    language_type() {
      return lang_conf.getLangObj('F_LANG', 'B_LANG')[KLK_LANG]
    }
  },
  async created() {
    klook.bus
      .$off('updateActStepStatus2bus')
      .$on('updateActStepStatus2bus', this.initActStepStatus)
    klook.bus
      .$off('updatePkgInfos2bus')
      .$on('updatePkgInfos2bus', this.updatePkgInfos2bus)
    this.navLoading = true
    await this.initActStepStatus(true)
    await this.updatePkgInfos2bus()
    this.navLoading = false
    this.package_id && (this.activePkgIds = ['' + this.package_id])
  },
  methods: {
    ...mapActions([
      'updateActivityStepStatusById',
      'updatePackageStepStatusById',
      'getPkgInfo2actions'
    ]),
    ...mapMutations(['setPkgInfos2mutations', 'setPkgStepStatusMap2mutations']),
    clickPkgHeader() {
      if (this.activePkgIds.length) {
        this.activePkgIds2copy = _.clone(this.activePkgIds)
        this.activePkgIds = []
      } else {
        this.activePkgIds = this.activePkgIds2copy
      }
    },
    getPkgActive(pkg, i) {
      let { query, path } = this.$route
      let pkgActive = 'pkg-active'
      if (pkg.package_id == query.package_id) {
        let pkgList = this.pkgObj.list
        return path.includes(pkgList[i].path) ? pkgActive : ''
      }
      return ''
    },
    calcPkgLock(i, pkgId) {
      let { pkgStepStatusMap: maps } = this
      if (pkgId in maps) {
        let obj = maps[pkgId]
        // Selling Info 特殊处理
        if (i === 3) {
          return !obj.price || !obj.timeslot
        }
        return i > Object.keys(obj).length
      }
      return false
    },
    calcPkgFinished(packageId, step) {
      const pgkSteps = this.pkgStepStatusMap[packageId]

      if (!pgkSteps) {
        return false
      }

      return Array.isArray(step)
        ? step.every((item) => !!pgkSteps[item])
        : pgkSteps[step]
    },
    handleChangeActive(keys) {
      keys.forEach((key) => {
        this.getPkgStepStatus(key)
      })
    },
    async updatePkgInfos2bus(data, newPkgId) {
      if (data) {
        this.$set(this, 'pkgInfos', data)
      } else {
        this.$set(
          this,
          'pkgInfos',
          await this.initPkgInfos({
            refresh: true
          })
        )
        await this.getPkgStepStatus(newPkgId, true)
      }
    },
    clickAddPackage() {
      if (this.showAddPkg) return
      // !this.activeKeys.includes('2') && this.activeKeys.push('2')
      let { pkgObj } = this
      if (pkgObj.addFlag || this.$route.path.includes('/act/activity/')) {
        pkgObj.addFlag = false
        this.$router.replace({
          path: `/act/package/info/${this.$route.params.id}`,
          query: {
            ...this.$route.query,
            package_type: 1,
            package_id: ''
          }
        })
        this.reloadPage2provide()
      }
    },
    async updateFinishStatus(save = false) {
      if (!isNaN(this.activity_id)) {
        let actStepStatusObj = this.actStepStatusObj
        this.changeLock = this.initFinishStatus(
          this.actObj.list,
          actStepStatusObj,
          'step'
        )
        this.pkgObj.addFlag = !!actStepStatusObj.activity_detail_info
        if (
          save &&
          actStepStatusObj.activity_detail_info &&
          (!this.pkgInfos.package_info || !this.pkgInfos.package_info.length)
        ) {
          this.clickAddPackage()
        }
      }
    },
    async initActStepStatus(sync = false) {
      let data = {
        activity_id: +this.activity_id,
        language: getEditLang(),
        refresh: true
      }
      if (sync) {
        return await this.updateActivityStepStatusById(data)
      } else {
        return this.updateActivityStepStatusById(data)
      }
    },
    async initPkgInfos(data = {}) {
      let { actStepStatusObj } = this
      if (!actStepStatusObj.activity_detail_info) return {}
      let pkgInfos = await this.getPkgInfo2actions({
        activity_id: +this.activity_id,
        refresh: data.refresh,
        custom_language: klook.getEditLang(),
        only_audit_package: true
      })
      return pkgInfos
    },
    async getPkgStepStatus(packageId, refresh) {
      let package_id = +packageId || +this.$route.query.package_id
      if (!package_id || (this.pkgStepStatusMap[package_id] && !refresh)) return
      let stepObj = await this.updatePackageStepStatusById({
        package_id,
        language: getEditLang(),
        refresh: true
      })
      this.$set(this.pkgStepStatusMap, package_id, stepObj || {})
      !this.activePkgIds.includes('' + package_id) &&
        this.activePkgIds.push('' + package_id)
    },
    initFinishStatus(arr = [], stepObj = {}, stepKey = 'step') {
      let count = 1
      arr.forEach((item) => {
        let stepStatus = stepObj[item[stepKey]]
        this.$set(item, 'finish', stepStatus === 1)
        if (item[stepKey] in stepObj) {
          count++
        }
      })
      return count
    },
    handleGoRoute(item, i, pkg = {}) {
      if (this.changeLock <= i && this.changeLock <= 2) return false
      if (this.calcPkgLock(i, pkg.package_id)) return false
      let { route } = item
      if (
        this.$route.path.includes(item.path) &&
        pkg.package_id == this.$route.query.package_id
      ) {
        return false
      }
      this.pkgObj.addFlag = true
      if (typeof route === 'function') {
        route()
      } else {
        if (!item.path) return false
        this.$router.replace({
          path: item.path + this.activity_id,
          query: {
            ...this.$route.query,
            package_type: pkg.package_type,
            package_id: pkg.package_id
          }
        })
        // this.reloadPage2provide()
      }
    }
  }
}
</script>

