    }
    .right-icon {
      float: right;
      font-size: 16px;
      color: #e8e8e8;
      &.finish {
        color: #36b37e;
      }
    }
    .panel-menu-wrap {
      position: relative;
      background: rgba(0, 145, 255, 0.05);
      .left-line {
        font-size: 16px;
        font-weight: 400;
        border-left: 4px solid rgba(0, 0, 0, 0.09);
        padding-left: 10px;
        line-height: 20px;
        &.publish-line {
          border-left: 4px solid #36b37e;
        }
      }
    }
    .menu-wrap {
      position: relative;
      padding: 10px 10px 10px 22px;
      background: rgba(0, 145, 255, 0.05);
      h3 {
        color: #0091ff;
        font-size: 16px;
        font-weight: 400;
      }
    }
    .menu-box {
      &.menu-act {
        padding: 0 0 8px 10px;
        li .text {
          max-width: 215px;
        }
      }
      padding: 10px 10px 6px 22px;
      margin: 0;
      li {
        &.pkg-active {
          border: 1px solid #0091ff;
          color: #0091ff;
        }
        position: relative;
        line-height: 36px;
        color: #000;
        margin-bottom: 4px;
        background-color: #fff;
        padding: 0 10px 0 10px;
        border-radius: 4px;
        cursor: pointer;
        box-sizing: border-box;
        .text {
          display: inline-block;
          max-width: 185px;
          height: 18px;
          line-height: 16px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          vertical-align: middle;
        }
        &:hover {
          background: rgba(0, 145, 255, 0.05);
        }
        &.on {
          color: #0091ff;
        }
        &.lock {
          color: #ccc;
          cursor: not-allowed;
        }
      }
    }
    .header-box {
      position: relative;
    }
    .action-box {
      position: absolute;
      z-index: 3;
      top: 0;
      right: 0;
      i:not(:last-child) {
        margin-right: 10px;
      }
      .search-box {
        width: 96px;
      }
      .btn-add-action {
        text-align: right;
        display: inline-block;
        width: 56px;
        color: #0091ff;
        cursor: pointer;
      }
    }
    .tip-box {
      background-color: #fff;
      padding: 0 22px;
      line-height: 37px;
      color: #000;
    }
  }
  .nav-pkg-box {
    height: calc(100vh - 270px);
    overflow: hidden;
  }
}
</style>
