const mutations = {
  setServerTime(state, v) {
    state.serverTime = v || ''
  },
  setPackages(state, v) {
    state.packages = v
  },
  setActItemData2mutations(state, v) {
    state.actItemData = v
  },
  setPkgInfos2mutations(state, v) {
    state.pkgInfos = v
  },
  setAllVariables2mutations(state, v) {
    state.allVariables = v
  },
  setPkgStepStatus2mutations(state, v) {
    state.pkgStepStatus = v
  },
  setPkgStepStatusMap2mutations(state, v) {
    state.pkgStepStatusMap = v
  },
  setActStepStatus(state, v) {
    state.actStepStatus = v
  },
  setPosition(state, v) {
    state.position = v
  },
  setLanguage(state, v) {
    state.language = v
  },
  setPublishedCategoryList(state, v) {
    state.publishedcCategoryList = v
  },
  //设置 层级类目信息
  setCategoryInfo(state, v) {
    state.categoryInfo = v
  },
  setAllVariables(state, v) {
    state.all_variables = v
  },
  setAllPackages(state, v) {
    state.all_packages = v
  },

  // other info data
  saveCountries(state, data) {
    state.countries = data
  },
  saveCities(state, data) {
    state.cities = data
  },
  saveAllLanguages(state, data) {
    state.allLanguages = data
  },

  // 价格日历
  setCalendarDate(state, data) {
    state.calendarDate = data
  },
  setPkgPendingApproval(state, data) {
    state.isPkgPendingApproval = data
  },
  setCalendarUnit(state, data) {
    state.calendarUnit = data
  },
  setPackageSkuData(state, data) {
    state.packageSkuData = _.cloneDeep(data)
  },
  setDestinationToday(state, data) {
    state.destinationToday = data
  },
  setPriceModel(state, data) {
    state.priceModel = data
  },
  resetCalendar(state) {
    // 这里先不清isPkgPendingApproval，因为会随时变化
    state.calendarDate = new Date()
    state.calendarUnit = null
    state.packageSkuData = {}
    state.destinationToday = moment().format('YY-MM-DD')
    state.priceModel = -1
  },

  // taxonomy picker
  setAllCountries(state, v) {
    state.allCountries = v
  },

  //
  setGroupsSummary(state, v) {
    state.groupsSummary = v
  },
  //
  setBeforeEachInterceptor(state, v) {
    state.beforeEachInterceptor.push(v)
  },
  clearBeforeEachInterceptor(state, tag = null) {
    if (tag) {
      state.beforeEachInterceptor = state.beforeEachInterceptor.filter(
        (item) => !item.tag
      )
    } else {
      state.beforeEachInterceptor = []
    }
  }
}

export default mutations
