export const getCurrentLocal = (
  allData,
  {
    type,
    field,
    langField = 'language',
    valField = 'value',
    defaultVal = 'Please input'
  } = {}
) => {
  let data = _.get(allData, `${type}.config.${field}`, [])
  let current = _.find(data, {
    [langField]: lang_conf.getLangObj('F_LANG', 'B_LANG')[KLK_LANG]
  })

  return (current && current[valField]) || defaultVal
}
export const getAllEditLang = ({ requiredEn = false } = {}) => {
  let common = [getEditLang(), getRefLang()].filter((item) => item)

  if (requiredEn) {
    return [...new Set([...common, 'en_US'])]
  }

  return [...new Set(common)]
}

export const getUuid = () => {
  return (
    'uuid-' +
    (+new Date()).toString(32).substr(0, 4) +
    '-' +
    Math.random()
      .toString(32)
      .substr(2, 6)
  )
}

export const getType = (value) => {
  return Object.prototype.toString.call(value).slice(8, -1)
}
export const getParamsKey = (params = {}, arr = [], options = {}) => {
  let obj = {}
  let defOpt = {
    paramsKey: 'paramsKey',
    valueKey: 'value',
    transform: {
      // Array: (arr) => {
      //   return arr.join(',')
      // }
    }
  }
  let opt = _.merge({}, defOpt, options)
  let { paramsKey, valueKey, transform } = opt
  const getType = (value) => {
    return Object.prototype.toString.call(value).slice(8, -1)
  }
  for (let k in params) {
    for (let i in arr) {
      let item = arr[i]
      if (item[paramsKey] === k) {
        let val = item[valueKey]
        obj[k] = getType(val) in transform ? transform[getType(val)](val) : val
      }
    }
  }
  return obj
}
export const getTableColumns = (columns = [], options = {}) => {
  let defOpt = {
    maps: {
      title: 'dataIndex'
    },
    langs: '', // 兼容对象和字符串
    defObj: {
      ellipsis: true
    }
  }
  let opt = _.merge({}, defOpt, options)
  let { maps, langs, defObj } = opt
  let arr = columns.map((item) => {
    for (let dk in defObj) {
      item[dk] = defObj[dk]
    }
    for (let k in maps) {
      let kv = item[maps[k]]
      if (typeof langs === 'string') {
        item[k] =
          item[k] ||
          __(`${langs}.${kv}`) ||
          __(`${langs}.${kv.split('.').pop()}`)
      } else {
        // 1、取key为title的值，2、取key为dataIndex的值，3、取点.分割最后部分的值尝试取值
        item[k] = item[k] || langs[kv] || langs[kv.split('.').pop()]
      }
    }
    return item
  })
  return arr
}
export const parsePlatform = (str) => {
  return str.replace(/\{\{([^}]+)\}\}/g, (...args) => {
    return args[1] === 'platform' ? lowerPlatForm : args[1]
  })
}
export const parseApiUrl = (obj) => {
  let retObj = {}
  for (let k in obj) {
    retObj[k] =
      getType(obj[k]) === 'Object' ? parseApiUrl(obj[k]) : parsePlatform(obj[k])
  }
  return retObj
}
export const parseStr1 = (str = '', obj) => {
  return str.replace(/\{([^}]+)\}/g, (...args) => {
    return obj[args[1]]
  })
}
export const parseStr = (str = '', obj) => {
  return str.replace(/\{\{([^}]+)\}\}/g, (...args) => {
    return obj[args[1]]
  })
}
export const getLocalStorage = (key) => {
  let val = window.localStorage.getItem(key)
  try {
    val = JSON.parse(val)
  } catch (error) {
    //
  }
  return val
}
export const setLocalStorage = (key, val) => {
  if (val !== null && typeof val === 'object') {
    window.localStorage.setItem(key, JSON.stringify(val))
  } else {
    window.localStorage.setItem(key, val)
  }
}

