export const validB_LANG = (language, back = true) => {
  if (language && !lang_conf.getLangArray('B_LANG').includes(language)) {
    alert('Invalid language')
    back && window.history.back(-1)
  }

  return language
}

export const getLangTitle = (language) => {
  if (validB_LANG(language)) {
    return lang_conf.getLangObj('B_LANG', 'LANG_TITLE')[language]
  }
  return ''
}

export const getEditLang = () =>
  validB_LANG(new URLSearchParams(location.search).get('lang') || 'en_US')

export const getRefLang = () =>
  validB_LANG(new URLSearchParams(location.search).get('ref') || '')

