<template>
  <div class="activity-management">
    <div class="activity-management-box">
      <div class="act-tab-box">
        <a-tabs
          class="common-tabs-style"
          :type="tabObj.type"
          :default-active-key="tabObj.key"
          v-model="tabObj.key"
          :tab-bar-gutter="0"
          @change="tabObj.handleChange"
        >
          <a-tab-pane
            v-for="tab in tabObj.tabs"
            :key="tab.key"
            :tab="tab.title"
          />
        </a-tabs>
      </div>
      <div class="act-select">
        <div
          v-for="item in filterSelect"
          :key="item.key"
          class="act-select-box"
        >
          <span class="label-box">{{ $t(`${item.$t}`) + ':' }}</span>
          <template v-if="item.formType === 'treeSelect' || item.treeData">
            <a-tree-select
              v-model="item.value"
              class="common-select-style common-icon-select-down"
              allow-clear
              :max-tag-count="1"
              treeCheckable
              :tree-data="item.treeData"
              :placeholder="item.placeholder || $t('global_please_select')"
              tree-default-expand-all
              treeNodeFilterProp="title"
            >
            </a-tree-select>
          </template>
          <template
            v-else-if="item.formType === 'cascader' || item.displayRender"
          >
            <a-cascader
              v-model="item.value"
              class="common-ascader-style"
              :options="item.options"
              :display-render="item.displayRender"
              expand-trigger="hover"
              :max-tag-count="1"
              :placeholder="item.placeholder || $t('global_please_select')"
            />
          </template>
          <template v-else-if="item.formType === 'select' || item.options">
            <a-select
              v-model="item.value"
              class="common-select-style"
              mode="multiple"
              :options="item.options"
              :max-tag-count="1"
              :placeholder="item.placeholder || $t('global_please_select')"
              show-arrow
              allow-clear
              optionFilterProp="children"
            />
          </template>
          <template
            v-else-if="item.formType === 'input-search' || !item.options"
          >
            <a-input
              v-model="item.value"
              class="common-input-style"
              :placeholder="item.placeholder || $t('global_please_input')"
              :maxLength="100"
              allow-clear
            />
          </template>
        </div>
        <div class="act-btn">
          <div class="act-btn-box">
            <a-button type="primary" @click="clickSearchBtn">{{
              $t('global_button_search')
            }}</a-button
            ><a-button type="link" @click="clickResetBtn">{{
              $t('global_reset')
            }}</a-button>
          </div>
        </div>
      </div>
      <div class="act-table">
        <div
          v-show="isMerchant && tabObj.key === 'activity' && checkAuth('edit')"
          class="ta-right btn-create-box"
        >
          <a-button type="primary" icon="plus" @click="clickCreateAct"
            ><span>{{ $t('act_create_new_activity') }}</span></a-button
          >
        </div>
        <div class="act-table-box">
          <tableAntd
            v-if="tableData.show"
            v-bind="{ tableData, pageData }"
            @pageChange="handlePageChange"
          >
            <template slot="package_id" slot-scope="id, row">
              <span
                v-if="row.package_archived"
                :title="`${id} - ${row.package_title}`"
                >{{ `${id} - ${row.package_title}` }}</span
              >
              <router-link
                v-else
                :to="{
                  path: `/act/package/info/${row.activity_id}?lang=${
                    row.language
                  }&package_id=${id}&package_type=1${
                    isMerchant ? '' : '&review=1'
                  }`
                }"
                target="_blank"
                ><span :title="`${id} - ${row.package_title}`">{{
                  `${id} - ${row.package_title}`
                }}</span></router-link
              >
            </template>
            <template slot="activity_id" slot-scope="id, row">
              <span
                v-if="row.package_archived"
                :title="`${id} - ${row.activity_title}`"
                >{{ `${id} - ${row.activity_title}` }}</span
              >
              <router-link
                v-else
                :to="{
                  path: `/act/activity/basic/${id}?lang=${row.language}${
                    isMerchant ? '' : '&review=1'
                  }`
                }"
                target="_blank"
                ><span :title="`${id} - ${row.activity_title}`">{{
                  `${id} - ${row.activity_title}`
                }}</span></router-link
              >
            </template>
            <template slot="package_count" slot-scope="pkgCount, row">
              <span
                @click="handleGoPackage(row)"
                class="number-right common-link"
                v-if="pkgCount > 0"
                >{{ pkgCount }}</span
              >
              <span class="number-right" v-else>{{ pkgCount }}</span>
            </template>
            <template slot="approval_status" slot-scope="val, row">
              <span
                v-if="val === 3 && !row.note"
                :class="[
                  'common-status-style',
                  mapsFilterFn('approval_status', val, 'class'),
                  row['package_archived'] ? 'archived' : ''
                ]"
                >{{ mapsFilterFn('approval_status', val) }}
              </span>
              <a-tooltip
                v-else
                overlayClassName="common-tooltip-style"
                :title="row.note"
              >
                <span
                  :class="[
                    'common-status-style',
                    mapsFilterFn('approval_status', val, 'class'),
                    row['package_archived'] ? 'archived' : ''
                  ]"
                  >{{ mapsFilterFn('approval_status', val) }}</span
                >
              </a-tooltip>
              <a-icon
                :style="{ cursor: row['package_archived'] ? 'text' : '' }"
                @click="clickChangeLog(row, $event)"
                type="clock-circle"
                class="clock-circle-style"
              />
            </template>
            <template slot="activity_status" slot-scope="val, row">
              <span
                :class="[
                  'common-status-style',
                  platformFn('platform_activity_status', val, 'class'),
                  row['package_archived'] ? 'archived' : ''
                ]"
                >{{ platformFn('platform_activity_status', val) }}</span
              >
            </template>
            <template slot="package_status" slot-scope="val, row">
              <span
                :class="[
                  'common-status-style',
                  mapsFilterFn('package_status', val, 'class'),
                  row['package_archived'] ? 'archived' : ''
                ]"
                >{{ mapsFilterFn('package_status', val) }}</span
              >
            </template>
            <template slot="package_archived" slot-scope="val">
              <span
                :class="[
                  'common-status-style',
                  mapsFilterFn('package_archived', val, {
                    mapKey: 'mapValue',
                    retKey: 'class'
                  })
                ]"
                >{{
                  mapsFilterFn('package_archived', val, { mapKey: 'mapValue' })
                }}</span
              >
            </template>
            <template slot="approval_activity" slot-scope="_, row">
              <div class="action-box common-action-style">
                <a-button
                  :disabled="row.approval_status !== 1"
                  v-auth="isAct ? 'actApprove' : 'pkgApprove'"
                  @click="adminObj.handleApprove(row)"
                  type="primary"
                  style="margin: 0 5px"
                  >{{ $t('approve') }}</a-button
                >
                <a-button
                  :disabled="row.approval_status !== 1"
                  v-auth="isAct ? 'actReject' : 'pkgReject'"
                  @click="adminObj.handleReject(row)"
                  style="margin: 0 5px"
                  >{{ $t('reject') }}</a-button
                >
              </div>
            </template>
            <template slot="action2admin" slot-scope="_, row">
              <div class="action-box common-action-style ta-center">
                <a-button
                  type="link"
                  size="small"
                  @click="actionObj.handlerPreview(row)"
                >
                  {{ $t('global_preview') }}
                </a-button>
                <!-- <a-button
                  v-if="row.activity_status === 1"
                  type="link"
                  size="small"
                  @click="adminObj.handleViewBasicInfo(row)"
                >
                  {{ $t('global_view') }}
                </a-button> -->
              </div>
            </template>
            <template slot="action" slot-scope="_, row">
              <div class="action-box common-action-style">
                <a-button
                  v-for="(action, i) in getFilterActions(row).filter(
                    (_, i) => i < 1
                  )"
                  :key="i"
                  type="link"
                  size="small"
                  :disabled="getFilterDisabled(row, action)"
                  v-bind="action.bind"
                  @click="action.on.click(row)"
                >
                  {{ action.text }}
                </a-button>
                <a-popover
                  v-if="getFilterActions(row).length > 1"
                  placement="topRight"
                  overlay-class-name="common-popover-style common-action-style"
                >
                  <template slot="content" class="common-more-box">
                    <div
                      class="common-more-box"
                      v-for="(action, i) in getFilterActions(row).filter(
                        (_, i) => i >= 1
                      )"
                      :key="i"
                    >
                      <a-button
                        type="link"
                        size="small"
                        :disabled="getFilterDisabled(row, action)"
                        v-bind="action.bind"
                        @click="action.on.click(row)"
                      >
                        {{ action.text }}
                      </a-button>
                    </div>
                  </template>
                  <a-button class="btn-more-style" type="link" size="small">{{
                    $t('btn.more')
                  }}</a-button>
                </a-popover>
              </div>
            </template>
          </tableAntd>
        </div>
      </div>
    </div>
    <template v-if="checkAuth('edit')">
      <createModal :modal-data="modalData" />
    </template>
    <stkModal :modal-data="stkData" />
    <a-modal
      v-bind="logData"
      v-model="logData.visible"
      class="change-log-modal"
    >
      <div slot="footer">
        <a-button type="primary" @click="logData.visible = false">{{
          $t('btn.okay')
        }}</a-button>
      </div>
      <div class="change-log-box">
        <a-table v-bind="logData.logTable" :loading="logData.loading">
          <template slot="timestamp_utc" slot-scope="val">
            <div>{{ val.split(' ')[0] }}</div>
            <div>{{ val.split(' ')[1] }}</div>
          </template>
        </a-table>
      </div>
    </a-modal>
  </div>
</template>

<script>
import tableAntd from '@activity/components/tableAntd'
import stkModal from '@activity/components/modalAntd/stkModal'
import createModal from './create/modal'
import dataLib from './dataLib'
import handleLib from './handleLib'
import { checkAuth } from '@/plugins/authDirective'
const { isMerchant, platform, actColumns, pkgColumns, logColumns } = dataLib
import { getParamsKey } from '@activity/utils'

export default {
  components: {
    tableAntd,
    stkModal,
    createModal
  },
  data() {
    return {
      isMerchant,
      adminObj: {
        handleViewBasicInfo: (row) => {
          this.actionObj.handlerView(row)
        },
        handleApprove: (row) => {
          handleLib.methods.handleApprove.call(this, row, {
            isAct: this.isAct,
            suc: () => {
              this.get_activities()
            }
          })
        },
        handleReject: (row) => {
          handleLib.methods.handleReject.call(this, row, {
            isAct: this.isAct,
            suc: () => {
              this.get_activities()
            }
          })
        }
      },
      antSelectOpen: false,
      actionObj: {
        preview_url: klook.getUrlByEnv(''),
        handlerArchive: (row) => {
          handleLib.methods.handlerArchive.call(this, row, {
            suc: () => {
              this.get_activities()
            }
          })
        },
        handlerUnarchive: (row) => {
          handleLib.methods.handlerUnarchive.call(this, row, {
            suc: () => {
              this.get_activities()
            }
          })
        },
        handlerPreview: (row) => {
          let url = `${this.actionObj.preview_url}/en-US/preview/activity/${row.activity_id}?lang=${row.language}`
          window.open(url)
        },
        handlerView: (row) => {
          let url = `${this.actionObj.preview_url}/en-US/activity/${row.activity_id}?lang=${row.language}`
          window.open(url)
        },
        handleSubmitActivity: async (row) => {
          let result = await handleLib.methods.get_activity_submit_status(
            row.activity_id
          )
          if (result) {
            if (result.status) {
              let activity_id = row.activity_id
              let obj = await ajax.get(
                {
                  url: ADMIN_API.act.get_activity_to_submit_packages,
                  params: { activity_id }
                },
                {
                  loading: true
                }
              )
              if (obj && (obj.submited_package || obj.to_be_submit_packages)) {
                let arr = []
                if (obj.submited_package) {
                  obj.submited_package.forEach((o) => (o.isFlag = true))
                  arr.push(...obj.submited_package)
                }
                if (obj.to_be_submit_packages) {
                  obj.to_be_submit_packages.length === 1 &&
                    (this.stkData.form.package_id_list = obj.to_be_submit_packages.map(
                      (o) => o.package_id
                    ))
                  arr.push(...obj.to_be_submit_packages)
                }
                this.stkData.keyFlag = 'activity'
                this.stkData.packageArr = arr
                this.stkData.visible = true
                this.stkData.row = row
              }
            } else {
              this.$modal.error({
                title: this.$t('merchant_submit_activity_action'),
                content: this.$t('activity_required_fields')
              })
            }
          }
        },
        handleSubmitPackage: async (row) => {
          let result = await handleLib.methods.get_package_submit_status(
            row.package_id
          )
          if (result) {
            if (result.status) {
              let arr = [{ ...row, checked: true }]
              this.stkData.keyFlag = 'package'
              this.stkData.packageArr = arr
              this.stkData.visible = true
              this.stkData.row = row
              this.stkData.activity_item = result.activity_item
            } else {
              this.$modal.error({
                title: this.$t('merchant_submit_package_action'),
                content: this.$t('package_required_fields')
              })
            }
          }
        },
        handleWithdrawSubmit: async (row, keyFlag) => {
          handleLib.methods.handleWithdrawSubmit.call(this, row, {
            keyFlag,
            suc: () => {
              this.get_activities()
            }
          })
        },
        handlerDelete: (row) => {
          this.$modal.confirm({
            maskClosable: true,
            title: '请确认是否删除吗？',
            cancelText: __('global_cancel'),
            okText: __('btn.okay'),
            onOk: () => {
              let url =
                this.tabObj.key === 'activity'
                  ? ADMIN_API.act.delete_activity
                  : ADMIN_API.act.delete_package
              let data =
                this.tabObj.key === 'activity'
                  ? { activity_id: +row.activity_id }
                  : {
                      package_id: +row.package_id
                    }
              ajax
                .post(
                  url,
                  {
                    data: data
                  },
                  {
                    msgOpt: {
                      isSucMsg: true
                    },
                    loading: true
                  }
                )
                .then(() => {
                  this.get_activities()
                })
            }
          })
        },
        // status: { 0:unpublished, 1: published, 2: draft, 3, em to edit, 4: in preview, }
        /*
         * 0: unpublished,
         * 1: published,
         * 2: draft
         * 3: em to edit
         * 4: in preview
         */
        hasbtn: {
          activity: [
            {
              $t: 'global_preview',
              type: 'preview',
              bind: {
                class: 'preview-wrap-style'
              },
              list: [0, 1, 4],
              on: {
                click: (row) => this.actionObj.handlerPreview(row)
              }
            },
            {
              platform: 'MERCHANT',
              $t: 'global_view',
              type: 'view', //跳转到c端
              list: [1],
              on: {
                click: (row) => this.actionObj.handlerView(row)
              }
            },
            {
              $t: 'global_delete',
              auth: 'delete',
              type: 'delete',
              list: (row) => {
                return (
                  row.activity_status === 0 &&
                  [0, 2].includes(row.approval_status)
                )
              },
              on: {
                click: (row) => this.actionObj.handlerDelete(row)
              },
              bind: {
                class: 'common-button-danger'
              }
            },
            {
              $t: 'merchant_submit_activity_action',
              auth: 'actSubmit',
              type: 'submitActivity',
              list: (row) => {
                let isSubmit =
                  row.activity_status !== 1 &&
                  row.package_count &&
                  [0, 2].includes(row.approval_status)
                // this.$set(row, 'disabled', !isSubmit)
                return isSubmit
              },
              on: {
                click: (row) => this.actionObj.handleSubmitActivity(row)
              }
            },
            {
              $t: 'btn.withdrawSubmit',
              auth: 'actWithdraw',
              type: 'withdrawSubmit',
              list: (row) => {
                return row.approval_status === 1
              },
              on: {
                click: (row) =>
                  this.actionObj.handleWithdrawSubmit(row, 'activity')
              }
            }
          ],
          package: [
            {
              $t: 'global_preview',
              type: 'preview',
              bind: {
                class: 'preview-wrap-style'
              },
              list: (row) => {
                return (
                  [0, 1, 4].includes(row.package_status) &&
                  !row.package_archived
                )
              },
              on: {
                click: (row) => this.actionObj.handlerPreview(row)
              }
            },
            {
              platform: 'MERCHANT',
              $t: 'global_view',
              type: 'view', // 跳转到c端
              list: [1],
              on: {
                click: (row) => this.actionObj.handlerView(row)
              }
            },
            {
              $t: 'global_delete',
              auth: 'delete',
              type: 'delete',
              list: (row) => {
                return (
                  (row.package_status === 0 &&
                    [0, 2].includes(row.approval_status)) ||
                  row.archive_status
                )
              },
              on: {
                click: (row) => this.actionObj.handlerDelete(row)
              },
              bind: {
                class: 'common-button-danger'
              }
            },
            {
              $t: 'package_list_archive',
              auth: 'pkgManage',
              type: 'archive',
              list: (row) => {
                return row.package_status === 0 && !row.package_archived
              },
              on: {
                click: (row) => this.actionObj.handlerArchive(row)
              }
            },
            {
              $t: 'package_list_unarchive',
              auth: 'pkgManage',
              type: 'unarchive',
              list: (row) => {
                return row.package_archived
              },
              on: {
                click: (row) => this.actionObj.handlerUnarchive(row)
              }
            },
            {
              $t: 'merchant_submit_package_action',
              auth: 'pkgSubmit',
              type: 'submitPackage',
              list: (row) => {
                let isSubmit =
                  row.package_status === 0 &&
                  [0, 2].includes(row.approval_status)
                // this.$set(row, 'disabled', !isSubmit)
                return isSubmit
              },
              on: {
                click: (row) => this.actionObj.handleSubmitPackage(row)
              }
            },
            {
              $t: 'btn.withdrawSubmit',
              auth: 'pkgWithdraw',
              type: 'withdrawSubmit',
              list: (row) => {
                return row.approval_status === 1
              },
              on: {
                click: (row) =>
                  this.actionObj.handleWithdrawSubmit(row, 'package')
              }
            }
          ]
        }
      },
      logData: {
        width: 750,
        maskClosable: true,
        title: __('activity_event_logs'),
        visible: false,
        loading: false,
        logTable: {
          bordered: true,
          pagination: false,
          dataSource: [],
          columns: logColumns,
          rowKey: (r, i) => i
        }
      },
      stkData: {
        activity_item: {},
        form: {
          note: '', // 原因,
          package_id_list: [
            // 活动和套餐必须都传，并且套餐属于活动,
          ]
        },
        row: {},
        keyFlag: '',
        visible: false,
        packageArr: [],
        on: {
          ok: async () => {
            let { stkData } = this
            // console.log(999, this.stkData.form, stkData.row)
            let actApproveStatus = stkData.activity_item.approval_status
            let isAct = stkData.keyFlag === 'activity'
            let isFlag = !isAct && [0, 2].includes(actApproveStatus) //特殊处理
            if (isFlag) {
              // 提交套餐判断活动是否可提交，可提交则调用提交活动接口，否则直接调用提交套餐接口
              stkData.form.package_id_list = [stkData.row.package_id]
            }
            let result =
              isAct || isFlag
                ? await handleLib.methods.submit_to_klook2activity({
                    activity_id: stkData.row.activity_id,
                    ...stkData.form
                  })
                : await handleLib.methods.submit_to_klook2package({
                    activity_id: stkData.row.activity_id,
                    note: stkData.form.note,
                    package_id: stkData.row.package_id
                  })
            stkData.visible = false
            result && this.get_activities()
            stkData.form.note = ''
            stkData.form.package_id_list = []
          }
        }
      },
      reqData: {
        params: {
          // platform2admin
          merchant_ids: '',
          // activity
          activity_status: undefined, // 不传时表示所有状态, 0:下架的, 1:发布的,
          approval_status: undefined, // ,
          leaf_category_ids: undefined, // ,
          city_ids: undefined, // ,
          keyword: '', // ,
          limit: 10, // ,
          page: 1, // ,
          // package
          activity_keyword: '',
          package_status: undefined,
          archive_status: undefined
        }
      },
      modalData: {
        visible: false
      },
      tableData: {
        show: true,
        loading: false,
        dataSource: [],
        columns: actColumns,
        rowKey: (r) => {
          return `${r.activity_id}${r.package_id || ''}`
        },
        rowClassName: (r, i) => {
          return r.package_archived ? 'common-row-archived' : ''
        }
      },
      pageData: {
        current: 1,
        pageSize: 10,
        total: 0
      },
      tabObj: {
        type: 'card',
        key: 'activity',
        tabs: [
          { key: 'activity', title: this.$t('global_activity') },
          { key: 'package', title: this.$t('global_package') }
        ],
        handleChange: (key, option) => {
          this.tableData.show = false
          this.$nextTick(() => {
            this.tableData.show = true
          })
          this.tabObj.key = key
          this.tableData.dataSource = []
          this.tableData.columns = this.tableColumns
          this.clickResetBtn(option)
        }
      },
      filterObj: {
        list: [
          {
            $t: 'merchant_id',
            platform: 'ADMIN',
            key: 'merchant_id',
            paramsKey: 'merchant_ids',
            options: []
          },
          {
            $t: 'package_id_title',
            tabKey: 'package',
            key: 'pkgKeyword',
            paramsKey: 'keyword',
            defVal: ''
          },
          {
            $t: 'activity_id_title',
            tabKey: 'package',
            key: 'actKeyword',
            paramsKey: 'activity_keyword',
            defVal: ''
          },
          {
            $t: 'activity_id_title',
            tabKey: 'activity',
            key: 'actKeyword',
            paramsKey: 'keyword',
            defVal: ''
          },
          {
            $t: 'global_city',
            key: 'city',
            paramsKey: 'city_ids',
            options: []
          },
          {
            $t: 'leaf_category',
            key: 'leafCategory',
            paramsKey: 'leaf_category_ids',
            treeData: []
          },
          {
            $t: 'package_list_archive',
            platform: 'MERCHANT',
            tabKey: 'package',
            key: 'archive',
            paramsKey: 'archive_status',
            options: klook.maps.package_archived
          },
          {
            $t: 'global_activity_status',
            tabKey: 'activity',
            key: 'activityStatus',
            paramsKey: 'activity_status',
            options: isMerchant
              ? klook.maps.activity_status
              : klook.maps.activity_status2admin
          },
          {
            $t: 'package_list_package_status',
            tabKey: 'package',
            key: 'packageStatus',
            paramsKey: 'package_status',
            options: klook.maps.package_status
          },
          {
            $t: 'merchant_approve_status',
            key: 'approvalStatus',
            paramsKey: 'approval_status',
            options: isMerchant
              ? klook.maps.approval_status
              : klook.maps.approval_status.filter((o) => o.value !== 0)
          }
        ]
      }
    }
  },
  computed: {
    isAct() {
      return this.tabObj.key === 'activity'
    },
    tableColumns() {
      return this.tabObj.key === 'activity' ? actColumns : pkgColumns
    },
    filterSelect() {
      return this.filterObj.list.filter((item) => {
        return (
          (!item.tabKey || item.tabKey === this.tabObj.key) &&
          (!item.platform || item.platform === platform)
        )
      })
    }
  },
  created() {
    this.initDefData()
    document.addEventListener('keypress', this.debounceKeypress)
    this.get_filter_option()
    this.get_activities()
  },
  methods: {
    initDefData() {
      let approveStatus = this.isMerchant ? undefined : [1]
      this.filterObj.list.forEach((item) => {
        if (item.paramsKey === 'approval_status') {
          this.$set(item, 'value', approveStatus)
          this.$set(item, 'defVal', approveStatus)
        }
      })
    },
    platformFn: klook.maps.platformFn,
    checkAuth: checkAuth,
    handleGoPackage(row) {
      this.tabObj.handleChange('package', {
        noQuery: true
      })
      this.filterObj.list.map((item) => {
        if (item.paramsKey === 'activity_keyword') {
          this.$set(item, 'value', '' + row.activity_id)
          this.clickSearchBtn()
        }
      })
    },
    async clickChangeLog(row) {
      if (row.package_archived) return
      let { logData, tabObj } = this
      logData.loading = true
      let url =
        tabObj.key === 'activity'
          ? ADMIN_API.act.get_change_log2act
          : ADMIN_API.act.get_change_log2pkg
      let params = {
        activity_id: row.activity_id, // in query, required, 查询的活动ID,
        package_id: row.package_id // in query, required, 查询的套餐ID,
      }
      logData.logTable.dataSource = []
      logData.visible = true
      let data = await ajax.get(url, {
        params
      })
      if (data && data.items) {
        logData.logTable.dataSource = data.items
      }
      logData.loading = false
    },
    debounceKeypress: _.debounce(function() {
      this.handleKeypress(...arguments)
    }, 300),
    handleKeypress(...args) {
      let e = args.pop()
      if (e.key === 'Enter') {
        this.get_activities()
      }
      return false
    },
    getFilterDisabled(row, action) {
      let typeArr = ['submitActivity', 'submitPackage']
      return !!(row.disabled && typeArr.includes(action.type))
    },
    getFilterActions(row) {
      let { actionObj, tabObj } = this
      let arr = []
      let status = `${tabObj.key}_status`
      let btnArr = actionObj.hasbtn[tabObj.key]
      btnArr.forEach((btnObj) => {
        if (
          typeof btnObj.list === 'function'
            ? btnObj.list(row)
            : btnObj.list.includes(row[status])
        ) {
          if (
            (!btnObj.auth || checkAuth(btnObj.auth)) &&
            (!btnObj.platform || btnObj.platform === platform)
          ) {
            arr.push({
              ...btnObj,
              text: this.$t(`${btnObj.$t}`)
            })
          }
        }
      })
      return arr
    },
    mapsFilterFn: klook.maps.filterFn,
    handlePageChange(page, pageSize) {
      this.updatePagination(page, pageSize)
      this.get_activities()
    },
    clickSubmitToKlook(row) {
      this.stkData.row = row
      this.stkData.visible = true
    },
    clickSearchBtn() {
      let { reqData, pageData } = this
      pageData.current = reqData.params.page = 1
      this.get_activities()
    },
    get_activities() {
      let { reqData } = this
      let params = getParamsKey(reqData.params, this.filterSelect)
      for (let k in reqData.params) {
        if (k === 'limit' || k === 'page') {
          continue
        }
        reqData.params[k] = params[k]
      }
      let { tableData } = this
      tableData.loading = true
      let url =
        this.tabObj.key === 'activity'
          ? ADMIN_API.act.filter_activity
          : ADMIN_API.act.filter_package
      ajax.post(url, { data: reqData.params }).then((data) => {
        this.pageData.total = data ? data.total : 0
        tableData.dataSource = data ? data.items : []
        tableData.loading = false
      })
    },
    // 筛选条件
    get_filter_option() {
      ajax.get(ADMIN_API.act.get_filter_option).then((data) => {
        if (!data) return
        let cityArr = this.filterObj.list.filter((item) => item.key === 'city')
        if (cityArr.length && data.cities) {
          cityArr[0].options = data.cities.map((item) => {
            let obj = {
              value: item.city_id,
              title: item.name
            }
            return _.merge({}, item, obj)
          })
        }
        let leafCategoryArr = this.filterObj.list.filter(
          (item) => item.key === 'leafCategory'
        )
        if (leafCategoryArr.length && data.category_tree) {
          leafCategoryArr[0].treeData = this.getCategoryTree(data.category_tree)
        }
        let merchants = this.filterObj.list.filter(
          (item) => item.key === 'merchant_id'
        )
        merchants[0].options =
          data.merchants.map((item) => {
            let obj = {
              value: item.merchant_id,
              title: `${item.merchant_id}-${item.merchant_name}`
            }
            return _.merge({}, item, obj)
          }) || []
      })
    },
    getCategoryTree(arr) {
      let newArr = arr.map((cate) => {
        cate.value = cate.category_id
        cate.label = cate.category_name
        cate.children = cate.sub_categories.map((sub) => {
          sub.value = sub.category_id
          sub.label = sub.category_name
          sub.children = sub.sub_categories.map((leaf) => {
            leaf.value = leaf.category_id
            leaf.label = leaf.category_name
            return leaf
          })
          return sub
        })
        return cate
      })
      return _.merge([], newArr)
    },
    clickCreateAct() {
      this.modalData.visible = !this.modalData.visible
    },
    // click reset btn
    clickResetBtn(option) {
      //注意option为event的情况处理
      this.filterObj.list.forEach((item) => {
        this.$set(item, 'value', item.defVal)
      })
      this.updatePagination(1, 10)
      if (!(option && option.noQuery)) {
        this.get_activities()
      }
    },
    updatePagination(page, limit) {
      let { reqData, pageData } = this
      pageData.current = reqData.params.page = page
      pageData.pageSize = reqData.params.limit = limit
    }
  }
}
</script>
