<template>
  <a-checkbox-group v-model="e_levy" class="package-levy-box">
    <a-checkbox :value="0b0001"> {{ $t('elevy_from_hk') }}</a-checkbox>
    <a-checkbox :value="0b0010"> {{ $t('elevy_stay_outside_hk') }}</a-checkbox>
    <a-checkbox :value="0b0100"> {{ $t('elevy_act_outside_hk') }}</a-checkbox>
    <a-checkbox :value="0b0000" :disabled="checkboxDisabled">
      {{ $t('elevy_none') }}</a-checkbox
    >
  </a-checkbox-group>
</template>

<script>
export default {
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: Number,
      required: true
    }
  },
  data() {
    return {}
  },
  computed: {
    checkboxDisabled() {
      return !!this.value && this.value !== 0b1000
    },
    e_levy: {
      get() {
        if (this.value === 0) {
          return [0]
        } else {
          return [0b0001, 0b0010, 0b0100].filter((e) => this.value & e)
        }
      },
      set(v) {
        this.$emit(
          'change',
          v.reduce((acc, cur) => acc | cur, 0b0000)
        )
      }
    }
  }
}
</script>

<style lang="scss">
.package-levy-box.ant-checkbox-group {
  padding: 8px 0 0 0;
  font-size: 0;
  label.ant-checkbox-wrapper {
    margin: 0 32px 16px 0;
    display: block;
  }
}
</style>
