<!-- transplant the original simple-mde here -->
<!-- have not consider extendsibility yet -->
<!--
  When u use and refresh in the </a-form-model-item>, u must configure the :key to </a-form-model-item>.
-->
<template>
  <markdown-editor
    class="common-md-style"
    ref="markdownEditor"
    v-model="child_value"
    :configs="mde_options"
    v-bind="$props"
  />
</template>

<script>
import './simplemde.scss'
import Vue from 'vue'
import VueSimplemde from 'vue-simplemde'

Vue.use(VueSimplemde)

import { common_defaults } from './config.js' // share same default

export default {
  name: 'SimpleMde',
  install(Vue) {
    // https://vuejs.org/v2/guide/plugins.html
    Vue.component(this.name, this)
  },
  model: {
    // https://vuejs.org/v2/guide/components.html#Form-Input-Components-using-Custom-Events
    prop: 'value', // this is actually bind to `current_items`
    event: 'change'
  },
  props: {
    value: {
      type: String
    },
    disabled: {
      type: Boolean,
      default: false
    },
    config: {
      type: Object,
      default: () => ({})
    },
    lang_text: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      child_value: '',
      mde_options: {
        ...common_defaults,
        // Init status bar DOM
        status: [
          {
            className: 'lang_text',
            defaultValue: (el) => {
              el.innerHTML = this.lang_text
            }
          }
        ],
        ...this.config
      }
    }
  },
  watch: {
    value: {
      immediate: true,
      handler(val) {
        this.child_value = val
      }
    },
    child_value(value) {
      this.$emit('change', value)
    },
    lang_text(newVal) {
      this.$children[0].simplemde.gui.statusbar.innerText = newVal
    },
    disabled: {
      immediate: true,
      handler(v) {
        this.$nextTick(() => {
          let codemirror = this.$refs.markdownEditor.simplemde.codemirror
          codemirror.doc.cantEdit = Boolean(v)
          codemirror.options.readOnly = Boolean(v)
          codemirror.display.wrapper.style.backgroundColor = v ? '#eee' : '#fff'
        })
      }
    }
  },
  mounted() {
    let codemirror = this.$refs.markdownEditor.simplemde.codemirror
    codemirror.on('blur', (event) => {
      this.$emit('blur', event)
      // Dispatch event to $parent for validate on form
      // FIXME
      // if (this.$store && this.$store.commit) {
      //   this.$store.commit('dispatch', {
      //     context: this,
      //     eventName: 'simple-mde.blur.dispatch', // Naming method reference Element.UI $emit event name
      //     params: {
      //       value: this.child_value
      //     }
      //   })
      // }
    })
  }
}
</script>

