  &.common-md-style {
    position: relative;
    .editor-toolbar {
    }
    .CodeMirror {
      min-height: 200px;
      line-height: 24px;
      .CodeMirror-scroll {
        min-height: 180px;
        max-height: 300px;
      }
    }
  }
  ul {
    padding-inline-start: 40px !important;
    li {
      list-style-type: disc;
    }
  }
}
</style>
