<template>
  <div class="compose">
    <!-- eslint-disable vue/html-self-closing -->
    <p v-html="showText"></p>
    <a-form-model
      class="attribute-form"
      v-if="show"
      ref="form"
      :model="form"
      label-position="right"
      layout="horizontal"
      labelAlign="left"
      :labelCol="{
        span: 7
      }"
      :wrapperCol="{
        span: 16,
        offset: 1
      }"
      @submit.native.prevent
    >
      <template v-if="chosen">
        <div
          v-for="variable in ui_var_map"
          :key="variable.key"
          style="margin-top: 6px;"
        >
          <component
            :is="cpnt_type[key_type[variable.type]]"
            v-model="currentData[variable.key]"
            :ref_data="ref_data[variable.key]"
            :display_html.sync="label_html[variable.key]"
            :form.sync="form[variable.key]"
            :chosen="chosen"
            :disabled="!chosen"
            :row="variable"
            :values="variable.values"
            :num_type="variable.type"
            :type="key_type[variable.type]"
            :var_map="variable"
            :config="option.config"
            v-bind="$attrs"
          ></component>
        </div>
      </template>
    </a-form-model>
  </div>
</template>

<script>
import {
  StructLink,
  StructInput,
  StructSelect,
  StructDatepicker,
  StructGlobalpicker
} from '../components/index'
import xssFilters from 'xss-filters'

let typeStruct = ADMIN_API.struct.dict([
  'string',
  ADMIN_API.struct.interface({
    attr_item_id: 'number',
    type: 'number?', // FIXME
    key: 'string',
    values: ADMIN_API.struct.union([
      'null|undefined',
      [
        ADMIN_API.struct.interface({
          create: 'boolean?', // create is mandatory , this is required when post
          id: 'number?', // omit when create,
          type: 'number?', // for backend to know if need translate, this is required when post
          value: 'string?'
        })
      ]
    ])
  })
])

const commonmark = require('commonmark')

export default {
  name: 'Compose',
  components: {
    StructLink,
    StructInput,
    StructSelect,
    StructDatepicker,
    StructGlobalpicker
  },
  model: {
    prop: 'data_var_map',
    event: 'change'
  },
  props: {
    ref_data: Object,
    ui_var_map: { type: Array },
    data_var_map: { type: Object },
    // 校验
    label: {
      type: String,
      default: ''
    },
    // disabled
    chosen: {
      type: Boolean,
      default: false
    },
    option: { type: Object }
  },
  inject: ['updateText'],
  data() {
    return {
      // this form is only used for validation
      form: {},
      label_html: {},
      show: true,
      key_type: {
        1: 'select',
        2: 'input',
        3: 'link',
        4: 'date',
        5: 'datetime',
        6: 'country',
        7: 'city'
      },
      cpnt_type: {
        input: 'StructInput',
        select: 'StructSelect',
        datetime: 'StructDatepicker',
        date: 'StructDatepicker',
        link: 'StructLink',
        country: 'StructGlobalpicker',
        city: 'StructGlobalpicker'
      },
      commonmark: null
      // currentData: this.data_var_map
    }
  },
  computed: {
    currentData: {
      get() {
        // console.log("get compose");
        return this.data_var_map
      }
    },
    // currentData: {
    //   get() {
    //     return this.data_var_map; //   },
    //   set(v) {
    //     // ADMIN_API.struct.dict([
    //     //   "string",
    //     //   {
    //     //     attr_item_id: "number",
    //     //     key: "string",
    //     //     values: [
    //     //       {
    //     //         create: "boolean",
    //     //         id: "number",
    //     //         value: "string",
    //     //         order: "number",
    //     //         is_free_text: "boolean?"
    //     //       }
    //     //     ]
    //     //   }
    //     // ]);
    //     this.$emit("change", v);
    //   }
    // },
    // showText
    showText() {
      let template = this.label
      _.forEach(this.ui_var_map, (v) => {
        let isSelect = [1, 4, 5, 6, 7].includes(+v.type)
        let className = isSelect ? 'variable-select' : 'variable-input'
        let value = this.label_html[v.key]

        let el = `<span class="variable ${className} ${
          value ? 'variable-complete' : 'variable-undone'
        }"> ${
          +v.type === 2 && value ? this.fmtMd(value, true) : value || v.key
        } </span>` // 2: input
        // if (this.key_type[v.type] === "link") {
        //   el = `<a class="variable is-link" href=${
        //     val.linkUrl
        //   } target='_blank'> ${val.linkName} </a>`;
        // }
        template = template.replace(v.key, el)
      })
      return template
    }
  },
  watch: {
    showText: {
      immediate: true,
      handler(next) {
        let obj = {
          label: this.label,
          data_var_map: {},
          _html: next
        }
        // for (let key in this.data_var_map) {\
        // (this.data_var_map[v.key].values || [])
        this.ui_var_map.forEach((v) => {
          obj.data_var_map[v.key] = {
            type: v.type,
            value: (_.get(this.data_var_map, `${v.key}.values`) || []).map(
              (v) => v.value
            )
          }
        })
        // }
        this.updateText(this.option, obj)
      }
    },
    // FIXME have to use watch? ?
    currentData: {
      deep: true,
      handler(v, oldv) {
        typeStruct.assert(_.cloneDeep(v))
        // console.log("set compose", v);
        // need to add type back to backend
        Object.entries(v).forEach(([k, obj]) => {
          let currentVar = this.ui_var_map.find((ui_var) => ui_var.key == k)
          obj.type = (currentVar && currentVar.type) || 1
        })
        // 这会生效吗
        if (!_.isEqual(v, oldv)) {
          this.$emit('change', v)
        }
      }
    },
    chosen() {
      this.$refs.form.resetFields()
    }
  },
  methods: {
    async validate() {
      let valid = await new Promise((resolve) => {
        this.$refs.form.validate((valid) =>
          valid ? resolve(true) : resolve(false)
        )
      })
      return valid
    },
    fmtMd(value, inline = false) {
      if (!this.commonmark) {
        this.commonmark = commonmark
      }
      let reader = new this.commonmark.Parser()
      let writer = new this.commonmark.HtmlRenderer()

      value = value.replace(/javascript:/g, encodeURIComponent('javascript:'))

      let parsed = reader.parse(xssFilters.inHTMLData(value)) // parsed is a 'Node' tree
      let html = writer.render(parsed)

      if (inline) {
        return html.replace(
          /(<p)(\s*.*>\s*.*<\/)(p>)/g,
          '<p style="display: inline;"' + '$2' + 'p>'
        )
      }
      return html
    }
    // key 去掉花括号
  }
}
</script>

<style lang="scss" scoped>
.compose ::v-deep {
  .ant-input + .ant-input,
  textareaant-input + textarea.ant-input,
  .ant-input + textarea.ant-input {
    margin-top: 10px;
  }
  .attribute-form {
    .ant-form-item-label label {
      display: inline-flex;
      white-space: break-spaces;
      line-height: 1em;
    }
  }
  p {
    .is-link {
      text-decoration: underline;
    }
  }
  .ant-form {
    margin-bottom: 4px;
    .ant-form-item {
      margin-bottom: 0;
      label.ant-form-item-label {
        padding-top: 18px;
        font-weight: 400;
        line-height: 1.2;
      }
    }
    .ant-select,
    textarea.a-input,
    .a-input {
      max-width: 400px;
      margin-top: 10px;
      display: block;
    }
  }
  .disabled .ant-form-explain {
    display: none;
  }
  .ant-form-item.is-required.disabled .ant-form-item-label:before {
    display: none;
  }

  .variable-complete {
    color: #16aa77;
    &.variable-input {
      background-color: rgba(198, 241, 221, 0.3);
    }
  }
}
</style>
