// 图片地址
const imageBaseUrl = 'https://res.klook.com/image/upload/'
// 文件夹
const folder = 'activities/'
// 图片地址前缀
const imageUrlPrefix = imageBaseUrl + folder
// 预览
const getPreviewUrl = (ratio) => {
  if (ratio) {
    return `c_fill,ar_${ratio},w_400/`
  } else {
    return 'c_fill,w_400/'
  }
}
// 比例
const desktopRatio = '1160:460'
const mobileRatio = '3:2'
// blur
const blur_pre_process = (name, ratio, s) =>
  `u_activities:${name},${s}_1.0,ar_${ratio},c_scale,e_blur:10000/`
// cloudinary 配置
const cloudinaryOptions = (activityId) => ({
  button_class: 'admin-cloudinary-upload-btn', // for test
  button_caption: 'Submit', // no effect so far
  cloud_name: 'klook',
  folder: 'activities',
  multiple: false,
  upload_preset: 'k8xu3bkg',
  sources: ['local'],
  tags: [activityId],
  max_file_size: 4000000, // 4M
  max_image_height: null, // need explicit set to remove cloudinary auto limit height to 1080
  max_image_width: 3000
})

export {
  imageBaseUrl,
  folder,
  getPreviewUrl,
  desktopRatio,
  mobileRatio,
  blur_pre_process,
  cloudinaryOptions,
  imageUrlPrefix
}
