<template>
  <!-- banner 图片上传 -->
  <a-form-model
    ref="form"
    :model="form"
    label-width="200px"
    class="photo-banner"
  >
    <a-form-model-item :rules="getRules()" prop="custom">
      <PhotoGroup
        :hideCopy="true"
        :needSetSize="true"
        ref="photo_group"
        :activity-id="activityId"
        :cur-lang="curLang"
        :ref-lang="refLang"
        :list="photo_list"
        :need-order="false"
        :need-desc="false"
        :need-alt="isEMpage"
        :need-display-as-banner="false"
        :allow-add="true"
        :allow-replace="false"
        :allow-remove="true"
        :list-all="list_all_format"
        :cur-lang-only.sync="cur_lang_only"
        :show-cur-lang-only="true"
        :limit="1"
        :banner-count="bannerCount"
        :trigger-banner-check="triggerBannerCheck"
        @copy-photo="copyPhoto"
        @validate-form="validate"
        @banner-count-change="$emit('banner-count-change')"
      />
      <p v-if="$attrs.description" class="text--italic">
        <!--{{ $attrs.description }}-->
      </p>
    </a-form-model-item>
  </a-form-model>
</template>
<script>
// banner
//    am: photo desc 隐藏 , photo alt 隐藏
//    em: photo desc 隐藏 , photo alt 必填
import PhotoGroup from '../photo-group'
import { savePhotos } from './api'
export default {
  name: 'PhotoBanner',
  components: { PhotoGroup },
  inject: ['activityId', 'curLang', 'refLang', 'isEMpage'],
  provide: function() {
    return {
      updateData: this.updateData
    }
  },
  props: {
    // 图片 list, 所有语言
    listAll: {
      type: Array,
      default: () => [],
      required: true
    },
    // 是否必填
    required: {
      type: Boolean,
      default: false
    },
    // banner总数
    bannerCount: {
      type: Number,
      default: 3
    },
    triggerBannerCheck: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      // 使图片仅作用于当前语言
      cur_lang_only: false,
      // 当前语言的图片 list
      photo_list: [],
      // 所有语言的图片列表, 用于弹窗
      list_all_format: [],
      form: {}
    }
  },
  watch: {
    cur_lang_only: {
      handler(next) {
        // !删除当前语言下的图片
        if (!next) {
          let params = {
            activity_id: +this.activityId,
            banner: [{ language_type: this.curLang, image: [] }],
            images: []
          }
          savePhotos(this.activityId, params)
        }
      }
    },
    listAll() {
      this.getCurLangPhoto()
    }
  },
  mounted() {
    // rainweb mock
    this.getCurLangPhoto()
  },
  methods: {
    // getCurLangPhoto
    async getCurLangPhoto() {
      // 转换所有语言 banner 的格式
      this.list_all_format = this.listAll.map((v) => {
        return {
          language_type: v.language_type,
          list:
            v.image && v.image.length ? [{ ...v.image[0], image: v.image }] : []
        }
      })
      //! 找到当前语言的图片列表 -------------
      //! 第一步, 找到当前语言的图片
      let banner_list =
        (
          this.list_all_format.find((v) => v.language_type === this.curLang) ||
          {}
        ).list || []
      //! is_default 为 0 标识应用于当前图片
      //! 第二步, 判断是否勾选了 "创建当前语言的图片"
      if (
        banner_list &&
        banner_list.length &&
        banner_list[0].is_default === 0
      ) {
        this.cur_lang_only = true
      } else {
        this.cur_lang_only = false
      }
      //! 如果是, 直接读取当前语言图片
      if (this.cur_lang_only) {
        this.photo_list = banner_list
        return
      }
      //! 如果不是, 先读取 All
      let all =
        (this.list_all_format.find((v) => v.language_type === 'ALL') || {})
          .list || []
      //! 如果 all 存在, 读取 All, 但是需要读取当前语言保存好的 desc && alt
      if (all.length) {
        all.forEach((v) => {
          //! 找到对应的图片, 读取描述 & alt
          let item = banner_list.find(
            (img) => _.get(img, 'image.0.name') === _.get(v, 'image.0.name')
          )
          if (item) {
            v.desc = item.desc || ''
            v.alt = item.alt || ''
          }
        })
        this.photo_list = all
        return
      }
      //! 最后, 如果 all 没有, 则读取 en_US
      if (!all.length) {
        banner_list =
          (this.list_all_format.find((v) => v.language_type === 'en_US') || {})
            .list || []
      }
      this.photo_list = banner_list
    },
    //! 从上传的图片中获取(复制图片)
    copyPhoto(images) {
      images[0].display_order = 1
      this.photo_list = [images[0]]
    },
    //! 获取数据
    getList() {
      return this.$refs.photo_group.getList()
    },
    // 校验
    validate() {
      return new Promise((resolve) => {
        this.$refs.form.validate(resolve)
      })
    },
    //! 转换为参数格式的数据
    getFormatParams() {
      let params = []
      let cur_list = _.cloneDeep(this.getList())
      // ! 是否保存到当前语言, 是的话 is_default: 0
      let is_default = +!this.cur_lang_only
      let image = cur_list.map((v) => {
        let base = {
          alt: v.alt,
          banner_display: +v.banner_display,
          desc: v.desc,
          display_order: v.display_order,
          img_id: 0,
          is_default
        }
        // delete v.ORIGIN.url;
        // delete v.DESKTOP.url;
        // delete v.MOBILE.url;
        return [
          { ...v.ORIGIN, ...base },
          { ...v.DESKTOP, ...base },
          { ...v.MOBILE, ...base }
        ]
      })[0]

      // 如果只作用于当前语言, 只传到当前语言
      params = [
        {
          language_type: this.curLang,
          image
        }
      ]
      if (!this.cur_lang_only) {
        // !是否保存到当前语言, 否时, 同时添加到 ALL
        // } else {
        // ! All 不需要保留 alt , desc
        params.push({
          language_type: 'ALL',
          image: _.cloneDeep(image || []).map((v) => {
            v.desc = ''
            v.alt = ''
            return v
          })
        })
        // }
      }
      return params
    },
    // 校验
    getRules() {
      // 非必填(指数量), 即可以没有活动图片, 但是图片的字段是必填的
      let validation = [
        {
          validator: (rule, value, callback) => {
            let res = this.$refs.photo_group.validateFields()
            res.valid ? callback() : callback(new Error(res.message))
          }
        }
      ]
      if (this.required) {
        validation.push({
          required: true,
          validator: (rule, value, callback) => {
            let res = this.$refs.photo_group.validateLength()
            res.valid ? callback() : callback(new Error(res.message))
          }
        })
      }

      return validation
    }
  }
}
</script>
<style lang="scss">
.photo-banner {
  .ant-form-item-control-wrapper {
  }
}
</style>
