<script>
// import tpl_special_multi_language_input from '../tpl_special_multi_language_input'
import tpl_package_levy from '../tpl_package_levy'
// import tpl_package_min_max_booking from '../tpl_package_min_max_booking'
// import { ImageUpload, LocalTimePicker } from '@activity/components/index'
import { getCurrentLocal, getLangTitle } from '@activity/utils'
import { mapGetters, mapMutations } from 'vuex'

import {
  FNB_TICKET_TYPE as fnb_ticket_types,
  fnb_package_types
} from '@activity/pages/package/package_const.js'

export default {
  components: {
    // tpl_special_multi_language_input,
    tpl_package_levy
    // tpl_package_min_max_booking,
    // ImageUpload,
    // LocalTimePicker
    // tpl_package_min_max_trip
  },
  model: {
    prop: 'value',
    model: 'change'
  },
  props: {
    activity_id: {
      type: Number,
      required: true
    },
    package_id: {
      type: [Number, String],
      required: true
    },
    value: {
      type: Object,
      required: true
    },
    schemaConfig: {
      type: Object,
      required: true
    },
    package_type: {
      type: [Number, String]
    },
    lockAutoPublish: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      fnb_ticket_types,
      fnb_package_types,

      cloudinary_options: {
        cloud_name: 'klook',
        multiple: false,
        upload_preset: 'k8xu3bkg',
        sources: ['local']
      },

      platforms: {
        0: this.$t('act_lan_account_both'),
        1: this.$t('act_lan_user_only'),
        2: this.$t('act_lan_agent_only')
      },

      isRequired: {
        required: true,
        message: 'Please fill this form'
      },

      new_package_id: this.package_id
    }
  },
  computed: {
    sensitive_info_list() {
      let reqData = this.$attrs.reqData
      let si = this.form.sensitive_info
      let arr = (reqData && _.merge([], reqData.sensitive_info_list)) || []
      let leng = arr.length - 1
      if (si.length === 0) {
        arr.forEach((item) => this.$set(item, 'disabled', false))
      } else if (si[0] === arr[leng].key) {
        arr.forEach((item, i) => this.$set(item, 'disabled', i !== leng))
      } else {
        this.$set(arr[leng], 'disabled', true)
      }
      return arr
    },
    fnbTypeChosen() {
      return Object.values(fnb_package_types).includes(+this.package_type)
    },
    form: {
      get() {
        return this.value
      },
      set(v) {
        this.$emit('change', v)
      }
    },
    package_info() {
      return this.$store.state.packages.find(
        (v) => v.package_id === +this.package_id
      )
    },
    isCreate: {
      cache: false,
      get: function() {
        return (
          this.$route.query.package_id === 'null' ||
          (window.klk_gvars.package_all &&
            window.klk_gvars.package_all.length === 0)
        )
      }
    },
    cur_pkg() {
      return this.package_id
        ? this.$store.state.packages.find(
            (v) => v.package_id == this.package_id
          )
        : {}
    },
    disabledPkgName() {
      return (
        this.$root.isSPU || (this.cur_pkg && this.cur_pkg.spec_status === 1)
      )
    }
  },
  watch: {
    'form.type': {
      immediate: false,
      handler(v) {
        if (this.isCreate) {
          if ([6, 7, 8].includes(v)) {
            this.form.ticket_type = fnb_ticket_types.fnb_open_ticket
          } else if ([9, 10, 11].includes(v)) {
            this.form.ticket_type = fnb_ticket_types.fnb_fixed_date
          }
        }
      }
    }
  },
  async beforeMount() {
    await new Promise((resolve) => this.$on('init_data', resolve))

    if (this.$root.isFNB) {
      this.form = {
        ...this.value,
        ticket_type: '',
        type: ''
      }
    }

    if (this.isCreate) {
      if (this.$root.isFNB) {
        let resp = await ADMIN_API.getActivityBindingArea({
          activity_id_list: String(this.activity_id)
        })
        const CITY_CODE_HK = 2 // tend to change
        let is_hk =
          _.get(resp.result[0], 'area_items.0.city.area_id') === CITY_CODE_HK
        const preset_fnb_data = {
          sales_channels: 1,
          e_levy: is_hk ? 0b0000 : 0b0100,
          transferable: 0
        }
        this.form = {
          ...this.form,
          ...preset_fnb_data
        }
      }
      return
    } else {
      if (this.$root.isFNB && this.cur_pkg) {
        // reset type for fnb BD to choose
        if (this.cur_pkg.type === 1) {
          this.cur_pkg.type = ''
        }
        if (this.cur_pkg.ticket_type === -1) {
          this.cur_pkg.ticket_type = ''
        }
      }
    }
    Object.assign(this.form, {
      ...this.value,
      ...this.cur_pkg
    })
  },
  methods: {
    ...mapGetters(['getPackages']),
    ...mapMutations(['setPackages']),
    getCurrentLocal: getCurrentLocal,
    getLangTitle: getLangTitle,
    checkSensitiveInfo: (rule, value, callback) => {
      if (!value.length) {
        callback(new Error('Please fill this form'))
      } else {
        callback()
      }
    },
    getRulesFactory(field) {
      return _.get(this.schemaConfig, `${field}.rules`, {})
    },
    displayFactory(field) {
      let validation = _.get(this.schemaConfig, `${field}.validation`, 3)
      return validation !== 3 || false
    },
    async validateForm() {
      let flag = true
      // if (this.displayFactory('pkg_name') && !this.$root.isSPU) {
      //   flag = (await this.$refs.pkg_name.validateForm()) && flag
      // }

      return new Promise((resolve) =>
        this.$refs.form.validate((valid) => resolve(valid && flag))
      )
    },
    getMultiAttrFactory(type, conf = {}) {
      let current = this.schemaConfig[type] || {}
      return {
        required: current.required,
        requiredEn: current.requiredEn,
        valField: type,
        label: this.getCurrentLocal(this.schemaConfig, {
          type,
          field: 'name_multilang',
          defaultVal: this.$t('package_info_name')
        }),
        placeholder: this.$t('package_info_name_placeholder'),
        description: this.getCurrentLocal(this.schemaConfig, {
          type,
          field: 'desc_multilang',
          defaultVal: ''
        }),
        ...conf
      }
    },
    getCurrentDescription(type) {
      return this.getMultiAttrFactory(type).description
    }
  }
}
</script>

