<template>
  <div class="photo-edit">
    <div class="check-list">
      <!-- 展示为 banner -->
      <div
        v-if="needDisplayAsBanner"
        style="display: inline-block; margin-right: 20px"
      >
        <a-checkbox
          v-model="currentConfig.banner_display"
          @change="$emit('banner-count-change')"
        >
          {{ $t('display_on_banner') }}
        </a-checkbox>
      </div>
      <div
        v-if="
          (needSetNoCropBanner && currentConfig.banner_display) ||
            $attrs.needSetSize
        "
        style="display: inline-block"
      >
        <!-- 不裁剪设置 banner -->
        <a-checkbox
          v-model="currentConfig.is_no_crop_banner"
          @change="isNoCropBannerChange"
        >
          {{ $t('no_size_limit') }}
        </a-checkbox>
      </div>
    </div>

    <a-row v-if="isSingle">
      <a-col>
        <Crop
          v-model="currentConfig.ORIGIN.url"
          class="photo-edit-image"
          style="width: 310px; height: 160px"
          :activity-id="+activityId"
          :pre-process.sync="currentConfig.ORIGIN.pre_process"
          :image-name.sync="currentConfig.ORIGIN.name"
          need-upload
          :need-crop="false"
          @uploaded="handleUploaded"
          @change="onChange"
        />
      </a-col>
    </a-row>
    <a-row v-else :gutter="5">
      <a-col class="base" :span="8">
        <div>base</div>
        <!-- base 裁剪 + 上传 -->
        <Crop
          v-model="currentConfig.ORIGIN.url"
          class="photo-edit-image"
          :activity-id="+activityId"
          :pre-process.sync="currentConfig.ORIGIN.pre_process"
          :image-name.sync="currentConfig.ORIGIN.name"
          need-upload
          @uploaded="handleUploaded"
          @change="onChange"
        />
      </a-col>
      <a-col class="desktop" :span="8">
        <div>desktop</div>
        <!-- desktop 裁剪 -->
        <Crop
          v-model="currentConfig.DESKTOP.url"
          class="photo-edit-image"
          :ratio="desktopRatio"
          :pre-process.sync="currentConfig.DESKTOP.pre_process"
          :image-name.sync="currentConfig.DESKTOP.name"
          @set-no-crop-banner="setNoCropBanner('DESKTOP')"
        />
      </a-col>
      <a-col class="mobile" :span="8">
        <div>mobile</div>
        <!-- mobile 裁剪 -->
        <Crop
          v-model="currentConfig.MOBILE.url"
          class="photo-edit-image"
          :ratio="mobileRatio"
          :pre-process.sync="currentConfig.MOBILE.pre_process"
          :image-name.sync="currentConfig.MOBILE.name"
          @set-no-crop-banner="setNoCropBanner('MOBILE')"
        />
      </a-col>
    </a-row>

    <!-- 图片信息编辑 -->
    <section style="margin-top: 20px">
      <div v-if="needCustomSize">
        <a-radio-group v-model="currentConfig.customSize">
          <a-radio value="custom-size">
            Cut photo to {{ `${sizeConf.width} x ${sizeConf.height}` }}
          </a-radio>
          <a-radio value="origin-size"> No photo size limit </a-radio>
        </a-radio-group>
      </div>
      <!-- 下载原图, download 属性不生效, 因为不是相对地址 -->
      <!--<div v-if="allowDownloadImage">-->
      <!--<a-->
      <!--class="el-icon-download"-->
      <!--style="cursor: pointer;"-->
      <!--@click="downloadOriginalImage"-->
      <!--&gt;-->
      <!--{{ $t('act_photo_download_original_image') }}-->
      <!--</a>-->
      <!--</div>-->
      <!--<div v-if="needSetNoCropBanner">-->
      <!--&lt;!&ndash; 不裁剪设置 banner &ndash;&gt;-->
      <!--<a-checkbox-->
      <!--v-model="currentConfig.is_no_crop_banner"-->
      <!--@change="isNoCropBannerChange"-->
      <!--&gt;-->
      <!--{{ $t('no_size_limit') }}-->
      <!--</a-checkbox>-->
      <!--</div>-->

      <!-- 图片顺序 -->
      <div v-if="needOrder" class="order-num">
        <a-select
          size="small"
          v-model="currentConfig.display_order"
          placeholder="Please select order"
        >
          <a-select-option
            v-for="num in orderOptions"
            :key="num"
            :label="num"
            :value="num"
            >{{ num }}</a-select-option
          >
        </a-select>
      </div>
      <!-- 图片 alt -->
      <div v-if="needAlt" class="fields">
        <a-input
          v-model="currentConfig.alt"
          :placeholder="$t('act_photo_alt_hint')"
        />
        <!-- 参考语言 -->
        <br />
        <a-input
          v-if="refConfig && refConfig.alt"
          v-model="refConfig.alt"
          disabled
          :placeholder="$t('act_photo_alt_hint')"
        />
      </div>
      <!-- 图片 desc -->
      <div v-if="needDesc" class="fields">
        <a-textarea
          class="text-hint"
          v-model="currentConfig.desc"
          :placeholder="$t('act_photo_desc_hint')"
        />
        <br />
        <!-- 参考语言 -->
        <a-textarea
          class="text-hint"
          v-if="refConfig && refConfig.desc"
          v-model="refConfig.desc"
          disabled
          :placeholder="$t('act_photo_desc_hint')"
        />
      </div>
      <!-- 删除按钮 -->
      <div v-if="allowRemove" class="remove-buttons">
        <i class="delete-btn" @click="removeOne">
          <svg-icon
            icon-name="trash"
            style="width: 10px; height: 10px; color: #fff"
          />
        </i>
      </div>
    </section>
  </div>
</template>
<script>
import {
  imageBaseUrl,
  folder,
  getPreviewUrl,
  desktopRatio,
  mobileRatio,
  blur_pre_process,
  imageUrlPrefix
} from '../const'
import Crop from './crop'

export default {
  name: 'PhotoEdit',
  components: { Crop },
  inject: ['updateData'],
  props: {
    //! 配置 => 包含基础信息, 以及 ORIGIN, DESKTOP, MOBILE 三种尺寸图片
    config: {
      type: Object,
      default: () => ({
        is_default: false,
        banner_display: false,
        alt: '',
        desc: '',
        display_order: 1,
        is_no_crop_banner: false,
        ORIGIN: {},
        DESKTOP: {},
        MOBILE: {}
      })
    },
    // 参考配置
    refConfig: {
      type: Object,
      default: () => ({})
    },
    //! 类型
    type: {
      type: String,
      default: 'act'
    },
    //! 图片列表
    total: {
      type: Number,
      default: 0
    },
    //! activityId
    activityId: {
      type: [Number, String],
      default: ''
    },
    //! 展示删除按钮
    allowRemove: {
      type: Boolean,
      default: true
    },
    // 自定义size
    needCustomSize: {
      type: Boolean,
      default: false
    },
    // 不裁剪设置原图
    needSetNoCropBanner: {
      type: Boolean,
      default: true
    },
    // 展示 "展示为banner" checkbox
    needDisplayAsBanner: {
      type: Boolean,
      default: true
    },
    // 需要图片描述
    needDesc: {
      type: Boolean,
      default: true
    },
    // 需要图片alt
    needAlt: {
      type: Boolean,
      default: true
    },
    // 需要图片排序
    needOrder: {
      type: Boolean,
      default: true
    },
    // 当前语言
    curLang: {
      type: String,
      default: ''
    },
    // 参考语种, 不传即不需要参考语言
    refLang: {
      type: String,
      default: ''
    },
    // 尺寸配置
    sizeConf: {
      type: Object,
      default: () => {}
    },
    // 只展示单张 , 不展示 desktop mobile - 活动指引图片
    isSingle: {
      type: Boolean,
      default: false
    },
    // 允许下载原图
    allowDownloadImage: {
      type: Boolean,
      default: true
    },
    // desktop时候的比例
    desktopRatio: {
      type: String,
      default: desktopRatio
    }
  },
  data() {
    return {
      // mobile 图片比例
      mobileRatio,
      // 宽高
      imageData: {},
      //! 排序下拉框的值
      orderOptions: []
    }
  },
  computed: {
    //! 当前的图片配置
    currentConfig: {
      get() {
        return this.config
      },
      set() {}
    }
  },
  watch: {
    'currentConfig.display_order': {
      handler() {
        this.$emit('changeOrder')
      }
    },
    //! 当 url, preprocess 改变时
    currentConfig: {
      immediate: true,
      deep: true,
      handler(next, old) {
        //! 生成图片信息
        // if (!_.isEqual(next, old)) {
        this.generateImageScheme(next)
        this.$emit('update:config', next)
        //更新最外层 v-model
        this.updateData && this.updateData()
        // }
      }
    },
    //! 排序下拉框的值, 数量变化时, 重新计算order下拉 dropdown ([1,2,3,4,5...]), 从1开始
    total: {
      immediate: true,
      handler(next) {
        let arr = new Array(next + 1).fill('')
        let options = Object.keys(arr).map(Number)
        // 去掉 0, 从 1 开始
        options.shift()
        // return options;
        this.orderOptions = options
      }
    }
  },
  methods: {
    // 处理
    async generateImageScheme(config) {
      // base 没有比例 ------
      config.ORIGIN.url = this.getUrl(config.ORIGIN)
      // destop ------
      config.DESKTOP.url = this.getUrl(config.DESKTOP, this.desktopRatio)
      // mobile ------
      config.MOBILE.url = this.getUrl(config.MOBILE, mobileRatio)
      // 是否不裁剪设置banner
      if (this.isNoCropBanner()) {
        config.is_no_crop_banner = true
      }
      // 获取图片长宽
      let { width, height } = await this.getImageData()
      // 记录图片宽高
      this.imageData = { width, height }
      config.origin_size = { width, height }
    },
    // 生成 url
    getUrl(target, ratio) {
      return (
        imageBaseUrl +
        target.pre_process +
        getPreviewUrl(ratio) +
        folder +
        target.name
      )
    },
    // 是否不裁剪设置banner, 通过 DESKTOP.pre_process 是否包含 u_activities
    isNoCropBanner() {
      let pre_process = this.currentConfig.DESKTOP.pre_process
      return pre_process && pre_process.includes('u_activities')
    },
    // 图片重新上传以后
    onChange(url) {
      let conf = this.currentConfig
      let image_name = url.split('/').pop()
      conf.name = image_name
      conf.url = image_name
      conf.ORIGIN.name = image_name
      conf.ORIGIN.pre_process = ''
      conf.DESKTOP.name = image_name
      conf.DESKTOP.pre_process = ''
      conf.MOBILE.name = image_name
      conf.MOBILE.pre_process = ''
      conf.is_no_crop_banner = false
      // 更新尺寸
      conf.ORIGIN.width = conf.DESKTOP.width = conf.MOBILE.width =
        conf.origin_size?.width
      conf.ORIGIN.height = conf.DESKTOP.height = conf.MOBILE.height =
        conf.origin_size?.height
    },
    // 记录尺寸
    handleUploaded(info) {
      let { width, height } = info
      this.currentConfig.origin_size = { width, height }
    },
    //! 删除, 抛出事件, 上级处理
    removeOne() {
      this.$emit('remove')
      this.$emit('banner-count-change')
    },
    // 改变 "不裁剪设置banner"
    async isNoCropBannerChange() {
      let conf = this.currentConfig
      // 图片名
      let name = conf.DESKTOP.name.split('.')[0]
      // 原图比例
      let ratio = this.imageData.width / this.imageData.height
      let desktop_arr = this.desktopRatio.split(':')
      let mobile_arr = mobileRatio.split(':')
      // destop 比例
      let desktop_ratio = desktop_arr[0] / desktop_arr[1]
      // mobile 比例
      let mobile_ratio = mobile_arr[0] / mobile_arr[1]
      // 是否选中, '不裁剪设置banner'
      if (conf.is_no_crop_banner) {
        // 判断比例
        if (ratio > desktop_ratio) {
          conf.DESKTOP.pre_process = blur_pre_process(
            name,
            this.desktopRatio,
            'w'
          )
        } else if (ratio < desktop_ratio) {
          conf.DESKTOP.pre_process = blur_pre_process(
            name,
            this.desktopRatio,
            'h'
          )
        } else {
          conf.DESKTOP.pre_process = ''
        }
        // 判断比例
        if (ratio > mobile_ratio) {
          conf.MOBILE.pre_process = blur_pre_process(name, mobileRatio, 'w')
        } else if (ratio < mobile_ratio) {
          conf.MOBILE.pre_process = blur_pre_process(name, mobileRatio, 'h')
        } else {
          conf.MOBILE.pre_process = ''
        }
      } else {
        conf.DESKTOP.pre_process = ''
        conf.MOBILE.pre_process = ''
      }
    },
    // 获取图片宽高
    async getImageData() {
      return new Promise((resolve) => {
        let image = new Image()
        image.onload = (e) => {
          resolve(e.target)
        }
        // image.src = this.currentConfig.ORIGIN.url;
        image.src = imageUrlPrefix + this.currentConfig.ORIGIN.name
      })
    },
    //! "不裁剪设置banner" 选项change方法
    setNoCropBanner(from) {
      if (this.currentConfig.is_no_crop_banner === false) {
        return
      }
      this.currentConfig.is_no_crop_banner = false
      if (from === 'DESKTOP') {
        this.currentConfig.MOBILE.pre_process = ''
      } else {
        this.currentConfig.DESKTOP.pre_process = ''
      }
    },
    // 下载原图
    downloadOriginalImage() {
      // :href="imageUrlPrefix + currentConfig.name"
      // :download="currentConfig.name"
      let x = new XMLHttpRequest()
      x.open('GET', `${imageUrlPrefix + this.currentConfig.name}`, true)
      x.responseType = 'blob'
      x.onload = function(e) {
        var url = window.URL.createObjectURL(x.response)
        var a = document.createElement('a')
        a.href = url
        a.download = ''
        a.click()
      }
      x.send()
    }
  }
}
</script>
