<template>
  <div id="package-basic-info-editable" class="basic-info">
    <div class="basic-info-box common-basic-form-style">
      <div v-if="!initLoadFinish" class="common-spin-box"><a-spin /></div>
      <div v-show="initLoadFinish">
        <div class="common-copy-wrap">
          <tpl_package_copy
            url="/prosrv/packages/copy"
            :klk_gvars="klk_gvars"
            :package_id="package_id"
            :handle-save="handleSaveCopy"
          >
            {{ $t('act_pkg_copy_info') }}
          </tpl_package_copy>
        </div>
        <div class="common-basic-box">
          <h2 :id="basicInfo.title">{{ basicInfo.title }}</h2>
          <common-field
            ref="common_fields1"
            v-model="form"
            v-bind="$props"
            :pkg="package_info"
            :schema-config="
              getSchemaConfig2keys(
                !$root.isMerchant ? ['pkg_name', 'pkg_subname'] : ['pkg_name'],
                schemaConfig
              )
            "
            @mounted="$emit('common_fields_init')"
          ></common-field>
          <tpl_package_merchant
            ref="package_marchant"
            v-model="form"
            v-bind="$props"
            :schema-config="schemaConfig"
            @merchant_init="merchant_init"
          ></tpl_package_merchant>
        </div>
        <div class="common-basic-box">
          <h2 :id="policiesRules.title">{{ policiesRules.title }}</h2>
          <common-field
            ref="common_fields2"
            v-model="form"
            v-bind="$props"
            :pkg="package_info"
            :schema-config="
              getSchemaConfig2keys(
                [
                  'ticket_type',
                  'confirmation_time',
                  'inventory_type',
                  'voucher_get_method',
                  'voucher_type',
                  'cancellation_policy',
                  'voucher_usage',
                  'voucher_code_level'
                ],
                schemaConfig
              )
            "
            @mounted="$emit('common_fields_init')"
          ></common-field>
        </div>
        <div class="common-basic-box">
          <h2 :id="specialSetup.title">{{ specialSetup.title }}</h2>
          <common-field
            ref="common_fields3"
            v-model="form"
            v-bind="$props"
            :pkg="package_info"
            :schema-config="
              getSchemaConfig2keys(
                ['min_max_bookings', 'calendar_month'],
                schemaConfig
              )
            "
            @mounted="$emit('common_fields_init')"
          ></common-field>
        </div>
        <div class="common-basic-box">
          <h2 :id="checkItems.title">{{ checkItems.title }}</h2>
          <tpl_package_info
            ref="package_info"
            v-model="form"
            v-bind="$props"
            :lock-auto-publish="lockAutoPublish"
            :schema-config="schemaConfig"
            :reqData="reqData"
            :activity_id="activity_id"
          ></tpl_package_info>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import tpl_package_merchant from '@activity/components/tpl_package_merchant'
import CommonField from '@activity/components/common_field'
import tpl_package_copy from '@activity/components/tpl_package_copy'
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
import { validatorDefault, getEditLang, scrollElError } from '@activity/utils'
import tpl_package_info from '@activity/components/tpl_package_info'
import { FNB_TICKET_TYPE } from '../package_const.js'
import moment from 'moment'

export default {
  inject: [
    'getActGlobal2provide',
    'refreshPkgs',
    'refreshPage',
    'reloadPage2provide',
    'rootChangeLoading'
  ],
  components: {
    tpl_package_info,
    tpl_package_copy,
    CommonField,
    tpl_package_merchant
  },
  props: {
    activity_id: {
      type: [Number, String],
      required: true
    },
    package_id: {
      type: [Number, String],
      required: true
    },
    package_type: {
      type: Number
    }
  },
  data() {
    return {
      initLoadFinish: false,
      reqData: null,
      orderArr: ['basicInfo', 'policiesRules', 'specialSetup', 'checkItems'],
      basicInfo: {
        title: this.$t('activity_basic_page_basic_info'),
        // require: true,
        style: 'finish',
        countObj: (() => {
          let adminObj = this.$root.isMerchant
            ? {}
            : {
                pkg_subname: 'package_details.0.subname'
              }
          let obj = {
            pkg_name: 'package_details.0.name',
            ...adminObj,
            pkg_merchant: 'merchant_id',
            merchant_confirm: 'merchant_self_confirm',
            pkg_contact: 'merchant_contact.0.number'
          }
          return obj
        })()
      },
      policiesRules: {
        title: this.$t('pkg.policiesRules'),
        // require: true,
        style: 'finish',
        countObj: {
          ticket_type: 'ticket_type',
          confirmation_time: 'confirmation_type',
          voucher_get_method: 'voucher_get_method',
          voucher_type: 'merchant_confirm_type',
          cancellation_policy: 'cancelation_type',
          voucher_usage: 'voucher_usage',
          voucher_code_level: 'voucher_level'
        }
      },
      specialSetup: {
        title: this.$t('pkg.specialSettings'),
        // require: true,
        style: 'finish',
        countObj: {
          min_max_bookings: 'min_participants',
          calendar_month: 'calendar_month'
        }
      },
      checkItems: {
        title: this.$t('pkg.checkItems'),
        require: true,
        style: 'finish',
        countObj: {
          show_account_type: 'sales_channels',
          transferable: 'transferable',
          escrow_guarantee: 'escrow_guarantee',
          does_it_include: {
            mapValue: 'e_levy',
            callback: (val) => {
              return val === 8 ? undefined : val
            }
          },
          sensitive_info: 'sensitive_info.0'
        }
      },
      actGlobal: null,
      // activeNames: ['package_info', 'package_type', 'merchant'],
      // klk_gvars: window.klk_gvars,

      cacheForm: null, // form的快照
      form: {
        calendar_month: undefined,
        sensitive_info: [],
        reconfirmation: false, // ？？？旧逻辑是隐藏DOM直接赋默认值

        package_details: [],
        price_status: 0,
        location_info: '',

        min_participants: 1,
        max_participants: 9999,

        min_trip: 1,
        max_trip: 255,

        auto_published: 0,
        auto_publish_time: {
          published: 0,
          unpublished: 0,
          published_time: '',
          unpublished_time: ''
        },

        sales_channels: 0,
        transferable: 0,
        e_levy: 0b1000,
        escrow_guarantee: false,
        //
        merchant_id: '',
        merchant_contact: [],
        merchant_self_confirm: undefined, // 不可修改
        //
        merchant_confirm_type: undefined,
        confirmation_type: undefined,
        stock_out_type: 'KLOOKCODE',
        cancelation_type: undefined,
        voucher_usage: undefined,
        voucher_level: undefined,
        voucher_get_method: 0,
        // fnb
        ticket_type: undefined,
        type: ''
      },

      isSPU: this.$root.isSPU,

      schemaConfig: {},
      struct_status: 0,

      new_package_id: this.package_id,

      lockAutoPublish: false,
      serverTime: '',
      startTimestamp: 0,
      MAX_AUTO_PUBLISH_TIME: 1000 * 60 * 30
    }
  },
  watch: {
    form: {
      handler() {
        this.$nextTick(function() {
          this.initFloorTimelineData()
          if (!this.cacheForm) {
            this.cacheForm = _.cloneDeep(this.form)
            console.log('cacheForm', this.cacheForm)
          }
        })
      },
      deep: true
    },
    '$route.query': {
      handler(q) {
        this.initPkgId(+q.package_id)
      }
    }
  },
  computed: {
    ...mapState({
      pkgInfos: 'pkgInfos',
      packages: 'packages'
    }),
    klk_gvars() {
      return {
        package_all: (this.pkgInfos && this.pkgInfos.package_info) || []
      }
    },
    serverTimestamp() {
      return this.getUTCTimestamp(this.serverTime)
    },
    isCreate: {
      cache: false,
      get: function() {
        return (
          !this.$route.query.package_id ||
          (window.klk_gvars.package_all &&
            window.klk_gvars.package_all.length === 0)
        )
      }
    },
    package_info() {
      let form = _.cloneDeep(this.form)
      return {
        ...form,
        ...this.currentPkgData
      }
    },
    language() {
      return this.$store.state.edit_lang.map((v) => v.language_type)
    },
    hasCreateFields() {
      return !!this.merchant_id
    }
  },
  async created() {
    // this.$message.error('Please fill in the form')
    window.s = this
    await Promise.all([
      new Promise((resolve) => this.$on('common_fields_init', resolve))
    ])
    await this.initPkgId(this.package_id)
    klook.bus.$emit('addHandleSave2bus', this.handleSave)
    this.initFloorTimelineData()
    let actGlobal = this.getActGlobal2provide()
    this.cateInfo = await this.getActCategory2action({
      activity_id: actGlobal.activity_id
    })
    this.merchant_id = this.package_info.merchant_id
    if (this.$root.isHotel) {
      this.package_info.ticket_type = FNB_TICKET_TYPE.fnb_open_ticket
    }
    // 先获得OB的配置和默认值，再 package info 覆盖
    await this.initObConfig()
    if (this.$root.isFNB && this.hasCreatePrice) {
      //
    } else {
      Object.assign(this.form, _.cloneDeep(this.package_info))
    }

    if (!this.form.merchant_contact || !this.form.merchant_contact.length) {
      this.form.merchant_contact = [
        {
          media: 'phone',
          number: ''
        }
      ]
    }
    this.$refs.common_fields1 && this.$refs.common_fields1.$emit('init_data')
    this.$refs.common_fields2 && this.$refs.common_fields2.$emit('init_data')
    this.$refs.common_fields3 && this.$refs.common_fields3.$emit('init_data')
    this.$refs.package_info && this.$refs.package_info.$emit('init_data')
    if (this.form.published) {
      this.lockAutoPublish = true
    } else {
      this.serverTime = this.getServerTime()
      this.startTimestamp = this.getCurrentTimestamp()
      let { published, published_time } = this.form.auto_publish_time
      if (published) {
        let diff =
          this.getUTCTimestamp(published_time) - this.getCurrentTimestamp()
        this.lockAutoPublish = diff > 0 && diff < this.MAX_AUTO_PUBLISH_TIME
      }
    }
    this.$nextTick(() => {
      this.initLoadFinish = true
    })
    this.$root.setMutationObserver('#package-basic-info-editable', this)
  },
  methods: {
    ...mapGetters(['getPackages', 'getServerTime']),
    ...mapActions([
      'getActCategory2action',
      'getPkgInfo2actions',
      'updatePkgStepStatus2action'
    ]),
    ...mapMutations(['setPackages']),
    validatorDefault: validatorDefault,
    merchant_init(obj) {
      if (typeof this.form.merchant_self_confirm === 'undefined') {
        this.form.merchant_self_confirm = obj.merchant_self_confirm //设置接口默认值
      }
    },
    initFloorTimelineData: _.debounce(function() {
      let { orderArr } = this
      let obj = {
        floorData: {
          list: orderArr.map((key) => {
            let obj = _.merge({}, this[key], this.getCountObj(this[key], key))
            return obj
          })
        }
      }
      klook.bus.$emit('floorTimeline2bus', obj)
      return obj
    }, 300),
    getCountObj(data, key) {
      let { form, schemaConfig } = this
      let { countObj = {} } = data
      let countKeys = Object.entries(countObj)
      let num = 0,
        count = countKeys.length
      let requireNum = 0,
        requireCount = 0
      countKeys.forEach((arr) => {
        let basic_attr_config = schemaConfig[arr[0]] || {}
        let val = arr[1]
        if (typeof val === 'string') {
          val = _.get(form, val)
        } else {
          val = val.callback(_.get(form, val.mapValue))
        }
        let isAdd = val || typeof val === 'number' || typeof val === 'boolean'
        isAdd && num++
        if (basic_attr_config.validation === 1) {
          requireCount++
          isAdd && requireNum++
        }
        // key == 'policiesRules' &&
        //   console.log(arr, val, basic_attr_config.validation === 1)
      })
      //特殊处理 start
      if (key == 'policiesRules') {
        if (form['voucher_get_method'] !== 0) {
          count -= 3
          requireCount -= 3
          if (
            form['confirmation_type'] !== 0 &&
            form['merchant_confirm_type']
          ) {
            num -= 1
            requireNum -= 1
          }
        }
      }
      //特殊处理 end
      let obj = {
        num,
        count,
        requireNum,
        requireCount,
        require: !!requireCount
      }
      return obj
    },
    async initPkgId(package_id) {
      if (package_id) {
        let reqData = await ajax.get(ADMIN_API.act.get_packages_basic_info, {
          params: {
            package_id,
            custom_language: klook.getEditLang(),
            page_from: this.$route.query.review ? 'bd_audit' : undefined
          }
        })
        if (reqData && reqData.package_id) {
          document.title = `${reqData.package_id}-${reqData.package_details[0].name}`
          let cpForm = _.merge({}, reqData, {
            sensitive_info: reqData.sensitive_info.split(',')
          })
          this.currentPkgData = cpForm || {}
          _.merge(this.form, cpForm)
        }
      }
    },
    getSchemaConfig2keys(arr, schemaConfig) {
      let obj = {}
      arr.forEach((key) => {
        obj[key] = schemaConfig[key]
      })
      return obj
    },
    getCurrentTimestamp() {
      return this.getUTCTimestamp(moment().format('YYYY-MM-DD HH:mm:ss'))
    },
    getUTCTimestamp(date) {
      return +moment.utc(date).valueOf() || 0
    },
    async publishValidator() {
      if (this.lockAutoPublish) {
        // If start locking, it cannot update, pass
        return true
      }
      let currentServerTimestamp =
        this.getCurrentTimestamp() - this.startTimestamp + this.serverTimestamp
      let diff =
        this.getUTCTimestamp(
          this.package_info.auto_publish_time.published_time
        ) - currentServerTimestamp
      if (diff > 0 && diff < this.MAX_AUTO_PUBLISH_TIME) {
        await this.$modal.info({
          title: this.$t('auto_publish_unpublish_tips_3')
        })
        return false
      }
      return true
    },
    async invalidAutoPublish() {
      let autoPublishValid = await this.publishValidator()
      if (!autoPublishValid) {
        await this.refreshPkgs()
        setTimeout(() => {
          this.refreshPage()
        })
        return true
      }
      return false
    },
    async handleSaveCopy({ source }) {
      if (await this.invalidAutoPublish()) {
        return
      }
      let { target } = await ajax.post(ADMIN_API.act.copy_package, {
        data: {
          source,
          activity_id: +this.activity_id,
          edit_language: getEditLang(),
          target: !this.package_id ? [] : [+this.package_id]
        }
      })

      this.$router.replace({
        path: `/act/package/info/${this.activity_id}`,
        query: {
          ...this.$route.query,
          package_id: target[0]
        }
      })
      await this.refreshPkgs()
      setTimeout(() => {
        this.refreshPage()
      })
    },
    validLevy(rule, value, callback) {
      if (this.form.e_levy === 8) {
        callback(new Error('Please fill this form'))
      } else {
        callback()
      }
    },
    validMerchantContact(rule, value, callback) {
      if (!_.isArray(value)) {
        this.formValid = false
      } else {
        this.formValid = value.every((item, index) => {
          if (value.length === 1 && index === 0) {
            // 对于第一个对象：
            // true: 都填 或 都不填 或 只填了media
            // false: 只填了number
            return !(_.isEmpty(item.media) && !_.isEmpty(item.number))
          } else {
            // 对于其它对象：
            // true: 都填
            // false: 只填了某一个 或 都不填
            return !(_.isEmpty(item.media) || _.isEmpty(item.number))
          }
        })
        if (this.formValid) {
          callback()
        } else {
          callback(new Error('Please input contact'))
        }
      }
    },
    async initObConfig() {
      let reqData = await this.getPkgInfo2actions({
        activity_id: this.activity_id,
        refresh: true,
        custom_language: klook.getEditLang(),
        only_audit_package: true
      })
      klook.bus.$emit('updatePkgInfos2bus', reqData)
      this.reqData = reqData
      let result = reqData.basic_attr_config
      this.schemaConfig = {
        ...result.reduce((acc, curr) => {
          let validator
          switch (curr.attr_id) {
            case 'pkg_contact': {
              validator = this.validMerchantContact
              break
            }
            case 'does_it_include': {
              validator = this.validLevy
              break
            }
            default:
              validator = this.validatorDefault
          }
          return {
            ...acc,
            [curr.attr_id]: {
              ...curr,
              config: (curr.config && JSON.parse(curr.config)) || {},
              ...(curr.attr_id === 'pkg_name' // 只有套餐名才是多语言组件
                ? {
                    required: curr.validation === 1,
                    requiredEn: true
                  }
                : {
                    // 其余的需要有对应的 rules 去判断
                    rules: {
                      trigger: 'blur',
                      required: curr.validation === 1,
                      validator
                    }
                  })
            }
          }
        }, {})
      }

      // OB 设置的默认值处理 OB => Form field
      Object.entries({
        merchant_confirm: 'merchant_self_confirm',
        confirmation_time: 'confirmation_type',
        // inventory_type: ['stock_out_type'], //不读OBT
        voucher_type: 'voucher_type',
        cancellation_policy: 'cancelation_type',
        voucher_usage: 'voucher_usage',
        voucher_code_level: 'voucher_level',
        voucher_get_method: 'voucher_get_method',
        calendar_month: 'calendar_month'
      }).forEach((item) => {
        this.setDefaultChoices(item[0], item[1])
      })
    },
    setDefaultChoices(fromField, toField) {
      let default_choices = _.get(
        this.schemaConfig,
        `${fromField}.config.default_choices`,
        null
      )
      if (default_choices !== null) {
        if (!Array.isArray(toField)) {
          toField = [toField]
        }
        toField.forEach((item) => _.set(this.form, item, default_choices))
      }
    },
    displayFactory(field) {
      return (
        (this.schemaConfig[field] &&
          this.schemaConfig[field].validation !== 3) ||
        false
      )
    },
    fmtPostData() {
      let data = _.cloneDeep(this.form)
      data.sensitive_info = data.sensitive_info.join()
      // 过滤掉默认的套餐联系人的数据
      // true: 只填media 或 都不填 （前提是只有一个对象）
      if (
        data.merchant_contact.length === 1 &&
        ((data.merchant_contact[0].media &&
          _.isEmpty(data.merchant_contact[0].number)) ||
          (_.isEmpty(data.merchant_contact[0].media) &&
            _.isEmpty(data.merchant_contact[0].number)))
      ) {
        data.merchant_contact.splice(0)
      }
      data.ticket_type = data.ticket_type === '' ? -1 : data.ticket_type
      return {
        ...this.currentPkgData,
        ...data,
        activity_id: +this.activity_id,
        package_id: this.package_id,
        type: this.$root.isFNB ? +this.form.type : +this.package_type
      }
    },
    async updatePackage(modify_id) {
      let published_time = _.get(
        this.form,
        'auto_publish_time.published_time',
        ''
      )
      if (
        this.form.published &&
        this.form.auto_published &&
        published_time &&
        this.getUTCTimestamp(published_time) - this.getCurrentTimestamp() > 0
      ) {
        this.$modal.info({
          title:
            'Can not set auto-publish for an already published package, please unpublish current package first.'
        })
        klook.abort()
      }

      let data = this.fmtPostData()
      let resp = await ajax.postBody(
        ADMIN_API.act.save_package_basic_info,
        {
          data: {
            ...data,
            modify_id,
            language: getEditLang()
          }
        },
        {
          loading: true,
          msgOpt: {
            isErrMsg: false
          }
        }
      )

      if (!resp.success) {
        await this.refreshPkgs()
        this.routerInfo()
        this.$nextTick(() => {
          this.refreshPage()
        })
        klook.abort()
        return false
      }

      // 更新 store
      this.setPackages(
        this.getPackages().map((item) => {
          if (item.package_id === data.package_id) {
            return {
              ...item,
              ...data
            }
          }
          return item
        })
      )
      return true
    },
    async addPackage(data = null) {
      data = data ? data : this.fmtPostData()
      let result = await ajax.post(
        ADMIN_API.act.create_package,
        {
          data
        },
        {
          msgOpt: {
            isModal: true
          }
        }
      )
      if (result && result.package_id) {
        this.new_package_id = +result.package_id
        this.cacheForm = _.cloneDeep(this.form)
        return true
      } else {
        return false
      }
    },
    routerInfo() {
      let package_type =
        (this.currentPkgData && this.currentPkgData.type) ||
        this.fmtPostData().type ||
        ''
      let query = {
        ...this.$route.query,
        package_id: this.package_id,
        package_type
      }
      if (!_.isEqual(query, this.$route.query)) {
        // 旧数据存在需要重新选 package_type 的情况
        this.$router.replace({
          path: `/act/package/info/${this.activity_id}`,
          query
        })
      }
    },
    async getValidateRes() {
      let validateRes = await Promise.all([
        (async () => {
          return this.$refs.common_fields1
            ? this.$refs.common_fields1.validate(true)
            : true
        })(),
        (async () => {
          return this.$refs.package_marchant
            ? this.$refs.package_marchant.validateForm(true)
            : true
        })(),
        (async () => {
          return this.$refs.common_fields2
            ? this.$refs.common_fields2.validate(true)
            : true
        })(),
        (async () => {
          return this.$refs.common_fields3
            ? this.$refs.common_fields3.validate(true)
            : true
        })(),
        (async () => {
          return this.$refs.package_info
            ? this.$refs.package_info.validateForm(true)
            : true
        })()
      ])
      return validateRes
    },
    async handleSave(type = 'save') {
      if (_.isEqual(this.cacheForm, this.form)) {
        this.$message.success(this.$t('global_success'))
        return
      }
      let modify_id =
        new Date().valueOf() + '' + parseInt(Math.random() * 10000)

      if (await this.invalidAutoPublish()) {
        return
      }

      let validateRes = await this.getValidateRes()
      if (validateRes.every((item) => item)) {
        this.rootChangeLoading(true)
        if (this.isCreate) {
          let isTrue = await this.addPackage()
          if (!isTrue) {
            this.rootChangeLoading(false)
            return
          }
          klook.bus.$emit('initQueryData2bus') // 更新活动状态
        }
        if (!this.isCreate && !(await this.updatePackage(modify_id))) {
          return false
        }
        this.updatePkgStepStatus2action({
          activity_id: this.activity_id,
          package_id: this.new_package_id,
          language: getEditLang(),
          status: 1,
          step: 'package_basic_info'
        })
        klook.bus.$emit('updatePkgInfos2bus', null, this.new_package_id)
        await this.refreshPkgs()
        this.routerInfo()
        await ajax.get(ADMIN_API.act.modify_completed, {
          params: {
            snapshot_modify_id: modify_id
          }
        })
        this.rootChangeLoading(false)
        this.$message.success(this.$t('global_success'))
        // setTimeout(() => {
        //   this.new_package_id &&
        //     this.$router.push({
        //       path: `/act/package/detail/${this.activity_id}`,
        //       query: {
        //         ...this.$route.query,
        //         package_id: this.new_package_id
        //       }
        //     })
        // }, 0)
      } else {
        this.$message.error('Please fill in all required fields')
        // this.activeNames = validateRes.reduce(
        //   (acc, curr, index) =>
        //     curr
        //       ? acc
        //       : [...acc, ['package_type', 'merchant', 'package_info'][index]],
        //   []
        // )
        this.$nextTick(() => {
          scrollElError()
        })
        return
      }
    },
    reviewEventLogs() {
      window.open(
        `/${window.KLK_LANG_PATH}act/activity/event_logs/${this.activity_id}?package_id=${this.package_id}&edit_flag=1,2,3`,
        '_blank'
      )
    }
  }
}
</script>

