<template>
  <div id="package-detail-editable" class="package-detail">
    <DetailGroups
      ref="detailGroups"
      class="package-detail-box"
      extra-type="PKG"
      :copyGroupList="true"
      :activity_id="activity_id"
      :package_id="package_id"
      :readonly="readonly"
      :disable-copy-pkg="disableCopyPkg"
      :key="package_id"
      @copyGroupList="handleCopy"
    />

    <a-modal
      width="720px"
      class="copy-groups-modal"
      :title="$t('act_copy_groups')"
      :visible.sync="visible"
      :maskClosable="false"
      @cancel="handleCancel"
    >
      <a-form-model
        ref="form"
        :model="form"
        :key="visible"
        layout="horizontal"
        :labelCol="{ span: 7 }"
        :wrapperCol="{ span: 17 }"
        :colon="false"
      >
        <a-form-model-item
          prop="type"
          :label="$t('act_copy_direction')"
          :rules="{
            required: true,
            validator: validatorArr
          }"
        >
          <a-radio-group v-model="form.type">
            <a-radio value="to">{{ $t('act_copy_to_other_pkg') }}</a-radio>
            <a-radio value="from">{{
              $t('act_copy_from_another_pkg')
            }}</a-radio>
          </a-radio-group>
        </a-form-model-item>

        <a-form-model-item
          prop="to_package_id_list"
          :label="$t('act_select_to_from')"
          :rules="{
            required: true,
            validator: validatorArr
          }"
        >
          <a-select
            v-model="form.to_package_id_list"
            style="width: 400px"
            show-search
            :mode="form.type === 'to' ? 'multiple' : 'default'"
          >
            <a-select-option
              v-for="pkg in packageListOpts"
              :key="pkg.value"
              :value="pkg.value"
            >
              {{
                [-1, 0].includes(pkg.value)
                  ? pkg.label
                  : pkg.value + '-' + pkg.label
              }}
            </a-select-option>
          </a-select>
        </a-form-model-item>

        <a-form-model-item
          prop="group_list"
          :label="$t('act_groups_to_copy')"
          :rules="{
            required: true,
            validator: validatorArr
          }"
        >
          <a-select
            v-model="form.group_list"
            style="width: 400px"
            show-search
            mode="multiple"
          >
            <a-select-option
              v-for="group in groupListOpts"
              :key="`${form.type}-${group.id}`"
              :value="group.id"
            >
              {{
                `${[-1, 0].includes(group.id) ? '' : group.id + '-'}${
                  group.name
                }`
              }}
            </a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>

      <div slot="footer">
        <a-button @click="visible = false"> {{ $t('global_cancel') }}</a-button>
        <a-button type="primary" @click="submitCopyHandle">{{
          $t('global_submit')
        }}</a-button>
      </div>
    </a-modal>
  </div>
</template>

<script>
import DetailGroups from '../../activityManagement/detail/detail_groups'
import { mapGetters, mapActions } from 'vuex'
import AFormModelItem from 'ant-design-vue/es/form-model/FormItem'

export default {
  inject: ['refreshPkgs', 'refreshPage'],
  data() {
    return {
      readonly: false,
      visible: false,
      form: {
        type: 'to',
        to_package_id_list: [],
        group_list: []
      },
      groupList: [],
      activity_status: 0
    }
  },
  watch: {
    'form.group_list': {
      deep: true,
      immediate: true,
      handler(v) {
        let group_list = (this.groupList || []).reduce(
          (acc, curr) => (-1 === curr.id ? acc : [...acc, curr.id]),
          []
        )
        if (v.some((item) => -1 === item)) {
          // includes All
          this.$set(this.form, 'group_list', group_list)
        }
        if (v.some((item) => 0 === item)) {
          this.$set(this.form, 'group_list', [])
        }
      }
    },
    'form.to_package_id_list': {
      deep: true,
      immediate: true,
      handler(v) {
        let to_package_id_list = _.flattenDepth([v])

        if (to_package_id_list.some((item) => -1 === item)) {
          // includes All
          this.$set(
            this.form,
            'to_package_id_list',
            this.packageList.map((item) => item.value)
          )
        }
        if (to_package_id_list.some((item) => 0 === item)) {
          this.$set(this.form, 'to_package_id_list', [])
        }
      }
    },
    'form.type': {
      immediate: true,
      handler(v) {
        if (v === 'to') {
          this.$set(this.form, 'to_package_id_list', [])
        } else {
          this.$set(this.form, 'to_package_id_list', '')
        }
      }
    }
  },
  mounted() {
    this.$root.setMutationObserver('#package-detail-editable', this)
  },
  computed: {
    disableCopyPkg() {
      return false && [0, 1].includes(this.activity_status)
    },
    activity_id() {
      return +this.$route.params.id
    },
    package_id() {
      return +this.$route.query.package_id
    },
    packageList() {
      return (this.getPkgInfos2getters().package_info || [])
        .filter((item) => item.package_id !== +this.package_id)
        .map((pkg) => ({
          value: pkg.package_id,
          label: this.getName(pkg)
        }))
    },
    packageListOpts() {
      if (this.form.type === 'to') {
        return [
          this.packageList.length !== this.form.to_package_id_list.length
            ? {
                value: -1,
                label: 'All'
              }
            : {
                value: 0,
                label: 'Clear All'
              },
          ...this.packageList
        ]
      } else {
        return this.packageList
      }
    },
    groupListOpts() {
      return [
        this.groupList.length !== this.form.group_list.length
          ? {
              id: -1,
              name: 'All'
            }
          : {
              id: 0,
              name: 'Clear All'
            },
        ...this.groupList
      ]
    }
  },
  components: {
    AFormModelItem,
    DetailGroups
  },
  methods: {
    ...mapGetters(['getPkgInfos2getters']),
    ...mapActions(['updatePkgStepStatus2action']),
    handleCopy({ list }) {
      this.groupList = list || []
      this.$set(this, 'form', this.$options.data().form)
      this.visible = true
    },
    validatorArr(rule, value, cb) {
      value = this.form[rule.field]
      if (value === '' || (Array.isArray(value) && !value.length)) {
        cb(new Error('Please select'))
      } else {
        cb()
      }
    },
    getName(data) {
      let current = _.find(data.package_local || [], {
        language_type: lang_conf.getLangObj('F_LANG', 'B_LANG')[KLK_LANG]
      })

      return (
        (current && current.name) || _.get(data, 'package_local[0].name', '')
      )
    },
    submitCopyHandle() {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          let data = {
            group_list: this.form.group_list,
            ...(this.form.type === 'to'
              ? {
                  to_package_id_list: this.form.to_package_id_list,
                  from_package_id: this.package_id
                }
              : {
                  to_package_id_list: [this.package_id],
                  from_package_id: this.form.to_package_id_list
                })
          }
          await ajax.postBody(ADMIN_API.act.copy_package_instance_data, {
            data
          })

          data.to_package_id_list.forEach((package_id) => {
            ;(async (package_id) => {
              await this.updatePkgStepStatus2action({
                status: 1,
                activity_id: this.activity_id,
                language: new URLSearchParams(location.search).get('lang'),
                step: 'package_detail_info',
                package_id: package_id
              })

              klook.bus.$emit('updatePkgInfos2bus', null, package_id)
            })(package_id)
          })

          if (this.form.type === 'form') {
            await this.refreshPkgs()
            await this.refreshPage()
          }

          this.visible = false
          this.$message.success(this.$t('global_success'))
          this.$set(this, 'form', this.$options.data().form)
        }
      })
    },
    handleCancel() {
      this.visible = false
    }
  }
}
</script>

<style lang="scss">
.copy-groups-modal .ant-form-item-label {
  label {
    white-space: pre-wrap;
    line-height: 1.5em;
    display: inline-block;
  }
}
</style>
