<template>
  <a-form-model ref="form" :model="form" label-width="0">
    <a-form-model-item class="duration__item" prop="single" :rules="rules">
      <a-radio-group v-model="form.single">
        <a-radio :value="true" :disabled="readonly">{{
          $t('global_fixed_time')
        }}</a-radio>
        <a-radio :value="false" :disabled="readonly">{{
          $t('act_enter_duration_range')
        }}</a-radio>
      </a-radio-group>
    </a-form-model-item>

    <template v-if="form.single !== ''">
      <a-form-model-item
        v-if="form.single"
        class="duration__item"
        prop="from"
        :rules="rules"
      >
        <duration v-model="form.from" :readonly="readonly" />
      </a-form-model-item>

      <template v-else>
        <a-form-model-item prop="from" class="duration__item" :rules="rules">
          <label class="duration__type">{{ $t('act_from') }}</label>
          <duration
            v-model="form.from"
            class="duration__body"
            :readonly="readonly"
          />
        </a-form-model-item>
        <a-form-model-item prop="to" class="duration__item" :rules="rules">
          <label class="duration__type">{{ $t('act_to') }}</label>
          <duration
            v-model="form.to"
            class="duration__body"
            :readonly="readonly"
          />
        </a-form-model-item>
      </template>
    </template>
  </a-form-model>
</template>

<script>
import duration from './tpl_duration.vue'

export default {
  name: 'DurationWidget',
  components: {
    duration
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: Object,
      required: true
    },
    required: {
      type: Boolean,
      default: false
    },
    config: {
      type: Object,
      default: () => ({})
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      form: {
        single: true,
        from: 0,
        to: 0
      }
    }
  },
  computed: {
    rules() {
      return {
        required: this.required,
        message: 'Please fill in this field.'
      }
    }
  },
  watch: {
    'form.single': {
      immediate: true,
      handler(v) {
        if (v) {
          this.form.to = 0
        }
      }
    },
    form: {
      deep: true,
      handler(v) {
        let data
        if (v.single === '') {
          // init data
          data = {}
        } else if (v.single) {
          data = {
            single: true,
            from: v.from,
            to: 0
          }
        } else {
          data = {
            single: false,
            from: v.from,
            to: v.to
          }
        }

        this.$emit('change', {
          ...this.value,
          value: JSON.stringify(data)
        })
      }
    },
    value: {
      immediate: true,
      deep: true,
      handler() {
        let value = this.value.value || {}
        let initData =
          typeof value === 'string'
            ? JSON.parse(value)
            : toString.call(value) === '[object Object]'
            ? value
            : {}
        Object.assign(this.form, {
          from: 0,
          to: 0,
          // single: initData.hasOwnProperty('from')
          single: Object.prototype.hasOwnProperty.call(initData, 'from')
            ? initData.to === initData.from
            : '',
          ...initData
        })
      }
    }
  },
  methods: {
    validateForm() {
      return new Promise((resolve) =>
        this.$refs.form.validate((valid) => resolve(valid))
      )
    }
  }
}
</script>

