$marginSize: 12px;

.duration_widget__container {
  min-width: 520px;
}

.duration__item {
  margin-bottom: 12px;
  &:last-child {
    margin-bottom: 0;
  }
  ::v-deep .ant-form-item-children {
    display: flex;
  }
}

.duration__type {
  display: inline-block;
  width: 50px;
  margin-right: $marginSize;
}
</style>
